.topTasks {
  $breakpoint: "sm";
  z-index: 9;
  transform: translate3d(0, 0, 0);
  pointer-events: none;
  position: relative;

  [aria-hidden="true"] {
    display: none !important;
  }

  .topTasks-back {
    width: 150vw;
    height: 150vh;
    position: fixed;
    display: block;
    background-color: rgba(0, 0, 0, 0.7);
    top: -50vh;
    left: -50vw;
    z-index: -1;
    opacity: 0;
    transition: opacity $transition-duration;
    pointer-events: none;
  }

  .topTasks-trigger {
    pointer-events: all;
    cursor: pointer;
    position: absolute;
    width: 70px;
    height: 70px;
    line-height: 70px;
    text-align: center;
    background-color: $color-red;
    color: $color-white;
    z-index: 10;
    border-radius: 50%;
    right: 18px;
    top: 10px;
    transform: translate3d(0, 0, 0);
    opacity: 0;
    overflow: hidden;
    transition: opacity $transition-duration * 2 background-color
      $transition-duration/2;

    @include media-breakpoint-up($breakpoint) {
      width: 70px;
      height: 70px;
    }
  }

  .topTasks-trigger-icon {
    position: absolute;
    @include absoluteCenter;
    transition: opacity $transition-duration,
      transform $transition-duration ease-in-out;
  }

  .topTasks-trigger-svg {
    svg {
      transform: rotate(-90deg);
      margin-top: -3px;
      fill: #fff;
    }

    svg,
    img {
      width: 22px;
      vertical-align: middle;
    }
  }

  .topTasks-trigger-icon-open {
    opacity: 1;

    @include media-breakpoint-up($breakpoint) {
      font-size: 1.8em;
    }
  }

  .topTasks-trigger-icon-close {
    opacity: 0;
  }

  .topTasks-col {
    transition: transform $transition-duration ease-in-out;
  }

  .topTasks-inner {
  }

  .topTask-expander {
    position: absolute;
    z-index: 1;
    bottom: -22px;
    left: 50%;
    transform: translateX(-50%);
    width: 50px;
    height: 50px;
    background: white;
    border-radius: 100%;
    box-shadow: 0 0 5px rgba(black, 0.2);
    pointer-events: auto;

    svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%) scale(1);
      width: 22px;
      fill: $navigationTextColor-desktop;
    }

    &.expanded {
      svg {
        transform: translateX(-50%) translateY(-50%) scale(-1);
      }
    }
  }

  .topTasks-list {
    transition: transform 0ms ease-in-out, opacity 0ms linear;
    transform-origin: top right;
    background-color: $color-red;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    @include list-inline;
    position: relative;
    opacity: 1;
    padding: 20px 0;
    pointer-events: all;

    @include media-breakpoint-up($breakpoint) {
      padding: 0;
      flex-direction: row;
    }

    .topTasks-task {
      margin-right: 0;
      position: relative;
      display: flex;
      flex: 1 0 1;
      flex-direction: row;
      align-items: center;
      @include flex-1;
      justify-content: flex-start;
      color: $color-white;
      padding: 10px 30px;
      min-width: 100%;

      @include media-breakpoint-up($breakpoint) {
        flex-wrap: wrap;
        flex: 1 0 33%;
        text-align: center;
        padding: 10px 20px 35px;
        flex-direction: column;
        min-width: 33.33333%;
      }

      @include media-breakpoint-up(lg) {
        flex-wrap: nowrap;
        padding: 25px 30px 35px;
        max-width: 16.66666667%;
        min-width: 0;
        flex: 1 0 16.66666667%;
      }

      &.topTasks-task-emphasize {
        .topTasks-task-icon {
          color: $color-yellow;
        }
      }

      &.is-selected {
        .topTasks-task-link {
          background-color: rgba(0, 0, 0, 0.1);
        }
      }
    }

    .topTasks-task-icon {
      font-size: 0;
      padding-right: 20px;
      min-width: 60px;
      text-align: center;

      @include media-breakpoint-up($breakpoint) {
        padding-right: 0;
        padding-bottom: 0;
        min-width: 50px;
      }

      img {
        width: 45px;
        height: auto;
        padding-right: 0;
        padding-bottom: 0;
        font-size: rem(45px);

        @include media-breakpoint-up($breakpoint) {
          width: auto;
          height: 50px;
        }
      }
    }

    .topTask-task-text {
      max-width: 100%;
      display: block;
      font-family: $font-family-bold;

      @include btnTypography;

      @include media-breakpoint-up($breakpoint) {
        font-size: 1.1rem !important;

        .site-theme-radius & {
          font-size: 1.1875rem !important;
        }
      }
    }

    .topTasks-task-link {
      position: absolute;
      @include absoluteExpand;
      transition: background-color $transition-duration linear;

      @include media-breakpoint-up($breakpoint) {
        &:hover {
          background-color: rgba(0, 0, 0, 0.1);
        }
      }
    }
  }

  &.topTasks-fixed,
  &.topTasks-fixed-always {
    position: fixed;
    width: 100%;
    transition: left $transition-duration ease-in-out;

    .topTasks-trigger {
      opacity: 1;
      transform: translate3d(0, 0, 0);
      animation: pulsate 2s ease-out;
      animation-iteration-count: 3;
    }

    .topTasks-list {
      opacity: 0;
      transform: translate3d(0, 0, 0) scale(0);
      pointer-events: none;
    }

    .topTask-expander {
      display: none;
    }
  }

  &.topTasks-fixed-show {
    .topTasks-list {
      opacity: 1;
      transform: translate3d(0, 0, 0) scale(1);
      pointer-events: all;
    }

    .topTasks-trigger {
      background-color: transparent;
      color: $color-white;
      animation: 0;
    }

    .topTasks-trigger-icon-open {
      opacity: 0;
    }

    .topTasks-trigger-icon-close {
      opacity: 1;
    }

    .topTasks-back {
      opacity: 1;
      pointer-events: all;
    }

    .topTask-expander {
      display: block;
    }
  }
}
.header-offsetY {
  .topTasks-fixed {
    .topTasks-col {
      transform: translate3d(0, -70px, 0);
      @include media-breakpoint-up(md) {
        transform: translate3d(0, -45px, 0);
      }
    }
  }
}
@keyframes pulsate {
  0% {
    transform: scale(0.9);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(0.9);
  }
}
