body {
    font-family: $font-family-regular;
    color: $color-text;
    font-size: 16px;
}

p {
    margin-bottom: 0;

    +p {
        margin-bottom: 1rem;
    }
}

a {
    &:focus,
    &:hover {
        text-decoration: none;
    }
}

blockquote {
    border: 0;
    margin-left: 0;
    padding: 0;
    font-size: 1em;

    p {
        @extend .blockquote;
    }

    footer {
        @extend .bodycopy;
        color: $color-aqua;

        &:before {
            content: none;
        }

        p {
            font-size:16px !important;
        }
    }
}
