.megaNavigation {
  &.megaNavigation-dk-private {
    .megaNavigation-menu-item {
      .megaNavigation-menu-item-link {
        &.megaNavigation-submenu-visible {
          .site-theme-radius & {
            background-color: $color-radius-light-blue;
            color: $color-radius-blue !important;
          }
        }
      }
    }
    .megaNavigation-submenu-item-link {
      &:hover {
        .site-theme-radius & {
          color: $color-radius-turquoise;
        }
      }
      &.megaNavigation-submenu-item-link-active {
        .site-theme-radius & {
          font-family: $font-family-bold-radius;
        }
      }
    }
    .megaNavigation-submenu-item-submenu-item-link {
      &:hover {
        .site-theme-radius & {
          color: $color-radius-turquoise;
        }
      }
      &.megaNavigation-item-active {
        .site-theme-radius & {
          font-family: $font-family-bold-radius;
          color: $color-radius-turquoise;
        }
      }
    }
  }
}
