.splash {
  display: flex;
  align-items: center;
  justify-content: center;
  @include flex-1; //this was previously outcommented?
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  overflow: hidden;
  @include media-breakpoint-down(sm) {
    &:after {
      content: "";
      height: 10px;
      position: absolute;
      bottom: 0;
      display: block;
      left: 0;
      right: 0;
    }
  }

  .splash-inner {
    position: relative;
    max-width: 400px;
    margin: 0 auto;
    width: 100%;
    padding-bottom: 100%;
    border-radius: 50%;
    display: table;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .splash-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 75%;
    overflow: hidden;
    z-index: 1;
    text-align: center;

    .link-arrow {
      margin-top: rem(10px);
      margin-left: 0px;
      padding-right: 0px;
    }

    i {
      font-size: 7rem;
    }

    h4 {
    }
  }

  .splash-link {
    border-radius: 50%;
    @include absolute-expand;
    z-index: 2;
  }

  .splash-cta {
  }
}

.row {
  &.row-narrow {
    @include media-breakpoint-up(md) {
      .splash {
        padding-left: 20px;
        padding-right: 20px;
      }
    }
  }
  &.row-fullwidth {
    .splash {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  &.row-widewidth {
    .splash {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}
