.splash {
  .splash-content {
    .site-theme-radius & {
      font-size: $text-size-splash-radius;
      line-height: $line-height-splash-radius;
    }
    h4 {
      .site-theme-radius & {
        font-size: $text-size-splash-radius;
        line-height: $line-height-splash-radius;
      }
    }
  }
  .splash-cta {
    .site-theme-radius & {
      font-size: $text-size-xsmall-radius;
      line-height: $line-height-xsmall-radius;
    }
  }
}
