.customerDetails {
  display: flex;

  .customerDetails-left {
    flex: auto;
    padding-right: $module-space-horisontal-left;
  }

  .customerDetails-right {
    flex: auto;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  .customerDetails-choices {
    display: flex;
    justify-content: flex-end;
  }

  .customerDetails-choice {
    margin-left: 30px;
    display: flex;
    flex: auto;
  }

  .customerDetails-choice-text {
    display: inline-block;
  }

  .customerDetails-choice-icon {
    display: inline-block;
    margin-left: 10px;
    i {
      color: $color-red;
    }
  }
}
.row {
  &.row-narrow {
    @include media-breakpoint-up(md) {
      .customerDetails {
        padding: $module-space-vertical-bottom $module-space-horisontal-left
          $module-space-vertical-bottom $module-space-horisontal-right;
      }
    }
    @include media-breakpoint-down(sm) {
      .customerDetails {
        padding: $module-space-vertical-bottom $module-no-space
          $module-space-vertical-bottom $module-no-space;
      }
    }
  }
  &.row-fullwidth {
    .customerDetails {
      padding: $module-space-vertical-bottom $module-space-horisontal-left
        $module-space-vertical-bottom $module-space-horisontal-right;
    }
  }
  &.row-widewidth {
    .customerDetails {
      padding: $module-space-vertical-bottom $module-space-horisontal-left
        $module-space-vertical-bottom $module-space-horisontal-right;
    }
  }
}
