.heroSlider {
  overflow: hidden;
  position: relative;
  display: flex;
  @include flex-1;
  flex-direction: column;

  .heroSlider-media {
    width: 100%;
    min-height: calc(56.25vw - 20px); // 16:9 format
    line-height: 0;
    display: block;
    overflow: hidden;
    position: relative;
    > * {
      width: 100%;
    }

    @include media-breakpoint-up(md) {
      max-height: 80vh;
    }
  }
  .heroSlider-img {
    z-index: -1;
    @include absolute-expand;
    @include background-image;
  }
  .heroSlider-headline {
    @include header-adaptive;
    word-wrap: break-word;
  }
  .heroSlider-body {
    //flex-grow: 1;
    margin-top: 20px;
  }
  .heroSlider-cta {
    margin-top: 30px;
  }
  .heroSlider-close {
    position: absolute;
    top: 0;
    right: 10;
    z-index: 1;
    cursor: default;
    display: none;
  }

  .heroSlider-content {
    position: relative;
    padding: 50px 50px 30px 30px;
    transform: translate3d(0, 0, 0);
    justify-content: flex-end;
    z-index: 1;
  }
  // @media (min-width:1465px) {
  //     height: calc(1430px * 0.5625);
  // }
  @include media-breakpoint-up(md) {
    justify-content: flex-end;
    flex-direction: row;
    align-items: center;

    height: calc(56.25vw - 20px);
    max-height: calc(
      80vh - 100px
    ); // make sure that the top image does not go over fold, the 100px is the menu

    /*.heroSlider-content{ 
             min-height: 500px; 
         }*/

    .heroSlider-media {
      height: 100%;
      min-height: 0;
      position: absolute;
      right: 0;
      top: 0;
    }
    &.heroSlider-media-video {
      max-height: none;
      height: auto;
      .heroSlider-media {
        max-height: none;
        position: static;
      }
      .heroSlider-video {
        min-height: 100%;
        width: 100%;
      }
      .heroSlider-content {
        position: absolute;
        right: 0;
        top: 0;
        justify-content: flex-end;
      }
    }
    .heroSlider-img {
      position: absolute;
      right: 0;
      top: 0;
      @include absolute-expand;
    }
    .heroSlider-content {
      display: flex;
      flex-direction: column;
      height: 100%;
      width: 50%;
      transition: transform $transition-duration * 3 $easing-snap,
        opacity $transition-duration * 3 $easing-snap,
        width $transition-duration $easing-smooth;
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
    &.heroSlider-content-hidden {
      .heroSlider-content {
        opacity: 0;
        transform: translate3d(100%, 0, 0);
      }
      .heroSlider-close {
        opacity: 0;
      }
    }
    .heroSlider-close {
      padding: 10px;
      display: none;
      opacity: 1;
      cursor: pointer;
      transition: opacity $transition-duration ease-in-out,
        transform $transition-duration ease-in-out;
      &:hover {
        transform: scale(1.5);
        ~ .heroSlider-content {
          transform: translate3d(20px, 0, 0) !important;
        }
      }
    }
    &:hover {
    }
  }
}

