  ),
  "radius-yellow": (
    "background-color": $color-radius-yellow,
    "text-color": $color-radius-blue
  )
);

@mixin btn {
  position: relative;
  display: inline-block;
  font-weight: $btn-font-weight;
  line-height: $btn-line-height;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  text-decoration: none;
  border: $input-btn-border-width solid transparent;
  margin: 3px;
  overflow: hidden;  
  @include button-size($btn-padding-y, $btn-padding-x, $font-size-base, $btn-border-radius);
  @include transition($btn-transition);

  // Share hover and focus styles
  @include hover-focus {
    text-decoration: none;
  }

  &:focus,
  &.focus {
    outline: 0;
    @include box-shadow($btn-focus-box-shadow);
  }

  // Disabled comes first so active can properly restyle
  &.disabled,
  &:disabled {
    cursor: $cursor-disabled;
    opacity: .65;
    @include box-shadow(none);
  }

  &:active,
  &.active {
    background-image: none;
    @include box-shadow($btn-focus-box-shadow);
  }
}

@mixin btnTheme($backgroundColor, $textColor) {
  @media not print {
    background-color: $backgroundColor;
    color: $textColor;

    &:hover {
      color: $textColor;
    }

    &:focus {
      color: $textColor;
    }
  }
}

@mixin btnTypography {
  font-size: $site-btn-font-size;
  line-height: $site-btn-line-height;
  font-family: $font-family-bold;
  font-weight: bold;
  letter-spacing: 0.2px;
}

@mixin btnCta {
  display: inline-flex;
  flex-direction: row-reverse;
  align-items: baseline;
  justify-content: flex-end;

  @include icon("arrow-right");

  &:before {
    display: inline-block;
    max-width: 0;
    margin-left: 0px;
    font-size: rem(10px);
    transform: translate3d(-3px, 0, 0);
    opacity: 0;
    transition: transform $transition-duration $easing-smooth,
      opacity $transition-duration $easing-smooth,
      margin-left $transition-duration $easing-smooth;
  }

  &:hover {
    padding-left: 23px;
    padding-right: 30px;

    &:before {
      margin-left: 10px;
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }
}

@mixin btnDropdown {
  $caretWidth: 50px;
  padding-right: rem($caretWidth) + $btn-padding-x;
  display: inline-flex;
  flex-direction: row-reverse;
  align-items: baseline;
  justify-content: flex-end;
  overflow: hidden;

  @include icon("arrow-down");

  &:before {
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    text-align: center;
    font-size: 0.7em;
    position: absolute;
    width: $caretWidth;
    right: 0;
    top: 0;
    height: 44px;
    line-height: 44px;
  }
}
