.exploration {
  overflow: visible;

  [class*=" col-"],
  [class^="col-"] {
    margin: 0;
    padding: 0;

    > * {
      margin-bottom: 0;
    }
  }

  $max-height: 615px;

  @include media-breakpoint-up(md) {
    height: $max-height;
  }

  position: relative;
  color: $color-text;

  .exploration-header {
    cursor: pointer;
  }

  .exploration-base-view {
    display: block;
    flex-direction: column;
    overflow: hidden;

    @include media-breakpoint-up(md) {
      display: flex;
      flex-direction: row;
    }

    .exploration-categories {
      list-style-type: none;
      padding: 0;
      margin-top: 40px;
      display: none;
    }

    .exploration-categories-mobile {
      display: block;
    }

    @include media-breakpoint-up(md) {
      .exploration-categories {
        display: block;
      }

      .exploration-categories-mobile {
        display: none;
      }
    }

    .landing-left {
      display: flex;
      flex: 1;

      @include media-breakpoint-up(md) {
        min-height: 0;
        min-width: 350px;
        height: $max-height;
        max-width: 35%;
      }

      flex-basis: 0%;
      overflow: auto;
      -webkit-overflow-scrolling: touch;
      padding: $module-space-vertical-bottom $module-space-horisontal-left
        $module-space-vertical-bottom $module-space-horisontal-right;
    }

    .landing-right {
      display: flex;
      @include flex-1;
      flex-direction: row;
      align-items: flex-start;
      max-width: 100%;
      overflow-y: hidden;
      overflow: auto;
      -ms-overflow-style: none;

      @include media-breakpoint-up(md) {
        min-height: 0;
        @include flex-2;
        height: $max-height;
        overflow: hidden;
      }
    }

    .exploration-scroll {
      flex: auto;
      flex-direction: row;
      justify-content: space-between;
      overflow: hidden;
      max-height: 0;

      @include media-breakpoint-down(sm) {
        max-width: 100% !important;
      }

      @include media-breakpoint-up(md) {
        margin-left: -1px;
        max-width: 0;
        max-height: none;
        flex: 0 0 45px;
        flex-direction: column;
      }

      &.theme-white {
        .exploration-scroll-dot {
          background-color: rgba($color-blue, 0.3);
        }

        .exploration-scroll-dot-active {
          background-color: $color-blue;
        }

        .exploration-scroll-down,
        .exploration-scroll-up {
          color: $color-blue;
        }
      }

      &.theme-backdrop-2 {
        .exploration-scroll-dot {
          background-color: rgba($color-aubergine, 0.3);
        }

        .exploration-scroll-dot-active {
          background-color: $color-aubergine;
        }

        .exploration-scroll-down,
        .exploration-scroll-up {
          color: $color-aubergine;
        }
      }
    }

    .exploration-scroll-indicator {
      align-items: center;
      display: flex;

      @include media-breakpoint-up(md) {
        display: none;
      }
    }

    .exploration-scroll-dots {
      display: flex;
      flex-direction: column;
      align-items: center;

      @include media-breakpoint-down(sm) {
        flex-direction: row;
        display: none;
      }
    }

    .exploration-scroll-dot {
      cursor: pointer;
      width: 10px;
      height: 10px;
      margin-bottom: 10px;
      background-color: rgba($color-white, 0.3);
      border-radius: 50%;

      @include media-breakpoint-down(sm) {
        margin-bottom: 0;
        margin-left: 10px;
      }
    }

    .exploration-scroll-dot-active {
      background-color: $color-white;
    }

    .exploration-scroll-down,
    .exploration-scroll-up {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 45px;
      height: 45px;
      color: $color-white;
      cursor: pointer;
    }

    .exploration-scroll-arrow-inactive {
      opacity: 0.2;
      cursor: default;
    }

    .exploration-scroll-up {
      transform: rotate(180deg);

      @include media-breakpoint-up(md) {
        transform: rotate(-90deg);
      }
    }

    .exploration-scroll-down {
      @include media-breakpoint-up(md) {
        transform: rotate(90deg);
      }
    }

    .exploration-category-item {
      font-family: $font-family-regular !important;

      body#lang-zh & {
        font-family: "Noto Sans TC", sans-serif !important;
      }

      body#lang-ja & {
        font-family: "Noto Sans Japanese", sans-serif !important;
      }

      body#lang-ko & {
        font-family: "Noto Sans KR", sans-serif !important;
      }

      margin-top: 20px;
      transition: padding 100ms ease-in-out;

      .exploration-category-link {
        font-family: $font-family-regular !important;

        body#lang-zh & {
          font-family: "Noto Sans TC", sans-serif !important;
        }

        body#lang-ja & {
          font-family: "Noto Sans Japanese", sans-serif !important;
        }

        body#lang-ko & {
          font-family: "Noto Sans KR", sans-serif !important;
        }

        margin: 0;
        transition: all $transition-duration ease;
      }

      &.exploration-category-item-active {
        padding-top: 20px;
        padding-bottom: 20px;

        .exploration-category-link {
          @extend .h4-medium;
          color: inherit !important;

          &:before {
            display: none;
          }
        }
      }
    }

    $categoryItemHeight: 205px;

    .category-items {
      display: flex;
      // height:100%;
      width: 100%;
      margin: 0;
      // height: 2*$categoryItemHeight;
      @include media-breakpoint-up(md) {
        flex-flow: row wrap;
        // height: auto;
      }

      &.category-view {
        @include media-breakpoint-down(sm) {
          .category-item {
            height: calc(56vw - 20px);
            flex: 1 0 100%;
            width: 100%;
            max-width: 100%;
          }
        }
      }
    }

    .category-item {
      display: flex;
      flex-direction: column;
      // flex:1 0 50%;
      // width: 50%;
      opacity: 0;
      height: $categoryItemHeight;
      flex: 1 0 50%;
      width: 50%;
      max-width: 50%;

      @include media-breakpoint-up(lg) {
        flex: 1 0 33.33333333%;
        width: 33.333333%;
        max-width: 33.333333%;
      }

      overflow: hidden;
      position: relative;

      &.category-item-textblock {
        padding: $module-space-horisontal-left;

        .category-item-text {
          position: static;
          padding: 0;
          transform: translate3d(0, 0, 0);
        }
      }

      &:hover {
        .category-item-text {
          transform: translate3d(0, 0, 0);
        }
      }
    }

    .category-item-textblock.theme-white {
      background-color: $color-white;

      * {
        color: $color-text !important;
      }
    }

    @mixin noHover {
      .category-item-image-block {
        .category-item-text {
          transform: translate3d(0, 0, 0);
          transition: none;
          background: linear-gradient(
            to bottom,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 0.4) 100%
          ) !important;

          > * {
            color: $color-white !important;
          }

          .site-theme-radius & {
            font-family: $font-family-bold-radius !important;
          }
        }

        &:hover {
          .category-item-text {
            transform: translate3d(0, 0, 0);
          }
        }
      }
    }

    &.no-hover {
      @include noHover;
    }

    @include media-breakpoint-down(sm) {
      @include noHover;
    }

    .category-item-link {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
    }

    .category-item-text {
      position: absolute;
      bottom: 0;
      z-index: 1;
      padding: 10px;
      width: 100%;
      transform: translate3d(0, 100%, 0);
      transition: transform $transition-duration ease-in-out;
    }

    .category-item-image {
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100% + 1px);
      height: 100%;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;

      @include media-breakpoint-up(md) {
        max-height: $max-height;
      }

      @include image-scale-up-transition;
    }
  }

  .exploration-details-view {
    position: relative;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;

    @include media-breakpoint-up(md) {
      position: absolute;
    }

    > * {
      @include media-breakpoint-up(md) {
        max-height: $max-height;
      }
    }
  }

  .exploration-details {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;

    @include media-breakpoint-up(md) {
      position: absolute;
      flex-direction: row;
      max-height: $max-height;

      .details-left {
        width: 50%;
        min-height: 0;
      }
    }

    > * {
      display: flex;
      flex-direction: column;

      @include media-breakpoint-up(md) {
        max-height: $max-height;
        flex-direction: row;
      }
    }

    .details-left {
      position: relative;
      height: $max-height;

      @include media-breakpoint-down(sm) {
        min-height: calc(50vw + 60px);
        position: static !important;
        width: 100%;
        height: auto !important;
      }
    }

    .details-image {
      @include media-breakpoint-down(sm) {
        position: static !important;
        height: auto !important;
        @include flex-1;
      }

      @include media-breakpoint-up(md) {
        position: absolute !important;
      }

      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      z-index: 0;
    }

    .details-navigation {
      display: flex;
      bottom: 0;
      width: 100%;
      z-index: 1000;
      min-height: 90px;
      @include flex-1;
      position: absolute;

      @include media-breakpoint-down(sm) {
        min-height: 45px;
      }
    }

    .details-navigation-left {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
      width: calc(100% - 80px);
      min-height: 90px;
      height: 100%;
      padding: 20px 30px;

      @include media-breakpoint-down(sm) {
        min-height: 45px;
        padding: 0;
      }

      > * {
        display: flex;
        flex-direction: row;
        @include flex-1;
        flex: 1 auto !important;
        align-items: center;
        justify-content: center;
        height: 100%;
      }

      .details-navigation-icon {
        width: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
      }

      .details-navigation-text {
        @include media-breakpoint-down(sm) {
          display: none;
        }
      }

      .details-navigation-prev {
        justify-content: flex-start;
        cursor: pointer;

        i {
          transition: transform $transition-duration/2 ease-in-out;
          transform: rotate(180deg);
        }

        .details-navigation-title {
          &:hover {
            i {
              transform: translateX(-10px) rotate(180deg);
            }
          }
        }
      }

      .details-navigation-next {
        justify-content: flex-end;
        text-align: right;

        i {
          transition: transform $transition-duration/2 ease-in-out;
        }

        .details-navigation-title {
          &:hover {
            i {
              transform: translateX(10px);
            }
          }
        }
      }
    }

    .details-navigation-title {
      margin-bottom: 0;
    }

    .details-navigation-right {
      display: flex;
      width: 80px;
    }

    .details-navigation-overview-trigger {
      display: flex;
      //flex: 1;
      @include flex-1 align-items: center;
      justify-content: center;
      text-align: center;
      line-height: 1.2;
      width: 80px;
    }

    .details-right {
      position: static !important;

      @include media-breakpoint-up(md) {
        position: absolute !important;
        width: 50%;
        height: 615px;
      }

      overflow: hidden;
      padding: 0 0 0 $module-space-horisontal-left;
      background-color: $color-white;
      color: $color-text;
    }

    .details-right-content {
      padding-top: 30px;
      padding-right: $module-space-horisontal-right;
      // padding-bottom: 100px;
      padding-bottom: 60px;

      @include media-breakpoint-up(md) {
        overflow: auto;
        max-height: 100%;
        padding-bottom: 0;
        //max-height: $max-height;
      }

      > * {
        overflow: hidden;
        max-height: 400px;

        @include media-breakpoint-up(md) {
          overflow: auto;
          max-height: 100%;
          //max-height: $max-height;
        }
      }

      @include media-breakpoint-down(sm) {
        &:after {
          content: "";
          position: absolute;
          bottom: 0;
          width: 100%;
          left: 0;
          height: 160px;
          background: -moz-linear-gradient(
            top,
            rgba(255, 255, 255, 0) 0%,
            rgba(255, 255, 255, 0) 1%,
            rgba(255, 255, 255, 1) 41%,
            rgba(255, 255, 255, 1) 52%
          ); /* FF3.6-15 */
          background: -webkit-linear-gradient(
            top,
            rgba(255, 255, 255, 0) 0%,
            rgba(255, 255, 255, 0) 1%,
            rgba(255, 255, 255, 1) 41%,
            rgba(255, 255, 255, 1) 52%
          ); /* Chrome10-25,Safari5.1-6 */
          background: linear-gradient(
            to bottom,
            rgba(255, 255, 255, 0) 0%,
            rgba(255, 255, 255, 0) 1%,
            rgba(255, 255, 255, 1) 41%,
            rgba(255, 255, 255, 1) 52%
          ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
          filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 ); /* IE6-9 */
        }
      }

      &.details-right-content-expanded {
        > * {
          max-height: none;
        }

        &:after {
          display: none;
          content: none;
        }
      }
    }

    .details-right-content-show-btns {
      &.details-right-content-expanded {
        padding-top: 60px;

        .details-right-content-show-more {
          display: none;

          @include media-breakpoint-up(md) {
            display: none !important;
          }
        }

        .details-right-content-show-less {
          display: inline-block;

          @include media-breakpoint-up(md) {
            display: none !important;
          }
        }
      }
    }

    .details-right-content-show-more {
      cursor: pointer;
      position: absolute;
      bottom: 63px;
      left: 30px;

      @include media-breakpoint-up(md) {
        display: none !important;
      }
    }

    .details-right-content-show-less {
      cursor: pointer;
      position: absolute;
      bottom: 63px;
      left: 30px;
      display: none;

      @include media-breakpoint-up(md) {
        display: none !important;
      }

      i {
        transform: rotate(-180deg);
      }
    }

    .details-right-back {
      position: absolute;
      cursor: pointer;
      top: 0;
      right: 0;
      padding: 15px;

      @include media-breakpoint-up(md) {
        top: 20px;
        right: 35px;
        background-color: transparent !important;
        color: $color-text;
      }
    }

    .theme-green {
      .details-navigation-overview-trigger {
        background-color: $color-blue;
      }
    }
  }

  @include media-breakpoint-up(md) {
    .exploration-details {
      .details-right {
        padding: $module-space-vertical-bottom 0 0 $module-space-horisontal-left;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    .details-navigation-left {
      min-height: 0;
    }
  }
}
.exploration-details
  .details-right
  .details-right-content
  .ajaxContent
  .richtext
  h3:first-child {
  @extend .theme-color-accent;
}
.exploration-details
  .details-right
  .details-right-content.details-header
  .ajaxContent
  .richtext
  h3:first-child {
  color: $color-radius-turquoise;
}
