.headerSlider-spacer {
  padding-top: $header-bottom-height-small + 10px;

  @include media-breakpoint-up($header-breakpoint) {
    padding-top: 0;
  }
}

.headerSlider {
  $background-color-mobile: $color-white;
  position: fixed;
  top: 0;
  z-index: 10;
  padding: 0;
  width: 100%;
  margin-bottom: 10px;
  @extend .container-fluid;
  border-bottom: 1px solid $color-border;
  transition: top $transition-duration $easing-snap;

  @include media-breakpoint-up($header-breakpoint) {
    border-bottom: 0;
  }
  // transform: translate3d(0,0,0);
  // top:0;
  // on scroll down, hide some of the header
  &.headerSlider-offsetY {
    top: -70px;
    // transform: translate3d(0,-100%,0);
    @include media-breakpoint-up($header-breakpoint) {
      top: -80px;
      // transform: translate3d(0,-45px,0);
    }
  }
  // // on scroll down, hide some of the header
  // transition: transform $transition-duration $easing-snap;
  // transform: translate3d(0,0,0);
  // &.headerSlider-offsetY {
  //     transform: translate3d(0,-100%,0);
  // }
  > * {
    padding: 0 5px;
  }

  .headerSlider-top {
    display: flex;
    align-items: center;
    flex-direction: row;
    width: 100%;
    margin: 0;
    position: relative;
    height: 70px;
    padding-right: 10px;
    background-color: $background-color-mobile;

    @include media-breakpoint-up($header-breakpoint) {
      background-color: transparent;
      margin: 0 auto;
    }

    @extend .row;
  }

  .headerSlider-top-inner {
    display: flex;
    flex: 1 !important;
    align-items: center;
    justify-content: space-between;

    &:first-child {
      padding-left: 5px;
    }

    &:last-child {
      padding-right: 5px;
    }
  }

  .headerSlider-navigation-trigger-desktop {
    position: relative;
    $size: 42px;
    display: none;
    float: right;
    cursor: default;
    background-color: rgba($color-dark-blue, 0.7);
    border-radius: 100%;
    width: $size;
    height: $size;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    div {
      position: absolute;
      top: 14px;
      left: 11px;
    }

    span {
      display: block;
      position: absolute;
      height: 2px;
      width: 20px;
      background: #fff;
      //border-radius: 9px;
      opacity: 1;
      left: 0;
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
      -webkit-transition: 0.25s ease-in-out;
      -moz-transition: 0.25s ease-in-out;
      -o-transition: 0.25s ease-in-out;
      transition: 0.25s ease-in-out;
    }

    span:nth-child(1) {
      top: 0px;
      -webkit-transform-origin: left center;
      -moz-transform-origin: left center;
      -o-transform-origin: left center;
      transform-origin: left center;
    }

    span:nth-child(2) {
      top: 6px;
      -webkit-transform-origin: left center;
      -moz-transform-origin: left center;
      -o-transform-origin: left center;
      transform-origin: left center;
    }

    span:nth-child(3) {
      top: 12px;
      -webkit-transform-origin: left center;
      -moz-transform-origin: left center;
      -o-transform-origin: left center;
      transform-origin: left center;
    }

    &:before {
      font-size: $header-bottom-height-small / 4.1;
      height: 0.9em;
      color: $color-white;
    }

    @include media-breakpoint-up($header-breakpoint) {
      display: inline-flex;
    }
  }

  .header-logo {
    margin-left: 0px;
    @include media-breakpoint-up(lg) {
      margin-left: 20px;
    }
  }
  .header-logo-image-mobile {
    width: auto;
    // width: $header-bottom-height-small*1.5;
    height: $header-bottom-height-small / 2.3;

    @include media-breakpoint-up($header-breakpoint) {
      display: none;
    }
  }

  .header-logo {
    margin-left: 0px;
    @include media-breakpoint-up($header-breakpoint) {
      margin-left: 20px;
    }
  }

  .header-logo-image-desktop {
    display: none;

    @include media-breakpoint-up($header-breakpoint) {
      display: block;
      // width: 120px;
      width: auto;
      height: 36px; // Looks better with other logos in 41px
    }
  }

  .header-left {
    display: flex;
    align-items: center;

    .header-navigation {
      width: 100%;
    }
  }

  .site-selector {
    opacity: 0;
    display: none; //makes it "un-tabbable" when hidden
    pointer-events: none;
    transition: opacity $transition-duration;
    color: $color-text;
    margin-top: 6px;
  }

  .header-desktop-link {
    width: 0;
    opacity: 0;
    color: $color-text;
    margin-top: 6px;
    margin-right: 15px;
    pointer-events: none;
    transition: opacity $transition-duration;

    @include media-breakpoint-up($header-breakpoint) {
      width: auto;

      &:hover {
        color: $color-text;
      }
    }
  }

  .header-tools {
    display: none;

    @include media-breakpoint-up(xl) {
      display: flex;
    }

    flex-direction: row;
    // align-items: center;
    align-items: flex-start;
    font-size: 19px;

    > * {
      align-content: center;
      margin-right: 20px;
    }

    > i {
      color: $color-text;
      vertical-align: middle;
    }
  }

  .header-site-selector {
    display: none;
    font-size: rem(14px);
  }

  .header-site-selector-item {
    color: $color-text;

    &.item-active {
      font-family: $font-family-bold;

      color: $color-blue;
    }

    @include media-breakpoint-up($header-breakpoint) {
      &:hover {
        color: $color-text;
      }
    }
  }

  .header-search {
    position: relative;
    z-index: 20;
    font-size: 16px;

    input {
      //     font-family: $font-family-light;
      //     border-radius: 50%;
      //     border:none;
      //     color: $color-white;
      //     padding: 0 35px 0 15px;
      //     height:41px;
      //     display: none;
      //     &:focus{
      //         outline: 0;
      //     }
    }
    // button{
    //     background-color: transparent;
    //     border:0;
    //     position: absolute;
    //     right:5px;
    //     top:50%;
    //     transform: translateY(-50%);
    // }
    .icon {
      vertical-align: middle;
      color: $color-text;
    }

    @include media-breakpoint-up($header-breakpoint) {
      // input{
      //     display: block;
      // }
      // button{
      //     background-color: transparent;
      //     border:0;
      //     position: absolute;
      //     right:5px;
      //     top:50%;
      //     transform: translateY(-50%);
      // }
      .icon {
        vertical-align: middle;
        color: $color-white;
      }
    }

    &.header-search-mobile {
      display: block;

      @include media-breakpoint-up(md) {
        display: none;
      }
    }

    &.header-search-desktop {
      display: none;

      @include media-breakpoint-up(md) {
        display: block;
      }
    }
  }

  &.headerSlider-navigation-open {
    .headerSlider-navigation-trigger-desktop {
      span:nth-child(1) {
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
        top: -2px;
        left: 3px;
      }

      span:nth-child(2) {
        opacity: 0;
      }

      span:nth-child(3) {
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -o-transform: rotate(-45deg);
        transform: rotate(-45deg);
        top: 12px;
        left: 3px;
      }
    }

    .headerSlider-navigation {
      @include media-breakpoint-up($header-breakpoint) {
        top: 0;
        opacity: 1;
        visibility: visible;
        transition: top 0ms, opacity 600ms, visibility 0s;
      }
    }

    .header-desktop-link,
    .site-selector {
      pointer-events: all;
      opacity: 1;
      display: block;
    }

    .awesomplete input {
      width: 100%;

      &:focus {
        padding: 10px 29px 10px 11px;
      }

      &::-webkit-input-placeholder {
        opacity: 1;
      }

      &:-ms-input-placeholder {
        opacity: 1;
      }
    }
  }

  .headerSlider-navigation {
    @include media-breakpoint-up($header-breakpoint) {
      display: block;
      top: -100%;
      opacity: 0;
      visibility: hidden;
      position: fixed;
      top: -9999px;
      left: 0;
      bottom: 0;
      right: 0;
      min-height: 100vh;
      height: 0;
      width: 100vw;
      background-color: $color-white;
      z-index: -1;
      overflow: scroll;
      transition: top 0ms, linear 600ms, visibility 0s, opacity 600ms;
    }
  }

  .headerSlider-navigation-inner {
    max-width: 1430px;
    margin: 0 auto;
  }

  .megaNavigation {
    @include media-breakpoint-up($header-breakpoint) {
      margin-top: 50px;
      // padding: 30px 30px 30px 160px;
      padding: 0 30px 0 25px;
      display: flex;
      // justify-content: center;
    }
  }
}

.is-experience-editing {
  .headerSlider {
    //top: 186px;
    position: relative;
    z-index: 101;
  }

  .headerSlider-navigation {
    //top: 186px !important;
    position: absolute;
    top: 0 !important;
    width: 100%;
  }
}

/**
    Experimental Nav Trigger
    - Nav Trigger Size has been increased +1 px to accomodate for alignment.
 */
.nav-trigger {
  .nav-trigger-wrapper {
    background-color: rgba(68, 83, 98, 0.7);
    border-radius: 50%;
    display: table;
    height: 42px;
    width: 42px;
    vertical-align: middle;
    text-align: center;
    &.megaNavigation-menu-trigger {
      display: none;
    }
    @media (max-width: 768px) {
      /*@include media-breakpoint-down($header-breakpoint) {*/
      background-color: $color-dark-blue;
      height: 36px;
      width: 36px;
      &.desktop-menu-trigger {
        display: none;
        cursor: pointer;
      }
      &.megaNavigation-menu-trigger {
        cursor: pointer;
        display: table;
      }
    }
    .nav-trigger-inner {
      vertical-align: middle;
      display: table-cell;
      -webkit-transition: 0.2s ease-in-out;
      -moz-transition: 0.2s ease-in-out;
      -o-transition: 0.2s ease-in-out;
      transition: 0.2s ease-in-out;
      span {
        margin: 0 auto 2px;
        width: 50%;
        height: 2px;
        background-color: #fff;
        display: block;
        border-radius: 5px;
        &:last-of-type {
          margin-bottom: 0;
        }
      }
      // Active Animation State
      &.active {
        -webkit-transform: rotate(-90deg);
        -moz-transform: rotate(-90deg);
        -o-transform: rotate(-90deg);
        transform: rotate(-90deg);
      }
    }
  }
}
