.orderflow {
  .orderflow-step {
    .page-headline {
      .site-theme-radius & {
        font-family: $font-family-regular-radius !important;
      }
      h2 {
        .site-theme-radius & {
          font-family: $font-family-bold-radius !important;
        }
      }
    }
    .accordion {
      .accordion-items {
        .accordion-items-item {
          &.is-open > .accordion-item-trigger {
            .site-theme-radius & {
              color: $color-radius-darkgrey;
            }
          }
        }
      }
    }
  }
}
