.social {
  .social-header {
    .site-theme-radius & {
      color: $color-white;
    }
  }
  .social-links-link {
    > a {
      .site-theme-radius & {
        font-family: $font-family-regular;
      }
    }
    .social-links-link-label {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
        color: $color-white;
      }
    }
  }
  .social-links-link-icon {
    .site-theme-radius & {
      color: $color-radius-blue;
      background-color: $color-white;
    }
  }
}
