.orderflow {
  form,
  .form {
    max-width: 100% !important; //overwriting the 700px limit
    width: 100%;

    .accordion {
      .form-group {
        ul.form-control-feedback {
          height: auto;

          li {
            list-style: none;

            &:before {
              content: none;
            }
          }
        }
      }
    }
  }

  .container-has-gutters {
    padding-left: 0px;
    padding-right: 0px;
  }

  .orderflow-centered {
    // max-width: 840px !important;
    align-self: center;
    width: 100%;
  }

  .bottom-space {
    margin-bottom: 50px;
  }

  .orderflow-step {
    background-color: white;

    .row {
      max-width: 930px;
    }

    .page-headline {
      max-width: 930px;
      margin-left: auto;
      margin-right: auto;

      @media screen and (max-width: 1020px) {
        padding: 15px 30px !important;
      }
    }

    &.product-step {
      background-color: transparent;
      padding: 0;

      .row {
        max-width: inherit;
      }
    }

    .form-column {
      padding: 15px 30px;

      .form-group {
        margin-top: 20px;
      }
    }

    .form-group {
      .form-terms {
        margin: 0px 20px;
      }
    }

    .form {
      max-width: 840px;
    }

    .delivery-details[hidden] {
      display: none;
    }

    .page-headline {
      padding: 15px;
      padding-top: 30px;
      display: block;
      font-family: $font-family-regular !important;

      body#lang-zh & {
        font-family: "Noto Sans TC", sans-serif !important;
      }

      body#lang-ja & {
        font-family: "Noto Sans Japanese", sans-serif !important;
      }

      body#lang-ko & {
        font-family: "Noto Sans KR", sans-serif !important;
      }

      margin-bottom: 20px;

      @media screen and(min-width: 375px) {
        padding: 30px 30px 0px 30px;
      }

      h2 {
        font-family: $font-family-bold !important;

        body#lang-zh & {
          font-weight: bold;
          font-family: "Noto Sans TC", sans-serif !important;
        }

        body#lang-ja & {
          font-weight: bold;
          font-family: "Noto Sans Japanese", sans-serif !important;
        }

        body#lang-ko & {
          font-weight: bold;
          font-family: "Noto Sans KR", sans-serif !important;
        }

        color: #e85757;
        font-size: 3.225rem;
        line-height: 1.2;
        letter-spacing: -1px;
        word-break: break-word;
        margin-top: 10px !important;

        @media screen and(max-width: 320px) {
          font-size: 2.5rem;
        }
      }

      p {
        margin-top: 10px;
        box-sizing: inherit;
        color: #3b4956;
        font-weight: normal;
        line-height: 1.5;
        font-size: 16px;
      }
    }

    table {
      margin-top: 10px !important;
      margin-bottom: 50px !important;

      .align-right {
        text-align: right !important;
      }
    }

    .product-intro {
      margin-bottom: 10px !important;
      // padding-left: 30px;
      h2 {
        margin-top: 25px !important;
        font-size: 30px !important;
      }
    }

    .sum-array {
      font-size: 12px;

      .top-border {
        border-top: 1px solid #ccd2d7 !important;
      }
    }

    div.expand-content {
      td {
        display: inline-block !important;
      }

      td + td {
        float: right !important;
      }
    }

    .richtext {
      table {
        tbody {
          tr {
            border-bottom: 1px solid #ccd2d7;

            td {
              padding: 10px 0px;
              font-family: $font-family-regular !important;

              body#lang-zh & {
                font-family: "Noto Sans TC", sans-serif !important;
              }

              body#lang-ja & {
                font-family: "Noto Sans Japanese", sans-serif !important;
              }

              body#lang-ko & {
                font-family: "Noto Sans KR", sans-serif !important;
              }
            }
          }

          tr:nth-child(even) {
            background-color: transparent;
          }

          tr:last-child {
            border-top: none !important;
          }
        }
      }

      table:not(:first-child) {
        margin-top: 30px;
      }

      .total-price {
        margin-top: 50px;

        td {
          font-family: $font-family-bold !important;

          body#lang-zh & {
            font-weight: bold;
            font-family: "Noto Sans TC", sans-serif !important;
          }

          body#lang-ja & {
            font-weight: bold;
            font-family: "Noto Sans Japanese", sans-serif !important;
          }

          body#lang-ko & {
            font-weight: bold;
            font-family: "Noto Sans KR", sans-serif !important;
          }

          border-top: 1px solid #ccd2d7 !important;
        }
      }

      .align-left {
        td {
          text-align: left !important;
        }
      }

      .contact-info {
        tr {
          border-bottom: none !important;
        }

        tbody {
          margin-bottom: 30px;
        }
      }

      .bold-font {
        td {
          font-family: $font-family-bold !important;

          body#lang-zh & {
            font-family: "Noto Sans TC", sans-serif !important;
            font-weight: bold;
          }

          body#lang-ja & {
            font-family: "Noto Sans Japanese", sans-serif !important;
            font-weight: bold;
          }

          body#lang-ko & {
            font-family: "Noto Sans KR", sans-serif !important;
            font-weight: bold;
          }
        }
      }
    }

    .page-info {
      // font-family: $font-family-regular;
      // margin-top: 30px;
      padding: 0px 30px;

      .form-terms {
        margin: 0px;
      }

      h3 {
        font-family: $font-family-bold !important;

        body#lang-zh & {
          font-family: "Noto Sans TC", sans-serif !important;
          font-weight: bold;
        }

        body#lang-ja & {
          font-family: "Noto Sans Japanese", sans-serif !important;
          font-weight: bold;
        }

        body#lang-ko & {
          font-family: "Noto Sans KR", sans-serif !important;
          font-weight: bold;
        }

        color: #3b4956;
        font-size: 20px;
        font-weight: 500px;
        line-height: 1.5;
        margin-bottom: 30px;
      }

      fieldset {
        margin: 10px 0px;
      }
    }

    .margin-top-30 {
      margin-top: 30px;
    }
    .orderflow-form {
      form {
        max-width: 100% !important;
      }
    }

    .accordion {
      @media screen and(min-width: 300px) {
        padding: 0px;
      }

      .accordion-items {
        margin: 0px 15px;

        @media screen and(min-width: 375px) {
          margin: 15px 0px 0px 0px;
        }

        .accordion-items-item {
          a {
            padding-left: 25px;
            font-weight: bolder;
            font-size: small;
          }

          &.is-open > .accordion-item-trigger {
            color: black;
            background-color: inherit;
            font-weight: bolder;

            &:before {
              color: black;
            }
          }

          .accordion-item-expanded-inner {
            //margin: 10px 0px;
            margin-bottom: 10px;
            border: 1px solid rgba(0, 0, 0, 0.2);

            p {
              margin-bottom: 10px;
            }

            .btn {
              float: left;
              margin-left: 0px !important;
              margin-bottom: 0px !important;
            }

            .padding-left-30 {
              padding-left: 25px;
            }

            .product {
              padding: 25px;

              @media screen and(max-width: 320px) {
                padding: 25px 5px;
              }
            }

            > .product-intro {
              padding-left: 30px;
            }
          }
        }

        .credit-info {
          padding: 0;
          display: flex;
          flex-direction: row;
          align-content: space-between;

          fieldset {
            margin-top: 20px;
          }

          fieldset:nth-child(2) {
            margin-top: 20px;
            margin-left: 30px;

            @media screen and(min-width: 300px) {
              margin-left: 15px;
            }
          }

          .field-label {
            margin-top: 30px;
            display: inline-block;
          }

          #dkpay {
            width: 66px;
            height: 40px;
            z-index: 100;
            left: 2px;
            top: 2px;
            text-decoration: none;
            background: url("../../assets/images/DK_Logo_CMYK_Konturstreg.png")
              no-repeat 0px 0px;
          }
          .form-required {
            color: $color-input-required;
          }
          .has-any-error {
            color: $color-red;
            font-size: 12px;
            margin-top: 0px !important;
            margin-left: 16px;
          }
        }

        .accordion-item-expanded-inner {
          @media screen and(min-width: 300px) {
            padding: 5px;
          }

          p {
            margin-bottom: 0px;
          }

          div[data-paymentconfirmation] {
            padding-left: 25px;
          }
        }
      }
    }
  }

  .margin-bottom-15 {
    margin-bottom: 15px !important;
  }

  .icon-check-mark {
    font-size: 1.2em;
    padding-top: 5px;
    cursor: pointer;
  }

  .proceed {
    margin-top: 10px;
  }

  .customerDetails-choices {
    flex-wrap: wrap;
  }

  input:required:invalid {
    outline: none;
  }

  input:-ms-tooltip {
    display: none;
  }

  input:required {
    box-shadow: none;
  }

  div.orderflow-centered {
    button.btn.btn-back:hover:before {
      -ms-transform: rotate(180deg); /* IE 9 */
      -webkit-transform: rotate(180deg); /* Chrome, Safari, Opera */
      transform: rotate(180deg) !important;
      display: inline-block;
      margin: 0px 10px 0px 10px;
    }

    button.btn.btn-back:before {
      -ms-transform: rotate(180deg); /* IE 9 */
      -webkit-transform: rotate(180deg); /* Chrome, Safari, Opera */
      transform: rotate(180deg) !important;
    }

    button.btn.btn-back {
      flex-direction: row;
    }
  }
}
