@import "../../scss/base/base";

.video-carousel {
  box-sizing: border-box;

  * {
    box-sizing: border-box;
  }

  $fadeInOutTransition: opacity 0.4s ease-in-out;

  position: relative;
  font-family: "Orsted Sans";
  min-height: calc(100vh - 70px);
  min-height: vh(100, var(--nav, 0px));
  width: 100%;
  background: #e5e5e5;
  color: $color-white;
  display: flex;
  flex-direction: column;

  @keyframes slide-up {
    from {
      transform: translateY(150%);
    }
    100% {
      transform: translateY(0);
    }
  }

  @keyframes slide-up-more {
    from {
      transform: translateY(0);
    }
    to {
      transform: translateY(-150%);
    }
  }

  @keyframes fade-in {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes fade-out {
    from {
      opacity: 1;
    }
    to {
      opacity: 0;
    }
  }

  @keyframes slide-left-out {
    from {
      transform: translateX(0);
    }
    to {
      transform: translateX(-150%);
    }
  }

  @keyframes slide-left-in {
    from {
      transform: translateX(150%);
    }
    to {
      transform: translateX(0);
    }
  }

  &__videos {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  &__video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0;
    transition: opacity 0.4s ease;

    @at-root .no-objectfit & {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      min-width: 100%;
      min-height: 100%;
      width: auto;
      height: auto;
    }

    &--show {
      opacity: 1;
      transition: opacity 1s ease;
    }
  }

  &__container {
    position: relative;
    flex: 1 0 100%;
    width: 100%;
    min-height: 100%;
    margin: 0 auto;
    max-width: 1264px;
    display: flex;
    flex-direction: column;

    .swiper-wrapper {
      align-items: flex-end;
    }
  }

  &__list {
    position: absolute;
    width: 100%;
    line-height: 0;
    bottom: 40px;
    padding-left: 22px;
    padding-right: 22px;

    @include media-desktop() {
      bottom: 48px;
    }
  }

  &__button-next {
    right: 0;
  }

  &__button-prev {
    left: 0;
  }

  &__button-next.swiper-button-next,
  &__button-prev.swiper-button-prev {
    margin-top: 0;
    top: auto;
    bottom: 56px;
    background-size: 16px 20px;
    transition: $fadeInOutTransition;

    &.swiper-button-disabled {
      opacity: 0;
    }
  }

  & &__list-item {
    text-align: center;
    font-size: 18px;
    flex: 0 0 auto;
    opacity: 0.7;
    transition: $fadeInOutTransition;
    min-height: 60px;
    padding-left: 6px;
    padding-right: 6px;

    @supports (-ms-ime-align: auto) {
      transition: none;
    }

    @include media-desktop() {
      display: flex;
      flex: 1 0 auto;
      padding-left: 10px;
      padding-right: 10px;
    }

    &:hover,
    &--active {
      opacity: 1;
      transition: $fadeInOutTransition;

      @supports (-ms-ime-align: auto) {
        transition: none;
      }
    }

    &--active .video-carousel__item-wrapper,
    &--inactive .video-carousel__item-wrapper {
      cursor: default;
      position: absolute;
      width: 100vw;
      width: calc(100vw - 44px);
      bottom: 0;
      display: flex;
      padding-left: 6px;
      padding-right: 6px;
      pointer-events: none;
      animation: slide-left-in 0.4s ease-in forwards;

      @include media-desktop() {
        position: relative;
        width: 100%;
        padding-left: 0;
        padding-right: 0;
        animation: none;
        pointer-events: auto;
      }
    }

    .video-carousel__item-title,
    .video-carousel__item-subtitle {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      @include media-desktop() {
        white-space: normal;
        overflow: visible;
        text-overflow: clip;
      }
    }

    @include media-desktop() {
      &--truncate-text {
        .video-carousel__item-title,
        .video-carousel__item-subtitle {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }

    &--inactive .video-carousel__item-wrapper {
      animation: slide-left-out 0.4s ease-in forwards;

      @include media-desktop() {
        animation: none;
      }
    }
  }

  &__item-wrapper {
    display: none;
    position: relative;
    align-items: flex-end;
    justify-content: stretch;
    left: 0%;
    width: 100%;
    flex-wrap: nowrap;
    cursor: pointer;

    @include media-desktop() {
      display: flex;
      pointer-events: auto;
      left: auto !important;
    }
  }

  &__item-content {
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 1 0 100%;
    max-width: calc(100% - 56px);
    text-align: left;
    margin-bottom: 20px;
  }

  &__item-title,
  &__item-subtitle {
    font-size: 14px;
    line-height: 22px;
  }

  &__item-title {
    font-weight: 700;
  }

  &__item-thumbnail {
    flex: 0 0 40px;
    height: 40px;
    margin-right: 16px;
    margin-bottom: 20px;

    @at-root .no-objectfit & {
      border-radius: 40px;
      background-color: transparent;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center;

      .video-carousel__thumbnail-image {
        opacity: 0;
      }
    }
  }

  &__thumbnail-image {
    object-fit: cover;
    border-radius: 50%;
    width: 100%;
    height: 100%;
  }

  &__item-progressbar {
    position: absolute;
    width: 100%;
    padding-left: 6px;
    padding-right: 6px;
    height: 2px;
    bottom: 0;
    left: 0;

    @include media-desktop() {
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  &__progressbar-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
  }

  &__progressbar-bar,
  &__progressbar-playbar {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    background-color: rgba(255, 255, 255, 0.4);
  }

  &__progressbar-playbar {
    width: 100%;
    transform-origin: 0% 0%;
    transform: scaleX(0);
    background-color: rgba(255, 255, 255, 1);
    transition: transform 0.4s linear;

    &--no-transition {
      transition: none;
    }
  }

  &__contents {
    flex: 1 0 auto;
    position: relative;
    z-index: 1;
  }

  &__content {
    position: absolute;
    max-width: (1440px / 2);
    height: 100%;
    font-weight: 700;
    opacity: 0;
    pointer-events: none;
    justify-content: center;
    top: 0;
    left: 50%;
    transform: translateX(-50%);

    .video-carousel__title {
      overflow: hidden;
    }

    &--active {
      opacity: 1;
      pointer-events: auto;

      .video-carousel__title-text {
        animation: slide-up 0.4s ease-in 0.4s;
        animation-fill-mode: forwards;
      }

      .video-carousel__button-toaster {
        animation: fade-in 0.4s ease-in 0.4s;
        animation-fill-mode: forwards;
      }
    }

    &--inactive {
      opacity: 1;

      .video-carousel__title-text {
        animation: slide-up-more 0.4s ease-in-out;
        animation-fill-mode: forwards;
      }

      .video-carousel__button-toaster {
        animation: fade-out 0.4s ease-in-out;
        animation-fill-mode: forwards;
      }
    }
  }

  &__title-wrapper {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    height: 50%;
  }

  &__title {
    padding-bottom: 10px;
    padding-top: 10px;
  }

  &__title-text {
    font-size: 25px;
    line-height: 35px;
    transform: translateY(150%);
    font-family: inherit !important;
    text-align: center;
    font-weight: inherit !important;
  }

  &__button-toaster-wrapper {
    display: flex;
    height: 50%;
    flex: 1 0 0;
    align-items: flex-start;
    justify-content: center;
  }

  &__button-toaster {
    opacity: 0;
    padding-top: 10px;
    cursor: pointer;

    &:hover .video-carousel__button-toaster-icon {
      transition: transform 0.2s ease-in-out;
      transform: scale(1.3);
    }

    &.videoBox--disabled {
      display: none;
    }
  }

  &__button-toaster-text {
    line-height: 24px;
    margin-right: 12px;
  }

  &__button-toaster-icon {
    vertical-align: middle;
    position: relative;
    display: inline-block;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.25);
    text-align: center;
    transition: transform 0.2s ease-in-out;

    &:before {
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      margin-left: -6px;
      margin-top: -7px;
      width: 12px;
      height: 14px;
      background-repeat: no-repeat;
      background-size: 12px 14px;
      background-image: url("data:image/svg+xml;base64,");
    }
  }
}
