.livestream {
  position: relative;
  overflow: hidden;

  .livestream-media {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    height: 100%;
    width: 100%;

    video {
      height: auto;
      min-height: 100%;
      min-width: 100%;
    }

    .livestream-image {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-size: cover;
    }
  }

  .livestream-content {
    @include media-breakpoint-up(md) {
      width: 50%;
    }

    .teaser {
      margin-bottom: 0;
      background: none;

      .teaser-content {
        min-height: 440px;
      }
    }
  }
}
