.job-filters.listFilters {
  margin-top: 72px;

  .form-col {
    flex: 1;

    &:first-of-type .form-group {
      @include media-breakpoint-up(md) {
        margin-left: 0;
      }
    }

    &:not(:last-child) {
      padding-right: 16px;
    }
  }

  .form-group {
    margin: 0;
  }

  legend {
    font-size: $text-size-body-small;
    line-height: $line-height-body-small;
  }

  .list-filters-text-dialog {
    display: none;
    font-family: $font-family-bold;
    margin-bottom: 24px;
    margin-top: 16px;
  }

  .job-filters-search {
    border: 1px solid $color-cold-grey-25;
    box-sizing: border-box;
    height: 44px;
    border-radius: 22px;
    width: 100%;
    padding: 10px 25px;
    margin-bottom: 32px;
    outline: none;

    &:hover {
      box-shadow: 0 0 0 3px $color-cold-grey-25;
    }
  }

  + .job-module {
    margin-top: 34px;
  }

  @include media-breakpoint-down(md) {
    .list-filters-wrapper.btn {
      background-color: $color-white !important;
      padding: 10px 25px;
      height: 44px;
      border: 0 !important;
      color: $color-black !important;
      font-size: $text-size-body !important;
    }
  }

  @include media-breakpoint-down(sm) {
    margin-top: 40px;
    padding: 0;

    .list-filters-wrapper,
    .list-filters-wrapper.btn {
      background-color: $color-white !important;
      border: 1px solid $color-cold-grey-25 !important;
      padding: 10px 25px;
      height: 44px;
    }

    .list-filters-text {
      color: $color-black;
      font-size: $text-size-body;
      font-family: $font-family-regular;
    }

    .listFilters-mobileTrigger {
      color: $color-blue;

      .icon-filter::before {
        font-size: 20px;
      }
    }

    .job-filters-search {
      margin-bottom: 20px;
    }

    + .job-module {
      margin-top: 52px;
    }
  }

  &.listFilters-mobile-open {
    .form {
      padding-top: 0;
      padding-bottom: 52px;
    }

    .list-filters-wrapper {
      height: auto;
      border: 0 !important;
      flex-direction: column;
    }

    .list-filters-text-dialog {
      display: block;
    }

    .job-filters-search {
      display: none;
    }

    .form-group,
    .form-col,
    .form-col:not(:last-child) {
      margin: 0;
      padding: 0;
    }

    .form-group {
      margin-bottom: 20px;
    }

    .listFilters-mobile-apply {
      width: 100%;
      justify-content: center;
      margin-top: 12px;
      height: 44px;
      align-items: center;

      a {
        width: 100%;
        justify-content: center;
        flex-direction: row;
      }
    }

    .listFilters-mobileTrigger-open.icon-close {
      color: $color-black;
    }
  }
}
