.twitter {
  @extend .teaser;
  overflow: hidden;
  @include media-breakpoint-up(md) {
    display: flex;
    @include flex-1;
    min-height: 440px;
    max-height: 1220px;
    position: absolute;
    top: 0;
    bottom: 0;
    max-height: none;
    width: 100%;
    overflow: auto;
  }
  padding: $module-space-vertical-top $module-space-horisontal-left 0
    $module-space-horisontal-right;
  background: $color-white;
  justify-content: flex-start;
  -webkit-box-pack: start;

  max-height: 70vh;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  .twitter-header {
    padding: 0 0 $spacing-large;
    font-size: 20px;
    font-family: $font-family-bold;
  }

  .twitter-feed {
    padding-right: 1px;
    border: 1px solid transparent;
  }
  iframe {
    width: 100%;
  }
}
