.customSelect {
  $transition-duration: 0.25s;
  cursor: pointer;
  padding-top: 0px !important;
  padding-bottom: 0px !important;
  transition: all $transition-duration ease-in-out;

  &.customSelect-fullwidth {
    width: 100%;

    select {
      width: 150%;
    }
  }

  &.focused {
    box-shadow: 0 0 0px 3px $color-input-border-focus;
  }

  &:not(.customSelect-inline) {
    @include btn;
    padding-left: 0;

    $props: map-get($buttonThemes, "blue");
    @include btnTheme(
      map-get($props, "background-color"),
      map-get($props, "text-color")
    );
    @include btnDropdown;
  }

  select {
    @include btnTypography;
    position: relative;
    z-index: 1;
    display: block;
    color: inherit;
    padding: 10px 69px 10px 20px;
    border: none;
    box-shadow: none;
    background: transparent;
    background-image: none;
    text-indent: 0.01px;
    text-overflow: ellipsis;
    width: 130%;
    width: calc(100% + 69px);
    margin-right: -69px;
    height: 100%;
    line-height: 22px;

    -webkit-appearance: none;
    -moz-appearance: none;

    option {
      color: $color-text;
      font-family: $font-family-regular;

      background-color: $color-white;
    }
    // ie styling
    &::-ms-expand {
      display: none;
    }

    &::-ms-value {
      background: none;
      color: $color-white;
    }
  }

  option {
    color: $color-white;
  }

  select:focus {
    outline: none;
  }

  &::before {
    margin-left: auto;
  }

  &.customSelect-inline {
    background: transparent;
    padding-right: 4px;
    position: relative;
    display: inline-block;

    select {
      color: $color-text;
      font-size: 1em !important;
      line-height: 1.2 !important;
      font-family: $font-family-bold;

      display: inline;
    }

    i.icon {
      font-size: 0.6em;
    }

    &:after {
      display: block;
      content: "";
      position: absolute;
      bottom: 10px;
      height: 2px;
      width: 100%;
      background-color: $color-dark-blue;
    }
  }
}
