.login {
  display: inline-block;
  float: right;
  line-height: 1;

  .login-container {
    position: relative;

    .btn-dropdown {
      cursor: pointer;
      padding: 10px 65px 10px 23px;
      min-width: 150px;

      a {
        color: #fff;

        &:hover {
          color: #fff;
        }
      }
    }

    .authenticated {
      display: inline-block;
      width: 265px;
      white-space: nowrap;
      overflow: hidden !important;
      text-overflow: ellipsis;
    }

    .no-dropdown:before {
      content: "";
      border-left: 0px;
    }

    .collapsing {
      background-color: #fff;
    }

    .profile-link-section,
    .logout-link-section {
      padding: 1rem 1.5rem 0;
    }
  }

  .collapsing {
    background-color: white;
    height: 0px;
    display: none;
  }

  .popover {
    z-index: 100;
    padding: 0;
    border-radius: 20px;
    //mobile positioning
    left: 0px !important;
    right: -1px !important;
    top: 75px !important;
    min-width: 100%;

    > .arrow {
      display: none;
    }

    &.bottom {
      margin-top: 0px;
    }
  }

  //desktop positioning
  .desktop-positioning .popover {
    left: -157px !important;
    right: -1px !important;
    top: -1px !important;
    min-width: 314px;
  }

  .desktop-positioning.authenticated {
    span.login-button.btn-dropdown,
    .popover span.btn-dropdown.login-menu {
      display: inline-block;
      padding: 10px 56px 10px 23px;
      margin: 0;

      &:focus,
      &.active {
        box-shadow: none;
      }
    }

    span.user-text {
      max-width: 160px;
      white-space: nowrap;
      overflow: hidden !important;
      margin-right: 20px;
      text-overflow: ellipsis;
      display: inline-block;
    }

    .popover {
      left: -1px !important;
      right: -1px !important;
      top: 0px !important;
      min-width: 150px;
      display: inline-block;
      border-radius: 23px;
      border-top-style: none;

      .btn-dropdown {
        border-radius: 23px 23px 0 0;

        &:before {
          transform: rotate(180deg);
          border-left: none;
          border-right: 1px solid rgba(255, 255, 255, 0.5);
        }
      }
    }
  }

  .popover-title {
    display: none; //we dont want to show a title on the login box, but if we (at some point) need the title, the styling is given below.
    font-size: 1.1875rem;
    line-height: 1.25rem;
    font-family: $font-family-bold;
    letter-spacing: 0.2px;
    padding: 11px 14px;
    margin-left: -30px;
    margin-right: -30px;
    margin-top: -30px;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    height: 45px;
    background-color: $color-red;
    color: white;
  }

  .popover-content {
    color: $color-text;
    padding: 0;

    .accordion {
      padding: 0 30px 0 30px;
      border-radius: 20px;

      .accordion-items {
        margin-bottom: 0px;
      }

      .btn {
        margin-top: 0 !important;
        margin-bottom: 0 !important;
        margin-right: 0 !important;
      }

      .accordion-item-trigger {
        font-size: 1.1875rem !important;
        line-height: 1.25rem !important;
        font-family: $font-family-bold, sans !important;
        letter-spacing: 0.2px;
        height: 44px;
        padding: 12px 50px 10px 30px;

        body#lang-zh & {
          font-weight: bold;
          font-family: "Noto Sans TC", sans-serif !important;
        }

        body#lang-ja & {
          font-family: "Noto Sans Japanese", sans-serif !important;
          font-weight: bold;
        }

        body#lang-ko & {
          font-family: "Noto Sans KR", sans-serif !important;
          font-weight: bold;
        }
      }

      .accordion-items-item:first-child {
        border-top: 0;

        .accordion-item-trigger {
          border-top-left-radius: 20px;
          border-top-right-radius: 20px;
        }
      }

      .accordion-item-expanded-inner {
        padding: 5px 30px;

        .div-login-show-password-reminder {
          margin-bottom: 2rem;
          display: inline-block;
        }

        .div-form-check {
          margin: 1.5rem 0;
        }
      }

      .accordion-items-item.is-open {
        .password {
          padding-right: 76px;
        }
      }
    }

    .control-label {
      width: 100%;

      .form-control-help-bs-tooltip.form-control-help-inline {
        position: absolute;
        right: 28px;
      }
    }

    .control-label.is-active {
      .form-control-help-bs-tooltip.form-control-help-inline {
        padding: 1px 0 0 1px;
        width: 20px;
        height: 20px;
      }
    }

    .form .form-check .check-box {
      display: inline;
      z-index: 100;
      opacity: 0;
      position: absolute;
    }

    span.chkrememberMe {
      display: inline-block;
      height: 16px;
      width: 16px;
      position: relative;
      border: solid 1px gray;
      top: 2px;
      margin-right: 4px;
      border-radius: 3px;
    }

    i.icon.icon-check-mark {
      display: none;
      top: -5px;
      position: absolute;
      right: -1px;
      margin-left: 0;
      color: #3b4956;
      height: 15px;
      width: 15px;
    }

    .form .form-group input[type="checkbox"]:checked ~ i {
      display: inline-block;
    }

    .login-message {
      margin-top: 10px;
      margin-bottom: 40px;
    }
  }

  .error {
    color: #d9534f;
    padding-top: 7px;
  }

  .login-show-password-reminder {
    position: absolute;
    cursor: pointer;
    font-family: $font-family-bold, "Helvetica Neue", Arial, sans-serif !important;
    font-size: inherit;
    line-height: inherit;
    letter-spacing: inherit;
  }

  .login-show-password-back-btn {
    position: absolute;
    bottom: 0;
    text-decoration: underline;
    cursor: pointer;

    &:hover {
      font-weight: bold;
    }
  }

  .link-arrow-right.login-show-password-back-btn:after {
    transition: padding 200ms cubic-bezier(0.42, 0, 0.83, 1.42);
    transition-property: padding;
    transition-duration: 200ms;
    transition-timing-function: cubic-bezier(0.165, 0.84, 0.26, 0.98);
    transition-delay: initial;
    font-family: $font-family-bold;
  }

  .link-arrow-right.login-show-password-back-btn {
    content: "";
    position: absolute;
    cursor: pointer;
    font-family: "OrstedSansRegular", "OrstedSansBold", sans, "Helvetica Neue",
      Arial, sans-serif !important;
    /* font-size: inherit; */
    /* line-height: inherit; */
    /* letter-spacing: inherit; */
    left: 3px;
    /* margin: 0; */
    /* padding: 0; */
  }

  .link-arrow-right.login-show-password-back-btn:after {
    transition: padding 200ms cubic-bezier(0.42, 0, 0.83, 1.42);
    transition-property: padding;
    transition-duration: 200ms;
    transition-timing-function: cubic-bezier(0.165, 0.84, 0.26, 0.98);
    transition-delay: initial;
    font-family: $font-family-bold;
  }

  .link-arrow-right {
    text-decoration: none !important;
    display: inline;
    backface-visibility: hidden;
    position: relative;
    /*padding-left: 1.25rem;*/
    margin-right: 0.625rem;
    -webkit-transition: padding 200ms cubic-bezier(0.165, 0.84, 0.26, 0.98);
    transition: padding 200ms cubic-bezier(0.165, 0.84, 0.26, 0.98);
  }

  .link-arrow-right:before {
    content: "";
    display: inline;
    padding-left: 5px !important;
    /* padding-right: 5px; */
    -webkit-transition: padding 200ms cubic-bezier(0.165, 0.84, 0.26, 0.98);
    transition: padding 200ms cubic-bezier(0.165, 0.84, 0.26, 0.98);
    font-family: $font-family-bold;
    position: initial;
    margin-right: 3px;
  }

  .link-arrow-right-hover:after,
  .link-arrow-right:hover:after {
    padding-left: 0px;
    padding-right: 10px;
  }

  .link-arrow-right.login-show-password-back-btn:hover:before {
    right: 5px;
    position: relative;
    padding-right: 0px;
  }
  /* Spinner for private login start*/
  .loader-private {
    border: 10px solid $color-border;
    border-radius: 50%;
    border-top: 10px solid;
    /*border-top-color: $color-blue;*/

    /*border-top-color: #3498db;*/
    width: 80px;
    height: 80px;
    -webkit-animation: spin 2s linear infinite;
    animation: spin 1s linear infinite;
    margin-bottom: 100px !important;
    margin-top: 100px !important;
    margin-right: auto;
    margin-left: auto;
    position: relative;
    display: none;
  }

  @-webkit-keyframes spin {
    0% {
      -webkit-transform: rotate(0deg);
    }

    100% {
      -webkit-transform: rotate(360deg);
    }
  }

  @keyframes spin {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }

    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  /* Spinner for private login end*/
  /* Spinner for bussiness login start*/
  .loader-bussiness {
    border: 10px solid $color-border;
    border-radius: 50%;
    border-top: 10px solid;
    /*border-top-color: $color-blue;*/

    /*border-top-color: #3498db;*/
    width: 80px;
    height: 80px;
    -webkit-animation: spin 2s linear infinite;
    animation: spin 1s linear infinite;
    margin-bottom: 100px !important;
    margin-top: 100px !important;
    margin-right: auto;
    margin-left: auto;
    position: relative;
    display: none;
  }

  @-webkit-keyframes spin {
    0% {
      -webkit-transform: rotate(0deg);
    }

    100% {
      -webkit-transform: rotate(360deg);
    }
  }

  @keyframes spin {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }

    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  /* Spinner for bussiness login end*/
  .show-password {
    position: absolute;
    top: 10px;
    right: 40px;

    &.is-active {
      display: inline-block;
    }
  }

  .has-danger,
  .has-success {
    .show-password {
      right: 65px;
    }

    .form-control-help-bs-tooltip.form-control-help-inline {
      //right: 15px !important;
      margin-right: 25px;
    }
  }

  #Password::-ms-clear,
  #Password::-ms-reveal {
    display: none;
  }

  .form-group.businesslogin,
  .form-group.privatelogin {
    margin-bottom: 30px;
  }
}

// this disables some animations
.popover {
  .accordion.login-box-accordion {
    .accordion-item-expanded {
      display: none;
      transition: none;
      overflow: hidden;
      color: $color-dark-blue;
    }

    .is-open {
      .accordion-item-expanded {
        display: block;
      }
    }
  }
}

@media (max-width: 768px) {
  body {
    cursor: pointer;
  }

  .headerClassic {
    top: 0;
    position: absolute;
  }

  .login {
    .popover {
      z-index: 100;
      padding: 0;
      border-radius: 0 0 18px 18px;
      overflow: hidden;
      left: -5px !important;
      right: -5px !important;
      top: 70px !important;
      width: 100vw;
      min-width: 100vw;

      .popover-content .accordion {
        .accordion-items-item:first-child .accordion-item-trigger {
          border-top-left-radius: 0px;
          border-top-right-radius: 0px;
        }
      }
    }
  }

  .popover.bottom {
    margin-top: 0px;
  }

  a.login-button.active {
    i {
      &:before {
        color: #4099da;
      }
    }
  }

  .login-menu-items {
    padding-bottom: 1rem;
  }

  .profile-link-section {
    padding: 1rem 1.5rem 0;
    padding-bottom: 1rem;
    border-bottom: 1px solid #ccd2d7;
    padding-top: 0.5rem;
  }

  .logout-link-section {
    padding: 1rem 1.5rem 0;
    padding-top: 0.5rem;
  }

  .collapsing {
    background-color: white;
    height: 0px;
    display: none;
  }

  .headerClassic form,
  .headerClassic .form {
    max-width: 100%;
  }
}

/* Landscape iPhone 6+, 7+, 8+ and some*/
@media only screen and (min-device-width: 414px) and (max-device-width: 736px) and (orientation: landscape) {
  .login .popover-content .accordion .accordion-item-expanded-inner {
    max-height: 370px;
    overflow-y: auto;
  }
}

/* Landscape iPhone 6, 6S, 7, 8 and some*/
@media only screen and (min-device-width: 375px) and (max-device-width: 667px) and (orientation: landscape) {
  .login .popover-content .accordion .accordion-item-expanded-inner {
    max-height: 330px;
    overflow-y: auto;
  }
}
/* Landscape iPhone 5, 5S, 5C, 5SE and some*/
@media only screen and (min-device-width: 320px) and (max-device-width: 568px) and (orientation: landscape) {
  .login .popover-content .accordion .accordion-item-expanded-inner {
    max-height: 275px;
    overflow-y: auto;
  }
}

/* Landscape iPhone 4, 4S and some*/
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) and (orientation: landscape) {
  .login .popover-content .accordion .accordion-item-expanded-inner {
    max-height: 275px;
    overflow-y: auto;
  }
}

/* Landscape - iPad 1, 2, 3, 4 & mini*/
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: landscape) {
  body {
    cursor: pointer;
  }
}

