h1,
.h1 {
  @include h1;
}

h2,
.h2 {
  @include h2;
}

.h2-bold {
  @include h2-bold;
}

h3,
.h3 {
  @include h3;
}

.h3-bold {
  @include h3-bold;
}

h4,
.h4 {
  // Mainly for splashes
  @include h4;
}

.h4-medium {
  // mainly for exploration
  @include h4-medium;
}

.h4-italic {
  // mainly for quotes
  @include h4-italic;
}

h5,
.h5,
.subheader,
.label {
  @include h5;
}

.bodycopy {
  font-family: $font-family-regular !important;

  .site-theme-radius & {
    font-family: $font-family-regular-radius !important;
    font-size: $text-size-body-radius;
    line-height: $line-height-body-radius;
  }

  body#lang-zh & {
    font-family: "Noto Sans TC", sans-serif !important;
  }

  body#lang-ja & {
    font-family: "Noto Sans Japanese", sans-serif !important;
  }

  body#lang-ko & {
    font-family: "Noto Sans KR", sans-serif !important;
  }
  font-size: $text-size-body;
  line-height: $line-height-body;
}

.bodycopy-small {
  font-size: $text-size-body-small;
  line-height: $line-height-body-small;
}

.arrow-link-style {
  font-family: $font-family-bold !important;

  .site-theme-radius & {
    font-family: $font-family-bold-radius !important;
    font-size: $text-size-xsmall-radius;
    line-height: $line-height-xsmall-radius;

    &:after {
      font-size: rem(17px);
    }
  }

  body#lang-zh & {
    font-weight: bold;
    font-family: "Noto Sans TC", sans-serif !important;
  }

  body#lang-ja & {
    font-weight: bold;
    font-family: "Noto Sans Japanese", sans-serif !important;
  }

  body#lang-ko & {
    font-weight: bold;
    font-family: "Noto Sans KR", sans-serif !important;
  }

  font-size: rem(19px);
  line-height: rem(20px);
  letter-spacing: 0.2px;
}

.btn-text {
  font-size: rem(19px);
  line-height: rem(20px);
  font-family: $font-family-bold !important;
  letter-spacing: 0.2px;

  .site-theme-radius & {
    line-height: 1.35rem !important; //looks better with the new fonts (1.55 is prettier for Ariel)
    font-family: $font-family-bold-radius !important;
  }

  body#lang-zh & {
    font-family: "Noto Sans TC", sans-serif !important;
    font-weight: bold;
  }

  body#lang-ja & {
    font-family: "Noto Sans Japanese", sans-serif !important;
    font-weight: bold;
  }

  body#lang-ko & {
    font-family: "Noto Sans KR", sans-serif !important;
    font-weight: bold;
  }
}

strong,
b {
  font-family: $font-family-bold;

  .site-theme-radius & {
    font-family: $font-family-bold-radius;
  }

  body#lang-zh & {
    font-weight: bold;
    font-family: "Noto Sans TC", sans-serif !important;
  }

  body#lang-ja & {
    font-weight: bold;
    font-family: "Noto Sans Japanese", sans-serif !important;
  }

  body#lang-ko & {
    font-weight: bold;
    font-family: "Noto Sans KR", sans-serif !important;
  }
}

em {
  font-family: inherit;
  font-style: italic;
}

body {
  font-size: $text-size-body;
  line-height: $line-height-body;
  .site-theme-radius,
  &.site-theme-radius {
    font-size: $text-size-body-radius;
    line-height: $line-height-body-radius;
  }
}

body {
  // Using ID tags to improve specificity because fonts are often set with !important in the solution, so that wont be enough to override them.
  &#lang-zh,
  &#lang-ja,
  &#lang-ko {
    line-height: 24px; // The Noto Sans fonts take up more space than Orsted/Metric fonts.
  }

  &#lang-zh {
    font-family: "Noto Sans TC", sans-serif !important;

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    em,
    p,
    a {
      font-family: "Noto Sans TC", sans-serif !important;
    }

    .article .article-excerpt {
      font-family: "Noto Sans TC", sans-serif !important;
    }

    .teaser .teaser-headline {
      font-family: "Noto Sans TC", sans-serif !important;
    }
  }

  &#lang-ja {
    font-family: "Noto Sans Japanese", sans-serif !important;

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    em,
    p,
    a {
      font-family: "Noto Sans Japanese", sans-serif !important;
    }

    .article .article-excerpt {
      font-family: "Noto Sans Japanese", sans-serif !important;
    }

    .teaser .teaser-headline {
      font-family: "Noto Sans Japanese", sans-serif !important;
    }
  }

  &#lang-ko {
    font-family: "Noto Sans KR", sans-serif !important;

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    em,
    p,
    a {
      font-family: "Noto Sans KR", sans-serif !important;
    }

    .article .article-excerpt {
      font-family: "Noto Sans KR", sans-serif !important;
    }

    .teaser .teaser-headline {
      font-family: "Noto Sans KR", sans-serif !important;
    }
  }
}
