@font-face {
  font-family: "CeraStencilPro-Black";
  src: url("//external-orstedcdn.azureedge.net/shared/fonts/ea/ceraprojv/v1/CeraStencilPro-Black.eot");
  src: url("//external-orstedcdn.azureedge.net/shared/fonts/ea/ceraprojv/v1/CeraStencilPro-Black.eot?#iefix")
      format("embedded-opentype"),
    url("//external-orstedcdn.azureedge.net/shared/fonts/ea/ceraprojv/v1/CeraStencilPro-Black.woff2")
      format("woff2"),
    url("//external-orstedcdn.azureedge.net/shared/fonts/ea/ceraprojv/v1/CeraStencilPro-Black.woff")
      format("woff"),
    url("//external-orstedcdn.azureedge.net/shared/fonts/ea/ceraprojv/v1/CeraStencilPro-Black.ttf")
      format("truetype");
}

