.accordion {
  @include flex-1;
  padding: $module-space-vertical-bottom $module-space-horisontal-left
    $module-space-vertical-bottom $module-space-horisontal-right;
  // background-color: $color-white;
  .accordion-header {
    // padding: $spacing-large 0 ($spacing-large+$spacing-large);
    padding-bottom: $module-space-vertical-bottom;
    font-family: $font-family-bold;

    @include asian-fonts;
    @include asian-fontweight-bold;
    font-size: 20px;
  }

  .accordion-items {
    list-style: none;
    padding: 0;
    margin: 0 (-$spacing-large);
  }

  .accordion-items-item {
    border-top: 1px solid rgba(0, 0, 0, 0.2);

    &.is-open {
      .accordion-item-trigger {
        color: $color-white;

        &:before {
          color: $color-white;
          transform: rotate(180deg);
        }
      }
    }
    // render other colors than theme-colors when not open.
    &:not(.is-open) {
      .accordion-item-trigger {
        background-color: inherit;

        &:not(:hover) {
          color: $color-text;
        }

        &:hover {
          &:before {
            color: $color-dark-blue;
          }
        }
      }
    }
  }

  .accordion-item-trigger {
    cursor: pointer;
    position: relative;
    display: block;
    padding: $spacing-regular ($spacing-large + 20) $spacing-regular
      $spacing-large;
    @extend .theme-color-secondary;
    @include icon("arrow-down");

    &:before {
      position: absolute;
      right: $spacing-large;
      top: 16px;
      font-size: 10px;
      color: $color-backdrop-1;
      transform-origin: center;
      transition: transform $transition-duration ease-in-out;
    }
  }

  .accordion-item-expanded {
    display: none;
    // Removed transition since it blocking jquery animation
    // transition: height $transition-duration ease-in-out;
    overflow: hidden;
    color: $color-dark-blue;

    &.multi-open {
      display: block;
    }
  }

  .is-open {
    .accordion-item-expanded {
      display: block;
    }
  }

  .accordion-item-expanded-inner {
    padding: 20px $spacing-large;
    position: relative;
  }

  &.theme-backdrop-2 {
    .accordion-header {
      color: $color-aubergine;
    }

    .is-open .accordion-item-trigger {
      background-color: $color-aubergine;
    }
  }
}
