.headerClassic {
  .header-link {
    .site-theme-radius & {
      font-family: $font-family-regular-radius;
      font-size: $text-size-xsmall-radius;
    }
  }
  .header-link-tab-style {
    &.header-link-active {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
      }
    }
  }
}
