.customlogin {
  padding-left: 30px;
  padding-right: 30px;
  .error {
    color: #d9534f;
    padding-top: 7px;
  }
  .show-password {
    position: absolute;
    top: 10px;
    right: 40px;

    &.is-active {
      display: inline-block;
    }
  }

  .has-danger,
  .has-success {
    .show-password {
      right: 65px;
    }

    .form-control-help-bs-tooltip.form-control-help-inline {
      //right: 15px !important;
      margin-right: 25px;
    }
  }
  span.chkrememberMe {
    display: inline-block;
    height: 16px;
    width: 16px;
    position: relative;
    border: solid 1px gray;
    top: 2px;
    margin-right: 4px;
    border-radius: 3px;
  }
  i.icon.icon-check-mark {
    display: none;
    top: -5px;
    position: absolute;
    right: -1px;
    margin-left: 0;
    color: #3b4956;
    height: 15px;
    width: 15px;
  }
  .form {
    margin-left: auto;
    margin-right: auto;
  }

  .form .form-group input[type="checkbox"]:checked ~ i {
    display: inline-block;
  }
  .form .form-check .check-box {
    display: inline;
    z-index: 100;
    opacity: 0;
    position: absolute;
  }
}
