.site-theme-radius {
  .login {
    .login-container {
      .btn-dropdown {
        padding-right: 23px;
        background-color: $color-radius-turquoise !important;
        min-width: 0;

        &::before {
          content: initial;
        }
      }
    }
  }
}
.login {
  .popover-title {
    .site-theme-radius & {
      background-color: $color-radius-blue;
    }
  }
  .popover-content {
    .accordion {
      .accordion-item-trigger {
        .site-theme-radius & {
          font-family: $font-family-bold-radius;
        }
      }
      .accordion-items-item.is-open {
        .site-theme-radius & {
          .theme-base-color-background {
            background-color: $color-radius-turquoise;
          }

          .theme-color-accent {
            color: $color-white;
          }
        }
      }
    }
  }
}
