.videoBox {
  @at-root .modal.fade &__modal-dialog {
    width: 100%;
    max-width: 100%;
    height: 100%;
  }

  &__modal-video {
    background-color: #000000;
    width: 100%;
    height: 100%;
  }

  &__modal-close {
    position: absolute;
    top: 30px;
    right: 20px;
    padding: 0;
    cursor: pointer;
    background: transparent;
    border: 0;
    outline: 0;
    width: 40px;
    height: 40px;
    overflow: hidden;

    font-size: 60px;
    line-height: 40px;
    font-weight: 300;
    color: #ffffff;
    font-family: "Orsted sans";
    text-shadow: none;

    &:focus {
      outline: none;
    }
  }
}
