.listFilters {
  padding: 10px;
  @include media-breakpoint-up(md) {
    padding: 0;
    margin-bottom: $module-space-vertical-top !important;
  }
  .form {
    max-width: none;
  }
  .listFilters-form-inner {
    display: flex;
    @include flex-1;
    flex-direction: column;
    align-items: flex-end;
    @include media-breakpoint-up(md) {
      flex-direction: row;
    }
  }
  .form {
    @include flex-1;
    flex-direction: column;
  }
  .form-col {
    display: none;
    justify-content: center;
    @include flex-1;
    @include media-breakpoint-up(md) {
      display: block;
      &:first-of-type {
        .form-group {
          margin-left: $module-space-horisontal-left;
        }
      }
      &:last-of-type {
        .form-group {
          margin-right: $module-space-horisontal-left;
        }
      }
    }
  }

  .form-group {
    display: flex;
    @include flex-1;
    flex-direction: column;
    margin-left: $module-space-horisontal-left/2;
    margin-right: $module-space-horisontal-left/2;
    legend {
      width: 100%;
    }
  }

  .listFilters-search {
    display: flex;
    @include flex-1;
    flex-direction: row;
    align-items: center;

    .form-group {
      display: flex;
      @include flex-1;
      justify-content: flex-end;
    }
    .form-search {
      input {
        @include media-breakpoint-up(md) {
          max-width: 60%;
        }
      }
    }
    @include media-breakpoint-down(sm) {
      align-items: center;
      justify-content: flex-start;
      .form-group {
        @include flex-1;
        margin-top: $module-space-horisontal-left/2;
        margin-left: 0;
      }
    }
  }

  .listFilters-mobileTrigger {
    font-size: 1.5em;
    cursor: pointer;
    display: none;
    @include media-breakpoint-down(sm) {
      display: flex;
      justify-content: center;
      .listFilters-mobileTrigger-closed {
        display: block;
      }
      .listFilters-mobileTrigger-open {
        display: none;
      }
    }
  }

  .listFilters-mobile-apply {
    display: none;
    justify-content: flex-end;
    .form-group {
      flex: 0 auto;
    }
  }
  @include media-breakpoint-down(sm) {
    &.listFilters-mobile-open {
      position: fixed;
      margin-bottom: 0;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      // background-color: $color-white;
      z-index: 100;
      display: flex;
      align-items: flex-end;
      &:before {
        content: "";
        display: block;
        position: fixed;
        background-color: rgba(#000, 0.7);
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        z-index: -1;
      }
      .listFilters-form-inner {
        justify-content: flex-start;
      }

      .form {
        z-index: 1;
        padding-bottom: 60px;
        padding-top: 60px;
        background-color: $color-white;
        justify-content: flex-end;
        position: absolute;
        // top:0;
        right: 0;
        bottom: 0;
        left: 0;
        // height: 100vh;
        overflow: auto;
      }

      .form-col {
        display: flex;
        flex: 0 0 auto;
      }

      .form-group {
        margin-left: $module-space-horisontal-left/2;
      }
      .listFilters-search {
        position: static;
        .form-group {
          padding: 0;
          margin: 0;
        }
      }
      .form-search {
        display: none;
      }

      .listFilters-mobileTrigger {
        position: absolute;
        top: $module-space-horisontal-left/2;
        right: $module-space-horisontal-left/2;
        .listFilters-mobileTrigger-closed {
          display: none;
        }
        .listFilters-mobileTrigger-open {
          display: block;
        }
      }
      .listFilters-mobile-apply {
        display: flex;
        @include flex-1;
        margin: auto;
      }

      @media screen and (orientation: landscape) {
        .form {
          z-index: 1;
          padding-bottom: 10px;
          padding-top: 60px;
          background-color: $color-white;
          justify-content: flex-end;
          position: absolute;
          // top:0;
          right: 0;
          bottom: 0;
          left: 0;
          // height: 100vh;
          overflow: auto;
        }
        .listFilters-mobileTrigger {
          position: absolute;
          top: 45px;
          right: $module-space-horisontal-left/2;
          .listFilters-mobileTrigger-closed {
            display: none;
          }
          .listFilters-mobileTrigger-open {
            display: block;
          }
        }
        .listFilters-mobile-apply {
          display: flex;
          @include flex-1;
          margin: auto;
          margin-top: 10px;
        }

        .form-group {
          margin-left: $module-space-horisontal-left/2;
          margin: 0;
          margin-top: 10px;
        }
      }
    }
  }
}
.list-filters-wrapper {
  @include media-breakpoint-down(sm) {
    display: inline-flex;
    width: 100%;
    -webkit-border-radius: 41px;
    border-radius: 41px;
  }

  .list-filters-text {
    display: none;

    @include media-breakpoint-down(sm) {
      display: inline-flex;
      margin: auto;
    }
  }
}
