.multiteaser {
  padding: 48px 120px;

  .align-6-columns-center-container {
    width: 100%;
    margin-left: auto;
    margin-right: auto;

    @media (min-width: 1440px) {
      max-width: 720px;
    }

    @media (max-width: 1439px) {
      max-width: 50vw;
    }

    @include media-breakpoint-down(md) {
      padding-left: 0;
      max-width: 100%;
    }
  }

  .align-text-component {
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
  }

  .communication-toolbox.headerSliderCorporate ~ main & {
    padding-left: 0;
    padding-right: 0;
  }

  a:hover {
    background-color: unset !important;
    color: unset !important;
  }

  @include media-breakpoint-down(md) {
    padding: 32px 8.33333%;
  }
}

.section .row.row-widewidth,
.section .row.row-narrow {
  .multiteaser {
    padding-top: 48px;
    padding-bottom: 48px;
  }
}

.section .wide-container {
  .multiteaser {
    @include media-breakpoint-down(sm) {
      padding: 32px 27px;
    }
  }
}

.narrow-container {
  .multiteaser {
    padding: 48px 0;

    @include media-breakpoint-down(sm) {
      padding: 32px 27px;
    }
  }
}

.multiteaser__item-image {
  padding: 0 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background-position: 50%;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  padding-top: 100%;
  max-height: 100%;
  max-width: 100%;
  border-radius: 50%;
  margin: 0 auto 0 auto;
  @include image-scale-up-transition;
  &:hover {
    @include image-scale-up;
  }
}
.multiteaser__item-image--square {
  border-radius: 0;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  z-index: -1;
  position: absolute;
  padding-top: 0;
}
.multiteaser__item-image-container--square {
  overflow: hidden;
  height: 0;
  width: 100%;
  padding-bottom: 56.25%;
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  margin-bottom: 16px;
}

.multiteaser__video {
  margin-bottom: 24px;

  @include media-breakpoint-down(sm) {
    margin-bottom: 16px;
  }
}

.multiteaser__item-image-container {
  flex-basis: auto;
  margin: 0 auto 24px auto;

  & + .multiteaser__item-link {
    margin-top: 0;
  }

  @include media-breakpoint-down(sm) {
    margin-bottom: 16px;
  }
}

.multiteaser__item-image-container--non-clickable {
  pointer-events: none;
}

.multiteaser__component-header {
  font-family: $font-family-bold !important;
  margin-bottom: 24px;

  @include media-breakpoint-down(sm) {
    margin-bottom: 16px;
  }
}

.multiteaser__items {
  justify-content: space-between;
  margin: 0 -10px 0 -10px;
  .multiteaser--5-items.multiteaser--square &,
  .multiteaser--4-items.multiteaser--square &,
  .multiteaser--3-items &,
  .multiteaser--2-items & {
    margin: 0 -16px 0 -16px;
  }
}
.multiteaser__item {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0 10px;
  .multiteaser--5-items.multiteaser--square &,
  .multiteaser--4-items.multiteaser--square &,
  .multiteaser--3-items &,
  .multiteaser--2-items & {
    padding: 0 16px;
  }

  .align-link-bottom {
    margin-top: auto;
  }
}
.multiteaser__item--center {
  text-align: center;
}
.multiteaser__item-header {
  font-family: $font-family-bold !important;
  margin-bottom: $body-margin;

  &.body {
    font-size: $text-size-body;
    line-height: $line-height-body;
  }

  @include media-breakpoint-down(sm) {
    margin-bottom: 8px;
  }
}
.multiteaser__item-paragraph {
  font-family: $font-family-regular;
  margin-bottom: $body-margin;

  &.body {
    font-size: $text-size-body;
    line-height: $line-height-body;
  }

  p {
    font-family: $font-family-regular;
    margin-bottom: $body-margin;
  }
  p:last-child {
    margin-bottom: 0;
  }

  @include media-breakpoint-down(sm) {
    margin-bottom: 8px;
  }
}
.multiteaser__item-link {
  display: inline-block;
  //styleName: Body / Bold;
  font-family: $font-family-bold;
  font-size: $text-size-body;
  line-height: 24px;
  color: $color-blue;
  justify-self: flex-end;

  &:not(.teaser-btn) {
    padding-right: 0;
    margin-right: 0;
  }
  &:after {
    padding-right: 0;
  }

  @include media-breakpoint-down(sm) {
    font-size: $text-size-body;
    line-height: 24px;
    margin-bottom: 16px;
  }
}

@include media-breakpoint-down(sm) {
  .multiteaser__item-image-container:not(.multiteaser__item-image-container--square) {
    flex: 0 0 50%;
    max-width: 50%;
    position: relative;
    width: 100%;
    min-height: 1px;
  }
}

@include media-breakpoint-up(md) {
  .multiteaser--5-items {
    .multiteaser__items {
      &:after {
        content: "";
        flex: auto;
      }
    }
  }
}
@include media-breakpoint-up(lg) {
  .multiteaser--5-items {
    &.multiteaser--square {
      .col-lg-3 {
        flex: 0 0 20%;
        max-width: 20%;
      }
    }
    .multiteaser__items {
      &:after {
        content: none;
        flex: auto;
      }
    }
  }
}
