.article {
  .article-headline {
    .site-theme-radius & {
      @include h1;
      font-family: $font-family-semi-bold-radius !important;
      @include text($text-size-xlarge-radius, $line-height-xlarge-radius, 0.5);
      margin-bottom: 20px;
      color: $color-radius-darkgrey;
    }
  }
  .article-subheadline {
    .site-theme-radius & {
      @include h2;
      font-family: $font-family-light-radius !important;
      @include text($text-size-large-radius, $line-height-large-radius);
      margin-bottom: 20px;
      color: $color-radius-blue;
    }
  }
  .article-excerpt {
    .site-theme-radius & {
      font-family: $font-family-regular-radius !important;
      @include text($text-size-xsmall-radius, $line-height-xsmall-radius);
    }
  }
  .article-content {
    &.site-theme-radius:not(.accordion-item-expanded) {
      font-family: Arial;
    }
  }
}
.news-module {
  .news-headline {
    .site-theme-radius & {
      color: $color-radius-blue;
      font-family: $font-family-semi-bold-radius;
      @include text($text-size-xsmall-radius, $line-height-xsmall-radius);
    }
  }
  .news-list-item {
    .news-list-item-date,
    .news-list-item-text {
      .site-theme-radius & {
        font-size: 15px;
        color: $color-radius-darkgrey;
        font-family: $font-family-regular-radius;
      }
    }
    &.highlighted {
      .news-list-item-text {
        .site-theme-radius & {
          font-size: 20px;
          line-height: 25px;
        }
      }
    }
  }
}
