@mixin ie10 {
  @media screen and (min-width: 0\0) {
    @content;
  }
}

@mixin flex-1 {
  flex: 1;
  flex-basis: initial; // Added to ensure that components aren't hidden behind the element below
  @include ie10 {
    flex: 1 0 auto;
  }
}

@mixin flex-2 {
  flex: 2;
  @include ie10 {
    flex: 2 0 auto;
  }
}

@mixin align-with-text {
  width: 100%;
  max-width: $legible-text-max-width;
  margin-left: auto;
  margin-right: auto;
  overflow: visible;
}

@mixin absoluteCenter {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin component-padding($breakpoint: lg) {
  padding: 0 $module-space-horisontal-left;

  @include media-breakpoint-up($breakpoint) {
    padding: 0 $component-space;
  }
}

@mixin background-image {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

@mixin aspect-ratio-16-9 {
  overflow: hidden;
  position: relative;
  height: 0;
  width: 100%;
  padding-bottom: 56.25%;
  display: table;
}

@mixin text-background-gradient {
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.6) 100%
  );
}

@mixin absolute-expand {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@mixin teaser-arrow-color($color) {
  .horizontalteaser-link-arrow {
    color: $color !important;
  }
}

@mixin link-inline-color($color) {
  .link-inline {
    color: $color;
    border-color: $color;

    &:hover {
      color: $color;
    }
  }
}

@mixin link-arrow-color($color) {
  &.link-arrow,
  .richtext {
    .link-arrow {
      color: $color;
      &:hover {
        color: $color;
      }
    }
  }
  .link-arrow {
    color: $color;
    &:hover {
      color: $color !important;
    }
  }
}

@mixin image-scale-up-transition {
  transition: transform 5000ms $easing-smooth;
}

@mixin image-scale-up {
  transform: scale(1.02);
}

@mixin clearFix {
  &:after {
    visibility: hidden;
    display: block;
    font-size: 0;
    content: " ";
    clear: both;
    height: 0;
  }
}

@mixin list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  overflow: hidden;
}

@mixin list-inline {
  list-style-type: none;
  padding: 0;
  margin: 0;
  overflow: hidden;

  > li {
    display: inline-block;
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }

    html[dir="rtl"] & {
      margin-right: 0;
      margin-left: 10px;

      &:last-child {
        margin-left: 0;
      }
    }
  }
}

@mixin absoluteExpand {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

@mixin media-desktop {
  @include media-breakpoint-up(md) {
    @content;
  }
}
