$ff-item-space: 20px;
$ff-item-space-1: 25px;
$ff-item-space-2: 17px;
$ff-item-space-3: 30px;
$ff-item-height-large: 670px;
$ff-item-height-small: 435px;

.ff-items {
  width: 100%;
  &.ff-items--left {
    .ff-item-video {
      &.embed-responsive {
        overflow: visible;
        display: flex;
        flex-basis: 100%;
      }
    }
  }
  .ff-item-video {
    .vjs-big-play-button {
      display: none;
    }
  }
}

.ff-items--left {
  padding: 0 $ff-item-space-2;
}

.ff-items--right {
  padding: 0 $ff-item-space-2;
}

.ff-item {
  margin-bottom: $ff-item-space-3;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.ff-item__tag,
.ff-item__date {
  display: block;
  font-size: 14px;
}
.ff-items--left {
  .ff-item-video + .ff-item__image-placeholder {
    display: none;
  }
}
.ff-items--right {
  .ff-item-video {
    display: none;
  }
}

.ff-items-mobile {
  .ff-item-video + .ff-item__image-placeholder {
    display: none;
  }
}

.ff-item__tag,
.ff-item__header {
  font-family: $font-family-bold !important;
}

.ff-item__date {
  margin-bottom: $ff-item-space;
}

.ff-item__tag + .ff-item__header {
  margin-top: $ff-item-space;
}

.ff-item__header {
  .isIe11 & {
    -ms-font-feature-settings: "kern" 1;
  }
  margin-bottom: $ff-item-space;
}

.ff-item__text {
  padding: $ff-item-space-1 $ff-item-space;
}

.ff-item__paragraph {
  width: 100%;
  display: none;
  flex: 1 0 0;
  align-self: stretch;
  max-height: unset;
  .isIe11 & {
    -ms-font-feature-settings: "kern" 1;
  }
}

.link-arrow--bottom {
  margin-bottom: $ff-item-space-1;
  margin-right: 0;
  padding-right: 0;
}
.ff-item--right .ff-item__text {
  .link-arrow--bottom {
    left: 0;
    margin-left: 0;
  }
}

.btn.btn--center {
  margin: 10px auto 60px auto !important;
  align-self: center;
}

// should be changed to img element later
.ff-item__image-placeholder {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  overflow: hidden;
  position: relative;
  height: 0;
  width: 100%;
  padding-bottom: 56.25%;
  position: relative;
  @include image-scale-up-transition;
  &:hover .ff-item__image {
    @include image-scale-up;
  }
  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}
.hide-on-mobile {
  display: none;
}

// should be changed to img element later
.ff-item__image {
  position: absolute;
  @include image-scale-up-transition;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  transform: scale(1);
  z-index: -1;

  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.btn-list {
  padding: 0 0 $ff-item-space-2 $ff-item-space-3;
  .btn {
    font-size: 14px !important;
    padding: 8px 13px;
    margin: 3px 1px 3px 1px;
  }
}
.btn--cursor-default:hover {
  cursor: pointer;
}
.btn.btn--inactive {
  background-color: $color-btn-inactive !important;
  color: $color-text !important;
  &:hover {
    color: $color-text !important;
  }
}

/* @TODO add styling when the item is hidden */
/* Double selector to increase specificity */
.ff-item.ff-item-hide {
  display: none;
}

/* @TODO specific modifier for filter function button */
.ff__btn__loadmore {
}

/* @TODO hide this button */
/* Double selector to increase specificity */
.ff__btn__loadmore.ff__btn__loadmore--hide {
  display: none;
}

.ff-outer-spacing {
  margin: 0 0 $ff-item-space-2 $ff-item-space-3;
}

.ff-dropdown-button {
  cursor: pointer;
  font-size: 14px !important;
  padding: 7px 18px 8px 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  .ff-dropdown-button__text {
    &:last-of-type {
      display: none;
    }
  }

  .icon {
    margin-left: 15px;
    font-size: 19px !important;
  }
}

.ff-dropdown-button--active {
  .icon {
    display: none;
  }
  .ff-dropdown-button__text {
    &:first-child {
      display: none;
    }
    &:last-of-type {
      display: inline;
    }
  }
}

.ff-tag-list__button {
  text-align: left;
  font-family: $font-family-bold !important;
  font-size: $text-size-body-small;
  background: white;
  border: 0;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

.ff-tag-list {
  display: none;
  list-style: none;
  padding: 0;
  overflow: hidden;
  li {
    padding: 0;
    margin: 0 0 15px 0;
    list-style: none;
  }
}

.ff-tag-list__button--selected {
  color: $color-blue;
}

@include media-breakpoint-between(md, xl) {
  .ff-item__paragraph {
    overflow: hidden;
  }
}

@include media-breakpoint-up(lg) {
  .hide-on-desktop {
    display: none;
  }

  .ff-item__paragraph {
    display: block;
  }
  .hide-on-mobile {
    display: block;
  }
  .ff-items--left {
    padding-right: $ff-item-space-2;
    padding-left: 0;
    flex: 1 1 0px;
    max-width: 50%;
  }

  .ff-items--right {
    padding-right: 0;
    padding-left: $ff-item-space-2;
    flex: 1 1 0px;
    max-width: 50%;
  }

  .ff-item {
    overflow: hidden;
    position: relative;
    margin-bottom: $ff-item-space;
    justify-content: flex-start;
    height: $ff-item-height-large;
    margin-bottom: 34px;
  }

  .ff-item-columns {
    display: flex;
    padding: 0 60px;
  }
  .ff-items--right .ff-item {
    height: $ff-item-height-small;
    flex-direction: row;
    min-height: auto !important;
    display: flex;
  }

  .ff-items--right .ff-item__text {
    width: 50%;
    padding: $ff-item-space-1 $ff-item-space-1 70px;
  }

  .ff-item__text {
    padding-bottom: 70px;
    transition: right 0.2s ease-in-out;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .slide-right {
    right: -40% !important;
  }
  .ff-items--right .ff-item__image-placeholder {
    height: 100% !important;
    padding-bottom: 100%;
    flex: 0 0 50%;
    max-width: 50%;
  }
  .ff-items--right .ff-item__image {
    height: 435px !important;
  }
  .link-arrow--bottom,
  .btn--bottom {
    position: absolute;
    bottom: $ff-item-space-1;
    margin: 0;
  }

  .btn-list {
    padding: $ff-item-space-1 68px;
  }
}

/* Separating mobile view and desktop with 2 container  */

.ff-items-mobile {
  display: block;
  padding: 0 $ff-item-space-3;
}

.ff-items-desktop {
  display: none;
}

@include media-breakpoint-up(lg) {
  .ff-items-mobile {
    display: none;
  }

  .ff-items-desktop {
    display: block;
  }

  .ff-item-columns.ff-items-desktop {
    display: flex;
  }

  /* @TODO add styling when the item is hidden */
  /* Double selector to increase specificity */
  .ff-item.ff-item-hide {
    display: none;
  }
}
