.multiNarrative {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  flex-grow: 1;
  min-height: 220px;
  overflow: hidden;
  position: relative;
  background-color: #d8d1ca;

  .narrative-text {
    @include flex-1;

    p {
      margin-bottom: 1rem;
    }
  }

  .narrative-text,
  .narrative-cta {
    padding-top: 35px;
  }

  .narrative-content {
    padding: 0 0 rem(33px) 0;
    display: flex;
    flex-direction: column;
    height: auto;
    background: none !important;
    @include flex-1;
  }

  [data-narrative-content] {
    opacity: 1;
    height: auto;
    transition: opacity $transition-duration * 1.5 ease-in-out;
  }

  .narrative-media-track,
  .narrative-content-track {
    overflow: hidden;
    display: flex;
    flex: 1 0 auto;
    flex-direction: row;
    height: auto;
    transition: transform 900ms $easing-smooth 20ms;
  }

  .narrative-media-track {
    transition: transform 900ms $easing-smooth;
  }

  .narrative-media-track {
    width: 300%;
  }

  .narrative-body {
    justify-content: flex-start;
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    height: 100%;
    overflow: hidden;
    flex: 1;
    padding: 30px;
  }

  .narrative-picker {
    width: 100%;
    //margin-top: 35px;
    .narrative-option.swiper-slide {
      width: 100%;
      height: auto;
      text-align: center;
      cursor: pointer;

      .option-img {
        border-radius: 50%;
        height: 0;
        width: 50%;
        padding-bottom: 50%;
        margin: 0 auto;
        overflow: hidden;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;

        @include media-breakpoint-up(lg) {
          width: 80%;
          padding-bottom: 80%;
        }

        transition: box-shadow $transition-duration linear;

        &.is-icon {
          background-size: 40%;
          background-color: $color-white;
        }
      }

      &:hover {
        .option-img {
          box-shadow: inset 0 0 0 5px $color-aubergine;
        }
      }

      &.swiper-slide-active {
        .option-img {
          box-shadow: inset 0 0 0 10px $color-aubergine;
        }
      }
    }

    .swiper-button-prev {
      left: 20px;
      //TODO: insert correct arrow when we have it in svg.
    }

    .swiper-button-next {
      right: 20px;
      //TODO: insert correct arrow when we have it in svg.
    }

    .swiper-button-next,
    .swiper-button-prev {
      background-color: white;
      height: 40px;
      width: 40px;
      border-radius: 50%;
      background-size: 15px 15px;
      margin-top: -60px;

      @include media-breakpoint-up(md) {
        height: 60px;
        width: 60px;
      }
    }

    .swiper-button-disabled {
      opacity: 0.5;
    }

    @include media-breakpoint-up(lg) {
      .swiper-button-disabled {
        display: none;
      }
    }
  }

  .link-arrow {
    color: #445362;
    font-family: $font-family-bold !important;

    body#lang-zh & {
      font-family: "Noto Sans TC", sans-serif !important;
      font-weight: bold;
    }

    body#lang-ja & {
      font-family: "Noto Sans Japanese", sans-serif !important;
      font-weight: bold;
    }

    body#lang-ko & {
      font-family: "Noto Sans KR", sans-serif !important;
      font-weight: bold;
    }

    letter-spacing: 0.2px;

    &:hover {
      color: #445362 !important;
    }
  }

  .option-title {
    font-size: 15px;
    padding-top: 15px;
    padding-right: 10px;
    padding-left: 10px;
  }

  @include media-breakpoint-up(sm) {
    .narrative-picker .narrative-option.swiper-slide {
      .swiper-button-next,
      .swiper-button-prev {
        margin-top: -60px;
      }
    }
  }

  @include media-breakpoint-up(md) {
    flex-direction: row;

    .narrative-media {
      @include aspect-ratio-16-9;
      height: auto;
      padding-bottom: 0;
      display: flex;
      flex: 1 0 50%;
      max-width: 50%;
    }

    .narrative-img {
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      height: auto;
      flex: 1;
      transition: opacity $transition-duration * 3 linear;
    }

    .narrative-content {
      padding-left: 0;
      padding-right: 0;
      flex: 1 0 50%;
      max-width: 50%;
    }

    &.narrative-image-right {
      flex-direction: row-reverse;

      .narrative-link {
        left: 0;
        right: 50%;
      }

      .narrative-content {
        padding-left: 30px;
      }
    }

    .swiper-container {
      width: 100%;
    }
  }
  //end media-breakpoint-up(md)
}
