$infographic-font-size-1: 1.35vw;
$infographic-font-size-2: 6.25vw;
$infographic-font-size-3: 4.167vw;
$infographic-font-size-4: 2.778vw;
$infographic-font-size-5: 1.736vw;
$infographic-font-size-6: 1.111vw;

$infographic-line-height-1: 1.3888888888888888vw;
$infographic-line-height-2: 6.667vw;
$infographic-line-height-3: 5vw;
$infographic-line-height-4: 3.333vw;
$infographic-line-height-5: 2.222vw;
$infographic-line-height-6: 1.528vw;
$infographic-line-height-7: 1.389vw;

$infographic-small: 1.389vw;
$infographic-space: 1.319vw;
$infographic-teaser-space-1: 0.694vw;
$infographic-teaser-space-2: 0.208vw;
$infographic-teaser-space-3: 3.472vw;
$infographic-teaser-space-4: 0.347vw;
$infographic-teaser-space-5: 2.1vw;
$infographic-teaser-space-6: 4.861vw;

$infographic-teaser-space-right: 2.083vw;

$infographic-teaser-btn-space: 0.764vw;
$infographic-teaser-btn-padding: $infographic-teaser-space-3
  $infographic-teaser-space-right $infographic-teaser-space-right;

.col-12:not(.col-md-6) > .teaser:not(.teaser-horisontal) {
  @include media-breakpoint-up(md) {
    .teaser-body {
      margin-top: 100px;
    }
  }
}

.teaser {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  flex-grow: 1;
  min-height: 220px;
  overflow: hidden;
  position: relative;

  &:not(.teaser-horisontal) {
    padding: 0px 40px;

    .teaser-media {
      &::before {
        position: absolute;
        height: 100%;
        width: 100%;
        content: "";
        z-index: 1;
        // make ent behind text when an image is present.
        transition: background $transition-duration * 4 $easing-smooth;
        background: linear-gradient(
          to bottom,
          rgba(0, 0, 0, 0.1) 0%,
          rgba(0, 0, 0, 0.4) 100%
        );
        background-position-y: 0%;
        background-size: 100%;
      }

      &.no-gradient::before {
        background: transparent;
      }
    }
  }

  .teaser-content {
    align-items: stretch;
  }

  .teaser-svg-img {
    @include image-scale-up-transition;
    position: absolute;
    width: 100%;
    height: auto;
  }

  @include media-breakpoint-down(sm) {
    .teaser-svg-img {
      min-width: 100%;
      min-height: 100%;
      height: auto;
    }

    padding-left: 27px;
    padding-right: 27px;
  }

  @include media-breakpoint-up(md) {
    &:not(.teaser-media-video) {
      min-height: 440px;
    }
  }

  &.infographic {
    .teaser-media {
      height: auto;
      width: 100%;
      padding-bottom: 0;

      .infographic__image {
        width: 100%;
        @include image-scale-up-transition;

        &:hover {
          @include image-scale-up;
        }
      }
    }

    &.teaser-media-video .teaser-media {
      padding-bottom: 56.25%;
    }
  }

  @include media-breakpoint-up(md) {
    &.infographic {
      min-height: auto;

      &.teaser-minimised {
        min-height: auto;
      }

      .teaser-content {
        .link-arrow {
          font-size: $infographic-font-size-1;
          line-height: $infographic-line-height-1;
        }
      }

      .infographic__video {
        margin-bottom: -1px;
      }

      .teaser-tagline {
        padding-bottom: $infographic-small;
        font-size: $infographic-space !important;
        line-height: $infographic-small !important;
      }

      &.teaser-media-video .teaser-media {
        padding-bottom: 0;
      }

      .teaser-content {
        .teaser-btn {
          padding: $infographic-teaser-btn-space $infographic-space;
          margin-top: $infographic-teaser-space-1;
          margin-bottom: $infographic-teaser-space-2;
          font-size: $infographic-space !important;
          line-height: $infographic-small !important;

          &:hover {
            padding-right: $infographic-teaser-space-right;
          }
        }

        padding: $infographic-teaser-btn-padding;
      }

      .richtext {
        h1,
        h2,
        h3,
        h4 {
          margin-bottom: $infographic-teaser-space-4;

          &.teaser-tagline-minimised {
            margin-bottom: $infographic-teaser-space-6;
            line-height: $infographic-line-height-7;
          }
        }
      }

      .teaser-body {
        margin-bottom: $infographic-teaser-space-5;
        margin-top: 0;
      }

      .teaser-body-minimised {
        margin-bottom: $infographic-teaser-space-right;
      }

      h1,
      h1 p {
        font-size: $infographic-font-size-2;
        line-height: $infographic-line-height-2;
      }

      h2,
      h2 p {
        font-size: $infographic-font-size-3;
        line-height: $infographic-line-height-3;
      }

      h3,
      h3 p {
        font-size: $infographic-font-size-4;
        line-height: $infographic-line-height-4;
      }

      h4,
      h4 p {
        font-size: $infographic-font-size-5;
        line-height: $infographic-line-height-5;
      }

      p,
      span.teaser-headline {
        font-size: $infographic-font-size-6;
        line-height: $infographic-line-height-6;
      }

      span.teaser-headline-accent,
      span.teaser-headline-accent p {
        font-size: $infographic-font-size-6;
      }
    }
  }

  @media (min-width: $site-width) {
    &.infographic {
      &.teaser-minimised {
        min-height: 370px;
      }

      .teaser-tagline {
        padding-bottom: 20px;
        font-size: 1.1875rem !important;
        line-height: 1.25rem !important;
      }

      .teaser-content {
        .teaser-btn {
          padding: 0.6875rem 1.1875rem;
          margin-top: 0.625rem;
          margin-bottom: 3px;
          font-size: 1.1875rem !important;
          line-height: 1.25rem !important;

          &:hover {
            padding-right: 30px;
          }
        }

        .link-arrow {
          font-size: 1.1875rem;
          line-height: 1.25rem;
        }

        padding: 50px 30px 30px 30px;
      }

      .richtext {
        h1,
        h2,
        h3,
        h4 {
          margin-bottom: 5px;

          &.teaser-tagline-minimised {
            margin-bottom: 70px;
            line-height: 20px;
          }
        }
      }

      .teaser-body,
      .teaser-body-minimised {
        p,
        span.teaser-headline,
        span.teaser-headline p {
          font-size: 16px;
          line-height: 22px;
        }

        h1,
        h1 p {
          @include text($text-size-xlarge, $line-height-xlarge, 0.5);
        }

        h2,
        h2 p {
          @include text($text-size-large, $line-height-large);
        }

        h3,
        h3 p {
          @include text($text-size-medium, $line-height-medium);
        }

        h4,
        h4 p {
          @include text($text-size-small, $line-height-small, 0.9);
        }
      }

      .teaser-body {
        margin-bottom: 31px;
      }

      .teaser-body-minimised {
        margin-bottom: 30px;
      }
    }
  }

  > * {
    position: relative;
    z-index: 1;
  }
  /*@include media-breakpoint-down(sm) {*/
  .teaser-media {
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
  }
  /*}*/
  .teaser-img {
    @include image-scale-up-transition;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    transform: scale(1);
    z-index: -1;

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

  .teaser-tagline {
    text-transform: uppercase;
    font-family: $font-family-regular;
    font-size: $text-size-body-small;
    font-weight: 400;
    line-height: 20px;
    padding-bottom: 20px;
    letter-spacing: 0.1em;
  }

  .teaser-tagline-minimised {
    @include btnTypography;
    margin-bottom: 70px;

    @include media-breakpoint-down(sm) {
      margin-bottom: 40px;
    }
  }

  .teaser-headline-accent,
  .teaser-headline-accent > h1,
  .teaser-headline-accent > h2,
  .teaser-headline-accent > h3,
  .teaser-headline-accent > h4 {
    font-family: $font-family-bold !important;

    body#lang-zh & {
      font-weight: bold;
      font-family: "Noto Sans TC", sans-serif !important;
    }

    body#lang-ja & {
      font-weight: bold;
      font-family: "Noto Sans Japanese", sans-serif !important;
    }

    body#lang-ko & {
      font-weight: bold;
      font-family: "Noto Sans KR", sans-serif !important;
    }
  }

  .teaser-content {
    display: flex;
    flex-direction: column;
    @include flex-1;

    @include media-breakpoint-down(sm) {
      padding: $component-container-padding-mobile;
    }
  }

  &.theme-image-dark {
    .teaser-media::before {
      background: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0) 0%,
        rgba(255, 255, 255, 0.6) 100%
      );
    }
  }

  .teaser-body,
  .teaser-body-minimised {
    flex-grow: 1;
    justify-content: flex-end;
    display: flex;
    flex-direction: column;
    margin-top: 24px;

    .teaser-headline,
    .teaser-headline-accent,
    h1,
    h2,
    h3,
    h4,
    h5 {
      padding-bottom: 8px;
    }

    @include media-breakpoint-up(md) {
      .teaser-headline,
      .teaser-headline-accent,
      h1,
      h2,
      h3,
      h4,
      h5 {
        padding-bottom: 24px;
        margin: 0px;
      }

      & > h4:last-child {
        max-width: 600px;
      }

      margin-top: 40px;
    }
  }

  .teaser-body-minimised {
    flex-grow: 1;
    justify-content: flex-end;
    display: flex;
    flex-direction: column;
  }

  .teaser-date {
    font-family: $font-family-bold;

    font-size: $text-size-body-small;
  }

  .teaser-cta {
    .link-arrow {
      font-family: $font-family-bold;
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: 24px;
      letter-spacing: 0em;
      text-align: left;
    }

    > * {
      vertical-align: middle;
      margin-top: rem(10px);
      margin-right: 10px;
    }
  }

  &-btn {
    @include btnCta;
  }

  .teaser-link {
    transition: background-color $transition-duration * 3 $easing-smooth;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 2;
    cursor: pointer;
  }

  &:not(.teaser-media-video) {
    &:hover {
      .teaser-media {
        .teaser-img {
          @include image-scale-up;
        }
      }
    }
  }

  &.inverted-colors {
    .teaser-tagline,
    .teaser-tagline-minimised {
      color: #3b4956;
    }

    .teaser-headline,
    .teaser-headline-accent {
      color: #3b4956;
      max-width: 600px;
    }

    .teaser-cta {
      .link-arrow {
        color: #3b4956 !important;

        &:hover {
          color: #3b4956 !important;
        }
      }

      .btn {
        background-color: #3b4956 !important;
        color: white !important;
      }
    }

    .teaser-media:not(.no-gradient)::before {
      background: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.3) 0%,
        rgba(255, 255, 255, 0.9) 100%
      );
    }
  }

  &.teaser-minimised .teaser-content {
    padding: $component-container-padding 0;
  }
}

.teaser-minimised {
  @include media-breakpoint-up(md) {
    &:not(.teaser-media-video) {
      min-height: 370px;
    }
  }
}

.news-presentation-video {
  position: absolute;
  height: auto;
  width: auto;
  top: 0;
  min-width: 100%;
  min-height: 100%;
}

.row-widewidth {
  .teaser:not(.teaser-horisontal):not(.teaser-minimised) {
    padding: 40px 120px;

    .teaser-content {
      max-width: 840px;
    }
  }

  .teaser {
    .teaser-minimised,
    .teaser-medium {
      padding: 0px 120px 40px 120px;
    }
  }
}

.row-fullwidth {
  .teaser:not(.teaser-horisontal),
  .teaser-minimised:not(.teaser-horisontal),
  .teaser-medium:not(.teaser-horisontal) {
    padding: 40px 120px;
  }
}

.row-narrow {
  .teaser:not(.teaser-horisontal) {
    @include media-breakpoint-up(md) {
      padding: 40px !important;
    }

    .teaser-content {
      max-width: 600px;
    }
  }
}

.row-widewidth,
.row-fullwidth,
.row-narrow {
  @include media-breakpoint-down(md) {
    .teaser,
    .teaser.teaser-horisontal {
      padding: 0 !important;

      .teaser-headline,
      .teaser-headline-accent {
        margin: 0;
      }

      .teaser-content {
        padding: 24px 26px;
      }
    }
  }
}

.wide-container .col-md-6 .teaser-body {
  max-width: 480px;
}

.image {
  &--scale {
    &:hover {
      .image__element {
        @include image-scale-up;
      }
    }
  }
  &__element {
    &.infographic__image {
      position: relative;
      height: auto;
    }
    @include image-scale-up-transition;
    width: 100%;
    height: 100%;
    transform: scale(1);
    object-fit: cover;
    object-position: center center;
    position: absolute;
  }
}

.teaser-media {
  &:hover {
    .image__element {
      @include image-scale-up;
    }
  }
}
