.stepindicator {
  display: flex;
  padding: 30px;
  justify-content: center;
  overflow: hidden;
  margin-bottom: 10px;
  height: 174px; // because IE doesn't set height automatically from children
  @media screen and (max-width: 425px) {
    padding: 15px;
  }
  .stepindicator-inner {
    display: flex;
    flex-direction: row;
    flex: 1;
    justify-content: space-between;
    //max-width: 970px; // max-width caused IE to render the component roughly 950px to the right of where it should've been.
  }

  .stepindicator-step {
    display: flex; //was none
    flex-direction: column;
    position: relative;
    flex: 1;
    text-align: center;
    @media (max-width: 490px) {
      max-width: 85px !important;
      word-break: break-all;
    }
    /*$displaywidth: 570px;
        @media (min-width: $displaywidth){ //if viewport <= displaywidth, hide inactive stepindicator-steps
                display: flex;
        }*/
    &:not(:first-child):before {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      top: 35px;
      right: 50%;
      background-color: $color-backdrop-1-50;
      z-index: 0;
    }
    &:not(:last-child):after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      top: 35px;
      left: 50%;
      background-color: $color-backdrop-1-50;
      z-index: 0;
    }
    &:last-child {
      &:after {
        display: none;
      }
    }
    &.step-active {
      display: flex;
      /*@media (min-width: $displaywidth){ //if viewport <= displaywidth, show active stepindicator-step
                display: flex;
            }*/
      .stepindicator-step-number {
        width: 70px;
        height: 70px;
        margin-top: 0;
        margin-bottom: 0;
        background-color: $color-red;
      }
      .stepindicator-step-text {
        font-family: $font-family-bold;

        color: $color-red;
        @media screen and (max-width: 375px) {
          font-size: 12px;
        }
      }
    }
  }
  .stepindicator-step-content {
    flex: 1;
  }
  .stepindicator-step-text {
    margin-top: 20px;

    justify-content: flex-end;
    @media screen and (max-width: 375px) {
      font-size: 12px;
    }
  }
  .stepindicator-step-number {
    font-family: $font-family-bold;

    margin-left: auto;
    margin-right: auto;
    position: relative;
    z-index: 1;
    margin-top: 10px;
    margin-bottom: 30px;
    width: 50px;
    height: 50px;
    background-color: $color-dark-blue;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $color-white;
    border-radius: 50%;
  }
}
