.emergency-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: rgba(59, 73, 86, 0.85);

  &:not(.emergency-closer) {
    h3 {
      padding-top: 39px;
    }
  }

  .emergency-dialog {
    position: fixed;
    width: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media (min-width: 576px) {
      max-width: 500px;
      margin: 30px auto;
    }

    .modal-header {
      display: block;
      padding: 15px 15px 0 0;
      border-bottom: none;
    }

    .modal-content {
      height: 90vh;
    }

    .modal-body {
      padding: 0px 15px 15px 15px;
      .modal-body-inner {
        padding: 0px 30px 30px 30px;

        .emergency-header {
          padding-bottom: 30px;
          margin-bottom: 0px;
        }

        .emergency-body {
          padding-bottom: 30px;
        }
      }
    }
  }
}
