.richtext {
  overflow: auto;
  padding-bottom: 1px;

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-bottom: 5px;
  }

  &:not(.richtext-teaser) a {
    @extend .link-inline;
  }

  &:not(.richtext-teaser) p {
    margin-bottom: 20px;
  }

  //STYLED BULLETS
  ul,
  ol {
    &.style-red {
      li::before {
        color: $color-red;
      }
    }

    &.style-blue {
      li::before {
        color: $color-blue;
      }
    }
  }

  img {
    margin-bottom: 20px;
    height: auto;
    width: 100%;
  }

  ul,
  ol {
    padding-left: 0.725em;
    margin-left: 1.25em;

    li::before {
      display: inline-block;
      width: 1.25em;
      margin-left: -1.25em;
    }
  }

  ul {
    list-style: none;

    li::before {
      content: "";
      font-family: serif; // Samsung Internet Browser does not default to a serif font style, thus the content "" becomes to large.
    }

    &.checkmark {
      li::before {
        content: "";
        font-family: inherit; // Ensures correct font for checkmark
      }
    }
  }

  ol {
    list-style: none;
    counter-reset: li;

    li {
      counter-increment: li;
    }

    li::before {
      content: counter(li);
      text-align: center;
      direction: rtl;
    }
  }

  & .btn-btn-cta,
  & a.btn-btn-cta,
  & a > .btn-btn-cta,
  & .rt-btn-darkblue,
  & a.rt-btn-darkblue,
  & a > .rt-btn-darkblue {
    text-decoration: none;
  }
}
