.tabs {
  .tabs-tabs {
    li {
      .site-theme-radius & {
        font-size: $text-size-body-radius;
      }
      &:hover {
        .site-theme-radius & {
          font-family: $font-family-bold-radius;
        }
      }
      &.is-open {
        .site-theme-radius & {
          font-family: $font-family-bold-radius;
        }
      }
      &.tabs-tabs-more {
        > span {
          .site-theme-radius & {
            font-family: $font-family-bold-radius;
          }
        }
      }
    }
  }
}
