@mixin breadcrumbs-left {
  .breadcrumbs--left {
    padding-left: 0;
    padding-right: 0;
    margin-left: 0;
    margin-right: 0;
  }
}

.breadcrumbs-container-ov {
  z-index: 1;
  align-items: flex-start;
}

.breadcrumbs--ov {
  margin: 30px auto 0 auto;
  padding: 0;
  display: flex;
  font-family: OrstedSansRegular, sans;
  font-size: 14px;
  color: #3b4956;
  letter-spacing: 0;
  line-height: 22px;
  max-width: 700px;
  width: 100%;
}

.breadcrumb-item--ov {
  list-style: none;
  position: relative;
  padding-right: 15px;
  white-space: nowrap;
}

@include breadcrumbs-left;

.breadcrumb-item--ov:not(:first-child) {
  padding-left: 5px;
}

.breadcrumb-item--ov::after {
  content: "/";
  position: absolute;
  top: 0;
  right: 2px;
}

.breadcrumb-link--ov {
  text-decoration: none;
  color: #3b4956;
  white-space: nowrap;
}

.breadcrumb-link--ov:hover {
  color: rgb(64, 153, 218);
}

.breadcrumbs-toggle {
  cursor: pointer;
  padding: 0 8px;
}

.breadcrumbs-toggle:hover {
  color: rgb(64, 153, 218);
}

.breadcrumbs-hidden {
  font-size: 16px;
  position: absolute;
  background: #fff;
  z-index: 1;
  min-width: auto;
  max-width: 375px;
  padding: 5px 15px 5px 0px;
  top: 100%;
  left: -60px;
  transform: translateY(16px);
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  display: none;
}

.breadcrumbs-hidden:after {
  content: "";
  width: 100%;
  height: 15px;
  background-color: #fff;
  top: 0;
  left: 0px;
  z-index: 2;
  position: absolute;
}

.breadcrumbs-hidden:before {
  position: absolute;
  top: -8px;
  left: 55px;
  content: "";
  display: inline-block;
  width: 15px;
  height: 15px;
  transform: rotate(45deg);
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  background-color: #fff;
  z-index: -1;
}

.breadcrumbs-hidden li {
  padding: 15px 20px 10px 20px !important;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  float: left;
  clear: both;
}

.breadcrumbs-hidden li:after {
  top: 14px;
}

@media screen and (min-width: 769px) {
  .breadcrumbs--ov {
    margin: 40px auto 0 auto;
    flex-wrap: wrap;
  }
  @include breadcrumbs-left;
}

@media screen and (max-width: 767px) {
  .breadcrumbs--ov {
    padding: 0 30px 0 30px;
    .row.row-narrow .col-12 & {
      padding: 0;
    }
  }
  @include breadcrumbs-left;
}

.breadcrumbs-language-container {
  max-width: 700px;
  margin: auto;
  display: flex;
  justify-content: flex-end;

  .pageLanguageSwitcher {
    margin-top: -20px;
  }
}
