.stock {
  $stock-color-red: #e85757;
  $stock-color-green: #24847b;
  $stock-color-blue: #4099da;

  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding: $module-space-vertical-top $module-space-horisontal-left
    $module-space-vertical-bottom $module-space-horisontal-right;
  background: $color-white;

  .stock-header {
    @extend .label;
    font-family: $font-family-bold !important;
  }

  .stock-data-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    flex-grow: 1;
    margin-bottom: 31px;
  }

  .stock-value {
    margin-top: $spacing-large;
    font-family: $font-family-bold;
    font-size: 90px;
    line-height: 1.1;
    color: $stock-color-blue;
  }

  .stock-delta {
    margin-bottom: $spacing-medium;
    font-family: $font-family-bold;
    color: $stock-color-green;

    &.is-negative {
      color: $stock-color-red;
    }
  }

  .stock-data {
    margin-bottom: -10px;
    th {
      font-weight: normal;
    }

    td {
      padding-left: $spacing-extralarge;
    }

    .stock-index-value,
    .stock-volume-value {
      font-family: $font-family-bold;
    }

    .stock-index-delta {
      margin-left: $spacing-tiny;
      color: $stock-color-green;

      &.is-negative {
        color: $stock-color-red;
      }
    }
  }
}
