.searchTypeahead {
  margin: 0 !important;
  align-items: center;
  // border-bottom: 1px solid $color-input-border;
  input {
    position: relative;
    z-index: 1;
    font-family: $font-family-regular;

    border-radius: 20px;
  }

  button {
    position: absolute;
    left: -99999px;

    i {
      vertical-align: middle;
    }
  }

  .awesomplete,
  .twitter-typeahead .tt-menu {
    width: 100%;
    display: inline-block;
    @include btnTypography;
    font-family: $font-family-regular !important;
    font-size: 16px !important;

    body#lang-zh & {
      font-family: "Noto Sans TC", sans-serif !important;
    }

    body#lang-ja & {
      font-family: "Noto Sans Japanese", sans-serif !important;
    }

    body#lang-ko & {
      font-family: "Noto Sans KR", sans-serif !important;
    }

    &:before {
      content: "";
      //background-color: rgba(0,0,0,0.7);
      display: block;
      position: absolute;
      top: calc(100% + 20px);
      left: -20px;
      right: -20px;
      bottom: -20px;
      min-width: calc(100% + 40px);
      z-index: -1;
      transition: opacity $transition-duration * 2;
      opacity: 1;
    }

    ul,
    .tt-dataset {
      width: 100%;
      border: 1px solid $color-input-border;
      background-color: $color-white;
      border-bottom-left-radius: 20px;
      border-bottom-right-radius: 20px;
      position: relative;
      z-index: 1;

      @include media-breakpoint-down(sm) {
      }
    }

    li,
    .tt-suggestion {
      position: relative;
      z-index: 1;
      padding: 10px 15px;

      &[aria-selected="true"],
      &.tt-cursor {
        background-color: $color-body-background;
      }

      &:last-child {
        border-bottom-left-radius: 20px;
        border-bottom-right-radius: 20px;
      }

      &:hover {
        background: $color-light-grey;
        cursor: pointer;
      }
    }

    mark,
    .mark,
    strong {
      background-color: transparent;
      padding: 0;
      margin: 0;
    }

    input {
      padding-right: $spacing-large;

      &::-ms-clear {
        display: none;
      }

      &::-ms-value {
        margin-right: $spacing-medium;
      }
    }
  }

  .awesomplete-open {
    input {
      border-top-left-radius: 20px !important;
      border-top-right-radius: 20px !important;
      border-bottom-left-radius: 0 !important;
      border-bottom-right-radius: 0 !important;
      border-bottom: 0 !important;
    }
  }

  .header-search-close {
    width: 30px;
    display: inline-block;

    i {
      font-size: 0.9em;
    }
  }
}
//Specific styling for typeahead when not used in the header
.form-search {
  .twitter-typeahead {
    width: 100%;
  }
  .twitter-typeahead .tt-menu {
    width: 100%;
    display: inline-block;
    font-family: $font-family-regular;

    .tt-dataset {
      // overflow: hidden;
      width: 100%; //calc(100% - 40px); the calc messed up typeahead width
      border: 1px solid $color-input-border;
      background-color: $color-white;
      color: $color-text;
      border-bottom-left-radius: 20px;
      border-bottom-right-radius: 20px;
      border-top: 1px solid $color-input-border;
      position: absolute;
      z-index: 1;
    }

    .tt-suggestion {
      position: relative;
      z-index: 1;
      padding: 10px 15px;
      border-radius: 0;

      &:last-child {
        border-bottom-left-radius: 20px !important;
        border-bottom-right-radius: 20px !important;
      }
      padding: 10px 15px;
      &[aria-selected="true"],
      &.tt-cursor {
        background-color: $color-body-background;
      }
      &:hover {
        background: $color-light-grey;
        cursor: pointer;
      }
    }

    strong {
      background-color: transparent;
      color: $color-blue;
      padding: 0;
      margin: 0;
    }

    input {
      transition: background-color $transition-duration ease;

      &:focus {
        background-color: $color-white;
        border: 1px solid $color-input-border;
        color: $color-text;
        &::-webkit-input-placeholder {
          color: $color-text;
        }
        &:-ms-input-placeholder {
          color: $color-text;
        }
      }
    }
    input:focus::-webkit-input-placeholder {
      color: $color-text;
    }

    input:focus:-ms-input-placeholder {
      color: $color-text;
    }
  }

  .awesomplete-open {
    input {
      color: $color-text;
      border-top-left-radius: 20px !important;
      border-top-right-radius: 0 !important;
      border-bottom-left-radius: 0 !important;
      border-bottom-right-radius: 0 !important;
    }
  }
}

.awesomplete {
  mark,
  .mark {
    color: $color-blue;
  }
}
