.teaser {
  .teaser-tagline {
    .site-theme-radius & {
      font-family: $font-family-bold-radius !important;
    }
  }
  .teaser-tagline-minimised {
    .site-theme-radius & {
      font-family: $font-family-bold-radius !important;
    }
  }
  .teaser-headline-accent,
  .teaser-headline-accent > h1,
  .teaser-headline-accent > h2,
  .teaser-headline-accent > h3,
  .teaser-headline-accent > h4 {
    .site-theme-radius & {
      font-family: $font-family-bold-radius !important;
    }
  }
  .teaser-media + .teaser-content {
    .site-theme-radius & {
      font-size: $text-size-body-radius;
    }
  }
  .teaser-date {
    .site-theme-radius & {
      font-family: $font-family-bold-radius;
      font-size: $text-size-body-radius;
    }
  }
}
