.theme-inherit-section {
  .list {
    .is-padded {
      padding-top: 0;
      padding-bottom: 0;
    }

    [class*="theme-"] {
      color: inherit;
  
      .list-title {
        color: inherit;
      }
  
      .list-items {
        color: inherit;
      }
  
      .list-items .list-items-item-value {
        color: inherit;
      }
  
      .link-arrow {
        color: inherit !important;
        
        &:hover {
          color: inherit !important;
        }
      }
    }
  }

  &.list .list.is-padded-vertical {
    padding-top: 0;
    padding-bottom: 0;
  }
}
