/**
  Search Field Styling
 */
@mixin optional-at-root($sel) {
  @at-root #{if(not &, $sel, selector-append(&, $sel))} {
    @content;
  }
}

@mixin placeholder {
  @include optional-at-root("::-webkit-input-placeholder") {
    @content;
  }

  @include optional-at-root(":-moz-placeholder") {
    @content;
  }

  @include optional-at-root("::-moz-placeholder") {
    @content;
  }

  @include optional-at-root(":-ms-input-placeholder") {
    @content;
  }
}

.search-limit-bounds {
  padding-right: 10px;

  .input-wrapper {
    background-color: rgba($color-text, 0.2);
    border-radius: 41px;
    display: inline-block;
    width: 41px;
    height: 41px;
    transition: background-color 0.5s ease;
    position: relative;
    float: right;

    &.hidden {
      background-color: transparent;
    }

    &.font-white {
      color: $color-white;

      input,
      button,
      i {
        color: $color-white;

        @include placeholder {
          color: $color-white;
        }
      }
    }

    input {
      background-color: transparent;
      border: none;
      color: $color-text;
      vertical-align: middle;
      padding: 8px 15px;
      display: none;
      font-family: $font-family-regular;

      width: 100%;

      &:focus {
        outline: 0;
      }
    }

    button {
      border: none;
      background-color: transparent;
      font-weight: bold;
      cursor: pointer;
      position: absolute;
      right: 5px;
      top: 27%;

      &:focus {
        outline: 0;
      }
    }
  }

  .tt-menu {
    background-color: $color-white;
    color: $color-text;
    width: 100%;
    border-radius: 5px;
    padding: 10px 0;

    .tt-dataset {
      .tt-suggestion {
        padding: 5px 10px;

        &.tt-selectable {
          cursor: pointer;

          &.tt-cursor,
          &:hover {
            background-color: rgba($color-text, 0.2);
          }
        }
      }
    }
  }
}

/*-------------------------------------------------------
  Search Result Page
 ------------------------------------------------------*/
.form,
.row {
  &.search-page-form {
    max-width: none;
    margin-left: 0;
    margin-right: 0;
    margin-top: 30px;
    margin-bottom: 10px;

    @include media-breakpoint-down(sm) {
      margin-top: 30px;
      margin-bottom: 1rem;

      fieldset {
        margin-top: 0px;
        margin-bottom: 0px;
      }
    }
  }
}

.search-category-form {
  max-width: 100%;
  margin-top: 30px;
  margin-bottom: 1rem;
}

@include media-breakpoint-up(md) {
  .search-category-form-group {
    margin-bottom: 5px !important;
    margin-top: 0px !important;
  }
}

.search-page-form-check {
  display: inline-block;
  height: 35px;
}

table.dataTable {
  border-collapse: collapse !important;
}

.list {
  .dataTables_wrapper {
    .dataTables_paginate {
      margin-top: 25px;
      margin-bottom: 25px;
      float: right;

      & > .paginate_button {
        &:first-child {
          border-radius: 41px 0 0 41px;
        }

        &:last-child {
          border-radius: 0 41px 41px 0;
        }
      }
    }
  }

  table.list-items.search-result-list {
    .list-header {
      background-color: transparent;
      color: $color-text;
      border-collapse: collapse !important;
      tr:hover {
        background-color: #fff !important;
      }
      th {
        padding: 10px 30px;

        &:first-child {
          padding-left: 10px;
        }
      }
    }

    tr,
    tr.list-items-item,
    .row-border {
      border-bottom: 1px solid rgba(198, 203, 208, 0.5);
    }
  }
}
