.custom-video-play-pause {
  position: absolute;
  top: 25px;
  left: 25px;
  transform: translate(-50%, -50%);
  right: 0;
  bottom: 0;
  z-index: 999;
  height: 50px;
  width: 50px;
  background-image: url("/Assets/icons/play_icon.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  cursor: pointer;
}
