.accordion {
  .accordion-header:empty {
    padding-bottom: 0;

    & + .accordion-items .accordion-items-item:first-child {
      border-top-color: transparent;
    }
  }

  &.theme-inherit-section {
    .theme-base-color-background {
      background: inherit;
    }
  
    .theme-color-accent,
    .accordion-item-expanded,
    .accordion-item-expanded-inner,
    .accordion-items-item.is-open .accordion-item-trigger,
    .accordion-items-item:not(.is-open) .accordion-item-trigger:not(:hover),
    .accordion-item-trigger:before,
    .accordion-items-item:not(.is-open) .accordion-item-trigger:hover:before,
    .accordion-items-item.is-open .accordion-item-trigger:before {
      color: inherit;
    }
  }
}
