.tabs {
  margin-bottom: $module-space-vertical-top !important;
  height: 100%;

  .tabs-header {
    @extend .h3-bold;
    margin-bottom: $spacing-medium;
    color: $color-text;
  }

  .tabs-tabs {
    list-style: none;
    margin: 0;
    padding: 0;

    li {
      color: $color-text;
      display: inline-block;
      padding: $spacing-regular $spacing-medium;
      font-size: 14px;

      cursor: pointer;

      &:hover {
        font-family: $font-family-bold;

        @include asian-fonts;
        @include asian-fontweight-bold;
      }

      &.is-open {
        background: rgba(216, 209, 202, 0.5);
        font-family: $font-family-bold;

        @include asian-fonts;
        @include asian-fontweight-bold;
        border-top-left-radius: $rounded-corner;
        border-top-right-radius: $rounded-corner;
      }

      &:not(.tabs-tabs-more):not(.is-open) {
        &:not(:hover) {
          color: $color-dark-blue;
        }
      }

      &.tabs-tabs-more {
        display: none;
        position: relative;
        padding: 0 0 7px;
        margin-right: 150px;

        @include media-breakpoint-down(sm) {
          margin-right: 50px;
        }

        > span {
          display: block;
          font-family: $font-family-bold;

          font-size: 24px;
          line-height: 14px;
          padding: $spacing-regular $spacing-medium;
        }

        .tabs-tabs-tab {
          display: block;
          border-bottom: 1px solid $color-border-grey;
          white-space: nowrap;

          &:last-child {
            border-bottom: 0;
          }
        }

        &:not(.is-open) {
          &:not(:hover) {
            color: $color-dark-blue;
          }
        }

        &.open {
          .tabs-tabs-more-dropdown {
            padding: 0;
            display: block;
            top: 90%;
            font-family: $font-family-base;
          }
        }
      }
    }
  }

  .tabs-content {
    //height: 100%; moved under col-md-6
    &.is-padded {
      background-color: rgba(216, 209, 202, 0.5);
      color: $color-dark-blue;
      padding: $spacing-medium;
      border-bottom-left-radius: $rounded-corner;
      border-bottom-right-radius: $rounded-corner;
      border-top-right-radius: $rounded-corner;
    }

    .tabs-content-tab {
      display: none;

      &.is-open {
        display: block;
        // fixes for teaser under tabs
        .teaser-media {
          position: absolute;
        }

        .teaser-content {
          position: absolute;
          height: 100%;
          left: 50%;
          width: 100%;
        }
      }
    }
  }
}

.tab-fix {
  display: flex;
  align-items: center;
  justify-content: center;

  .tab-fix-center {
  }
}

.col-md-6 {
  .tabs {
    .tabs-content {
      height: 100%;
    }
  }
}

.row {
  &.row-narrow {
    @include media-breakpoint-up(md) {
      .tabs {
        padding: $module-no-space $module-space-horisontal-right;
      }
    }
  }
  &.row-fullwidth {
    .tabs {
      padding: $module-no-space $module-space-horisontal-right;
    }
  }
  &.row-widewidth {
    .tabs {
      padding: $module-no-space $module-space-horisontal-right;
    }
  }
}
