// Article Styling
.job__header,
.job__sub-header,
.job__aside-list {
  margin-bottom: $module-space-horisontal-left;
}

.job__header {
  letter-spacing: normal;
  color: $color-blue;
  @include media-breakpoint-up(lg) {
    margin-bottom: $module-space-horisontal-left;
  }
}

.job__btn-group {
  display: flex;
}

.job__aside {
  margin-bottom: $module-space-horisontal-left;

  @include media-breakpoint-down(md) {
    margin-top: 20px;
  }
}

.job__aside-additional-content {
  margin-bottom: 10px;
}

.job__aside-icons {
  img {
    width: 136px;
    margin-right: 12px;
    margin-bottom: 12px;
  }
}

.job__aside-list {
  margin-top: 0;
  margin-right: 0;
  margin-left: 0;
  padding: 0;
}

.job__aside-inner {
  .job__aside-list:only-child {
    margin-bottom: 0;
  }
}

.job__aside-list-item {
  margin-bottom: 3px;

  strong {
    margin-right: 4px;
  }
}
.job__aside-header,
.job__aside-video {
  margin-bottom: 15px;
}
.job__aside-small {
  font-size: 14px;
}
.job__btn {
  text-decoration: none;
}

.job__aside-title {
  display: block;
  margin-bottom: 10px;
}

.job__aside-title--video {
  margin-bottom: 10px;
}

.job__aside-video {
  width: 100%;
}

[class*="col"] {
  > .job {
    margin-left: 0px;
    margin-right: 0px;
  }
}

.job {
  h1,
  h2,
  h4,
  h5,
  strong {
    font-family: $font-family-bold !important;
  }
}
.job__aside-inner {
  padding: $module-space-horisontal-left 25px;
  background-color: $color-light-grey;
}
.job__content {
  margin-bottom: 0;
}

.btn.btn--mobile-center {
  display: block;
  margin: 0 auto;
  width: max-content;
}
@include media-breakpoint-up(lg) {
  .btn.btn--mobile-center {
    margin: 0;
    width: auto;
    display: initial;
  }
}
@include media-breakpoint-down(md) {
  .row.row-widewidth .job__article {
    padding: 0;
    margin: 0 !important;
  }
}

.job__article {
  width: 100%;
  margin: 40px auto 20px;
  overflow-y: hidden;

  @include media-breakpoint-up(md) {
    margin-top: -100px;
    z-index: 0;
  }

  .breadcrumbs-container-ov {
    margin-bottom: 40px;
  }

  @include media-breakpoint-down(sm) {
    .breadcrumbs--ov {
      padding-left: 0;
      padding-right: 0;
    }
  }

  @include media-breakpoint-up(lg) {
    .col-lg-5 {
      flex: 0 0 37.8%;
      max-width: 37.8%;
    }

    .breadcrumbs-container-ov {
      margin-bottom: 50px;
    }
  }

  .breadcrumbs--ov {
    margin: 0;
  }
}

.job__content-inner {
  background-color: $color-white;
  padding: 20px;
  padding-bottom: 10px;
  max-width: 1045px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;

  @include media-breakpoint-up(lg) {
    padding: $module-space-horisontal-left 60px 0 60px;
    &.job__content-inner--large-space {
      padding-top: 40px;
      padding-bottom: 30px;
    }
  }
}

.job__content-inner {
  .job__content-column,
  .job__aside-inner {
    ul {
      margin-left: 0;
      padding-left: 0;
      list-style: none !important;

      li {
        padding-left: 32px;
        position: relative;

        &:before {
          font-size: 16px;
          margin-left: 0;
          content: "";
          font-family: $font-family-regular;
          position: absolute;
          left: 0;
          top: 4px;
          line-height: 16px;
        }
      }
    }
  }

  .job__aside-inner {
    ul li {
      padding-left: 10px;
    }
  }
}

.job__content-column {
  padding-right: 15px;
  strong {
    @extend h5;
  }
  .job & p:first-child em {
    @extend h4;
    font-family: $font-family-regular !important;
    font-style: normal;
  }
}
.job__aside-benefit img {
  width: 30px !important;
}
// benefit aside
.job__aside-benefit {
  width: 100%;
  align-items: center;
  align-content: stretch;
  margin-bottom: 12px;

  img.job__aside-benefit-image {
    max-width: none;
    width: auto;
    margin: 0;
    flex: 0 0 30px;
  }

  > p {
    flex: 1 0 0;
    padding-left: 12px;
    margin: 0 !important;
  }
}

.job-module {
  .list {
    .job-list {
      overflow: hidden;

      &.list-items {
        .list-header .list-header-column {
          border: 0;
          background-color: $color-white;
        }

        &.list-type-table {
          .list-items-item-column {
            padding: 16px 20px 16px 0;

            &:last-child {
              padding-right: 0;
            }

            &:first-child {
              padding-left: 10px;
            }
          }

          td:not(.dataTables_empty):first-child {
            font-family: "OrstedSansBold", sans;
            color: $color-blue;
          }
        }

        tr {
          border-bottom: 1px solid $color-cold-grey-25;
        }
      }

      &.no-results {
        tbody tr {
          border-bottom: 0;

          td {
            font-family: "OrstedSansBold", sans;
            height: 100px;
            vertical-align: middle;
          }
        }

        ~ .dataTables_paginate {
          display: none;
        }
      }
    }

    .dataTables_wrapper .dataTables_paginate {
      @include media-breakpoint-down(md) {
        float: none;
        justify-content: center;
      }

      .paginate_button,
      .ellipsis {
        width: 44px;
        height: 44px;
        display: inline-block;
        padding: 0.75rem 0.8rem;
        border: 1px solid $color-cold-grey-25;
        border-right-width: 0;
        text-align: center;
      }

      > .paginate_button:last-child {
        border-right-width: 1px;
      }
    }

    table.job-list {
      @include media-breakpoint-down(md) {
        width: 100% !important; // not thebest way, but have to override datatables inline width

        .list-items-item-column-hide-md {
          display: none;
        }

        tbody tr {
          td:first-child {
            width: 65%;
            min-width: 65%;
          }

          td:nth-child(4) {
            width: 25%;
          }

          &:first-child {
            td {
              &:before {
                padding-bottom: 5px;
              }
            }
          }
        }
      }
    }
  }
}

.job-splash {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  overflow: hidden;
  padding-left: 20px;
  padding-right: 20px;

  .job-splash-inner {
    position: relative;
    max-width: 400px;
    margin: 0 auto;
    width: 100%;
    padding-bottom: 100%;
    border-radius: 50%;
    display: table;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .job-splash-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 75%;
    max-height: 50%;
    overflow: hidden;
    z-index: 1;
    text-align: center;

    h1 {
      font-family: $font-family-bold !important;
    }

    .link-arrow {
      margin-right: 0px;
      padding-right: 0px;
    }
  }
}

.job__aside-icons img {
  display: block;
  margin-bottom: 0;
  margin-right: 0;
  width: 100%;
}

.job__aside-icons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}

.job__aside-icons-img {
  flex: 0 0 49%;
  padding: 5px 0;
}

.job-apply-button-container {
  @include media-breakpoint-down(md) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    .job__btn-newline {
      flex-basis: 100%;
      height: 0;
    }
  }
}

.btn.job-details-apply {
  display: inline-block;
}

// Override fonts of non english page eg: tw,jp,kr
body#lang-zh,
body#lang-ko,
body#lang-ja {
  .job {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    .h1,
    .h2,
    .h3,
    .h4,
    .h5,
    .h6,
    strong,
    .btn,
    .btn:hover {
      font-family: $font-family-bold !important;
    }

    div,
    p,
    span,
    em,
    a,
    a:hover,
    a:focus {
      font-family: $font-family-regular !important;
    }
  }
}
