.headerSearch {
  // your styles here
  position: absolute;
  right: 0;
  top: -26px;
  pointer-events: none;
  @include media-breakpoint-up(md) {
    //top: -2px;
    top: 0;
  }

  .headerSearch-icon {
    transition: all $transition-duration ease-in-out $transition-duration/2;
    position: relative;
    opacity: 1;
    z-index: 2;
    color: black;
    pointer-events: all;
    position: absolute;
    right: 12px;
    top: 12px;

    i {
      color: $color-text;
    }

    &:hover {
      i {
        color: $color-blue;
      }
    }
  }

  .headerSearch-input,
  .tt-menu {
    font-weight: normal !important;
  }

  //for accesibility we need a focus style
  .headerSearch-input:focus {
    outline: 0;
    box-shadow: 0 0 0 2px rgba(64, 153, 218, 0.25);
  }

  .headerSearch-form {
    //@extend .form;

    transition: all $transition-duration ease-in-out $transition-duration/2;
    max-width: none;
    margin-bottom: 0;
    pointer-events: none;
    @media all and (-ms-high-contrast: none) {
      width: calc(100vw - 18px);
    }
    width: 100vw;
    position: relative;
    top: 60px;
    right: -46px;
    .headerClassic & {
      right: -56px;
    }
    padding: 10px 20px 20px 10px;
    background: white;
    opacity: 0;
    @include media-breakpoint-up($header-breakpoint) {
      background: transparent;
      overflow: inherit;
      position: static;
      width: 300px;
      padding: 0 0 0 50px;
      &:before {
        background: linear-gradient(
          90deg,
          rgba(255, 255, 255, 0) 0%,
          rgba(255, 255, 255, 1) 10%
        );
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 53px;
        min-width: 100%;
        z-index: -1;
      }
    }

    .twitter-typeahead {
      width: 100%;

      .tt-menu {
        position: relative !important;
        @include media-breakpoint-up($header-breakpoint) {
          position: absolute !important;
        }
      }
    }
  }

  &.headerSearch-visible {
    width: auto;
    overflow: visible;
    .headerSearch-icon {
      transition: all $transition-duration ease-in-out;
      /*pointer-events: none;*/
    }

    .headerSearch-form {
      opacity: 1;
      pointer-events: all;
      &:before {
        content: "";
      }
    }
    &.headerSearch-dark {
    }
  }

  &.headerSearch-dark {
    border: 0;
    position: relative;

    button {
      z-index: 1;
    }

    input {
      position: relative;
      z-index: 1;
      //background-color: rgba($color-backdrop-1, .75);
      background-color: rgba($color-dark-blue, 0.7);
      font-family: $font-family-regular;

      color: $color-white;
      border: 0;
      border-radius: 42px !important;
      height: 42px !important;
      width: 42px;
      padding: 10px 12px;

      &:focus {
        width: 100%;
      }
    }

    input::-webkit-input-placeholder {
      color: $color-white;
      opacity: 1;
    }

    input:-ms-input-placeholder {
      color: $color-white;
      opacity: 1;
    }

    .headerSearch-icon {
      opacity: 1;
      pointer-events: all;
      top: 8px;
    }

    .headerSearch-form {
      opacity: 1;
      pointer-events: all;
      background: transparent;
      width: auto;
      overflow: visible;
      &:before {
        display: none;
      }
    }

    .awesomplete,
    .twitter-typeahead .tt-menu,
    .searchTypeahead {
      //width: 100%;
      //width: 42px;

      &:before {
        display: none;
      }

      ul,
      .tt-dataset {
        // overflow: hidden;
        width: 100%; //calc(100% - 40px); the calc messed up typeahead width
        // border: 0;
        //background-color: rgba($color-text, .7);
        background-color: $color-white;
        //color: $color-white;
        color: $color-text;
        // padding-bottom:10px;
        border-top: 1px solid $color-input-border;
        position: absolute;
      }

      li,
      .tt-suggestion {
        padding: 10px 15px;
        border-radius: 0;

        // &:hover,
        // &[aria-selected="true"] {
        //     color: rgba($color-white, .9);
        //     background-color: rgba($color-text, .7);
        // }
        &:last-child {
          border-bottom-left-radius: 20px !important;
          border-bottom-right-radius: 20px !important;
        }
      }

      .mark,
      mark,
      strong {
        background-color: transparent;
        color: $color-blue;
        padding: 0;
        margin: 0;
      }

      input {
        transition: background-color $transition-duration ease;
        padding-right: 26px;

        &:focus {
          background-color: $color-white;
          border: 1px solid $color-input-border;
          color: $color-text;

          &::-webkit-input-placeholder {
            color: $color-text;
            opacity: 1;
          }
          &:-ms-input-placeholder {
            color: $color-text;
            opacity: 1;
          }
        }
      }
      input:focus::-webkit-input-placeholder {
        color: $color-text;
        opacity: 1;
      }

      input:focus:-ms-input-placeholder {
        color: $color-text;
        opacity: 1;
      }

      &.awesomplete-open,
      &.tt-open {
        input {
          color: $color-text;
          // border-top-left-radius: 20px!important;
          // border-top-right-radius: 20px!important;
          // border-bottom-left-radius: 0!important;
          // border-bottom-right-radius: 0!important;
        }
      }
    }
  }
}
