.theme {
  padding: 20px;

  @each $themeName, $theme-props in $themes {
    &-#{$themeName} {
      @include theme-colors(
        $themeName,
        map-get($theme-props, background-color),
        map-get($theme-props, text-color),
        map-get($theme-props, link-color),
        map-get($theme-props, button-theme-class),
        map-get($theme-props, secondary-color),
        map-get($theme-props, secondary-background-color),
        map-get($theme-props, keyline)
      );
    }
  }

  .btn {
    @include btnCta;
  }

  .richtext {
    a {
      text-decoration: underline;
    }

    a.btn {
      text-decoration: none;
    }
  }
}

//end site-theme-radius

/**
  Splash Themes
  - theme-radius
  - theme-blue (overwrite)
  - theme-red (overwrite)
  - theme-white (overwrite)
 */
.splash {
  &.theme-radius {
    @include splash(
      $color-radius-yellow,
      $color-radius-darkgrey,
      $color-radius-blue
    );
  }
}

.background-color-forced-white {
  background-color: $color-white !important;
}

.background-color-forced-transparent {
  background-color: inherit !important;
}
