
$fontRegularRadius: 'Metric-Regular';
$fontLightRadius: 'Metric-Light';
$fontSemiBoldRadius: 'Metric-SemiBold';
$fontBoldRadius: 'Metric-Bold';
$fontItalicRadius: 'Metric-RegularItalic';

@font-face {
  font-family: 'MetricRegular';
  font-style: normal;
  font-weight: 400;
  src: url('Metric-Regular.woff') format('woff'),
  url('Metric-Regular.otf') format("opentype"),
  url('Metric-Regular.eot');
}

@font-face {
  font-family: 'MetricLight';
  font-style: normal;
  font-weight: 200;
  src: url('Metric-Light.woff') format('woff'),
  url('Metric-Light.otf') format("opentype"),
  url('Metric-Light.eot');
}

@font-face {
  font-family: 'MetricBold';
  font-style: normal;
  font-weight: 800;
  src: url('Metric-Bold.woff') format('woff'),
  url('Metric-Bold.otf') format("opentype"),
  url('Metric-Bold.eot');
}

@font-face {
  font-family: 'MetricSemiBold';
  font-style: normal;
  font-weight: 600;
  src: url('Metric-SemiBold.woff') format('woff'),
  url('Metric-SemiBold.eot');
}

@font-face {
  font-family: 'MetricItalic';
  font-style: normal;
  font-weight: 400;
  src: url('Metric-RegularItalic.woff') format('woff'),
  url('Metric-RegularItalic.eot');
}
