// classes for positioning background-images based on focal-point
.background-image-top-left {
  background-position: left top !important;
  &.image__element {
    object-position: left top;
  }
}
.background-image-top-center {
  background-position: center top !important;
  &.image__element {
    object-position: center top;
  }
}
.background-image-top-right {
  background-position: right top !important;
  &.image__element {
    object-position: right top;
  }
}
.background-image-center-left {
  background-position: left center !important;
  &.image__element {
    object-position: left center;
  }
}
.background-image-center-center {
  background-position: center center !important;
  &.image__element {
    object-position: center center;
  }
}
.background-image-center-right {
  background-position: right center !important;
  &.image__element {
    object-position: right center;
  }
}
.background-image-bottom-left {
  background-position: left bottom !important;
  &.image__element {
    object-position: left bottom;
  }
}
.background-image-bottom-center {
  background-position: center bottom !important;
  &.image__element {
    object-position: center bottom;
  }
}
.background-image-bottom-right {
  background-position: right bottom !important;
  &.image__element {
    object-position: right bottom;
  }
}

.overflow-hidden {
  overflow: hidden;
}

.list-inline {
  @include list-inline;
}

.list {
  @include list;
}

.red,
.header-red {
  @extend .text-red;
}

.blue,
.header-blue {
  @extend .text-blue;
}

body.site-theme-radius,
.site-theme-radius {
  font-family: $font-family-regular-radius;
}
