[data-icon],
[class^="icon-"],
[class*=" icon-"] {
  @include icon;
}

[data-icon]:before {
  content: attr(data-icon);
}

.icon {
  display: inline-flex;

  &:before {
    font-size: 1em;
    width: 1em;
    vertical-align: middle;
  }

  @each $iconName, $iconProps in $dongMaineIcons {
    &-#{$iconName}:before {
      content: $iconProps;
    }
  }
}

i {
  &.show-icon-onfontload {
    opacity: 0;
  }
}

.show-icon-onfontload {
  &:before {
    opacity: 0;
  }
}
