$dongMaineIcons: (
  "arrow-down": "\63",
  "arrow-right": "\6c",
  "basket": "\7a",
  "burger": "\62",
  "card-view": "\74",
  "check-mark": "\43",
  "close": "\6b",
  "facebook": "\77",
  "filter": "\72",
  "flower": "\68",
  "leaf": "\67",
  "linkedin": "\78",
  "list-view": "\73",
  "lock": "\66",
  "magnifier": "\64",
  "pencil": "\41",
  "phone": "\65",
  "rss": "\61",
  "spare": "\69",
  "toptask-flytte": "\6d",
  "toptask-kontakt": "\6e",
  "toptask-maaler": "\6f",
  "toptask-priser": "\70",
  "toptask-selvbetjening": "\71",
  "toptask": "\79",
  "torso": "\6a",
  "twitter": "\76",
  "unlock": "\42",
  "youtube-play": "\75",
) !default;

@mixin icon($iconName: false) {
  &:before {
    font-family: "dong-maine";
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    speak: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    @if $iconName {
      content: map-get($dongMaineIcons, $iconName);
    }
  }

}
