.announcement {
  display: flex;
  @include flex-1;

  .announcement-teaser {
    padding-right: 0;
    padding-left: 0;

    .teaser {
      margin-bottom: 0;
    }
  }

  .announcement-list {
    padding: $spacing-large 0;
    max-width: $legible-text-max-width;
    width: 100%;
    margin: 0 auto;

    .announcement-list-title {
      @extend h3;
      //color: $color-blue;
      margin: 0 $spacing-large $spacing-large;
      letter-spacing: normal;
    }

    .list-items-item:hover {
      cursor: pointer;
    }

    .announcement-cta {
      margin: 0 $spacing-large;
    }

    .list {
      margin: 0 0 $spacing-large;

      .list-items.list-type-compact .list-items-item-column {
        padding: 0 $spacing-large;

        &:first-child {
          padding-top: $spacing-regular;
        }

        &:last-child {
          padding-bottom: $spacing-regular;
        }
      }
    }

    &.theme-backdrop-2 .list-items.list-type-compact tr {
      border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    }
  }

  &.theme-backdrop-2 .list-items.list-type-compact tr {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }
}
.announcement-article {
  .announcement-attachments {
    font-size: 80%;

    .announcement-attachments-label {
      font-weight: 600;
    }

    .announcement-attachments-attachment {
      display: block;
    }
  }
}

.announcement-module {
  .list {
    .announcements-main-list {
      overflow: hidden;
      width: auto !important; // mime: Added to auto resize the table for "Regulatory reporting", For some strange reason the was fixed
    }

    .announcements-main-list.list-items.list-type-table
      .list-items-item-column.list-time-col {
      @include media-breakpoint-up(md) {
        width: 122px !important; // not thebest way, but have to override datatables inline width
      }

      width: 75px !important;
    }

    .list-header .list-header-column:first-child,
    .list-items-item .list-items-item-column:first-child {
      display: none;
    }

    .list-items-item
      .list-items-item-column.list-items-item-column-haslink-mobile
      .list-items-item-value.hide-date-md {
      margin-bottom: 5px;
    }

    @include media-breakpoint-up(md) {
      .list-header .list-header-column:first-child,
      .list-items-item .list-items-item-column:first-child {
        display: block;
      }

      .list-items-item
        .list-items-item-column.list-items-item-column-haslink-mobile
        .list-items-item-value.hide-date-md {
        display: none;
      }

      .list-items-item
        .list-items-item-column.list-items-item-column-haslink-mobile
        .list-items-item-column-link {
        display: none;
      }
    }
  }
}
