.narrative-text a {
    text-decoration: underline;
    color: inherit;
    &:hover{
        text-decoration: none;
    }
}

.teaser-body a {
    text-decoration: underline;
    color: inherit;

    &:hover {
        text-decoration: none;
    }
}

.link-inline {
    text-decoration: underline;

    &:hover {
        text-decoration: none;
    }

    &-blue {
        @extend .link-inline;
        color: $color-blue;
        border-color: $color-blue;

        &:hover {
            color: $color-blue;
        }
    }

    &-red {
        @extend .link-inline;
        color: $color-red;
        border-color: $color-red;

        &:hover {
            color: $color-red;
        }
    }
}

.link-arrow-hover {
    color: $color-blue;
    text-decoration: none;

    &.link-arrow-dark-blue {
        color: $color-text!important;
    }

    &.link-arrow-red {
        color: $color-red!important;
    }

    &.link-arrow-green {
        color: $color-aqua!important;
    }

    &.link-arrow-aubergine {
        color: $color-aubergine!important;
    }

    &.link-arrow-yellow {
        color: $color-yellow!important;
    }

    &.link-arrow-white {
        color: $color-white!important;
    }

    &.link-arrow-grey {
        color: $color-blue!important;
    }

    &:after {
		padding-left: 10px;
		padding-right: 0px;
    }
}

.link-arrow {
    text-decoration: none!important;
    display: inline;
    backface-visibility: hidden;
    @extend .arrow-link-style;
    color: $color-blue;
    position: relative;
    padding-right: rem(20px);
    margin-right: rem(10px);
    transition: padding $transition-duration $easing-smooth;

    &:after {
        content: "";
        display: inline;
		padding-left: 5px;
		padding-right: 5px;
        transition: padding $transition-duration $easing-smooth;
        font-family: $font-family-bold;
    }

    &.link-arrow-reverse {
        display: inline-block;

        &:before {
            transform: rotate(180deg);
            margin-right: 10px;
            margin-bottom: 5px;
        }
    }

    &:hover {
        @extend .link-arrow-hover;
    }

    &.link-arrow-dark-blue {
        color: $color-text!important;
    }

    &.link-arrow-blue {
        color: $color-blue!important;
    }

    &.link-arrow-red {
        color: $color-red!important;
    }

    &.link-arrow-green {
        color: $color-aqua!important;
    }

    &.link-arrow-yellow {
        color: $color-yellow!important;
    }

    &.link-arrow-aubergine {
        color: $color-aubergine!important;
    }

    &.link-arrow-white {
        color: $color-white!important;
    }

    &.link-arrow-grey {
        color: $color-backdrop-1!important;
    }
}

a {
    color: inherit;

    &:hover {
        text-decoration: none;
    }

    &:focus {
        color: inherit;
    }
}

a[href^="tel:"] {
    text-decoration: none;
}


    .link-left-arrow{
        color: #000;
        cursor: pointer;
        &:before {
        content: "";
        display: inline;
        padding-left: 5px !important;
        -webkit-transition: padding 200ms cubic-bezier(0.165, 0.84, 0.26, 0.98);
        transition: padding 200ms cubic-bezier(0.165, 0.84, 0.26, 0.98);
        font-family: $font-family-bold;
        position: initial;
        margin-right: 3px;
        color: #000;
    }
    }

    .show-login-password {
        position: absolute;
        top: 10px;
        right: 40px;

        &.is-active {
            display: inline-block;
        }
    }
