.onward-links {
  .onward-link {
    .onward-content {
      .onward-tagline {
        .site-theme-radius & {
          font-size: $text-size-splash-radius;
          line-height: $line-height-splash-radius;
        }
      }
      .onward-cta a {
        .site-theme-radius & {
          font-size: $text-size-xsmall-radius;
          &:hover {
            color: $color-radius-blue !important;
          }
        }
      }
    }
  }
}
