@media only screen and (max-width: 1000px) {
  .lbl-toggle {
    padding: 15px 15px;
    background: #8ecdc8;
    font-size: 20px;
  }
  .wrap-collabsible.col-in-viewport-inited.col-in-viewport {
    display: inherit;
    padding: 0px 20px;
    margin-top: 25px;
    width: 100%;
  }
  .category-filter-checkbox + label:before {
    padding: 0.5px;
  }
  .wrap-collabsible.col-in-viewport-inited.col-was-in-viewport.col-in-viewport {
    display: inherit;
  }

  .search_category_list.col-in-viewport-inited.col-in-viewport.col-was-in-viewport {
    display: none;
  }

  .search_category_list.col-in-viewport-inited.col-in-viewport.col-was-in-viewport.col-in-viewport {
    display: none;
  }

  .list.search_category_list.col-in-viewport-inited.col-was-in-viewport {
    display: none;
  }

  .search_result_list.col-in-viewport-inited.col-in-viewport.col-was-in-viewport {
    -webkit-box-flex: 0;
    flex: 0 0 100%;
    max-width: 100%;
    padding: 20px;
  }
  .list.search_result_list.col-in-viewport-inited.col-was-in-viewport {
    -webkit-box-flex: 0;
    flex: 0 0 100%;
    max-width: 100%;
    padding: 20px;
  }
  .search_category_list {
    display: none;
  }

  .search-result-col {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media only screen and (min-width: 1000px) {
  .search-result-module {
    .form .form-search .twitter-typeahead {
      margin-right: 25px;
    }

    .form .form-search .btn.search-result-submit-btn {
      border-top-left-radius: 40px;
      border-bottom-left-radius: 40px;
      padding: 0px 50px;
      margin-right: 0 !important;
    }

    .form #search {
      border-top-right-radius: 21px;
      border-bottom-right-radius: 21px;
    }

    .lbl-toggle {
      padding: 0px;
      background: transparent;
      font-size: 0px;
    }

    .wrap-collabsible.col-in-viewport-inited.col-in-viewport {
      display: none;
    }

    .category-filter-checkbox + label:before {
      padding: 2px;
    }

    .search-result-module
      .list
      .list-items
      .list-type-table
      .list-items-item-column {
      padding: 25px;
    }

    .wrap-collabsible.col-in-viewport-inited.col-was-in-viewport.col-in-viewport {
      display: none;
    }

    .wrap-collabsible.col-in-viewport-inited.col-was-in-viewport {
      display: none;
    }

    .search_category_list.col-in-viewport-inited.col-in-viewport.col-was-in-viewport {
      display: -webkit-inline-box;
    }

    .search_category_list {
      display: block;
    }
  }
}

.search-result-module {
  @include media-breakpoint-down(sm) {
    .form-search {
      .btn.search-result-submit-btn {
        margin-right: 0 !important;
      }
    }
  }

  margin-bottom: 50px !important;

  .search-result-list {
    overflow: hidden;
    padding: 50px 30px 30px 30px;

    .word_break {
      word-break: break-word;
      word-wrap: break-word;
    }

    @include media-breakpoint-down(md) {
      width: 100% !important; // not thebest way, but have to override datatables inline width
    }

    .max {
      font-family: $font-family-bold !important;
    }

    .med {
      font-family: $font-family-italic !important;
    }
  }
  ////////
  input[type="checkbox"],
  input[type="radio"] {
    opacity: 0;
    position: absolute;
  }

  input[type="checkbox"],
  input[type="checkbox"] + label,
  input[type="radio"],
  input[type="radio"] + label {
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
    user-select: none;
  }

  .category-filter-checkbox + label:before {
    font-family: "FontAwesome";
    display: inline-block;
    vertical-align: middle;
    width: 22px;
    height: 22px;
    margin-right: 15px;
    text-align: center;
    font-size: 13px;
    color: white;
    transition: color 0.4s;
    border: 1px solid #b9bac1;
  }

  .lbl-toggle::before {
    content: "\f00c";
    border-radius: 0px;
    float: right;
  }

  .category-filter-checkbox + label:before {
    content: "\f00c";
    border-radius: 5px;
  }

  input:checked + label:before {
    background: #4099da;
    color: #fff;
  }

  input[type="checkbox"][disabled] + label:before,
  input[type="radio"][disabled] + label:before {
    color: #d0d0d0;
    background: #bfbfbf;
    border: 1px solid #656b64;
  }

  input.checkbox-selected-backbground:checked + label {
    font-weight: bold;
    color: #4099da;
  }

  input[type="checkbox"].checkbox-selected-backbground:checked + label {
    border-radius: 2px;
  }

  input[type="radio"].checkbox-selected-backbground:checked + label {
    border-radius: 10px;
  }

  .breadcrumbs--sov {
    padding: 0px;
    margin: 0px;
  }

  .searchForm {
    max-width: 100%;
  }

  .breadcrumb-item--sov:last-child:after {
    content: none;
  }

  .search_category_headline,
  .search_results_headline {
    font-weight: 900;
    padding: 15px 15px;
    margin-bottom: 45px;
    background: #8ecdc8;
    font-size: 20px;
  }

  .search_results_radiuselnetheadline_theme {
    background: #384967 !important;
    color: #fff !important;
  }

  .search_category_headline_text {
    padding: 20px 20px;
    background: green;
  }

  .wrap-collabsible {
    margin-bottom: 1.2rem 0;
  }

  input[type="checkbox"] {
    display: none;
  }

  .lbl-toggle {
    display: block;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.25s ease-out;
    font-weight: 900;
  }

  .lbl-toggle::before {
    content: "\003e";
    font-family: monospace;
    display: inline-block;
    vertical-align: middle;
    margin-right: 0.7rem;
    color: black;
    transform: translateY(-2px);
    transform: rotate(90deg);
    transition: transform 0.2s ease-out;
  }

  .toggle:checked + .lbl-toggle::before {
    background: transparent;
    color: black;
    transform: rotate(-90deg) translateX(-3px);
  }

  .collapsible-content {
    max-height: 0px;
    overflow: hidden;
    transition: max-height 0.25s ease-in-out;
  }

  .toggle:checked + .lbl-toggle + .collapsible-content {
    max-height: 350px;
  }

  .toggle:checked + .lbl-toggle {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }

  .collapsible-content .content-inner {
    border-bottom-left-radius: 7px;
    border-bottom-right-radius: 7px;
    padding: 1.5rem 1.5rem 0rem 1rem;
  }
  @include media-breakpoint-up(md) {
    padding: 1.5rem 1.5rem 0rem 1.5rem;
  }

  .search_category_headline {
    margin-right: 20px;
  }

  .search-result-col {
    margin-bottom: 25px;
  }

  .form-check {
    padding: 0px 10px;
  }

  .row.row-narrow .col-12.form-check {
    padding-left: 0;
  }

  @include media-breakpoint-up(lg) {
    .row.row-narrow .col-12.form-check {
      padding-left: 10px;
    }
  }

  .list .list-items.list-type-table .list-items-item-column {
    padding: 25px 15px;
  }

  .breadcrumb-item--sov {
    font-size: 14px;
  }

  .list table.list-items.search-result-list .list-header th:first-child {
    padding-top: 0px;
  }

  .list-item-description {
    font-size: 16px;
    line-height: 26px;
  }

  .sorting_disabled.list-items-item-column.list-items-item-column-haslink {
    padding: 5px;
  }

  #total_search_result {
    color: #797979;
    font-size: 16px;
    padding-left: 15px;
  }

  .searchpage_title {
    font-size: 20px;
    line-height: 28px;
    padding-top: 8px;
  }

  @include media-breakpoint-down(md) {
    .breadcrumbs--sov {
      margin: 0px auto 0 auto;
      flex-wrap: wrap;
    }
  }

  .breadcrumbs--sov {
    padding: 0;
    display: -webkit-box;
    display: flex;
    font-family: $font-family-regular;
    font-size: 14px;
    color: #3b4956;
    letter-spacing: 0;
    line-height: 22px;
    width: 100%;
  }

  .breadcrumb-item--sov {
    list-style: none;
    position: relative;
    padding-right: 12px;
    white-space: nowrap;

    &:after {
      content: "/";
      position: absolute;
      top: 0;
      padding: 0px 3px;
    }
  }
}
@media only screen and (min-width: 300px) {
  .search_result_line {
    width: 100%;
    border-bottom: 1px solid rgba(198, 203, 208, 0.5);
    padding-bottom: 20px;
  }
}

@media only screen and (min-width: 1000px) {
  .search_result_line {
    width: inherit;
    border-bottom: inherit;
    padding-bottom: inherit;
  }
}
