$brand-primary: $color-blue;
$body-bg: $color-body-background;
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 769px,
  lg: 992px,
  xl: 1200px
);
$grid-gutter-width-base: 0;
$font-family-sans-serif: "OrstedSansRegular", "Helvetica Neue", Arial,
  sans-serif;
$btn-padding-x: rem(19);
$btn-padding-y: rem(11);
$btn-border-radius: 41px;
$btn-transition: background-color $transition-duration/2,
  padding $transition-duration $easing-smooth;
$btn-focus-box-shadow: 0 0 0px 3px $color-input-border-focus;
$pagination-color: $color-text;
$pagination-bg: transparent;
$pagination-border-color: $color-border-grey;
$pagination-hover-color: $color-blue;
$pagination-hover-bg: transparent;
$pagination-hover-border: $color-border-grey;
$pagination-active-color: $color-blue;
$pagination-active-bg: transparent;
$pagination-active-border: $color-border-grey;
$pagination-disabled-color: $color-border-grey;
$pagination-disabled-bg: transparent;
$pagination-disabled-border: $color-border-grey;
