.imageSlider {
  .swiper-container {
    width: 100%;
    height: auto;
    padding-top: 36px;

    @include media-breakpoint-down(lg) {
      padding-top: 24px;
    }
  }

  .swiper-button-prev,
  .swiper-button-next {
    margin-top: 0;
  }

  .swiper-button-prev {
    left: 44px;
    transform: translate3d(0, 0, 0);
    transition: transform 0.2s cubic-bezier(0.165, 0.84, 0.26, 0.98);

    &:hover {
      transform: translate3d(-10px, 0, 0);
    }
  }

  .swiper-button-next {
    right: 44px;
    transform: translate3d(0, 0, 0);
    transition: transform 0.2s cubic-bezier(0.165, 0.84, 0.26, 0.98);

    &:hover {
      transform: translate3d(10px, 0, 0);
    }
  }

  .swiper-wrapper {
    @include media-breakpoint-down(lg) {
      width: 95vw;
    }

    @include media-breakpoint-down(md) {
      margin-left: 0;
    }

    margin-left: -32px;

    @include media-breakpoint-down(lg) {
      &.in-viewport-inited.in-viewport {
        animation: bounceMobileSlider 0.2s linear;
        animation-delay: 0.5s;
      }
    }
  }

  .swiper-youtube-control {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    z-index: 10;
    display: none;

    @include media-breakpoint-down(lg) {
      display: block;
    }
  }

  .swiper-slide {
    flex-direction: column;
    overflow: hidden;
    width: 100%;
    height: auto;

    @include media-breakpoint-down(lg) {
      max-width: calc(100% - 20px);
    }

    .video-js.vjs-fluid {
      height: 100%;
      z-index: -1;
    }

    &:hover {
      img,
      .video-js video {
        transform: scale(1.05, 1.05);
      }
    }

    img,
    .video-js video,
    .teaser-video-iframe {
      transition: transform 5s cubic-bezier(0.165, 0.84, 0.26, 0.98);
      object-fit: cover;
      width: 100%;
      height: 100%;
      z-index: -1;
      display: inline-block;
      transform: scale(1);
      position: absolute;
      top: 0;
    }

    & > span {
      font-family: $font-family-regular !important;
      font-style: normal;
      margin-top: 24px;
      color: transparent;
      transition: all 1s;
    }

    &-active {
      &:before {
        content: "";
        padding-top: 56.25%;
        display: block;
        position: relative;
        z-index: -1;
      }

      .video-js, .teaser-video-iframe {
        position: absolute;
        top: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
      }

      &:hover {
        transform: scale(1, 1);
      }

      & > span {
        color: black;
        transition: all 1s;
      }
    }
  }

  .swiper-button-prev,
  .swiper-button-prev:after,
  .swiper-button-next,
  .swiper-button-next:after {
    display: flex;
    justify-content: center;
    align-items: center;
    content: none;
    background-image: none;
    opacity: 0;

    @include media-breakpoint-down(lg) {
      display: none;
    }
  }

  .swiper-button-disabled {
    display: none;
  }

  @keyframes arrowMoveLeft {
    from,
    to {
      transform: translate3d(0, 0, 0);
    }

    50% {
      transform: translate3d(-10px, 0, 0);
    }
  }

  @keyframes arrowMoveRight {
    from,
    to {
      transform: translate3d(0, 0, 0);
    }

    50% {
      transform: translate3d(10px, 0, 0);
    }
  }

  @keyframes bounceMobileSlider {
    from,
    to {
      margin-left: 0;
    }

    50% {
      margin-left: -5px;
    }
  }

  .swiper-description {
    display: block;
    padding-top: 24px;
    padding-bottom: 32px;
    font-family: Orsted Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;

    @include media-breakpoint-down(lg) {
      padding: 18px 20px 55px 20px;
    }

    > span {
      display: none;
    }
  }

  .swiper-header {
    @include media-breakpoint-down(lg) {
      padding: 0 20px 0px 20px;
    }

    > .title {
      padding: 60px 0 16px 0;
      font-family: Orsted Sans;
      font-style: normal;
      font-weight: bold;
      font-size: 36px;
      line-height: 44px;

      @include media-breakpoint-down(lg) {
        padding-top: 40px;
      }
    }

    > .sub-title {
      font-family: Orsted Sans;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 24px;
    }
  }

  &.swiper-theme_ {
    &light {
      .swiper-button-prev,
      .swiper-button-next {
        & svg path {
          color: $color-dark-blue;
        }
      }

      .swiper-slide-next,
      .swiper-slide-prev {
        &:before {
          background: rgba(255, 255, 255, 0.5);
          content: "";
          position: absolute;
          width: 100%;
          height: 100%;
          z-index: 1;
        }
      }
    }

    &dark {
      .swiper-button-prev,
      .swiper-button-next {
        & svg path {
          fill: white;
        }
      }

      .swiper-slide-next,
      .swiper-slide-prev {
        &:before {
          background: rgba(59, 73, 86, 0.4);
          content: "";
          position: absolute;
          width: 100%;
          height: 100%;
          z-index: 1;
        }
      }
    }

    &blue-white {
      background-color: $color-blue;

      > .swiper-header {
        color: white;
      }

      .swiper-description {
        color: white;
      }
    }

    &white-cold-grey {
      background-color: white;

      .swiper-header {
        color: $color-dark-blue;
      }

      .swiper-description {
        color: $color-dark-blue;
      }
    }

    &cold-grey-6-cold-grey {
      background-color: $color-cold-grey;

      .swiper-header {
        color: $color-dark-blue;
      }

      .swiper-description {
        color: $color-dark-blue;
      }
    }

    &cold-grey-blue {
      background-color: $color-cold-grey;

      .swiper-header {
        color: $color-blue;
      }

      .swiper-description {
        color: $color-blue;
      }
    }

    &cold-grey-white {
      background-color: $color-dark-blue;

      .swiper-header {
        color: white;
      }

      .swiper-description {
        color: white;
      }
    }
  }
}
