.modal {
    width:100%;
    height:100%;

    &.fade {
        .modal-dialog {
            top: 50%;
            left: 50%;
            margin: 0px !important;
            width: calc(100% - 30px);
            position: fixed;
            transform: translate(-50%, -50%);
        }
    }
    // overrides bootstrap - bug - when viewport is small - top part off modal is cut off and not center in viewport
    .modal-dialog {
        margin-top: 0;
        margin-bottom: 0;

        @include media-breakpoint-down(sm) {
            margin-left: 15px !important;
            margin-right: 15px !important;
        }

        &.left-bottom {
            position: fixed;
            top: auto !important;
            bottom: 15px;
            left: 15px;
            margin: 0px;
            transform: translate(0,0) !important;

            @include media-breakpoint-down(sm) {
                margin-left: 0 !important;
            }
        }
    }

    button.close {
        position: absolute;
        top: 20px;
        right: 20px;
    }

    .modal-content {
        display: block;

        .modal-body {
            display: block;
            padding: 40px;
        }
    }

    &.no-padding {
        .modal-body {
            padding: 0;
        }
    }

    &.dark-theme {
        button.close {
            color: #3B4956;
        }
    }
}

body.modal-open{
    overflow:hidden;
}
.modal-lg {
  max-width: 720px !important;
}
.modal-backdrop {
    background-color: rgba($color-dark-blue, 0.85);
}