.teaserCircle {
  .teaser-content {
    .site-theme-radius & {
      font-size: $text-size-body-radius;
    }
  }
  .teaser-quotation {
    .site-theme-radius & {
      margin-bottom: -30px;
      font-family: $font-family-regular-radius;
      font-size: 90px;
    }
  }
  .teaser-quote {
    .site-theme-radius & {
      font-family: $font-family-regular-radius;
      font-size: $text-size-quote-radius;
      line-height: $line-height-quote-radius;
    }
    footer {
      cite {
        .site-theme-radius & {
          font-family: $font-family-bold-radius;
        }
      }
    }
  }
}
