@mixin theme-colors($theme-name, $color-primary, $color-base, $color-accent, $btn-class: 'blue', $color-secondary: $color-dark-blue, $color-theme: $color-white, $color-key-line: $color-dark-blue) {
  color: $color-base;

  .theme-primary-color-background,
  &.theme-primary-color-background,
  .section & .theme-primary-color-background,
  .section &.theme-primary-color-background {
    background-color: $color-primary;
  }

  .theme-base-color-background,
  &.theme-base-color-background,
  .section & .theme-base-color-background,
  .section &.theme-base-color-background {
    background-color: $color-base;
  }

  .theme-accent-color-background,
  &.theme-accent-color-background,
  .section & .theme-accent-color-background,
  .section &.theme-accent-color-background {
    background-color: $color-accent;
  }

  .theme-accent-color-background-secondary,
  &.theme-accent-color-background-secondary,
  .section & .theme-accent-color-background-secondary,
  .section &.theme-accent-color-background-secondary {
    background-color: $color-theme;
  }

  .theme-primary-border-color,
  &.theme-primary-border-color,
  .section & .theme-primary-border-color,
  .section &.theme-primary-border-color {
    border-color: $color-primary;
  }

  .theme-accent-border-color,
  &.theme-accent-border-color,
  .section & .theme-accent-border-color,
  .section &.theme-accent-border-color {
    border-color: $color-accent;
  }

  .theme-color-primary,
  &.theme-color-primary,
  .section & .theme-color-primary,
  .section &.theme-color-primary {
    color: $color-primary;
  }

  .theme-color-accent,
  &.theme-color-accent,
  .section & .theme-color-accent,
  .section &.theme-color-accent {
    color: $color-accent;
  }

  .theme-color-base,
  &.theme-color-base,
  .section & .theme-color-base,
  .section &.theme-color-base {
    color: $color-base;
    fill: $color-base;
  }

  .theme-color-secondary {
    color: $color-secondary;
  }

  .theme-keyline-border,
  &.theme-keyline-border {
    border-color: $color-key-line;
  }

  & .btn,
  [class*="theme-"] & .btn,
  [class*="theme-"] [class*="theme-"] & .btn {
    $theme-props: map-get($buttonThemes, $btn-class);

    @include btnTheme(
      map-get($theme-props, background-color),
      map-get($theme-props, text-color),
    );
  }

  @include link-arrow-color($color-accent);



  &.section .btn-btn-cta {
    $theme-props: map-get($buttonThemes, $btn-class);

    @include btnTheme(
      map-get($theme-props, background-color),
      map-get($theme-props, text-color),
    );
  }

  @include teaser-arrow-color($color-accent);

  .section & {
    @include link-arrow-color($color-accent);

    .btn-btn-cta {
      $theme-props: map-get($buttonThemes, $btn-class);
  
      @include btnTheme(
        map-get($theme-props, background-color),
        map-get($theme-props, text-color),
      );
    }
  }
}

/**
 * The site-themes are css classes that govern text-color in articles
 */
@mixin site-theme($text-color) {

  &,
  &.article {
    .article-subheadline {
      color: $text-color;
    }
  }

  &.article-content-inner h2 {
    color: $text-color;
  }

  .richtext {
    a:hover {
      text-decoration: none;
    }
  }

  .colored-text {
    color: $text-color;
  }

  .link-arrow {
    color: $text-color;
  }
}

//some components are calling this mixin
@mixin theme($theme-name: 'blue') {
  $theme-props: map-get($themes, $theme-name);
  @include theme-colors(
    $theme-name,
    map-get($theme-props, background-color),
    map-get($theme-props, text-color),
    map-get($theme-props, link-color),
    map-get($theme-props, button-theme-class),
    map-get($theme-props, secondary-color),
    map-get($theme-props, secondary-background-color),
    map-get($theme-props, keyline),
  )
}

/*--------------------- The following is used to overwrite component-themes when using site-theme-radius. ----------------------------*/

/*--------------------------------------------------------------
  Mixin for extended theming
  - header (headerClassic Top)
  - navigation (headerClassic Bottom)
  - topTasks
  - heroOverlay
  - teaser
  - splash
  - onwardlinks
--------------------------------------------------------------*/
@mixin header($background-color, $background-highlight-color, $text-color, $text-highlight-color, $tab-text-color) {
  header {
    &.header {
      &.headerClassic {
        .headerClassic-top {
          background-color: $background-color;

          .headerClassic-top-inner {
            nav {
              &.header-links {
                .header-link {
                  color: $text-color;

                  &.header-link-tab-style {
                    color: $tab-text-color;

                    &.header-link-active {
                      background-color: $background-highlight-color;
                      color: $text-highlight-color;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@mixin navigation($background-bottom-color, $text-color, $text-highlight-color, $dropdown-background-color, $dropdown-text-color, $dropdown-text-highlight-color, $button-color, $button-text-color) {
  header {
    &.header {
      &.headerClassic {
        .headerClassic-mobile-links {
          & > * i {
            color: $button-color;
          }
        }

        .headerClassic-bottom {
          background-color: $background-bottom-color;

          .headerClassic-bottom-inner {
            .header-megaNavigation {
              .megaNavigation {
                .megaNavigation-menu {
                  .megaNavigation-menu-item {
                    &:last-child {
                      border-bottom: none;

                      @include media-breakpoint-down(sm) {
                        border-bottom: 1px solid rgba($color-border-grey, 0.5);
                      }
                    }

                    .megaNavigation-menu-item-link {
                      color: $text-color;

                      &:hover {
                        color: $text-highlight-color;
                      }

                      &.megaNavigation-item-active {
                        color: $text-highlight-color;
                      }

                      &.megaNavigation-submenu-visible {
                        @include media-breakpoint-down(sm) {
                          background-color: $dropdown-text-color;
                          color: $dropdown-text-highlight-color;
                        }
                      }
                    }

                    .megaNavigation-submenu {
                      @include media-breakpoint-up(md) {
                        background-color: $dropdown-background-color;
                      }

                      .megaNavigation-submenu-inner {
                        .megaNavigation-submenu-item-link {
                          &.megaNavigation-submenu-item-link-active {
                            color: $text-color;
                          }
                        }
                      }
                    }
                  }

                  .megaNavigation-menu-siteselector {
                    background-color: $dropdown-text-color;
                  }
                }

                .megaNavigation-menu-siteselector {
                  .megaNavigation-menu-siteselector-item {
                    background-color: $dropdown-text-color;
                    color: $dropdown-text-highlight-color;

                    &.megaNavigation-menu-siteselector-item-active {
                      background-color: $dropdown-text-highlight-color;
                      color: $dropdown-text-color;
                    }
                  }
                }
              }
            }

            .headerClassic-bottom-right {
              .header-search {
                i {
                  &.icon {
                    color: $text-color;
                  }
                }
              }
            }

            .header-selfservice {
              .btn-dropdown {
                background-color: $button-color;
                color: $button-text-color;
                line-height: 1.6rem !important; //looks better with the radius font / Ariel
              }
            }
          }
        }
      }
    }
  }
}

/**
  TopTasks Mixin
  - Primary Color
  - Primary Background Link Color (usually #fff)
 */
@mixin topTasks($primary-color, $primary-background-link-color) {
  .topTasks-inner {
    .topTasks-list {
      background-color: $primary-color;
      color: $primary-background-link-color;

      .topTasks-task-emphasize,
      .topTasks-task-emphasize .topTasks-task-link,
      .topTasks-task-emphasize .topTasks-task-icon,
      .topTasks-task-emphasize .topTasks-task-icon img {
        color: $primary-background-link-color;
      }
    }
  }

  .topTasks-trigger {
    background-color: $primary-color;

    &:hover {
      background-color: $primary-color;
    }
  }
}

/**
  Hero Overlay Mixin
  - Header Color
  - Accent Color
  - Button Text Color (may be removed -- seems to always be #fff)
 */
@mixin heroOverlay($header-color, $accent-color, $button-text-color) {
  .heroOverlay-content {
    .teaser {
      .teaser-content {
        .teaser-tagline {
          color: $accent-color;
        }

        .teaser-body {
          .teaser-headline {
            color: $header-color;
          }
        }

        .teaser-cta {
          .btn {
            &.teaser-btn {
              background-color: $accent-color;
              color: $button-text-color;
            }
          }
        }
      }
    }
  }
}

/**
  Splash Mixin
  - Background Color
  - Text Color
  - Link Color
 */
@mixin splash($background-color, $text-color, $link-color) {
  .splash-inner {
    background-color: $background-color;

    .splash-content {
      .splash-content-body {
        color: $text-color;
      }

      .splash-cta {
        .link-arrow {
          color: $link-color;
        }

        .btn.teaser-btn {
          background-color: $link-color;
          color: $color-white;
        }
      }
    }
  }
}

/**
  Onward Links Mixin
  - Background Color
  - Accent Color
  - Link Text Color
 */
@mixin onward-links($background-color, $accent-color, $link-text-color) {
  background-color: $background-color;

  .onward-link {
    .onward-media {
      .link-img {
        &.is-icon {
          background-color: $accent-color;
        }
      }
    }

    .onward-content {
      .onward-tagline {
        color: $link-text-color;
      }

      .onward-cta {
        .link-arrow {
          color: $link-text-color;
        }
      }
    }
  }
}

