.list {
  .site-theme-radius & {
    font-size: $text-size-body-small-radius;
    line-height: $line-height-body-small-radius;
  }
  .list-title {
    .site-theme-radius & {
      @include h3;
      font-family: $font-family-light-radius !important;
    }
  }
  .list-header {
    .list-header-column {
      .list-header-column-name {
        .site-theme-radius & {
          color: $color-white;
        }
      }
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
      }
      .list-header-column-cta {
        a {
          .site-theme-radius & {
            color: $color-white;
            font-family: $font-family-bold-radius !important;
          }
          &:hover {
            .site-theme-radius & {
              color: $color-white;
            }
          }
        }
      }
    }
  }
  .list-items {
    .site-theme-radius & {
      color: $color-radius-darkgrey;
    }
    &.list-show-labels {
      .list-items-item-column {
        &:before {
          .site-theme-radius & {
            font-family: $font-family-bold-radius;
          }
        }
      }
    }
    .list-items-item-value-type-header {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
      }
    }
    &.list-type-download {
      .link-arrow {
        .site-theme-radius & {
          @include h5-bold;
          font-family: $font-family-bold-radius !important;
        }
      }
    }
    &.list-type-table {
      .list-header .list-header-column {
        .site-theme-radius & {
          background-color: $color-radius-blue;
          border-left: 1px solid $color-radius-blue;
          border-right: 1px solid $color-radius-blue;
          color: white;
        }
      }
    }
  }
  .dataTables_wrapper .dataTables_paginate .paginate_button.current {
    .site-theme-radius & {
      background-color: transparent;
      color: $color-radius-blue;
      font-weight: bolder;
    }
  }
}
