$navigationBackgroundColor-mobile: $color-white !default;
$navigationBackgroundColor-desktop: $color-white !default;

$navigationTextColor: $color-text !default;
$navigationTextColor-mobile: $color-text !default;

$navigationTextColor-desktop: $color-text !default;
$navigationTextColor-hover: $color-red !default;
$navigationTextColor-active: $color-red !default;

$submenuBackgroundColor: $color-white !default;
$submenuTextColor: $color-text !default;
$submenuTextColor-hover: $color-white !default;

$submenuLinkTextColor: $color-text !default;
$submenuLinkTextColor-hover: $color-white !default;

$submenuTextColor-active: $color-aubergine !default;
$submenuLinkTextColor-active: $color-aubergine !default;
$megaNavigation-submenu-visible-background: $color-red !default;
$megaNavigation-submenu-visible-color: $color-white !default;

$link-fontsize: 16px;
.megaNavigation-small-font {
}
.megaNavigation {
  // display: none;
  justify-content: flex-end;
  flex-direction: row;

  @include media-breakpoint-up($header-breakpoint) {
    justify-content: flex-start;
    width: 100%;
    margin-top: 32px; //change as part of defect 909 fix
    align-items: flex-end;
    display: flex;
    z-index: 0;
  }

  .megaNavigation-trigger {
    $size: ($header-bottom-height-small / 2) + 1; //+1 to get it to 36px
    display: inline-flex;
    margin-left: 10px;
    float: right;
    background-color: $color-dark-blue;
    border-radius: 100%;
    width: $size;
    height: $size;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    position: relative;

    div {
      position: absolute;
      top: 11px;
      left: 8px;
    }

    span {
      display: block;
      position: absolute;
      height: 2px;
      width: 20px;
      background: #fff;
      opacity: 1;
      left: 0;
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
      -webkit-transition: 0.25s ease-in-out;
      -moz-transition: 0.25s ease-in-out;
      -o-transition: 0.25s ease-in-out;
      transition: 0.25s ease-in-out;
    }

    span:nth-child(1) {
      top: 0px;
      -webkit-transform-origin: left center;
      -moz-transform-origin: left center;
      -o-transform-origin: left center;
      transform-origin: left center;
    }

    span:nth-child(2) {
      top: 6px;
      -webkit-transform-origin: left center;
      -moz-transform-origin: left center;
      -o-transform-origin: left center;
      transform-origin: left center;
    }

    span:nth-child(3) {
      top: 12px;
      -webkit-transform-origin: left center;
      -moz-transform-origin: left center;
      -o-transform-origin: left center;
      transform-origin: left center;
    }

    @include media-breakpoint-up($header-breakpoint) {
      float: none;
      display: none;
    }
  }

  .megaNavigation-menu {
    display: block;
    position: absolute;
    left: -110%;
    top: $header-bottom-height-small;
    z-index: 10;
    width: 100vw;
    height: calc(100vh - #{$header-bottom-height-small + 20});
    @include list;
    margin: 0 -5px;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    background-color: $navigationBackgroundColor-mobile;
    transition: left $transition-duration $easing-snap;
    border-top: 1px solid $color-border-grey;
    font-size: 19px;

    @include media-breakpoint-up($header-breakpoint) {
      .site-theme-radius & {
        font-size: $text-size-xsmall-radius;
      }

      padding-bottom: 0;
      font-size: 1em;
      border-top: 0;
      position: static;
      display: flex;
      flex-wrap: wrap;
      width: auto;
      height: auto;
      overflow: visible;
      background-color: $navigationBackgroundColor-desktop;
    }
  }

  &.megaNavigation-menu-visible {
    .megaNavigation-trigger {
      span:nth-child(1) {
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        transform: rotate(45deg);
        top: -1px;
        left: 3px;
      }

      span:nth-child(2) {
        opacity: 0;
      }

      span:nth-child(3) {
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -o-transform: rotate(-45deg);
        transform: rotate(-45deg);
        top: 13px;
        left: 3px;
      }
    }

    .megaNavigation-menu {
      left: 0;
    }
  }

  @mixin openSubmenu {
    display: flex;
    align-item: center;
    align-content: center;

    @include media-breakpoint-up($header-breakpoint) {
      display: inline-block;

      &:before {
        transform: translate3d(-50%, 0, 0);
        opacity: 1;
        transition: transform $transition-duration linear,
          opacity $transition-duration 100ms linear;
      }
    }

    .megaNavigation-submenu {
      opacity: 1;
      border-top: 20px solid $color-white;
      max-height: none;

      @include media-breakpoint-up($header-breakpoint) {
        border-top: 52px solid $color-white;
        transition: max-height 0ms ease-in-out, opacity 200ms linear,
          border-top 0ms linear;
      }
    }
  }
  /*  @include media-breakpoint-up(md) {
        // when on ipad landscape, we have no hover but desktopmenu.
        //.no-touch & { //NOTE - JJE: I have removed this to enable hover on devices with both hover (mouse) and touch (such as Kaspers' windows PC)
        // NOTE - RT: This is handled by javascript in megaNavigation.js
        &:not(.megaNavigation-slider) .megaNavigation-menu-item:not(.megaNavigation-no-submenu) {
            &:hover {
                @include openSubmenu;
            }
        }
        //}
    }*/
  @include media-breakpoint-up($header-breakpoint) {
    //background-layer moved here to fix bug where the mega-menu would get a weird background-opacity layer on ipad pro's (and large touch devices in general).
    .no-touch & {
      .megaNavigation-menu-item.megaNavigation-menu-item-submenu-open {
        .megaNavigation-submenu-trigger {
          &:after {
            opacity: 1;
            height: 100vh;
            transition: opacity $transition-duration, height 0ms linear;
          }
        }
      }
    }
  }

  .megaNavigation-menu-siteselector {
    display: flex;
    align-items: stretch;
    background-color: $color-backdrop-2;

    .megaNavigation-menu-siteselector-item {
      flex: 0 1 auto;
      padding: 20px 25px;
      color: $color-text;

      &.megaNavigation-menu-siteselector-item-active {
        background-color: $color-white;
        color: $color-text;
        font-family: $font-family-bold;
      }
      //hack for aligning the items correctly on mobile
      .bottom-aligner {
        display: table;
        vertical-align: bottom;
        height: 100%;

        .bottom-cell {
          display: table-cell;
          vertical-align: middle;
        }
      }
    }

    .megaNavigation-menu-siteselector-logon {
      display: flex;
      flex: 1;
      justify-content: flex-end;
      padding-right: 10px;
    }

    @include media-breakpoint-up(md) {
      display: none;
    }
  }

  .megaNavigation-menu-item {
    backface-visibility: hidden;
    background-color: transparent;
    flex-direction: column;
    display: flex;
    word-break: break-word;
    word-wrap: break-word;

    @include media-breakpoint-up($header-breakpoint) {
      display: inline-block;

      &:before {
        content: " ";
        height: 0;
        bottom: -62px;
        z-index: 2;
        width: 0;
        margin-top: -10px;
        // the arrow hack
        border-left: 10px solid transparent;
        border-top: 10px solid $color-white;
        border-right: 10px solid transparent;
        position: absolute;
        left: 50%;
        pointer-events: none;
        opacity: 0;
        transform: translate3d(-50%, -20px, 0);
        transition: transform $transition-duration/2 linear,
          opacity $transition-duration/2 linear;
      }
    }

    &:last-child {
      border-bottom: none;
    }

    @include media-breakpoint-up($header-breakpoint) {
      border-bottom: 0;
      position: relative;

      &.megaNavigation-menu-item-submenu-open {
        @include openSubmenu;
      }
    }

    &.megaNavigation-menu-item-logon {
      flex-direction: row;
    }

    &:hover {
      @include media-breakpoint-up($header-breakpoint) {
        z-index: 5;
      }
    }

    &.active-sub {
      @include openSubmenu;
    }
  }

  .megaNavigation-menu-item {
    &:first-of-type {
      .megaNavigation-menu-item-link {
        border-top: 0;
      }
    }

    &.hidden-md-up:last-child {
      .megaNavigation-menu-item-link {
        border-bottom: 1px solid rgba($color-border-grey, 0.5);
      }
    }

    &.active-sub,
    &.megaNavigation-item-active {
      .megaNavigation-menu-item-link {
        color: $navigationTextColor-active;
        font-family: $font-family-bold;

        .megaNavigation-submenu-trigger-arrow {
          &:before {
            color: inherit;
          }
        }
      }
    }

    &.megaNavigation-item-active {
      .megaNavigation-menu-item-link {
        color: $navigationTextColor-active;
      }
    }
  }

  .megaNavigation-menu-item-link {
    padding: 20px 30px 20px 30px;
    border-top: 1px solid rgba($color-border-grey, 0.5);
    @include flex-1;
    color: $navigationTextColor-mobile;

    @include media-breakpoint-up($header-breakpoint) {
      transition: color $transition-duration/2 linear,
        padding $transition-duration/2 ease-in-out;
      border-top: 0;
      color: $navigationTextColor-desktop;
      padding: 5px 12px;
      font-size: 19px;
      font-family: $font-family-bold;
      letter-spacing: 0.2px;

      .site-theme-radius & {
        font-family: $font-family-bold-radius;
        font-size: $text-size-xsmall-radius;
      }

      &:hover {
        color: $navigationTextColor-hover;
      }
    }
    // @include media-breakpoint-up(lg){
    //     padding:5px 12px;
    // }
    &.megaNavigation-item-active {
      font-family: $font-family-bold;
    }

    &.megaNavigation-menu-item-link-active {
      color: $navigationTextColor-active;
      font-family: $font-family-bold;

      letter-spacing: 0.2px;
    }

    &.megaNavigation-submenu-visible {
      background-color: $megaNavigation-submenu-visible-background;
      color: $megaNavigation-submenu-visible-color;
      border-bottom: 0;
      border-top: 0;

      ~ .megaNavigation-submenu {
        opacity: 1;
      }
    }
  }

  @at-root .megaNavigation-small-font {
    @include media-breakpoint-only($header-breakpoint) {
      .megaNavigation-menu-item-link {
        font-size: 14px;
        padding: 5px 8px;
      }
    }
  }

  .megaNavigation-submenu-trigger {
    @include media-breakpoint-up($header-breakpoint) {
      &:after {
        opacity: 0;
        content: "";
        position: absolute;
        background-color: rgba(0, 0, 0, 0.5);
        z-index: -1;
        top: 0;
        left: 0;
        width: 100vw;
        height: 0;
        transition: opacity $transition-duration linear,
          height 0ms linear $transition-duration;
      }

      .megaNavigation-submenu-trigger-arrow {
        position: relative;
        margin-left: 4px;
        font-family: "dong-maine" !important;
        font-size: 0.7em !important;

        &:before {
          content: "\63";
          display: inline-block;
          width: 18px;
          height: 18px;
          vertical-align: text-bottom;
        }
      }
    }
  }

  .megaNavigation-submenu {
    @include flex-1;
    @include list;
    display: flex;
    max-height: 0;
    flex-direction: column;
    background-color: $submenuBackgroundColor;
    opacity: 0;
    z-index: 1;

    @include media-breakpoint-up($header-breakpoint) {
      overflow: hidden;
      position: fixed;
      flex-direction: row;
      margin: 0 auto;
      left: 0;
      width: 100vw;
      max-height: 0;
      border-top: 0px solid $color-white;
      transition: max-height 400ms ease-in-out, opacity 200ms 400ms,
        border 0ms linear 400ms;
    }
  }

  .megaNavigation-submenu-inner {
    display: flex;
    flex-direction: column;
    padding-left: 0px;
    padding-right: 0px;
    padding-top: 5px;
    padding-bottom: 15px;

    @include media-breakpoint-up($header-breakpoint) {
      flex-direction: row;
      width: 100%;
      max-width: 1470px;
      padding-left: $module-space-horisontal-left;
      padding-right: $module-space-horisontal-left;
      padding-bottom: $module-space-horisontal-left + 8px;
      padding-top: $module-space-horisontal-left + 20px;
      margin: 0 auto;
      flex-wrap: wrap;
    }
  }

  .no-touch & {
    .megaNavigation-submenu-inner.no-subitems {
      //if the mega-menu has no subitems, then it should have no padding on desktop-devices (note: on touch-devices there will always be atleast 1 li)
      @include media-breakpoint-up($header-breakpoint) {
        padding: 0;
      }
    }
  }

  .megaNavigation-submenu-item {
    padding: 20px 30px 10px 30px;

    &:first-child() {
      flex: initial;
    }

    @include media-breakpoint-up($header-breakpoint) {
      display: flex;
      flex-direction: column;
      margin-right: $module-space-horisontal-left;
      min-width: 250px;
      max-width: 30%;
      padding: 10px;
      background-color: transparent;
      border-bottom: 0;
      flex: 0;
    }
  }

  .megaNavigation-submenu-item-section {
    @include media-breakpoint-up(md) {
      //display: none;
      align-self: flex-start;
      width: 100%;
      max-width: none;
      min-width: 100%;
    }

    .megaNavigation-submenu-item-link {
      font-family: $font-family-bold;
      cursor: text;
    }
  }

  .megaNavigation-submenu-section-link {
    @extend .link-arrow-dark-blue;
  }

  .megaNavigation-submenu-item-link {
    display: inline-block;
    margin-bottom: 20px;
    color: $submenuTextColor;
    font-size: 1em;
    font-family: $font-family-bold;

    @include media-breakpoint-up($header-breakpoint) {
      margin-bottom: 12px;
    }

    &:hover {
      color: $submenuTextColor;
    }

    &.megaNavigation-submenu-item-link-active {
      color: $submenuTextColor;
    }
  }

  .megaNavigation-submenu-item-submenu {
    @include list;
  }

  .megaNavigation-submenu-item-submenu-item {
    display: flex;
    flex-direction: column;
  }

  .megaNavigation-submenu-item-submenu-item-link {
    line-height: 1.2;
    margin-bottom: 20px;
    color: $submenuLinkTextColor;

    @include media-breakpoint-up($header-breakpoint) {
      margin-bottom: 12px;
    }

    &:hover {
      color: $submenuLinkTextColor-hover;
    }

    &.megaNavigation-submenu-item-active {
      color: $submenuLinkTextColor-active;
      font-family: $font-family-bold;
    }
  }
}

.logincustpopover {
  max-width: inherit !important;
  max-height: 100% !important;
  border: initial !important;
  background-color: initial !important;
  position: relative !important;
  top: 0 !important;
  left: 0 !important;
  transform: none !important;
  overflow-x: hidden !important;
  overflow-y: hidden !important;
  padding: 0px 20px 0 20px !important;

  @include media-breakpoint-down(sm) {
    [class^="col-"] {
      padding: 0px !important;
    }
  }
  .tabs-tabs li.is-open {
    background-color: rgba(216, 209, 202, 0.5);
  }
}
.logincustpopover::before,
.popover::after {
  position: initial !important;
}

