.moving-form {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  flex-grow: 1;
  min-height: 220px;
  overflow: hidden;
  position: relative;

  .form-area {
    padding: $module-space-vertical-top $module-space-horisontal-left
      $module-space-vertical-bottom $module-space-horisontal-right;
    display: flex;
    flex-direction: column;
    @include flex-1;
    flex: 1 !important;
    max-width: 100%;
    background-color: $color-backdrop-2;
    color: $color-dark-blue;

    .form-headline {
      @include h5-bold;
      padding-bottom: 20px;
    }

    .questions {
      legend {
        line-height: 42px;
        font-size: 16px;
        padding-right: 10px;
      }
    }

    fieldset {
      p {
        max-width: 650px;
      }
    }
  }

  .content-area {
    background-color: $color-backdrop-2;
    color: $color-dark-blue;
    padding: $module-space-vertical-top $module-space-horisontal-left
      $module-space-vertical-bottom $module-space-horisontal-right;
  }

  .form-area,
  .content-area {
    h1,
    h2,
    h3,
    h4 {
      color: $color-aubergine;
    }
  }

  .customSelect {
    background-color: #fff;
    border: 1px solid #b2b2b2;
    min-width: 50%;

    &:before {
      border-left: 1px solid rgba(51, 51, 51, 0.5);
      color: #3b4956;
    }

    select {
      color: #3b4956;

      &::-ms-value {
        background: none;
        color: #3b4956;
      }
    }
  }

  form {
    width: 100%;
  }

  @include media-breakpoint-up(md) {
    display: table;
    display: flex;
    flex-direction: row;

    .form-area {
      .questions {
        legend {
          line-height: 45px;
          font-size: 18px;
        }

        .customSelect {
          clear: none;
        }
      }
    }

    .content-area {
      padding-left: $module-space-horisontal-left + 5px;
      flex: 1 0 50%;
      max-width: 50%;
    }
  }
}

.moving-form-contenttable {
  width: 50%;
  display: table-cell;
  @include media-breakpoint-down(sm) {
    max-width: 100% !important;
    width: 100%;
    display: block;
  }
}

@media screen and (max-width: 992px) {
  _:-ms-fullscreen,
  :root .moving-form {
    display: block;
  }
}

@media screen and (min-width: 992px) {
  _:-ms-fullscreen,
  .moving-form .content-area {
    display: table !important;
    height: 100%;
  }
}

@media screen and (min-width: 560px) {
  .moving-form > * {
    width: 100% !important;
    max-width: 100% !important;
    display: block !important;
  }
}

@media screen and (min-width: 992px) {
  .moving-form > * {
    width: 50% !important;
    max-width: 50% !important;
    display: table-cell !important;
    display: inline-flex !important;
    flex-direction: column;
  }
}

@media screen and (max-width: 992px) {
  .moving-form {
    flex-direction: column;
  }
}
