.picture {
  line-height: 0;
  transform: translateZ(0);
  display: block;
  min-height: calc(100vw / 1.777);

  .picture__image {
    min-width: 100%;
    max-width: 100%;
    opacity: 0;
    transition: opacity 250ms linear;
  }

  &.picture--loaded {
    min-height: 0;

    .picture__image {
      opacity: 1;
    }
  }
}
