.expandCollapseCircle {
  padding: 20px 0px;

  .expandCollapseCircle-module-heading {
    margin-bottom: 24px;
    padding-left: 30px;
    padding-right: 30px;
  }

  .expandCollapseCircle-module {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 30px;
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }

    @include media-breakpoint-up(md) {
      flex-direction: row;
    }

    .expandCollapseCircle-module-content-intro {
      @include media-breakpoint-up(md) {
        overflow: hidden;
      }
    }

    .expandCollapseCircle-module-image {
      @include image-scale-up-transition;
      background-position: 50%;
      background-size: cover;
      background-repeat: no-repeat;
      width: 227px;
      height: 227px;
      max-height: 100%;
      max-width: 100%;
      transform: scale(1);
      border-radius: 50%;
      margin-bottom: 20px;

      @include media-breakpoint-up(md) {
        // height: auto;
        margin-bottom: auto;
        margin-right: 35px;
      }
    }

    .expandCollapseCircle-icon {
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 140px;
    }

    .expandCollapseCircle-module-content {
      text-align: center;
      max-width: 100%;

      @include media-breakpoint-up(md) {
        text-align: left;
        width: 100%;
      }

      > a {
        position: relative;
        display: inline-block;
        transition: transform 1s ease;
        transform-origin: center;
        margin-top: 20px;
        font-weight: bold;

        &:after {
          content: "\63";
          font-family: "dong-maine" !important;
          display: inline-block;
          position: relative;
          top: 0;
          left: 15px;
          width: 15px;
          //height: 10px;
          font-size: 8px;
          color: #3b4956;
        }
      }
    }

    .expandCollapseCircle-module-content-expanded {
      height: 0;
      max-height: 0;
      overflow: hidden;
    }

    &.is-expanded {
      .expandCollapseCircle-module-content-expanded {
        margin-top: 20px;
        max-height: 9999px;
        height: auto;
        transition: max-height 3s ease;
      }

      .expandCollapseCircle-module-content {
        a.js-expandCollapseCircle-expand-toggle {
          &:after {
            transform: rotate(180deg);
          }
        }
      }
    }
  }

  &.expandCollapseCircle-theme-blue {
    .expandCollapseCircle-module-heading h4 {
      color: $color-blue;

      &:after {
        color: $color-blue;
      }
    }

    .expandCollapseCircle-module-content {
      .expandCollapseCircle-module-content-intro h4,
      > a {
        color: $color-blue;

        &:after {
          color: $color-blue;
        }
      }
    }
  }

  &.expandCollapseCircle-theme-red {
    .expandCollapseCircle-module-heading h4 {
      color: $color-red;

      &:after {
        color: $color-red;
      }
    }

    .expandCollapseCircle-module-content {
      .expandCollapseCircle-module-content-intro h4,
      > a {
        color: $color-red;

        &:after {
          color: $color-red;
        }
      }
    }
  }

  &.expandCollapseCircle-theme-aubergine {
    .expandCollapseCircle-module-heading h4 {
      color: $color-aubergine;

      &:after {
        color: $color-aubergine;
      }
    }

    .expandCollapseCircle-module-content {
      .expandCollapseCircle-module-content-intro h4,
      > a {
        color: $color-aubergine;

        &:after {
          color: $color-aubergine;
        }
      }
    }
  }

  &.expandCollapseCircle-theme-radius-blue {
    .expandCollapseCircle-module-heading h4 {
      color: $color-radius-blue;

      &:after {
        color: $color-radius-blue;
      }
    }

    .expandCollapseCircle-module-content {
      .expandCollapseCircle-module-content-intro h4,
      > a {
        color: $color-radius-blue;

        &:after {
          color: $color-radius-blue;
        }
      }
    }
  }

  &.expandCollapseCircle-theme-default {
    .expandCollapseCircle-module-heading h4 {
      color: #3b4956;

      &:after {
        color: #3b4956;
      }
    }

    .expandCollapseCircle-module-content {
      .expandCollapseCircle-module-content-intro h4,
      > a {
        color: #3b4956;

        &:after {
          color: #3b4956;
        }
      }
    }
  }
}
