.timeline {
  display: flex;
  flex-direction: column;
  $spacing-large: 20px;
  $border-color: $color-backdrop-1;

  .timeline-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    @include flex-1;
    padding: $spacing-large;
    border-radius: $rounded-corner;
    background-color: $color-aqua;
    color: $color-text;
  }

  .timeline-title {
    display: flex;
    flex: 0 1 auto;
    align-items: center;
  }

  .timeline-cta {
    display: flex;
    flex: 0 1 auto;
    align-items: center;
    justify-content: flex-end;

    > * {
      margin-right: 0;
      padding-right: 0;
    }
  }

  .timeline-main {
    display: flex;
    margin-top: $spacing-large;
    flex-direction: column;
    position: relative;
    padding-bottom: 20px;
    @include media-breakpoint-up(md) {
      flex-direction: row;
    }
    @include media-breakpoint-down(sm) {
      height: auto !important;
    }
  }
  .timeline-anchor-container-arrow-up,
  .timeline-anchor-container-arrow-down {
    position: absolute;
    left: 20px;
    cursor: pointer;
    opacity: 1;
    transition: color $transition-duration;
    transform: translateZ(0);
    font-size: $text-size-body;
    line-height: $line-height-body;
    font-family: $font-family-regular;
    &.timeline-arrow-disabled {
      color: $color-backdrop-1;
      cursor: default;
    }
  }
  .timeline-anchor-container-arrow-up {
    transform: rotate(-90deg);
    top: 20px;
    display: none;
  }
  .timeline-anchor-container-arrow-down {
    bottom: 0px;
    transform: rotate(90deg);
    display: none;
  }
  .has-overflow {
    .timeline-anchor-container-arrow-up {
      display: block;
    }
    .timeline-anchor-container-arrow-down {
      display: block;
    }
  }

  .timeline-mobile-anchors {
    display: none;
  }
  @include media-breakpoint-down(sm) {
    .timeline-mobile-anchors {
      display: block;
      padding: 20px;
    }
  }
  .timeline-main-left {
    display: none;
    @include media-breakpoint-up(md) {
      display: flex;
    }
    flex-direction: column;
    position: relative;
    flex: 0 1 auto;
    max-width: 230px;
    min-width: 230px;
    overflow: hidden;

    &.has-overflow {
      padding-top: 40px;
      .timeline-main-left-inner {
        max-height: calc(100% - 30px);
        padding-top: 10px;
        padding-bottom: 10px;
      }
      &:before {
        display: block;
      }
      &:after {
        display: block;
      }
    }
    &:before {
      content: "";
      position: absolute;
      display: none;
      z-index: 1;
      top: 40px;
      left: 0;
      right: 0;
      width: 100%;
      height: 30px;
      z-index: 1;
      background: linear-gradient(
        0deg,
        rgba($color-white, 0) 0%,
        rgba($color-white, 1) 70%
      );
      pointer-events: none;
    }
    &:after {
      content: "";
      position: absolute;
      display: none;
      z-index: 1;
      bottom: 20px;
      left: 0;
      right: 0;
      width: 100%;
      height: 30px;
      z-index: 1;
      background: linear-gradient(
        180deg,
        rgba($color-white, 0) 0%,
        rgba($color-white, 1) 70%
      );
      pointer-events: none;
    }
  }

  .timeline-main-left-inner {
    width: 100%;
    position: relative;
    overflow: hidden;
    max-height: 100%;
    width: calc(100% + 20px);
  }

  .timeline-main-right {
    @include flex-1;
    overflow-x: hidden;
    overflow-y: auto;
    @include media-breakpoint-up(md) {
      margin-left: 20px;
      max-width: calc(100% - 230px - 20px);
    }
  }
  .timeline-main-right-inner {
    @include flex-1;
    width: 100%;
  }

  .timeline-anchor-container {
    padding: 20px;
    position: relative;
    &:before {
      content: "";
      display: block;
      position: absolute;
      left: $spacing-large + $spacing-large/2;
      height: calc(100% - 40px);
      width: 1px;
      top: 20px;
      background-color: $border-color;
      z-index: -1;
    }
  }

  .timeline-anchor {
    position: relative;
    color: $color-text;
    font-size: $text-size-body-small;
    font-family: $font-family-bold;

    @include asian-fonts;
    @include asian-fontweight-bold;

    display: flex;
    align-items: center;
    $circle-size: $spacing-large;
    cursor: pointer;
    word-break: break-word;
    word-wrap: break-word;

    &:before {
      transition: all $transition-duration;
      content: "";
      display: table;
      width: $circle-size;
      height: $circle-size;
      border-radius: $circle-size/2;
      border: 4px solid $color-body-background;
      margin-right: $spacing-large/2;
      background-color: $color-body-background;
      padding: 6px;
      box-shadow: 0 0 0 1px $border-color;
    }

    &:hover {
      &:before {
        border: 4px solid $color-blue;
        background-color: $color-body-background;
      }
    }

    &.timeline-anchor-active {
      &:before {
        border: 4px solid $color-body-background;
        background-color: $color-blue;
      }
    }

    + .timeline-anchor {
      margin-top: $spacing-large;
    }
  }

  .timeline-anchor-content {
    position: relative;
    pointer-events: none;
    display: flex;
    @include flex-1;
    flex-direction: row;
    flex-wrap: wrap;
    overflow: hidden;
    height: 0;
    width: 100%;
    transform: translate3d(0, 0, -20px);
    opacity: 0;
    padding: 0;
    transition: max-height 0ms,
      transform $transition-duration * 4 $easing-smooth,
      opacity $transition-duration * 2 linear;
    color: $color-text;
    justify-content: space-between;

    &.timeline-anchor-content-active {
      padding: $spacing-large;
      transition: max-height $transition-duration * 4,
        transform $transition-duration * 4 $easing-smooth,
        opacity $transition-duration * 2 linear;
      height: 100%;
      transform: translate3d(0, 0, 0);
      opacity: 1;
      pointer-events: all;
    }
    .richtext {
      width: 100%;
    }
  }

  .timeline-card {
    border-radius: $rounded-corner;
    display: flex;
    overflow: hidden;
    color: $color-text;
    border: 1px solid $color-border-grey;
    display: flex;
    flex: 1 0 100%;
    margin-bottom: $spacing-large;
    min-height: 170px;
    width: 50%;
    transition: background-color $transition-duration;
    @include media-breakpoint-up(md) {
      max-width: calc(50% - 5px);
    }

    &:hover {
      .timeline-card-right {
        background-color: $color-white;
      }
    }
  }

  .timeline-card-left {
    padding: $spacing-large;
    display: flex;
    flex: 1 1 0;
    border-right: 1px solid $color-border-grey;
    background-color: #eaebec;
    position: relative;
    display: block;
  }

  .timeline-card-left-media {
    width: 100%;
    height: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      max-width: 120px;
      max-height: 100%;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.31);
    }
  }

  .timeline-card-right {
    padding: $spacing-large;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: space-between;
    flex: 3;
  }

  .timeline-card-title {
    flex: 0 1 auto;
    @extend .bodycopy-small;
    font-family: $font-family-bold;
  }

  .timeline-card-cta {
    display: flex;
    flex: 1 0 auto;
    align-items: flex-end;

    .link-arrow {
      @extend .bodycopy-small;
      font-family: $font-family-bold;

      color: $color-text;
    }
  }
}
