@import "../../scss/global/typography";
@import "../../scss/base/fonts";
@import "../../scss/base/color";

.multiSelectDropdown {
  &.multiSelectDropdown--stacked,
  .section .row.row-narrow [class^="col-"] > * &.multiSelectDropdown--stacked {
    margin-bottom: 20px !important;
    @include media-breakpoint-up(md) {
      margin-bottom: 0 !important;
    }
  }

  &.dropdown-menu {
    .dropdown-item {
      &::before {
        content: "";
        display: flex;
        flex-shrink: 0;
        width: 20px;
        height: 20px;
        background: $color-white;
        border-radius: 4px;
        text-align: center;
        margin-right: 8px;
        border: 1px solid $color-cold-grey-25;
        align-items: center;
        justify-content: center;
        font-family: "dong-maine";
      }

      &.selected::before {
        content: "\43";
        background: $color-blue;
        color: $color-white;
      }

      &[disabled]:before {
        background: $color-cold-grey-10;
      }
    }
  }

  .dropdown-selected-options {
    margin: 0;
    padding: 0;

    li:last-child {
      border-bottom: 1px solid $color-cold-grey-25;
    }
  }
}
