.product {
  opacity: 0.4;
  border: 1px solid transparent;
  position: relative;
  overflow: hidden;
  transition: opacity 200ms;
  margin-top: 10px;
  margin-right: 5px;
  margin-left: 5px;
  height: auto;
  min-height: 100%;

  .product-highlight {
    position: absolute;
    top: -120px;
    right: -120px;
    width: 240px;
    height: 240px;
    transform: rotate(45deg);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding: 15px;
    text-align: center;
    color: $color-white;
  }

  &.product-highlighted {
    border: 1px solid $color-red !important;
  }

  &.product-active,
  &.product-hover {
    opacity: 1;
  }

  .product-intro {
    margin-bottom: 30px;
  }

  .product-headline {
    font-family: $font-family-bold !important;

    body#lang-zh & {
      font-weight: bold;
      font-family: "Noto Sans TC", sans-serif !important;
    }

    body#lang-ja & {
      font-weight: bold;
      font-family: "Noto Sans Japanese", sans-serif !important;
    }

    body#lang-ko & {
      font-weight: bold;
      font-family: "Noto Sans KR", sans-serif !important;
    }
  }

  .product-price {
    margin-bottom: 30px;
  }

  h2.product-price-amount {
    padding-right: 5px;
  }

  .product-price-amount,
  .product-price-basis {
    font-family: $font-family-bold !important;

    body#lang-zh & {
      font-weight: bold;
      font-family: "Noto Sans TC", sans-serif !important;
    }

    body#lang-ja & {
      font-weight: bold;
      font-family: "Noto Sans Japanese", sans-serif !important;
    }

    body#lang-ko & {
      font-weight: bold;
      font-family: "Noto Sans KR", sans-serif !important;
    }

    display: inline-block;
  }

  .product-details {
    margin-top: 30px;
  }

  .product-details-expand {
    .expand-trigger {
      &:after {
        content: "Show detailed price";
      }

      font-family: $font-family-bold;

      body#lang-zh & {
        font-weight: bold;
        font-family: "Noto Sans TC", sans-serif !important;
      }

      body#lang-ja & {
        font-weight: bold;
        font-family: "Noto Sans Japanese", sans-serif !important;
      }

      body#lang-ko & {
        font-weight: bold;
        font-family: "Noto Sans KR", sans-serif !important;
      }

      .expand-trigger-icon {
        width: 15px;
        height: 15px;
        background-color: $color-dark-blue;
        display: inline-block;
        border-radius: 15px;
        color: $color-white;
        padding: 5px 3px;
        vertical-align: middle;
        line-height: 0.4;
        text-align: center;
        font-size: 0.9em;
        margin-right: 10px;
        margin-top: -3px;
      }
    }

    .detailedprice-content {
      table {
        tr {
          &.sum-row {
            font-family: $font-family-bold !important;

            body#lang-zh & {
              font-weight: bold;
              font-family: "Noto Sans TC", sans-serif !important;
            }

            body#lang-ja & {
              font-weight: bold;
              font-family: "Noto Sans Japanese", sans-serif !important;
            }

            body#lang-ko & {
              font-weight: bold;
              font-family: "Noto Sans KR", sans-serif !important;
            }

            border-top: 1px solid $color-backdrop-1-50;
            border-bottom: 1px solid $color-backdrop-1-50;
          }

          td {
            &:last-child {
              text-align: right;
            }
          }
        }
      }
    }

    .expand-content-visible {
      .expand-trigger {
        &:after {
          content: "Hide detailed price";
        }
      }

      border-bottom: 1px solid $color-backdrop-1-50;
      padding-bottom: 40px;
      margin: 0 -30px;
      padding-left: 30px;
      padding-right: 30px;

      .expand-trigger-icon {
        transform: rotate(45deg);
      }
    }
  }

  table {
    margin-top: 20px;
    margin-bottom: 10px;
    width: 100%;

    tr {
      td {
        padding: 5px 0;
      }
    }

    &.table-list {
      tr {
        border-bottom: 1px solid $color-backdrop-1-50;
      }
    }
  }

  .richtext-product {
    table {
      tr {
        border-bottom: 1px solid $color-backdrop-1-50;
      }
    }
  }

  .product-ctas {
    margin-top: 20px;
  }

  .product-add {
    margin-top: 30px;
  }

  .remove-product-link {
    //text-decoration: underline !important;
    //color: red !important;
    font-family: $font-family-bold;

    color: #99a4ae;
    text-decoration: underline;

    &:before {
      display: inline-block;
      content: "\00D7";
      padding-right: 5px;
      text-decoration: underline;
    }
    /* A bug in IE makes it nesseccary to first set text decoration to underline only to override it with text-decoration none. Reference: https://stackoverflow.com/a/21902566 */
    &:before {
      text-decoration: none;
    }
  }
}
#detailedPriceModal {
  .btn {
    margin-top: 20px;
  }
}

.row {
  &.row-narrow {
    @include media-breakpoint-up(md) {
      .product {
        padding: 30px;
      }
    }
    @include media-breakpoint-down(sm) {
      .product {
        padding: 30px 0px;
      }
    }
  }
  &.row-fullwidth {
    .product {
      padding: 30px;
    }
  }
  &.row-widewidth {
    .product {
      padding: 30px;
    }
  }
}
