.sticky-button {
  width: 100%;
  position: fixed;
  bottom: 0;
  right: 0;
  .is-experience-editing & {
    position: relative;
    bottom: auto;
    right: auto;
  }
  &__btn,
  .headerSliderCorporate .headerSlider .headerSlider-top & &__btn,
  .scTextWrapper {
    &:hover {
      color: inherit;
    }
    text-align: center;
    float: right;
    position: relative;
    width: 100%;
    font-family: $font-family-bold !important;
    text-decoration: none;
    cursor: pointer;
    padding: 9px 24px;
    line-height: 24px;
    @include media-breakpoint-down(sm) {
      &--cookie-bot {
        text-align: left;
        padding: 9px 24px 9px 66px;
      }
    }
  }
  .scTextWrapper,
  [sc-part-of] {
    background-color: $color-blue;
  }
}

@include media-breakpoint-up(md) {
  .sticky-button {
    position: absolute;
    bottom: -65px;
    right: 40px;
    &__btn,
    .scTextWrapper {
      max-width: 190px;
      width: auto;
      border-radius: 0 0 22px 22px;
    }
  }
}
