// #region Variables
$font-family-regular: "OrstedSansRegular", "Helvetica Neue", Arial, sans-serif;
$font-family-light: "OrstedSansLight", "OrstedSansRegular", "Helvetica Neue",
  Arial, sans-serif;
$font-family-bold: "OrstedSansBold", "OrstedSansRegular", "Helvetica Neue",
  Arial, sans-serif;
$font-family-black: "OrstedSansBlack", "OrstedSansRegular", "Helvetica Neue",
  Arial, sans-serif;
$font-family-italic: "OrstedSansItalic", "OrstedSansRegular", "Helvetica Neue",
  Arial, sans-serif;

$font-family-regular-radius: "MetricRegular", Arial;
$font-family-light-radius: "MetricLight", Arial;
$font-family-bold-radius: "MetricBold", Arial;
$font-family-semi-bold-radius: "MetricSemiBold", Arial;
$font-family-italic-radius: "MetricItalic", Arial;

//H1 64px LH 77px   Mobile 42.88 51.8 (66.7%)
$text-size-xlarge: 64px;
$line-height-xlarge: 1.2;

$text-size-xlarge-radius: 64px;
$line-height-xlarge-radius: 1.2;

//H2 40-48 Mobile 26.8 32.2
$text-size-large: 40px;
$line-height-large: 1.2;

$text-size-large-radius: 46px;
$line-height-large-radius: 1.2;

//H3 32-38.4   Mobile 21.44 25.7
$text-size-medium: 32px;
$line-height-medium: 1.2;

$text-size-medium-radius: 46px;
$line-height-medium-radius: 1.2;

$text-size-medium-radius-exploration: 40px;
$line-height-medium-radius-exploration: 1.2;

//H4 24-29  Mobile 16 19
$text-size-small: 24px;
$line-height-small: 1.2;

$text-size-small-radius: 46px;
$line-height-small-radius: 1.2;

//H5 20-28   Mobile 13.4 18.76  Note: UX asked to make mobile size the same as desktop
$text-size-xsmall: 20px;
$line-height-xsmall: 1.4;

$text-size-xsmall-radius-table: 20px;

$text-size-xsmall-radius: 20px;
$line-height-xsmall-radius: 1.4;

//Body 16-24  Mobile same
$text-size-body: 16px;
$line-height-body: 1.5;

$text-size-body-radius: 20px;
$line-height-body-radius: 1.4;

$text-size-body-small-radius: 16px;
$line-height-body-small-radius: 1.125;

//Body small 14-20  Mobile same
$text-size-body-small: 14px;
$line-height-body-small: 1.428;

$text-size-label-radius: 14px;
$line-height-label-radius: 1;

$text-size-splash-radius: 32px;
$line-height-splash-radius: 1.06; // should just be 1.1, 1.2, 1.3?

$text-size-quote-radius: 30px;
$line-height-quote-radius: 1.06;

$body-margin: 16px;

$header-spacing-bottom: 24px;
$header-spacing-small-bottom: 16px;

// #endregion

$body-margin: 16px;

// #region mixins
@mixin text($size: 20px, $lineHeight: $size + 4px, $mobileSizeFactor: 0.67) {
  font-size: rem($size);
  line-height: $lineHeight;

  @include media-breakpoint-down(xl) {
    font-size: rem($size);
  }

  @include media-breakpoint-down(md) {
    font-size: rem($size * ($mobileSizeFactor + 0.2));
  }

  @include media-breakpoint-down(sm) {
    font-size: $size * $mobileSizeFactor;
    line-height: rem(($size * $lineHeight) * $mobileSizeFactor);
  }
}

@mixin header-adaptive {
  font-family: $font-family-light !important;
  .site-theme-radius & {
    font-family: $font-family-light-radius !important;
  }

  font-size: $text-size-small;
  line-height: $line-height-small;
  @include media-breakpoint-up(md) {
    font-size: $text-size-medium;
    line-height: $line-height-medium;
  }
  @include media-breakpoint-up(lg) {
    font-size: $text-size-large;
    line-height: $line-height-large;
  }
  @include media-breakpoint-up(xl) {
    font-size: $text-size-xlarge;
    line-height: $line-height-xlarge;
  }

  @include asian-fonts;
}

@mixin h1 {
  font-family: $font-family-regular !important;
  .site-theme-radius & {
    font-family: $font-family-semi-bold-radius !important;
    @include text($text-size-xlarge-radius, $line-height-xlarge-radius, 0.5);
  }
  @include text($text-size-xlarge, $line-height-xlarge);
  margin-bottom: 0;
}

@mixin h2 {
  font-family: $font-family-regular !important;
  .site-theme-radius & {
    font-family: $font-family-light-radius !important;
    @include text($text-size-large-radius, $line-height-large-radius);
  }
  @include text($text-size-large, $line-height-large);
  margin-bottom: 0;
}

@mixin h2-bold {
  font-family: $font-family-bold !important;

  .site-theme-radius & {
    font-family: $font-family-bold-radius !important;
    @include text($text-size-large-radius, $line-height-large-radius);
  }

  @include asian-fonts;
  @include asian-fontweight-bold;

  @include text($text-size-large, $line-height-large);
  margin-bottom: 0;
}

@mixin h3 {
  font-family: $font-family-regular !important;
  .site-theme-radius & {
    font-family: $font-family-light-radius !important;
    @include text($text-size-medium-radius, $line-height-medium-radius);
  }
  @include text($text-size-medium, $line-height-medium);
  margin-bottom: 0;

  @include asian-fonts;
}

@mixin h3-bold {
  @include h3;
  font-family: $font-family-bold !important;

  .site-theme-radius & {
    font-family: $font-family-bold-radius !important;
  }

  @include asian-fonts;
  @include asian-fontweight-bold;

  margin-bottom: 0;
}

@mixin h4 {
  font-family: $font-family-regular !important;

  .site-theme-radius & {
    font-family: $font-family-regular-radius !important;
    @include text($text-size-small-radius, $line-height-small-radius);
  }

  @include text($text-size-small, $line-height-small);
  margin-bottom: 0;

  @include asian-fonts;
}

@mixin h4-medium {
  font-family: $font-family-regular !important;

  .site-theme-radius & {
    font-family: $font-family-light-radius !important;
    @include text(
      $text-size-medium-radius-exploration,
      $line-height-medium-radius-exploration,
      0.9
    );
  }

  @include text($text-size-small, $line-height-small, 0.9);
  margin-bottom: 0;

  @include asian-fonts;
}

@mixin h4-italic {
  @include h4;
  font-family: $font-family-italic !important;
  font-style: italic;
  .site-theme-radius & {
    font-family: $font-family-italic-radius !important;
    font-style: normal;
  }
}

@mixin h4-bold {
  @include h4;
  font-family: $font-family-bold !important;

  .site-theme-radius & {
    font-family: $font-family-bold-radius !important;
  }

  @include asian-fonts;
  @include asian-fontweight-bold;
}

@mixin h5 {
  font-family: $font-family-regular !important;

  .site-theme-radius & {
    font-family: $font-family-regular-radius !important;
    @include text($text-size-xsmall-radius, $line-height-xsmall-radius);
  }

  @include asian-fonts;

  @include text($text-size-xsmall, $line-height-xsmall);
  margin-bottom: 0;
}

@mixin h5-bold {
  @include h5;
  font-family: $font-family-bold !important;

  .site-theme-radius & {
    font-family: $font-family-bold-radius !important;
  }

  @include asian-fonts;
  @include asian-fontweight-bold;
}

@mixin asian-fonts {
  body#lang-zh & {
    font-family: "Noto Sans TC", sans-serif !important;
    overflow: hidden !important;
  }

  body#lang-ja & {
    font-family: "Noto Sans Japanese", sans-serif !important;
    overflow: hidden !important;
  }

  body#lang-ko & {
    font-family: "Noto Sans KR", sans-serif !important;
    overflow: hidden !important;
  }
}

@mixin asian-fonts-customselect {
  font-size: rem(19px) !important;
  line-height: rem(20px) !important;
  font-family: "dong-maine" !important;
  letter-spacing: 0.2px;

  .site-theme-radius & {
    line-height: 44px !important;
    font-family: "dong-maine" !important;
  }
}

@mixin asian-fontweight-bold {
  body#lang-zh &,
  body#lang-ja &,
  body#lang-ko & {
    font-weight: bold;
  }
}
// #endregion
