.toaster-btn-container {
  text-align: center;
  margin-bottom: 10px !important;
}

.toaster-btn-container--align-left {
  text-align: left;
  .row-narrow & {
    margin-left: 0;
    @include media-breakpoint-up(md) {
      margin-left: 130px;
    }
  }
  .row-widewidth & {
    margin-left: $module-space-horisontal-left;
  }
}

.modal-no-backdrop {
  overflow: visible !important;
}

.modal {
  .row {
    > .col-in-viewport-inited {
      transition: none;
      transform: none;
      opacity: 1;
    }
  }

  .toaster-content {
    padding: 40px 40px 0 40px;
    overflow-y: auto;

    .toaster-image-and-text {
      h1,
      h2,
      h3,
      h4,
      h5 {
        margin-bottom: 30px;
      }

      p:last-child {
        margin-bottom: 0px;
      }

      &:empty {
        margin-bottom: -20px;
      }
    }

    .toaster-placeholder {
      padding-bottom: 40px;
      &:empty {
        margin-top: 0;
      }

      margin-top: 20px;

      form.sitecore-form-initialized {
        width: 100%;
        height: 100%;
        margin: 0px;
        overflow: auto;

        h3 {
          margin-bottom: 20px;
        }

        .form-submit-border {
          button[type="submit"],
          input[type="submit"] {
            display: inline-block;
            margin-bottom: 0px !important;
            margin-right: 3px !important;
          }
        }
      }
    }
  }
}
.pnbodycontent {
  padding-top: 0px !important;
}
