#ui-datepicker-div.ui-datepicker {
  .site-theme-radius & {
    font-family: $font-family-regular-radius;
  }
  .ui-datepicker-calendar {
    th {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
      }
    }
    td {
      a.ui-state-default {
        &.ui-state-highlight {
          .site-theme-radius & {
            font-family: $font-family-bold-radius;
          }
        }
      }
    }
  }
}
.datepicker {
  th {
    &.dow {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
      }
    }
  }
  td {
    &.today {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
      }
    }
  }
}
