.errorHandling-messages {
  color: #445362;
  height: 400px;
  border: 1px solid #a52a2a;
  overflow: auto;
  background-color: #eecece;
  padding: 10px;
}

.errorHandling-messages li {
  list-style-type: none;
}

.errorHandling-header {
  text-align: center;
}
.errorHandling-header h3 {
  color: #a52a2a;
  font-weight: bold;
}

.missingDatasource {
  color: #445362;
  background-color: #f8f3e0;
  border: 1px solid #e4d972;
  text-align: center;
  min-height: 400px;
  padding: 10px;
}

.missingDatasource h4 {
  color: #a52a2a;
}

.errorHandling-header h4 {
  color: black;
  font-weight: bold;
  font-size: 120%;
}

.missingDatasource img {
  max-width: 100%;
  max-height: 250px;
  width: auto;
  height: auto;
}
