.heroOverlay {
  position: relative;
  overflow: hidden;
  display: flex;
  @include flex-1;
  flex-direction: column-reverse;
  background-color: transparent;

  .heroOverlay-content {
    display: flex;
  }

  .heroOverlay-media {
    position: relative;
    height: 0;
    padding-bottom: 56%;
    display: table;
    width: 100%;
    overflow: hidden;
  }

  .heroOverlay-image {
    //from base/transitions.scss
    @include background-image;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    //from base/transitions.scss
    @include image-scale-up-transition;
    z-index: 0;
  }

  .heroOverlay-video {
    // @include media-breakpoint-up(md) {
    @include absoluteCenter;
    min-width: 100%;
    min-height: 100%;
    z-index: 0;
    max-width: 100%;

    .vjs-tech {
      width: 100%;

      @media screen and (max-width: 1024px) {
        height: 100%;
        width: auto;
      }

      @media screen and (max-width: 769px) {
        height: 100%;
        width: 100%;
        position: absolute;
      }
    }
  }

  &.heroOverlay-light {
    @include theme("white-opacity");
  }

  &.heroOverlay-light-blue {
    @include theme("white-blue-opacity");
  }

  &.heroOverlay-dark {
    .teaser {
      @include theme("dark-blue-opacity");
    }
  }

  &.heroOverlay-dark-blue-radius {
    @include theme("dark-blue-opacity");
  }

  @include media-breakpoint-up(md) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 90px 0;

    .heroOverlay-media {
      height: auto;
      padding-bottom: 0;
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
    }

    .heroOverlay-content {
      width: 50%;
      z-index: 1;

      &:hover {
        ~ .heroOverlay-media {
          .heroOverlay-image {
            //from base/transitions.scss
            @include image-scale-up;
          }
        }
      }
    }
  }

.row-widewidth &,
  .row-narrow & {
    .heroOverlay-content .teaser:not(.teaser-horisontal) {
      padding: 50px 30px 30px 30px;
    }

    //TO DO: assess hero overlay on mobile
    @include media-breakpoint-down(md) {
      .heroOverlay-content :not(.teaser-horisontal) .teaser-content {
        padding: 50px 30px 30px 30px;
      }
    }
  }
}

.heroOverlay-minimised {
  @include media-breakpoint-up(md) {
    padding: 60px 0;
  }
}
