.share {
  .row & {
    margin-bottom: 16px;
  }
  .wide-container &,
  .full-container & {
    margin-left: $module-space-horisontal-left;
    margin-right: $module-space-horisontal-left;
  }
  .share-links {
    list-style: none;
    padding: 0;

    .share-links-link {
      display: inline-block;
      margin: 0 0 0 $spacing-tiny;
      vertical-align: top;

      a:hover {
        .share-links-link-icon {
          background-color: $color-blue;
        }
      }

      .share-links-link-icon {
        display: inline-block;
        width: 40px;
        height: 40px;
        border-radius: 20px;
        padding-top: 2px;
        background-color: $color-aqua;
        color: $color-white;
        font-size: 20px;
        text-align: center;
        line-height: 1.5;
      }

      &.is-type-facebook {
        .share-links-link-icon {
          @include icon("facebook");
        }
      }
      &.is-type-twitter {
        .share-links-link-icon {
          @include icon("twitter");
        }
      }
      &.is-type-linkedin {
        .share-links-link-icon {
          padding-top: 1px;
          @include icon("linkedin");
        }
      }
    }
  }

  .social-flex {
    $max-width: $legible-text-max-width;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    &.align-with-text {
      max-width: $max-width;
      margin: 0 auto;
    }
  }

  .social-circle {
    justify-content: center;
    width: 40px;
    height: 40px;
    background-color: $color-aqua;
    color: $color-white;
    border-radius: 50%;
    margin-left: 10px;
    &:first-child {
      margin-left: 0;
    }
    overflow: hidden;
    text-decoration: none;
    cursor: pointer;

    &:hover {
      background-color: $color-blue;
    }

    svg {
      fill: currentColor;
    }

    img {
      max-width: 20px;
      height: 20px;
      width: auto;
    }

    &.is-type-facebook {
      @include icon("facebook");
    }
    &.is-type-twitter {
      @include icon("twitter");
    }
    &.is-type-linkedin {
      padding-top: 1px;
      @include icon("linkedin");
    }
  }
}
.col-6 {
  &.share-align-right {
    align-items: flex-end;
  }
}

.share-component {
  width: 100%;
}
