@import "../../scss/base/base";

@mixin add-gutter($gutterSize: 16px) {
  .container-fluid {
    padding-left: $gutterSize;
    padding-right: $gutterSize;

    &.narrow-container {
      .row-narrow {
        max-width: none;
      }

      @include media-desktop {
        max-width: 960px + ($gutterSize * 2);
        margin-left: auto;
        margin-right: auto;
      }
    }

    &.full-container {
      .row-fullwidth {
        margin-left: (-1 * $gutterSize);
        margin-right: (-1 * $gutterSize);
      }
    }

    &.wide-container {
      .row-widewidth {
        margin-left: (-1 * $gutterSize);
        margin-right: (-1 * $gutterSize);
      }
    }
  }

  [class^="col-"] {
    padding-left: $gutterSize;
    padding-right: $gutterSize;
  }
}

@function strip-units($number) {
  @return $number / ($number * 0 + 1);
}

$enableGutter: false;

.section {
  $site-width-unitless: strip-units($site-width);
  position: relative;
  padding-top: 60px;
  padding-bottom: 60px;

  @include media-desktop {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  .container-fluid {
    position: relative;
    padding-top: 30px;
    padding-bottom: 30px;

    @include media-desktop {
      padding-top: 40px;
      padding-bottom: 40px;
    }
  }

  [class^="col-"],
  .row.row-widewidth [class^="col-"],
  .row.row-narrow [class^="col-"] {
    position: relative;

    > * {
      margin-top: 0 !important;
      margin-bottom: 0 !important;
    }
    
    > .theme-inherit-section {
      padding-top: 0;
      padding-bottom: 0;
    }
  }

  @if $enableGutter {
    &--gutter {
      @include add-gutter();
    }
  }

  .btn {
    text-decoration: none;
  }

  .richtext a.btn {
    text-decoration: none;
  }

  .theme-inherit-section {
    background-color: transparent !important;
    color: inherit !important;
    
    strong, b {
      color: inherit !important;
    }
  }

  @import "sectionAccordion";
  @import "sectionColoredContentBox";
  @import "sectionList";
  @import "sectionStock";
  @import "sectionTable";
  @import "sectionTabs";
  @import "sectionTimeline";
}
