.communication-toolbox {
  .footer {
    border: 0;
    min-height: 364px;

    a[href] {
      text-decoration: underline;
    }

    > .row > div {
      margin-bottom: 36px;
    }

    &.theme-white-blue {
      color: $color-white;
      background: $color-blue;
    }

    &.theme-white-grey {
      color: $color-white;
      background: $color-dark-blue;
    }

    &.theme-blue-warm-grey {
      color: $color-blue;
      background: $color-warm-grey;
    }

    &.theme-grey-warm-grey {
      color: $color-dark-blue;
      background: $color-warm-grey;
    }
  }

  @at-root html#{&} {
    min-height: 100%;
    height: 100%;

    body {
      min-height: 100%;
      height: 100%;
    }

    main {
      min-height: 100%;
      position: relative;

      > .communication-toolbox {
        position: absolute;
        bottom: 0;
        width: 100%;
      }
    }
  }
}
