.stepindicator {
  .stepindicator-step {
    &.step-active {
      .stepindicator-step-text {
        .site-theme-radius & {
          font-family: $font-family-bold-radius;
        }
      }
    }
  }
  .stepindicator-step-number {
    .site-theme-radius & {
      font-family: $font-family-bold-radius;
    }
  }
}
