.sticky-bar {
  padding: 11px 25px;
  background-color: $color-blue;
  color: white;
  align-items: center;
  bottom: -46px;
  position: absolute;
  left: 0;
  width: 100%;
  display: flex;
  opacity: 0;
  .is-experience-editing & {
    bottom: auto;
    position: relative;
    opacity: 1;
  }
  @include media-breakpoint-up(md) {
    padding: 17px 30px;
    bottom: -57px;
    &__header {
      font-size: 19px;
    }
    &__btn {
      font-size: 16px;
    }
  }

  &--show {
    opacity: 1;
  }
  &__header {
    margin-right: 30px;
  }
  &__btn {
    color: white !important;
  }
  &__close-btn {
    &:hover,
    &:focus {
      color: $white;
      opacity: 1;
    }
    margin-left: auto;
    color: $white;
    opacity: 1;
    text-shadow: none;
  }
}
