$defaultBtnColor: "blue";

.btn {
  @include btn;
  $props: map-get($buttonThemes, $defaultBtnColor);
  @include btnTheme(
    map-get($props, "background-color"),
    map-get($props, "text-color")
  );
  @include btnTypography;

  @each $name, $props in $buttonThemes {
    &-#{$name} {
      @include btnTheme(
        map-get($props, "background-color") !important,
        map-get($props, "text-color") !important
      );
    }
  }

  &-inactive {
    @include btnTheme($color-backdrop-1-75, $color-backdrop-1-25);
    cursor: default;
    pointer-events: none;
  }
}

.btn-cta {
  @include btnCta;
}

.btn-default {
  $props: map-get($buttonThemes, $defaultBtnColor);
  @include btnTheme(
    map-get($props, "background-color"),
    map-get($props, "text-color")
  );
  @include btnCta;
}

//in case the background is blue. There could be a solution for inverse color schemes
.theme-blue-white {
  .btn-default {
    $props: map-get($buttonThemes, "white");
    @include btnTheme(
      map-get($props, "background-color"),
      map-get($props, "text-color")
    );
    @include btnCta;
  }
}

.btn-btn-cta {
  @include btn;
  @include btnTypography;
  $props: map-get($buttonThemes, $defaultBtnColor);
  @include btnTheme(
    map-get($props, "background-color"),
    map-get($props, "text-color")
  );
  @include btnCta;
}

.btn-dropdown {
  @include btnDropdown;
}

.btn-round {
  padding: 0;
  width: 42px;
  height: 42px;
  &:before {
    font-size: 0.6em;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  &.btn-round-small {
    width: 30px;
    height: 30px;
  }
}

.rt-btn-darkblue {
  @include btn;
  @include btnTypography;
  $props: map-get($buttonThemes, "dark-blue");
  @include btnTheme(
    map-get($props, "background-color"),
    map-get($props, "text-color")
  );
  @include btnCta;
}

