.interactive-timeline {
    display: flex;
    flex-direction: column;
    min-height: 730px;
    height: calc(100vh - 60px);
    $spacing-large: 20px;
    $border-color: $color-backdrop-1;
    color: white;
    overflow: hidden;

    @include media-breakpoint-down(sm) {
      justify-content: space-between;
      height: 100vh;
      min-height: unset;
    }
  
    .teaser-media {
      position: absolute;
      height: 100%;
      width: 100%;
      display: none;

      @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
        top: 0px;
      }
  

      .teaser-img {
        &:first-child {
          z-index: 2;
        }

        position: absolute;
        width: 100%;
        height: 100%;
        background-position: center; 
        background-repeat: no-repeat; 
        background-size: cover;
        // transition: opacity 2s linear;

        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
          top: 0px;
        }
    
        .teaser-gradient {
          height: 100%;
          background-image: linear-gradient(90deg, rgba(2,0,36,0.5) 0%, rgba(0,0,0,0) 70%, rgba(0,0,0,0) 100%);

          @include media-breakpoint-down(sm) {
            background-image: linear-gradient(90deg, rgba(2,0,36,0.3) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0.3) 100%);
          }
        }

        @include media-breakpoint-down(sm) {
          height: 100vh;
        }
      
      }
    }

    .timeline-header {
      display: flex;
      flex-direction: row;
      color: white;
      margin: 140px 0 50px 120px;

      @include media-breakpoint-down(sm) {
        margin: 30px 27px;
      }
    }
  
    .timeline-title {
      font-size: $text-size-medium;
      line-height: $line-height-medium;
      color: white;
      display: flex;
      flex: 0 1 auto;
      align-items: center;
      z-index: 5;

      @include media-breakpoint-down(sm) {
        font-family: $font-family-bold;
        font-size: $text-size-xsmall;
        line-height: $line-height-xsmall;
        
        align-items: flex-start;
      }
    }
  
    .timeline-cta {
      display: flex;
      flex: 0 1 auto;
      align-items: center;
      justify-content: flex-end;
  
      > * {
        margin-right: 0;
        padding-right: 0;
      }
    }
  
    .timeline-main {
      z-index:3;
      display: flex;
      margin-top: $spacing-large;
      flex-direction: column-reverse;
      position: relative;
      padding-bottom: 20px;
      @include media-breakpoint-up(md) {
        flex-direction: row;
      }
      @include media-breakpoint-down(sm) {
        height: auto !important;
      }
    }
    .timeline-anchor-container-arrow-up,
    .timeline-anchor-container-arrow-down {
      position: absolute;
      left: 20px;
      cursor: pointer;
      opacity: 1;
      transition: color $transition-duration;
      transform: translateZ(0);
      font-size: $text-size-body;
      line-height: $line-height-body;
      font-family: $font-family-regular;
      &.timeline-arrow-disabled {
        color: $color-backdrop-1;
        cursor: default;
      }
    }
    .timeline-anchor-container-arrow-up {
      transform: rotate(-90deg);
      top: 20px;
      display: none;
    }
    .timeline-anchor-container-arrow-down {
      bottom: 0px;
      transform: rotate(90deg);
      display: none;
    }
    .has-overflow {
      .timeline-anchor-container-arrow-up {
        display: block;
      }
      .timeline-anchor-container-arrow-down {
        display: block;
      }
    }
  
    .timeline-mobile-anchors {
      display: none;
    }
    @include media-breakpoint-down(sm) {
      .timeline-mobile-anchors {
        display: none;
        padding: 20px;
      }
    }
    .timeline-main-left {
      display: flex;
      flex-direction: column;
      padding-left: 120px;
      margin-right: 64px;

      position: relative;
      flex: 0 1 auto;
      max-width: 320px;
      min-width: 230px;
      overflow: hidden;

      @include media-breakpoint-down(sm) {
        flex-direction: row;
        max-width: unset;
        padding-left: 27px;
        overflow: visible;
      }
  
      &.has-overflow {
        padding-top: 40px;
        .timeline-main-left-inner {
          max-height: calc(100% - 30px);
          padding-top: 10px;
          padding-bottom: 10px;
        }
        &:before {
          display: block;
        }
        &:after {
          display: block;
        }
      }
      &:before {
        content: "";
        position: absolute;
        display: none;
        z-index: 1;
        top: 40px;
        left: 0;
        right: 0;
        width: 100%;
        height: 30px;
        z-index: 1;
        background: linear-gradient(
          0deg,
          rgba($color-white, 0) 0%,
          rgba($color-white, 1) 70%
        );
        pointer-events: none;
      }
      &:after {
        content: "";
        position: absolute;
        display: none;
        z-index: 1;
        bottom: 20px;
        left: 0;
        right: 0;
        width: 100%;
        height: 30px;
        z-index: 1;
        background: linear-gradient(
          180deg,
          rgba($color-white, 0) 0%,
          rgba($color-white, 1) 70%
        );
        pointer-events: none;
      }
    }
  
    .timeline-main-left-inner {
      width: 100%;
      position: relative;
      overflow: hidden;
      max-height: 100%;
      
      @include media-breakpoint-down(sm) {
        overflow: visible;
      }

      .interactive-timeline-anchor-container {
        margin-top: -4px;
        @include media-breakpoint-down(sm) {
          display: flex;
          width: 3000px;
          margin-top: 30px;
          margin-bottom: 30px;
        }

        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
          margin-top: 0px;
        }
      }

    }
  
    .interactive-timeline-main-right {
      @include flex-1;
      overflow-x: hidden;
      overflow-y: auto;
      margin: 0px 27px;
      @include media-breakpoint-up(md) {
        margin-left: 20px;
        max-width: calc(100% - 230px - 20px);
      }

      .interactive-timeline-main-right-inner {
        @include flex-1;
        max-width: 600px;
  
        .interactive-timeline-anchor-content-container {
          .interactive-timeline-anchor-content {
            display: none;
            font-size: $text-size-xsmall;
            line-height: $line-height-xsmall;
            font-family: $font-family-regular;

            .mobile-title {
              font-size: $text-size-body;
              line-height: $line-height-xsmall;
              margin-bottom: $text-size-body;

              @include media-breakpoint-up(md) {
                display: none
              }
            }

            div {
              font-size: $text-size-xsmall;
              line-height: $line-height-xsmall;
              margin-bottom: 30px;

              @include media-breakpoint-down(sm) {
                font-size: $text-size-body;
                margin-bottom: 16px;
              }
            }

            h4 {
              margin-bottom: 20px;
              @include media-breakpoint-down(sm) {
                display: none;
                margin-bottom: 16px;
              }
            }

            a {
              font-family: $font-family-bold;

              @include media-breakpoint-down(sm) {
                font-size: $text-size-body;
              }
            }

            &.interactive-timeline-anchor-content-active {
              display: block;
            }
          } 
        }
      }
    }

    .timeline-anchor-container {
      padding: 20px;
      position: relative;
      &:before {
        content: "";
        display: block;
        position: absolute;
        left: $spacing-large + $spacing-large/2;
        height: calc(100% - 40px);
        width: 1px;
        top: 20px;
        background-color: $border-color;
        z-index: -1;
      }

      @include media-breakpoint-down(md) {
        display: flex;
        width: 3000px;
      }
    }
  
    .timeline-anchor {
      position: relative;
      font-size: $text-size-body;
      font-family: $font-family-regular;
      min-height: 60px;
  
      @include asian-fonts;
      @include asian-fontweight-bold;

      @include media-breakpoint-down(sm) {
        font-size: 0;
        width: 38vw;
        min-height: 0;
      }
  
      display: flex;
      align-items: center;
      $circle-size: 16px;
      cursor: pointer;
      word-break: break-word;
      word-wrap: break-word;

      @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
        align-items: flex-start;
        margin-top: 4px;
      }

      &:before {
        font-size: 8px;
        text-align: center;
        transition: all 200ms;
        content: "";
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 16px;
        height: 16px;
        border-radius: 16px;
        border: 2px solid #f3f3f3;
        margin-right: 20px;
        @include media-breakpoint-down(sm) {
          font-size: 9.5px;
        }

        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
          margin-top: 4px;
          padding-bottom: 1px;
          font-size: 9px;
        }
      }
  
      &:after {
        position: absolute;
        border-left: 2px solid white;
        height: calc(100% - 13px);
        display: block;
        content: '';
        z-index: 1;
        top: 36px;
        margin-left: 7px;

        @include media-breakpoint-down(sm) {
          border-left: 0px;
          border-top: 2px solid white;
          height: 1px;
          width: calc(100% - 15px);
          top: 6px;
          left: 9px;
          overflow: hidden;
        }

        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
          margin-left: -29px;
          margin-top: 4px;
          height: calc(100% - 10px);
          top: 15px;
        }
      }

      &:last-child:after {
        border: 0px;

        @include media-breakpoint-down(sm) {
          border: 0px;
        }
      }
  
      &:hover {
        &:before {
          border: 4px solid $color-white;
          background-color: $color-white;

          @include media-breakpoint-down(sm) {
            background-color: unset;
          }
        }
      }
  
      &.timeline-anchor-active {
        font-family: $font-family-bold;

        &:before {
          border: 2px solid $color-body-background;
          content: "";
        }
      }
  
      + .timeline-anchor {
        @include media-breakpoint-down(sm) {
          margin-top: 0;
        }
      }
    }
  
    .timeline-anchor-content {
      position: relative;
      pointer-events: none;
      display: flex;
      @include flex-1;
      flex-direction: row;
      flex-wrap: wrap;
      overflow: hidden;
      height: 0;
      width: 100%;
      transform: translate3d(0, 0, -20px);
      opacity: 0;
      padding: 0;
      transition: max-height 0ms,
        transform $transition-duration * 4 $easing-smooth,
        opacity $transition-duration * 2 linear;
      color: $color-text;
      justify-content: space-between;
  
      &.timeline-anchor-content-active {
        padding: $spacing-large;
        transition: max-height $transition-duration * 4,
          transform $transition-duration * 4 $easing-smooth,
          opacity $transition-duration * 2 linear;
        height: 100%;
        transform: translate3d(0, 0, 0);
        opacity: 1;
        pointer-events: all;
      }
      .richtext {
        width: 100%;
      }
    }
  
    .timeline-card {
      border-radius: $rounded-corner;
      display: flex;
      overflow: hidden;
      color: $color-text;
      border: 1px solid $color-border-grey;
      display: flex;
      flex: 1 0 100%;
      margin-bottom: $spacing-large;
      min-height: 170px;
      width: 50%;
      transition: background-color $transition-duration;
      @include media-breakpoint-up(md) {
        max-width: calc(50% - 5px);
      }
  
      &:hover {
        .timeline-card-right {
          background-color: $color-white;
        }
      }
    }
  
    .timeline-card-left {
      padding: $spacing-large;
      display: flex;
      flex: 1 1 0;
      border-right: 1px solid $color-border-grey;
      background-color: #eaebec;
      position: relative;
      display: block;
    }
  
    .timeline-card-left-media {
      width: 100%;
      height: 100%;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
  
      img {
        max-width: 120px;
        max-height: 100%;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.31);
      }
    }
  
    .timeline-card-right {
      padding: $spacing-large;
      display: flex;
      flex-direction: column;
      align-items: stretch;
      justify-content: space-between;
      flex: 3;
    }
  
    .timeline-card-title {
      flex: 0 1 auto;
      @extend .bodycopy-small;
      font-family: $font-family-bold;
    }
  
    .timeline-card-cta {
      display: flex;
      flex: 1 0 auto;
      align-items: flex-end;
  
      .link-arrow {
        @extend .bodycopy-small;
        font-family: $font-family-bold;
        margin-top: 30px;
        color: $color-text;
      }
    }

    .link-arrow {
      cursor: pointer;
    }
  }
  
.interactive-timeline-modal {
  position: fixed;
  max-width: 1440px;
  top: 0;
  background: $color-white;
  z-index: 10;
  width: 100%;
  height: 100%;
  transition-property: top;
  transition-duration: 1s;

  button {
    cursor: pointer;
  }

  &.closed {
    top: 100%;
  }

  &.show {
    top: 0;
  }

  .timeline-modal-header {
    height: 70px;
    display: flex;
    align-items: center;
    padding-left: 27px;
    padding-right: 27px;
    color: $color-black;
  }

  .timeline-modal-header-close {
    display: flex;
    margin-left: auto;
    color: $color-blue;
    background: none;
    border: 0;
    outline: none;
    width: 14px;
    height: 14px;

    .icon-close {
      height: 14px;
      font-size: 14px;
    }
  }

  .timeline-modal-content {
    height: calc(100% - 70px);
    max-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    &.loading {
      .timeline-modal-loader {
        position: absolute;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        padding: 4px 0 0 5px;
        content: ' ';
        border: 15px solid $color-cold-grey-10;
        border-top: 15px solid $color-blue;
        animation: spin 1s linear infinite;
      }
     
      iframe {
        visibility: hidden;
      }
    }
  }

  iframe {
    width: 100%;
    max-height: 100%;
    height: 100%;
    border: 0;
  }
}

