@mixin align-coloredContentBox {
  width: 100%;
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
  overflow: visible;
}

.coloredContentBox {
  padding: $module-space-vertical-top $module-space-horisontal-left
    $module-space-vertical-bottom $module-space-horisontal-right;
  overflow: hidden;

  .coloredContentBox-inner {
    max-width: 1430px;
    margin: 0 auto;
    height: auto;

    > * {
      max-width: $legible-text-max-width;
    }
  }

  &.align-center {
    .coloredContentBox-inner {
      text-align: center;
      max-width: $legible-text-max-width;
    }
  }

  .coloredContentBox-headline {
    @include h3;
  }

  .coloredContentBox-content {
    @include h4;
  }

  .coloredContentBox-content {
    margin-top: 10px;
  }

  .coloredContentBox-cta {
    margin-top: $module-space-vertical-bottom;

    + .teaser-cta {
      margin-top: $module-space-vertical-bottom;
    }

    & .article {
      //  @include media-breakpoint-down(lg) {
      padding: 0px;
      // }
    }
  }
}

.body-text-alignment {
  &.coloredContentBox {
    $original-legible-text-max-width: $legible-text-max-width;
    $legible-text-max-width: 960px;

    .coloredContentBox-inner {
      @include media-breakpoint-down(xl) {
        @include align-coloredContentBox;
      }
    }

    &.align-center {
      .coloredContentBox-inner {
        max-width: $legible-text-max-width;
      }
    }

    .coloredContentBox-headline {
      @include media-breakpoint-down(xl) {
        @include align-coloredContentBox;
        max-width: $original-legible-text-max-width;
      }
    }

    .coloredContentBox-content {
      @include media-breakpoint-down(xl) {
        @include align-coloredContentBox;
        max-width: $original-legible-text-max-width;
      }
    }

    .coloredContentBox-cta {
      @include media-breakpoint-down(xl) {
        @include align-coloredContentBox;
      }

      + .teaser-cta {
        @include media-breakpoint-down(xl) {
          @include align-coloredContentBox;
          max-width: $original-legible-text-max-width;
        }
      }
    }
  }
}
