.megaNavigation {
  &.megaNavigation-slider {
    $navigationBackgroundColor-mobile: $color-white;
    $navigationBackgroundColor-desktop: transparent;

    $navigationTextColor-mobile: $color-text;
    $navigationTextColor-desktop: $color-text;
    $navigationTextColor-hover: $color-blue;

    $navigationTextColor-active-mobile: $color-blue;
    $navigationTextColor-active-desktop: $color-blue;

    $submenuBackgroundColor-mobile: $color-white;
    $submenuBackgroundColor-desktop: $color-white;

    $megaNavigation-submenu-visible-background: $color-blue;
    $megaNavigation-submenu-visible-color: $color-white;

    $submenuTextColor-mobile: $color-text;
    $submenuTextColor-desktop: $color-text;
    $submenuTextColor-active: $color-blue;
    $submenuTextColor-hover: $color-blue;
    //
    $submenuLinkTextColor: $color-text;
    $submenuLinkTextColor-hover: $color-blue;
    $submenuLinkTextColor-active: $color-blue;

    .megaNavigation-menu {
      border-top: 0;
      margin: 0;
      max-width: 1430px;
      border-top: 1px solid rgba(198, 203, 208, 0.5);

      @include media-breakpoint-up(md) {
        border-top: 0;
        position: relative;
        left: 0;
        flex-direction: column;
      }
    }

    .megaNavigation-trigger {
      background-color: $color-dark-blue;
      margin-left: 0;
    }

    .megaNavigation-menu {
      background-color: $navigationBackgroundColor-mobile;
      // border-top: 1px solid $color-border-grey;
      @include media-breakpoint-up($header-breakpoint) {
        background-color: $navigationBackgroundColor-desktop;
        width: 380px;
      }
    }

    .megaNavigation-menu-item {
      @include media-breakpoint-up(md) {
        position: static;
        // padding: 0 36px 0 0;
        padding-right: 15px;
        padding-left: 0;
        margin-bottom: 15px;

        &:first-of-type {
          .megaNavigation-menu-item-link {
            padding-left: 0;
          }
        }

        &:after,
        &:before {
          content: none;
        }
      }

      &.megaNavigation-item-active {
        .megaNavigation-menu-item-link {
          font-family: $font-family-bold !important;

          body#lang-zh & {
            font-weight: bold;
            font-family: "Noto Sans TC", sans-serif !important;
          }

          body#lang-ja & {
            font-weight: bold;
            font-family: "Noto Sans Japanese", sans-serif !important;
          }

          body#lang-ko & {
            font-weight: bold;
            font-family: "Noto Sans KR", sans-serif !important;
          }

          color: $submenuLinkTextColor-active;
        }
      }

      &.active-sub {
        .megaNavigation-menu-item-link {
          color: #fff;

          @include media-breakpoint-up(md) {
            color: $submenuLinkTextColor-active;
          }
        }
      }
    }

    .megaNavigation-menu-item-link {
      color: $navigationTextColor-mobile;

      @include media-breakpoint-up(md) {
        border-top: 0;
      }

      &.megaNavigation-item-active {
        color: $navigationTextColor-active-mobile;
      }

      &.megaNavigation-menu-item-link-active {
        color: $navigationTextColor-active-mobile;
        // @include media-breakpoint-up($header-breakpoint) {
        //     color: $navigationTextColor-active-desktop;
        // }
      }

      &.megaNavigation-submenu-visible {
        color: $navigationTextColor-hover;

        @include media-breakpoint-down(sm) {
          background-color: $megaNavigation-submenu-visible-background;
          color: $megaNavigation-submenu-visible-color;
          font-family: $font-family-bold;
        }

        @include media-breakpoint-up($header-breakpoint) {
          @include h3-bold;
          background-color: $submenuBackgroundColor-desktop;
          // padding-top:10px;
          position: relative;
        }
      }

      @include media-breakpoint-up($header-breakpoint) {
        @include h3;
        line-height: 1;
        // font-family: $font-family-bold;
        // font-size: 2.8vw;
        // white-space: nowrap;
        color: $navigationTextColor-desktop;
        padding-left: 0;
        padding-right: 0;

        &:hover {
          color: $navigationTextColor-hover;
          letter-spacing: 0;
          // &:after{
          //     content: "";
          //     width: 100%;
          //     height: 400%;
          //     background-color: rgba($submenuBackgroundColor-desktop, 0.1);
          //     opacity: 1;
          //     position: absolute;
          //     left: 0;
          //     top: 98%;
          //     z-index: 1;
          // }
        }

        &.megaNavigation-menu-item-link-active {
          color: $navigationTextColor-desktop;
          letter-spacing: 0;
        }

        &.megaNavigation-submenu-visible {
          color: $navigationTextColor-hover;
        }
      }
    }

    .megaNavigation-submenu {
      background-color: $submenuBackgroundColor-mobile;

      @include media-breakpoint-up($header-breakpoint) {
        position: absolute;
        background-color: $submenuBackgroundColor-desktop;
        width: calc(90vw - 100%);
        max-width: 960px;
        max-height: 0;
        left: 100%;
        top: 0;
        margin-top: 0;
        margin-left: auto;
        margin-right: auto;
        border-top: 0 !important;
        opacity: 0;
        transition: opacity 0.4s;
      }
    }

    .megaNavigation-submenu-inner {
      @include media-breakpoint-up(md) {
        padding: 15px;
        margin: 0;
        max-height: calc(100vh - 150px);
        overflow: auto;
      }
    }

    .megaNavigation-submenu-trigger {
      &:after {
        content: none;
      }
    }

    .megaNavigation-submenu-item {
      @include media-breakpoint-up(md) {
        margin-right: 40px;
        margin-bottom: 40px;
        padding: 0;

        + .megaNavigation-submenu-item {
          //max-width: 30%;
          flex: 0 1 auto;
          padding: 20px;
          width: 50%;
          margin: -20px;
          margin: 0 0 0 -20px;
        }
      }

      @include media-breakpoint-up(lg) {
        + .megaNavigation-submenu-item {
          max-width: 30%;
          min-width: 210px;
          flex: 0 1 auto;
        }
      }
    }

    .megaNavigation-submenu-item-link {
      color: $submenuTextColor-mobile;
      width: 100%;
      word-break: break-word; // added both word-wrap and word-break to make it work in IE
      word-wrap: break-word;

      &:hover {
        color: $submenuTextColor-hover;
      }

      &.megaNavigation-submenu-item-link-active {
        color: $submenuTextColor-active;
      }

      @include media-breakpoint-up(md) {
        color: $submenuTextColor-desktop;
        font-size: 16px;
      }
    }

    .megaNavigation-submenu-item-submenu-item-link {
      color: $submenuLinkTextColor;
      width: 100%;
      word-break: break-word; // added both word-wrap and word-break to make it work in IE
      word-wrap: break-word;

      &:hover {
        font-family: $font-family-bold;

        letter-spacing: 0.25px;
        color: $submenuLinkTextColor-hover;
      }

      @include media-breakpoint-up(md) {
        font-size: 16px;
      }

      &.megaNavigation-item-active {
        color: $submenuLinkTextColor-active;
        font-family: $font-family-bold;
      }
    }

    .megaNavigation-submenu-item-section {
      > a {
        font-size: 19px;
      }

      @include media-breakpoint-up(md) {
        margin-bottom: 20px;
      }
    }

    .megaNavigation-submenu-section-link {
      margin-bottom: 10px;
      color: $color-dark-blue !important;

      @include media-breakpoint-down(sm) {
        font-family: $font-family-bold;
      }

      &:hover {
        color: $submenuTextColor-hover !important;
      }
    }

    &.megaNavigation-smallsite {
      @include media-breakpoint-up($header-breakpoint) {
        .megaNavigation-menu {
          text-align: center;
          left: 50%;
          transform: translateX(-50%);
        }

        .megaNavigation-menu-item,
        .megaNavigation-menu-item-link {
          padding-right: 0;
        }
      }
    }
  }
}
