.site-theme-radius {
  // Base Color Settings
  @include link-inline-color($color-radius-blue);
  // Top header
  @include header(
    $color-radius-blue,
    $color-white,
    $color-white,
    $color-radius-blue,
    $color-white
  );
  // Navigation
  @include navigation(
    $color-white,
    $color-radius-blue,
    $color-radius-turquoise,
    $color-radius-light-blue,
    $color-radius-blue,
    $color-white,
    $color-radius-blue,
    $color-white
  );

  // Top Tasks
  .topTasks {
    @extend .topTasks.theme-radius;
  }

  .table thead {
    background-color: $color-radius-blue;
    color: $color-white;
  }

  .richtext {
    .table-mobile-head {
      background-color: $color-radius-blue;
      color: $color-white;
    }
  }

  :not(.splash-content-body) > h4 {
    color: $color-radius-blue;
  }

  // Accordion Theme Color Overwrite
  .accordion {
    .accordion-header {
      &.theme-color-primary {
        color: $color-radius-blue;
      }
    }

    .accordion-items {
      .accordion-items-item {
        .accordion-item-trigger,
        .accordion-item-trigger:hover {
          color: $color-radius-blue;
        }
      }
    }

    &.theme-white-radius {
      .accordion-items {
        .accordion-items-item {
          .accordion-item-trigger {
            background-color: $color-white;
          }

          .accordion-item-trigger:hover {
            color: $color-blue;
          }

          &.is-open {
            .accordion-item-trigger:hover {
              color: $color-blue;
            }
          }
        }
      }

      .accordion-item-expanded {
        color: $color-radius-blue;
      }

      .article {
        .article-content {
          .article-content-inner {
            color: $color-radius-blue;
          }
        }
      }
    }

    &.theme-darkblue-radius {
      .accordion-header {
        &.theme-color-primary {
          color: $color-white;
        }
      }

      .accordion-items {
        .accordion-items-item {
          .accordion-item-trigger {
            color: $color-white;
            background-color: $color-radius-blue;
          }

          .accordion-item-trigger:hover {
            color: $color-blue;
          }
        }
      }

      .accordion-items {
        .accordion-items-item {
          &.is-open {
            .accordion-item-trigger {
              color: $color-white;
            }

            .accordion-item-trigger:hover {
              color: $color-blue;
            }
          }
        }
      }

      .accordion-item-expanded {
        color: $color-white;
      }

      .article {
        .article-content {
          .article-content-inner {
            color: #fff;
          }
        }
      }
    }
  }

  // Sitemap
  .sitemap {
    .sitemap-top {
      background-color: $color-radius-blue;

      a {
        color: $white;
      }

      &.theme-white-blue {
        &.theme-primary-color-background {
          background-color: $white;
          color: $color-radius-blue;

          a {
            color: $color-radius-blue;
          }
        }
      }
    }

    .sitemap-theme-bottom {
      @extend .theme-white-blue;

      &.theme-primary-color-background {
        background-color: $white;
      }
    }
  }

  // OnWard Links
  .onward-links {
    @include onward-links(
      $color-radius-light-blue,
      $color-radius-turquoise,
      $color-radius-blue
    );
  }

  // Article
  .article {
    .article-date {
      color: $color-radius-blue;
    }

    .article-headline {
      color: $color-radius-turquoise;
    }

    .article-subheadline {
      color: $color-radius-blue;
    }

    .article-excerpt {
      color: $color-radius-blue;
    }

    .article-content {
      .article-content-inner {
        color: $color-radius-darkgrey;

        a {
          color: $color-radius-blue;
        }
      }
    }

    .share {
      .share-links {
        .share-links-link {
          .share-links-link-icon {
            background-color: $color-radius-blue;
            line-height: 35px; //the metric font messes up the lineheight so the icons dont align properly

            &:hover {
              background-color: $color-radius-turquoise;
            }
          }
        }
      }
    }
  }

  // Teaser Circle/Quotes
  .teaserCircle {
    color: $color-radius-blue;

    .teaser-content {
      .teaser-quotation.theme-color-primary,
      .teaser-quote .theme-color-primary,
      .teaser-text.theme-color-primary {
        color: $color-radius-blue;
      }
    }
  }

  // Tabs
  .tabs {
    .tabs-tabs-tab {
      &.theme-color-primary {
        color: $color-radius-turquoise;

        &:hover {
          color: $color-radius-blue;
        }
      }
    }
  }

  // Announcement
  .announcement {
    .announcement-list {
      .announcement-list-title {
        color: $color-radius-blue;
      }
    }
  }

  // List
  .list {
    .list-header {
      background-color: $color-radius-blue;
    }
  }

  // Stock
  .stock {
    .stock-header {
      color: $color-radius-blue;
    }

    .stock-data-container {
      .stock-value {
        color: $color-radius-turquoise;
      }

      .stock-delta {
        color: $color-radius-light-blue;
      }
    }

    .stock-cta {
      .btn.btn-default {
        background-color: $color-radius-blue;
        color: $color-white;
      }
    }
  }

  // Timeline
  .timeline {
    .timeline-header {
      &.theme-radius {
        .timeline-title {
          color: $color-radius-turquoise;
        }
      }
    }

    .timeline-main {
      .timeline-anchor-container {
        .timeline-anchor-active:before {
          background-color: $color-radius-turquoise;
        }
      }
    }
  }

  /**
    Multi-Narrative
    - theme-radius
   */
  .multiNarrative {
    background-color: $color-radius-light-blue;

    .narrative-content {
      color: $color-radius-blue;

      .narrative-content-track {
        .narrative-cta {
          .link-arrow {
            color: $color-radius-blue;
          }
        }
      }
    }

    .narrative-picker {
      .narrative-option {
        .option-img {
          &.theme-color-primary {
            color: $color-radius-blue;
          }
        }
      }
    }
  }

  /**
      TopTask Themes
      - theme radius
     */
  .topTasks {
    &.theme-radius {
      @include topTasks($color-radius-turquoise, $color-white);
    }
  }
}
