.product {
  .product-headline {
    .site-theme-radius & {
      font-family: $font-family-bold-radius !important;
    }
  }
  .product-price-amount,
  .product-price-basis {
    .site-theme-radius & {
      font-family: $font-family-bold-radius !important;
    }
  }
  .product-details-expand {
    .expand-trigger {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
      }
    }
    .detailedprice-content {
      table {
        tr {
          &.sum-row {
            .site-theme-radius & {
              font-family: $font-family-bold-radius !important;
            }
          }
        }
      }
    }
  }
  .remove-product-link {
    .site-theme-radius & {
      font-family: $font-family-bold-radius;
    }
  }
}
