.list table.dataTable.no-footer {
  border-bottom: 0 !important;
}

.list table.dataTable thead th,
.list table.dataTable thead td {
  border-bottom: 0 !important;
}

.list table.dataTable.stripe tbody tr.odd,
.list table.dataTable.display tbody tr.odd {
  background-color: transparent;
}

.list table.dataTable tbody tr {
  background-color: transparent;
}

.list {
  $list-link-color: $color-aubergine;
  $list-background: $color-aqua;
  font-size: $text-size-body-small;
  line-height: $line-height-body-small;
  flex: 1 1 auto;
  overflow: visible;

  &__factbox {
    max-width: 334px;

    @include media-breakpoint-down(md) {
      max-width: 100%;
    }
  }

  &-title__closed {
    display: none;
  }

  &--toggle {
      height: 70px;
      overflow: hidden;

      .list-title__open {
        display: none;
      }
  
      .list-title__closed {
        display: block;
      }

      .list-items {
        display: none !important;
      }
  }
  
  &-title__toggle {
    display: none;
  }

  &.list--collapseable {
    .list-title__toggle {
      display: block;
    }
  }

  &.is-padded {
    height: 100%;
    padding: $component-container-padding;
  }

  &.link-list-container {
    height: 100%;
  }

  .list-title {
    font-family: $font-family-bold;
    font-size: $text-size-xsmall;
    line-height: $line-height-xsmall;

    @include media-breakpoint-down(md) {
      font-family: $font-family-bold;
      font-size: $text-size-body;
      line-height: $line-height-body;
    }

    margin-bottom: $spacing-regular;
    display: flex;
    justify-content: space-between;
    cursor: pointer;

    @include media-breakpoint-up(md) {
      .list-title__toggle {
        display: none;
      }
    }
  }

  .list-header {
    background-color: $list-background;

    .list-header-column {
      .list-header-column-name {
      }

      padding: $spacing-regular $spacing-large;
      font-family: $font-family-bold;

      @include asian-fonts;
      @include asian-fontweight-bold;

      @include clearFix;

      &:first-child {
        padding-left: $spacing-regular;
      }

      &:last-child {
        padding-right: $spacing-regular;
      }

      .list-header-column-sortable {
        color: $color-text;

        &.is-active {
          display: inline-flex;
          flex-direction: row-reverse;
          @include icon("arrow-down");
          color: $color-text;

          &:before {
            margin-left: 4px;
            margin-top: 2px;
          }
        }
      }

      .list-header-column-cta {
        float: right;
        display: none;

        @include media-breakpoint-up(md) {
          display: block;
        }

        a {
          margin-right: 0;
          font-family: $font-family-bold !important;

          @include asian-fonts;
          @include asian-fontweight-bold;
          font-size: $text-size-body-small;
          color: $color-text;

          &:hover {
            color: $color-text;
          }
        }
      }

      .list-header-column-view-toggle {
        display: none;

        @include media-breakpoint-up(md) {
          display: block;
        }

        float: right;

        a {
          margin-left: $spacing-regular;
          color: rgba($color-text, 0.5);
        }

        .icon {
          vertical-align: middle;
        }
      }
    }
  }

  .list-items {
    color: $color-text;
    padding: 0;
    width: 100%;

    a:not(.link-arrow) {
      color: $list-link-color;
    }

    .list-items-item,
    tr {
      position: relative;
      overflow: hidden;
    }

    .list-items-item-column,
    td {
      padding: $spacing-tiny $spacing-regular;
      vertical-align: top;
      position: relative;
      overflow: visible;

      &.should-fill {
        width: 100%;
      }

      &.should-not-wrap {
        white-space: nowrap;
      }
    }

    .list-items-item-column-image {
      @include media-breakpoint-up(md) {
        width: 100px;
        height: 100px;
      }

      padding: $spacing-tiny $spacing-regular;
      vertical-align: top;
      position: relative;
      overflow: visible;
    }

    .list-items-item-column-link {
      position: absolute;
      top: 0px;
      left: 0;
      width: 1440px;
      height: 600px;
      z-index: 1;
    }

    .list-items-item-value {
      display: block;

      &.list-items-item-value-type-image {
        width: 168px;
        padding-bottom: 56.25%;
        height: 0;
        background-size: cover;
        background-position: center;

        @include media-breakpoint-down(sm) {
          width: 100%;
        }
      }
    }

    &.list-show-labels {
      .list-items-item-column {
        &:before {
          display: block;
          content: attr(data-label);
          font-family: $font-family-bold;
        }
      }
    }

    &.list-has-row-hover {
      .list-items-item,
      tr {
        transition: background-color $transition-duration linear;

        &:hover {
          background-color: rgba($color-backdrop-1, 0.15) !important;

          .list-items-item-value-type-cta {
            a:not(.btn) {
              color: $color-text;
            }
          }
        }
      }
    }

    .list-items-item-value-type-header {
      font-family: $font-family-bold;

      @include asian-fonts;
      @include asian-fontweight-bold;
    }

    .list-items-item-value-type-cta {
      margin-top: $spacing-regular;

      a:not(.btn) {
        color: $color-backdrop-1;
      }
    }
    /*** COMPACT LISTS ***/
    &.list-type-compact {
      .list-items-item,
      tr {
        border-bottom: 1px solid $color-border-grey;
      }

      .list-items-item-column {
        display: block;
        padding: 0 $spacing-regular-2;

        &:first-child {
          padding-top: $spacing-regular-2;
        }

        &:last-child {
          padding-bottom: $spacing-regular-2;
        }
      }
    }
    /*** DOWNLOAD LISTS ***/
    &.list-type-download {
      display: table;
      table-layout: fixed;

      tbody {
        width: 100%;
        min-width: 100%;
        max-width: 100%;
      }

      table {
      }

      .list-items-item,
      tr {
        border-bottom: 1px solid $color-border-grey;
      }

      .list-items-item-column {
        padding: $spacing-regular-2;

        &:first-child {
          padding-left: 0;
        }

        &:last-child {
          padding-right: 0;
        }
      }

      .link-arrow {
        font-size: 14px;
        display: inline;
        font-family: $font-family-regular !important;
        color: inherit;
        @include asian-fonts;
      }
    }
    /*** NORMAL LISTS ***/
    &.list-type-list {
      a.list-header-column-view-toggle-list {
        color: $color-text;
      }

      .list-items-item,
      tr {
        border-bottom: 1px solid $color-border-grey;
      }

      .list-items-item-column,
      td {
        padding: $spacing-medium $spacing-large;

        &:first-child {
          padding-left: $spacing-regular;
        }

        &:last-child {
          padding-right: $spacing-regular;
        }
      }

      .list-items-item-value-type-cta {
        &:first-child {
          margin-top: 0;
        }
      }

      @include media-breakpoint-down(sm) {
        &:not(.list-no-collapse) .list-items-item-column,
        &:not(.list-no-collapse) td {
          display: block;
          padding: $spacing-tiny $spacing-regular-2;

          &:first-child {
            padding-top: $spacing-regular-2;
          }

          &:last-child {
            padding-bottom: $spacing-regular-2;
          }
        }
      }
    }
    /*** TABLE LISTS ***/
    &.list-type-table {
      .list-header .list-header-column {
        background-color: $color-aqua;
        border-left: 1px solid $color-aqua;
        border-right: 1px solid $color-aqua;
      }

      a.list-header-column-view-toggle-list {
        color: $color-text;
      }

      .list-items-item,
      tr {
        border-bottom: 1px solid $color-border-grey;
      }

      .list-items-item-column {
        padding: $spacing-regular $spacing-large;

        &:first-child {
          padding-left: $spacing-regular;
        }

        &:last-child {
          padding-right: $spacing-regular;
        }
      }

      @include media-breakpoint-down(sm) {
        &:not(.list-no-collapse) {
          .list-header {
            position: absolute;
            top: -9999px;
            left: -9999px;
          }

          .list-items-item-column {
            display: block;
            padding: $spacing-tiny $spacing-regular-2;

            &:before {
              display: block;
              content: attr(data-label);
              font-family: $font-family-bold;

              .site-theme-radius & {
                font-family: $font-family-bold-radius;
              }
            }

            &:first-child {
              padding-top: $spacing-regular-2;
            }

            &:last-child {
              padding-bottom: $spacing-regular-2;
            }
          }
        }
      }
    }
    /*** CARD LISTS ***/
    &.list-type-cards {
      max-width: 100%;

      a.list-header-column-view-toggle-cards {
        color: $color-text;
      }

      .list-header-column {
        display: none;

        &.list-header-column-has-cta {
          display: block;
        }

        &.list-header-column-has-view-toggle {
          display: block;
        }
      }

      tbody {
        display: flex;
        flex-wrap: wrap;
        margin: 0 (-$spacing-regular);
        @include flex-1;

        @include media-breakpoint-down(sm) {
          display: block;
        }

        .list-items-item,
        tr {
          display: block;
          flex: 1 1 33.33%;
          width: 33.333%;
          max-width: 33.333%;
          padding: $spacing-regular $spacing-regular $spacing-extralarge;
        }
      }

      .list-items-item,
      tr {
        display: block;

        @include media-breakpoint-down(sm) {
          width: 100%;
          max-width: 100%;
          padding-bottom: $spacing-tiny;
          border-bottom: 1px solid $color-border-grey;

          .list-items-item-column {
            padding: $spacing-tiny $spacing-regular-2;

            &:first-child {
              padding-top: $spacing-regular-2;
            }

            &:last-child {
              padding-bottom: $spacing-regular-2;
            }
          }
        }
      }

      .list-items-item-column {
        display: block;
      }

      .list-items-item-value-type-image {
        padding-bottom: 56%;
        width: 100%;
        height: 0;
      }
    }
    // NEWS LIST
    &.news-list {
      .list-items-item-column:first-child,
      td:first-child {
        padding: 0;
      }
    }
  }

  &.theme-backdrop-2 .list-type-download .list-items-item {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }

  .link-arrow.link-arrow-dark-blue {
    &.theme-darkblue-radius .list-type-download .list-items-item {
      border-bottom: 1px solid $color-white;
    }

    .theme-darkblue-radius & {
      color: $color-white !important;
    }
  }

  .dataTables_wrapper .dataTables_paginate .paginate_button.current {
    background-color: rgba(198, 203, 208, 0.5);
  }
}

.row {
  &.row-narrow, &.row-fullwidth, &.row-widewidth {
    .list .is-padded-vertical {
      padding: $component-container-padding;

      @include media-breakpoint-down(sm) {
        padding: $component-container-padding-mobile;
      }
    }

    .is-padded {
        padding: 0;
    }
  }
}

#a1c40fc8-e0be-4773-93f5-3a70c029c0e0 > section:nth-child(3) > div > div > div.list.theme-cold-grey-75-white.theme-primary-color-background.theme-color-base.list--collapseable > div > div

.search-result-module {
  @include media-breakpoint-down(sm) {
    .list
      .list-items.list-type-table:not(.list-no-collapse)
      .list-items-item-column {
      padding: 26px 0px;
    }
  }

  .list .list-items.list-type-table .list-items-item-column:first-child {
    padding: 25px 15px;
  }
}

.article {
  .list {
    max-width: 334px;

    @include media-breakpoint-down(md) {
      width: 100%;
      max-width: 100%;
    }
  }

  .article-placeholder-left {
    .list {
      margin-left: auto;
    }
  }
}
