.rsssignup-bottom-space {
  padding-bottom: 30px;
}

.rsssignup,
.rsssignoff {
  .error {
    padding-left: 16px;
    color: $color-red;
    font-size: 12px;
    &#Privacy-error {
      display: block;
      padding-left: 22px;
    }
  }

  .form-group .is-active .form-required {
    color: $color-dark-blue;
  }
}
@include media-breakpoint-up(md) {
  .row-widewidth .rsssignup-container {
    padding: 10px 20px 0px 20px;
  }

  .row-narrow .rsssignup-container {
    padding: 10px 20px 0px 20px;
  }

  .row-fullwidth .rsssignup-container {
    padding: 10px 20px 0px 20px;
  }
}
