.article-template{
    margin-top: 50px;
}

.module-padding{
    padding: $module-space-vertical-bottom $module-space-horisontal-left $module-space-vertical-bottom $module-space-horisontal-right;
}

body{
    background: $color-body-background;
}
main{
    background: white;
    max-width: 1440px;
    margin: 0 auto;
}


.container-fluid{
    @include media-breakpoint-down(sm){
        >.row{
            > [class*='col'] {
                .article:not(.article--narrow-media), .tabs, .topImage{
                    border-bottom:10px solid $color-transparent;
                }
            }
        }
    }
}
.container-has-gutters{
    background: $color-body-background;
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 10px;
    >.row{
        @include media-breakpoint-up(md){
            margin-bottom:10px;
        }
        > [class*='col'] {
            background-color: $color-white;
            padding-left:5px;
            padding-right:5px;
            @include media-breakpoint-down(sm){
                border-bottom: 10px solid $color-border;
            }
            >*{
                border-bottom:0;
            }
        }
    }
}
