.sitemap {
  position: relative;

  .sitemap-theme-top {
    @extend .theme-blue;
  }

  .sitemap-theme-bottom {
    @extend .theme-aubergine;
  }

  .row {
    width: 100%;
    overflow: hidden;
  }
  // your styles here
  .sitemap-top {
    &.has-open-expand {
      border-top: 1px solid $color-body-background;
    }
    > .row {
      margin-bottom: 0;
      transition: background-color $transition-duration * 2 ease-in-out;
    }
  }

  .sitemap-top,
  .sitemap-bottom {
    > * {
      padding: $module-space-vertical-bottom $module-space-horisontal-left
        $module-space-vertical-bottom $module-space-horisontal-right;

      @include media-breakpoint-up(md) {
        padding: $module-space-vertical-top $module-space-horisontal-left
          $module-space-vertical-bottom $module-space-horisontal-right;
      }
    }
  }

  .sitemap-top-right {
    position: relative;
    @include media-breakpoint-up(md) {
      padding-left: $module-space-horisontal-left;
      &:before {
        content: "";
        width: 1px;
        height: 800px;
        background-color: rgba($color-dark-blue, 0.1);
        overflow: visible;
        display: block;
        position: absolute;
        margin-left: -1px;
        left: 0;
        top: -100px;
      }
    }
  }

  .sitemap-bottom-left {
    display: flex;

    > * {
      margin-bottom: 40px;
    }

    @include media-breakpoint-up(md) {
      flex-direction: row;
      flex-wrap: wrap;

      > * {
        margin-right: 80px;
        // margin-left: 50px;
        &:first-child {
          margin-left: 0px;
        }

        flex: 0 1 auto;
      }
    }
  }

  .sitemap-bottom-right {
    > * {
      margin-bottom: 20px;
    }
  }

  .sitemap-divider {
    width: 1px;
    height: 100%;
    background-color: rgba($color-white, 0.5);
  }

  .sitemap-list {
    display: flex;
    flex-direction: row;
    min-width: 50%;
    flex-wrap: wrap;
  }

  .sitemap-list-item {
    flex: 1 0 100%;
    position: relative;

    &:after {
      content: "";
      height: 1px;
      width: 200%;
      background-color: rgba($color-white, 0.2);
      display: block;
      position: absolute;
      left: -50%;
    }

    @include media-breakpoint-up(md) {
      margin-bottom: 20px;
      &:after {
        content: none;
      }

      flex: 1 0 40%;
      margin-bottom: 15px;
      margin-right: 50px;
    }
  }

  .sitemap-sublist {
    font-size: 19px;
    @include list;
    margin-bottom: 20px;
    @include media-breakpoint-up(md) {
      font-size: 1em;
      margin-bottom: 0;
    }

    > li {
      margin-bottom: 20px;
      line-height: 1.1;
    }
  }

  p {
    font-size: $text-size-body-small;
    margin-bottom: 0; // Added this because there was a weird p+p{margin-bottom}.
  }

  .sitemap-sublist-item {
    margin-top: 10px;
  }

  .sitemap-some-icons {
    @include list-inline;
    margin-top: 20px;

    a {
      color: $color-blue;
      &:hover {
        color: $color-aqua;
      }
    }

    .icon {
      font-size: 2em;
    }
  }

  .expand-trigger {
    @include icon("arrow-right");
    display: inline;
    position: relative;

    &:before {
      transform: rotate(90deg);
      transform-origin: center;
      font-size: 0.7em;
      height: 0.9em;
      vertical-align: middle;
      text-align: center;
      margin-left: 10px;
      margin-top: 0.5em;
      transition: transform $transition-duration ease-in-out;
      position: absolute;
      right: -30px;
    }
  }

  .expand {
    display: inline-block;
    // color:$color-blue;
    @include media-breakpoint-down(sm) {
      margin-top: 20px;
    }

    .expand-content {
      margin-top: 20px;
    }

    &.expand-content-visible {
      .expand-trigger {
        &:before {
          transform: rotate(-90deg);
        }
      }
    }
  }

  .trustpilot {
    > * {
      margin-right: 5px;
    }

    font-size: 12px;
  }
}
