.container-fluid form,
.form,
.toaster-placeholder form {
  $transition-duration: 0.25s;
  $font-size-desktop: 15px;
  $font-size-mobile: 16px;
  max-width: $legible-text-max-width;
  text-align: center;

  > * {
    text-align: left;
  }

  button[type="submit"],
  input[type="submit"] {
    float: right;
    cursor: pointer;
    margin-right: 0px !important;

    &.search-result-submit-btn {
      margin-bottom: 0px !important;
      margin-right: 20px !important;
    }
  }

  button:not([type="submit"]) {
    @include btnCta;
    float: right;
  }

  fieldset {
    margin-top: 50px;
  }

  legend {
    display: inline-block;
    width: auto;
    padding: 0 0 0 0;
    margin-bottom: 0px;
    line-height: 1.2;
  }

  .form-col {
    justify-content: flex-end;
  }

  .no-top-space {
    margin-top: 0;
  }

  .field-validation-error {
    color: $color-red;
  }

  .form-group {
    position: relative;
    margin-top: 30px;
    width: auto;

    li.list-group-item.list-group-item-warning {
      padding-right: 32px;
    }
    // Added padding for legends in form-group. These are, for instance, the help symbols in form selector.
    legend {
      float: left;
      padding: 0;
      margin: 0 0 0 0;
    }

    &.auto-width {
      display: inline-flex;
    }

    input {
      border-radius: 21px;
      border-color: $color-input-border;
      padding: 10px 16px;
      height: 44px;
      font-size: $font-size-mobile;
      cursor: pointer;
      outline: none;
      transition: all $transition-duration ease-in-out !important;

      @include media-breakpoint-up(sm) {
        font-size: $font-size-desktop;
      }
      // color: $color-backdrop-1;
      color: $color-text;

      &:disabled {
        cursor: default;
        background-color: $color-white;
        border-color: $color-input-border-disabled;
      }

      &.form-control-success {
        background-image: none;
      }

      &.form-control-danger {
        background-image: none;
      }

      &[type="email"] {
        padding-right: 0;
      }

      &[type="radio"],
      &[type="checkbox"] {
        &:focus {
          outline: 3px solid $color-input-border-focus;
          opacity: 1 !important; // Due to opacity:0 in login box
        }
      }
      // Hide number input spinners
      &[type="number"] {
        & {
          -moz-appearance: textfield;
        }

        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
          -webkit-appearance: none;
          margin: 0;
        }
      }

      &:focus {
        border-color: $color-input-border-focus !important;
        border-width: 3px;
      }
    }

    input.btn-default {
      $props: map-get($buttonThemes, "blue");
      @include btnTheme(
        map-get($props, "background-color"),
        map-get($props, "text-color")
      );
    }

    textarea {
      border-color: $color-input-border;
      font-size: $font-size-mobile;
      transition: all $transition-duration ease-in-out !important;

      @include media-breakpoint-up(sm) {
        font-size: $font-size-desktop;
      }

      padding: 10px 16px;
      // color: $color-backdrop-1;
      color: $color-dark-blue;

      &:disabled {
        cursor: default;
        background-color: $color-white;
        border-color: $color-input-border-disabled;
      }

      &.form-control-success {
        background-image: none;
      }

      &.form-control-danger {
        background-image: none;
      }

      &:focus {
        border-color: $color-input-border-focus;
        border-width: 3px;
      }
    }

    .form-maxlength-indicator {
      position: absolute;
      display: block;
      bottom: 4px;
      right: 15px;
      font-size: 12px;
      color: $color-text;
    }

    label {
      &.show-as-placeholder,
      &.control-label {
        position: absolute;
        width: 100%;
        z-index: 1;
        left: 0px;
        padding: 0 20px;
        top: 0.6em;
        transform: translateY(0);
        transition: all $transition-duration ease-in-out;
        color: $color-text;
        font-size: $font-size-mobile;

        @include media-breakpoint-up(sm) {
          font-size: $font-size-desktop;
        }

        &.is-active {
          transform: translateY(-30px);
          font-size: 12px;
          color: $color-text;

          &.control-label:after,
          .inner-label:after {
            color: $color-text;
          }

          .form-control-help-bs-tooltip {
            width: 18px;
            height: 18px;
            border-radius: 9px;
            line-height: 18px;
          }
        }

        &.is-disabled {
          color: rgba($color-backdrop-1, 0.5);
        }
      }
    }

    .form-required {
      color: $color-input-required;
    }

    legend {
      font-family: $font-family-bold;

      font-size: 18px;
      margin-bottom: 0.5rem;
    }

    input[type="checkbox"],
    input[type="radio"] {
      vertical-align: 1px;
      height: auto;

      & ~ span {
        margin-left: $spacing-tiny;
        font-size: $font-size-mobile;

        @include media-breakpoint-up(sm) {
          font-size: $font-size-desktop;
        }

        a {
          text-decoration: underline;
        }
      }

      &:checked ~ span {
        color: $color-blue;
        font-family: $font-family-bold;

        a {
          color: $color-blue;
        }
      }

      &:disabled {
        opacity: 0.5;

        & ~ span {
          opacity: 0.5;
        }
      }
    }

    &.has-success {
      &:after {
        position: absolute;
        display: block;
        top: 9px;
        right: 9px;
        width: 26px;
        height: 26px;
        border-radius: 13px;
        padding: 1px 0 0 5px;
        font-family: $font-family-bold;

        background-color: $color-input-success;
        color: $color-white;
        content: none;
      }
    }

    &.disable-validation-checkmark {
      &:after {
        display: none;
      }
    }

    &.has-danger,
    &.has-error {
      &:after {
        position: absolute;
        display: block;
        top: 9px;
        right: 9px;
        width: 26px;
        height: 26px;
        border-radius: 13px;
        padding: 1px 0 0 11px;
        font-family: $font-family-bold;

        background-color: $color-input-error;
        color: $color-white;
        content: "!";
      }

      input,
      textarea {
        border-color: $color-input-border-error;
      }

      .form-control-feedback,
      .help-block.field-validation-error {
        margin-left: 16px;
        color: $color-red;
        font-size: 12px;
      }

      .checkbox {
        margin-right: 40px;
      }
    }
    //fixes for forms when experience editing
    .is-experience-editing &.has-success {
      &:after {
        top: 33px;
      }
    }

    .is-experience-editing &.has-danger,
    .is-experience-editing &.has-error {
      &:after {
        top: 33px;
      }
    }

    .form-control-help-bs-tooltip {
      position: relative;
      margin-left: 15px;
      display: inline-block;
      right: -5px;
      width: 26px;
      height: 26px;
      border-radius: 13px;
      text-align: center;
      line-height: 26px;
      font-family: $font-family-regular;

      background-color: $color-input-help;
      color: $color-white;
      cursor: pointer;

      &.form-control-help-inline {
        position: relative;
        display: inline-block;
        top: auto;
        right: auto;
        margin-left: 10px;
        clear: both;
        overflow: visible;

        @include media-breakpoint-down(md) {
          &:after {
            top: 37px;
            right: auto;
            left: -5px;
            width: auto;
          }

          &:before {
            top: 28px;
            right: auto;
          }
        }
      }
    }

    .is-experience-editing & {
      .form-control-help-bs-tooltip {
        top: 33px;
      }
    }

    label {
      &.is-active {
        .form-control-help {
          width: 18px;
          height: 18px;
        }
      }

      .form-control-help {
        position: static;
        display: inline-block;
        width: 26px;
        height: 26px;
        border-radius: 50%;
        // padding: 1px 0 0 9px;
        font-family: $font-family-regular;

        background-color: $color-dark-blue;
        color: $color-white;
        cursor: pointer;
      }
    }

    .form-control-help {
      position: absolute;
      display: block;
      top: 8px;
      right: 6px;
      width: 26px;
      height: 26px;
      border-radius: 50%;
      padding-top: 1px;
      // padding: 1px 0 0 9px;
      text-align: center;
      font-family: $font-family-regular;

      background-color: $color-input-help;
      color: $color-white;
      cursor: pointer;

      &:hover {
        &:after {
          background: $color-input-help;
          text-align: left;
          top: 37px;
          color: #fff;
          content: attr(data-help);
          right: -5px;
          padding: 15px;
          position: absolute;
          z-index: 98;
          width: 220px;
          font-family: $font-family-regular;

          font-size: 12px;
          line-height: 16px;
        }

        &:before {
          border: solid;
          border-color: $color-input-help transparent;
          border-width: 0 9px 9px 9px;
          top: 28px;
          content: "";
          right: 4px;
          position: absolute;
          z-index: 99;
        }
      }

      &.form-control-help-inline {
        position: relative;
        display: inline-block;
        top: auto;
        right: auto;
        margin-left: 10px;
        clear: both;
        overflow: visible;

        @include media-breakpoint-down(md) {
          &:after {
            top: 37px;
            right: auto;
            left: -5px;
            width: auto;
          }

          &:before {
            top: 28px;
            right: auto;
          }
        }
      }
    }

    ul.form-control-feedback {
      height: 0px;
      margin: 0px !important;
      padding-left: 16px;

      li {
        list-style: none;
      }
    }
  }

  .form-search {
    display: flex;
    @include flex-1;
    flex-direction: row;

    input {
      display: inline-flex;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      @include flex-1;
    }
    // .form-control{
    //     width: auto;
    // }
    .btn {
      cursor: pointer;
      // flex:auto;
      display: inline-block;
      // position: absolute;
      // right: 0;
      // top:1px;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
      margin-left: 0px !important;
      margin-top: 0px !important;
    }
  }

  .form-attachment {
    clear: both;

    .form-attachment-file {
      margin-bottom: 10px;

      input {
        width: 1px;
        height: 1px;
        position: absolute;
        display: none;

        &:focus + label {
          box-shadow: 0 0 0px 3px $color-input-border-focus;
        }
      }

      .form-attachment-remove-file {
        display: none;
      }

      label {
        margin-bottom: 0;
        cursor: pointer;
      }

      &.has-file {
        label {
          cursor: default;
          min-width: 150px;
          pointer-events: none;

          &:hover {
            background-color: $color-white;
          }
        }

        .form-attachment-remove-file {
          display: inline-block;
        }
      }
    }
  }

  .form-terms {
    label {
      display: inline;
    }
  }

  .form-select {
    clear: both;
    display: flex;
    @include flex-1;

    select {
      width: 200%;
    }

    + .form-control-help {
      top: 0;
      right: 10px;
    }
  }

  .form-check {
    clear: both;
  }

  .form-control-datepicker {
    &[readonly] {
      background: $color-white;
    }
  }
  .form-control.datepicker[readonly] {
    background-color: $color-white !important;
  }
  //enables sitecore to detect form-controls that shoule have some inline functionality
  .is-inline-form-control {
    label.control-label {
      position: static;
      display: inline-block;
      width: auto;
      //  float: left;
      font-family: $font-family-bold;

      font-size: 18px;
      margin-bottom: 0.5rem;
    }

    label {
      margin-right: 40px;
    }

    .form-control-help-bs-tooltip {
      top: 0;
    }

    &.has-error {
      &:after {
        top: 0;
      }
    }

    &.has-success {
      &:after {
        top: 0;
      }
    }
  }

  &.is-inline-form-control.has-error {
    &:after {
      top: 0;
    }
  }

  div.radio td {
    padding-right: 20px;
  }
}

.form-inline .customSelect {
  width: 100%;
}
