.required-field .control-label:before {
  display: none;
}

.required-field:not(.has-inner-label) .control-label:after {
  color: #e85757;
  content: "*";
  margin-left: 5px;
}

.required-field .control-label:after {
  color: #e85757;
  content: "*";
  margin-left: -32px;
}
.required-field .checkbox:after {
  display: none;
}

//
// Tooltips
// --------------------------------------------------

$tooltip-arrow-width: 5px;
$tooltip-max-width: 300px;
$tooltip-min-width: 220px;
// Base class
.tooltip {
  &.top {
    margin-top: -3px;
    padding: $tooltip-arrow-width 0;
  }

  &.right {
    margin-left: 3px;
    padding: 0 $tooltip-arrow-width;
  }

  &.bottom {
    margin-top: 3px;
    padding: $tooltip-arrow-width 0;
  }

  &.left {
    margin-left: -3px;
    padding: 0 $tooltip-arrow-width;
  }
}
// Wrapper for the tooltip content
.tooltip-inner {
  max-width: $tooltip-max-width;
  min-width: $tooltip-min-width;
  background-color: $color-input-help;
  padding: 15px;
  font-family: $font-family-regular;

  font-size: 12px;
  line-height: 16px;
  border-radius: 0;
  text-align: left;
}
// Arrows
.tooltip-arrow {
  position: absolute;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
}

.tooltip {
  &.top .tooltip-arrow {
    bottom: 0;
    left: 50%;
    margin-left: -$tooltip-arrow-width;
    border-width: $tooltip-arrow-width $tooltip-arrow-width 0;
    border-top-color: $color-input-help;
  }

  &.top-left .tooltip-arrow {
    bottom: 0;
    right: $tooltip-arrow-width;
    margin-bottom: -$tooltip-arrow-width;
    border-width: $tooltip-arrow-width $tooltip-arrow-width 0;
    border-top-color: $color-input-help;
  }

  &.top-right .tooltip-arrow {
    bottom: 0;
    left: $tooltip-arrow-width;
    margin-bottom: -$tooltip-arrow-width;
    border-width: $tooltip-arrow-width $tooltip-arrow-width 0;
    border-top-color: $color-input-help;
  }

  &.right .tooltip-arrow {
    top: 50%;
    left: 0;
    margin-top: -$tooltip-arrow-width;
    border-width: $tooltip-arrow-width $tooltip-arrow-width $tooltip-arrow-width
      0;
    border-right-color: $color-input-help;
  }

  &.left .tooltip-arrow {
    top: 50%;
    right: 0;
    margin-top: -$tooltip-arrow-width;
    border-width: $tooltip-arrow-width 0 $tooltip-arrow-width
      $tooltip-arrow-width;
    border-left-color: $color-input-help;
  }

  &.bottom .tooltip-arrow {
    top: 0;
    left: 50%;
    margin-left: -$tooltip-arrow-width;
    border-width: 0 $tooltip-arrow-width $tooltip-arrow-width;
    border-bottom-color: $color-input-help;
  }

  &.bottom-left .tooltip-arrow {
    top: 0;
    right: $tooltip-arrow-width;
    margin-top: -$tooltip-arrow-width;
    border-width: 0 $tooltip-arrow-width $tooltip-arrow-width;
    border-bottom-color: $color-input-help;
  }

  &.bottom-right .tooltip-arrow {
    top: 0;
    left: $tooltip-arrow-width;
    margin-top: -$tooltip-arrow-width;
    border-width: 0 $tooltip-arrow-width $tooltip-arrow-width;
    border-bottom-color: $color-input-help;
  }
}

.form-submit-border {
  margin-top: 15px;
}

button[type="submit"],
input[type="submit"] {
  margin: 20px !important;
  margin-bottom: 30px !important;
  transition: all $transition-duration ease-in-out !important;

  &:focus {
    box-shadow: 0 0 0px 3px $color-input-border-focus;
  }
}
// center all sitecore forms - don't think it's a good idea
form.sitecore-form-initialized {
  margin: 30px auto;
  > form {
    width: initial;
  }
}

.formLoader {
  display: block;
  border: 4px solid #ffffff;
  border-top: 4px solid #4e9dd4;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
}

.formLoaderContainer {
  height: 37px;
  text-align: right;
  width: 100%;
  padding-top: 27px;

  & > div {
    position: relative;
    right: -57px;
    display: inline-block;
  }
}

.anonymous {
  padding: 20px 30px;
}

.row {
  &.row-narrow {
    @include media-breakpoint-up(md) {
      form.sitecore-form-initialized {
        width: 100%;
      }
      .anonymous {
        padding: 20px 30px;
      }
    }
    @include media-breakpoint-down(sm) {
      form.sitecore-form-initialized {
        width: 100%;
      }
      .anonymous {
        padding: 20px 0px;
      }
    }
  }
  &.row-fullwidth {
    form.sitecore-form-initialized {
      width: 100%;
    }
    .anonymous {
      padding: 20px 30px;
    }
  }
  &.row-widewidth {
    form.sitecore-form-initialized {
      width: 100%;
    }
    .anonymous {
      padding: 20px 30px;
    }
  }
}

