/* Base template setup
---------------------------------------------------------------------- */
/* Markers */

.mapw {
  width: 100%;
  clear: both;
  margin: auto;

  .marker {
    display: block;
  }

  .ico-renewableenergy {
    width: 1px;
    height: 1px;
    position: absolute;
    z-index: 100;
  }

  .ico-renewableenergy:hover .inx .ico {
    background-position: 0px -59px;
  }

  .ico-renewableenergy .inx .ico {
    background: url("../../assets/images/marker-mill-t1.png") no-repeat 0px 0px;
  }

  .ico-powerstation {
    width: 1px;
    height: 1px;
    position: absolute;
    z-index: 100;
  }

  .ico-powerstation:hover .inx .ico {
    background-position: 0px -59px;
  }

  .ico-powerstation .inx .ico {
    background: url("../../assets/images/marker-powerplant-t1.png") no-repeat
      0px 0px;
  }

  .ico-office {
    width: 1px;
    height: 1px;
    position: absolute;
    z-index: 100;
  }

  .ico-office:hover .inx .ico {
    background-position: 0px -59px;
  }

  .ico-office .inx .ico {
    background: url("../../assets/images/marker-office-t1.png") no-repeat 0px
      0px;
  }

  .ico-license {
    width: 1px;
    height: 1px;
    position: absolute;
    z-index: 100;
  }

  .ico-license:hover .inx .ico {
    background-position: 0px -59px;
  }

  .ico-license .inx .ico {
    background: url("../../assets/images/marker-license-t1.png") no-repeat 0px
      0px;
  }

  .mrk .inx {
    width: 1px;
    height: 1px;
    position: relative;
  }

  .mrk .inx .ico {
    width: 38px;
    height: 59px;
    position: absolute;
    z-index: 100;
    left: -19px;
    top: -59px;
    cursor: pointer;
  }
  /* Tooltip */
  #mapstooltip {
    position: absolute;
    left: 0px;
    top: 0px;
    z-index: 999999;
    background: #fff;
    font-size: 11px;
    display: none;
    font-weight: bold;
    color: #222;
    line-height: 110%;
    margin: -8px 0px 0px 25px;
    //font-family: 'lucida grande', tahoma, verdana, arial, sans-serif;
    border: 1px solid #c1c1c1;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.3);
    text-align: left;
    border-radius: 3px;
  }

  #mapstooltip .rel {
    width: 100%;
    position: relative;
    z-index: 100;
  }

  #mapstooltip .rel #mapstooltip-arrow-left {
    width: 9px;
    height: 16px;
    position: absolute;
    z-index: 101;
    left: -9px;
    top: 6px;
    background: url("../../assets/images/icons-sprite-t1.png") no-repeat -25px -1px;
    display: block;
  }

  #mapstooltip .rel #mapstooltip-arrow-right {
    width: 9px;
    height: 16px;
    position: absolute;
    z-index: 101;
    right: -9px;
    top: 6px;
    background: url("../../assets/images/icons-sprite-t1.png") no-repeat -34px -1px;
    display: none;
  }

  #mapstooltip .rel #mapstooltip-text {
    clear: both;
    padding: 7px 15px 7px 10px;
    white-space: pre;
  }
  /* Map overlay */
  #ioverlay {
    width: 200px;
    height: 320px;
    position: absolute;
    left: 0px;
    top: 0px;
    z-index: 999999;
    margin: -328px 0px 0px -100px;
    text-align: left;
    cursor: default;
  }

  #ioverlay .rel {
    width: 200px;
    height: 320px;
    position: relative;
  }

  #ioverlay .rel .abs {
    position: absolute;
    z-index: 100;
    left: 0px;
    bottom: 0px;
    background: #fff;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(50, 50, 50, 0.9);
    -moz-box-shadow: 0px 0px 10px 0px rgba(50, 50, 50, 0.9);
    box-shadow: 0px 0px 10px 0px rgba(50, 50, 50, 0.9);
  }

  #ioverlay .rel .pad {
    clear: both;
    padding: 0px 10px 0px 10px;
  }

  #ioverlay .rel .pad .hd {
    width: 100%;
    height: 35px;
    clear: both;
    position: relative;
  }

  #ioverlay .rel .pad .hd .close {
    width: 20px;
    height: 20px;
    position: absolute;
    right: -2px;
    top: 8px;
  }

  #ioverlay .rel .pad .hd .close .rl {
    width: 100%;
    height: 100%;
    position: relative;
  }

  #ioverlay .rel .pad .hd .close .rl a {
    width: 16px;
    height: 16px;
    position: absolute;
    z-index: 100;
    left: 2px;
    top: 2px;
    text-decoration: none;
    background: url("../../assets/images/icon-cross-t1.png") no-repeat 0px 0px;
  }

  #ioverlay .rel .pad .hd .close .rl a:hover {
    background-position: 0px -16px;
  }

  #ioverlay .rel .pad .hd .renewableenergy {
    width: 26px;
    height: 22px;
    position: absolute;
    z-index: 100;
    left: 0px;
    top: 6px;
    background: url("../../assets/images/pop-icon-mill-t1.png") no-repeat;
  }

  #ioverlay .rel .pad .hd .powerstation {
    width: 21px;
    height: 20px;
    position: absolute;
    z-index: 100;
    left: 0px;
    top: 7px;
    background: url("../../assets/images/pop-icon-powerplant-t1.png") no-repeat;
  }

  #ioverlay .rel .pad .hd .office {
    width: 21px;
    height: 14px;
    position: absolute;
    z-index: 100;
    left: 0px;
    top: 11px;
    background: url("../../assets/images/pop-icon-office-t1.png") no-repeat;
  }

  #ioverlay .rel .pad .hd .license {
    width: 25px;
    height: 18px;
    position: absolute;
    z-index: 100;
    left: 0px;
    top: 8px;
    background: url("../../assets/images/pop-icon-license-t1.png") no-repeat;
  }

  #ioverlay .rel .arrow {
    width: 16px;
    height: 9px;
    position: absolute;
    z-index: 100;
    left: 50%;
    bottom: -9px;
    margin: 0px 0px 0px -8px;
    background: url("../../assets/images/icons-sprite-t1.png") no-repeat -9px -9px;
    display: none;
  }

  #ioverlay .rel .pad .photo {
    width: 100%;
    clear: both;
    margin-bottom: 10px;
    font-size: 12px;
    color: #000;
    line-height: 120%;
  }

  #ioverlay .rel .pad .photo img {
    width: 100%;
    display: block;
  }

  #ioverlay .rel .pad .title {
    width: 100%;
    clear: both;
    margin-bottom: 10px;
    font-size: 16px;
    color: $color-text;
    line-height: 120%;
    padding: 10px 0px 0px 0px;
    //font-family: 'FelbridgeDONGEnergyW01-Bd';
  }

  #ioverlay .rel .pad .description {
    width: 100%;
    clear: both;
    margin-bottom: 14px;
    font-size: 12px;
    color: $color-text;
    line-height: 120%;
  }

  #ioverlay .rel .pad .description a {
    color: inherit;
    &:hover {
      color: inherit;
    }
  }

  #ioverlay .rel .pad .link {
    width: 100%;
    clear: both;
    padding: 8px 0px 8px 0px;
    border-top: 1px solid $color-text;
  }

  #ioverlay .rel .pad .link a {
    font-size: 11px;
    color: $color-aubergine !important;
    line-height: 120%;
    text-decoration: none;
    @extend .link-arrow;
  }

  #ioverlay .rel .pad .link a:hover {
    color: $color-aubergine !important;
  }

  .map img {
    max-width: inherit !important;
  }
  /* Setup */
  .filter {
    clear: both;

    .head-t1 {
      width: 100%;
      clear: both;
      margin: 0px 0px 25px 0px;
      padding: 0px 20px 0px 20px;
    }

    .head-t1 p {
      margin: 0;
      font-size: 22px;
      color: $color-aubergine;
      line-height: 110%;
      /*text-transform: uppercase;*/
      //font-family: 'FelbridgeDONGEnergyW01-Bd';
    }

    .filt-t1 {
      height: 90px;
      clear: both;
      position: relative;
      z-index: 100;
      background: #d8d1ca;
    }

    .filt-t1 .lft {
      position: absolute;
      left: 0px;
      top: 0px;
      z-index: 100;
    }

    .filt-t1 .lft ul {
      margin: 0;
      padding: 0;
      list-style-type: none;
      float: left;
      padding: 10px 0px 10px 10px;
    }

    .filt-t1 .lft ul li {
      width: 130px;
      height: 70px;
      float: left;
      background: url("../../assets/images/seperator-t1.png") no-repeat right
        top;
    }

    .filt-t1 .lft ul li a {
      height: 70px;
      display: block;
      position: relative;
      text-decoration: none;
    }

    .filt-t1 .lft ul li a .lbl {
      width: 70px;
      height: 70px;
      padding: 0px 0px 0px 60px;
      display: table-cell;
      vertical-align: middle;
      font-size: 10px;
      color: #696e68;
      line-height: 130%;
      /*text-transform: uppercase;*/
      //font-family: 'FelbridgeDONGEnergyW01-Bd';
    }

    .filt-t1 .lft ul li a .ico {
      width: 40px;
      height: 40px;
      position: absolute;
      left: 10px;
      top: 15px;
    }

    .filt-t1 .lft ul li a .renewableenergy {
      background: url("../../assets/images/icon-mill-t1.png") no-repeat 0px 0px;
    }

    .filt-t1 .lft ul li a .powerstation {
      background: url("../../assets/images/icon-powerplant-t1.png") no-repeat
        0px 0px;
    }

    .filt-t1 .lft ul li a .office {
      background: url("../../assets/images/icon-office-t1.png") no-repeat 0px
        0px;
    }

    .filt-t1 .lft ul li a .license {
      background: url("../../assets/images/icon-licenses-t1.png") no-repeat 0px
        0px;
    }

    .filt-t1 .lft ul li a:hover .lbl {
      color: $color-aubergine;
    }

    .filt-t1 .lft ul li a:hover .ico {
      background-position: 0px -40px;
    }

    .filt-t1 .lft ul li .active .lbl {
      color: $color-aubergine;
    }

    .filt-t1 .lft ul li .active .ico {
      background-position: 0px -40px;
    }

    .filt-t1 .lft ul .last {
      background: none;
    }

    .filt-t1 .rgt {
      position: absolute;
      right: 20px;
      bottom: 10px;
      z-index: 100;
    }

    .filt-t1 .rgt .mrls {
      display: block;
      padding: 0px 0px 28px 0px;
      text-align: right;
      font-size: 12px;
      font-weight: bold;
      color: #696e68;
      line-height: 130%;
      /*text-transform: uppercase;*/
      text-decoration: none;
    }

    .filt-t1 .rgt .mrls .ico {
      width: 14px;
      height: 14px;
      position: absolute;
      z-index: 100;
      right: 0px;
      bottom: 10px;
      background: url("../../assets/images/arrow-t1.png") no-repeat 0px 0px;
    }

    .filt-t1 .rgt .mrls:hover {
      color: $color-aubergine;
    }

    .filt-t1 .rgt .less .ico {
      background-position: 0px 0px;
    }

    .filt-t1 .rgt .less:hover .ico {
      background-position: 0px -28px;
    }

    .filt-t1 .rgt .more .ico {
      background-position: 0px -14px;
    }

    .filt-t1 .rgt .more:hover .ico {
      background-position: 0px -42px;
    }

    .filt-t1 .arr {
      width: 18px;
      height: 10px;
      position: absolute;
      z-index: 100;
      right: 20px;
      bottom: -10px;
      background: url("../../assets/images/arrow-t2.png") no-repeat right top;
      display: none;
    }

    .filt-t2 {
      width: 100%;
      clear: both;
      display: none;
    }

    .filt-t2 .pad {
      clear: both;
      padding: 20px 0px 0px 20px;
    }

    .filt-t2 .pad .head-t2 {
      width: 100%;
      clear: both;
      margin: 0px 0px 10px 0px;
    }

    .filt-t2 .pad .head-t2 p {
      margin: 0;
      font-size: 12px;
      color: #696e68;
      line-height: 130%;
      /*text-transform: uppercase;*/
      //font-family: 'FelbridgeDONGEnergyW01-Bd';
    }

    .filt-t2 .pad .flt {
      width: 100%;
      clear: both;
      margin: 0px 0px 15px 0px;
    }

    .filt-t2 .pad .flt ul {
      margin: 0;
      padding: 0;
      list-style-type: none;
      float: left;
    }

    .filt-t2 .pad .flt ul li {
      float: left;
    }

    .filt-t2 .pad .flt ul li a {
      float: left;
      padding: 5px 0px 5px 25px;
      position: relative;
      font-size: 14px;
      color: #696e68;
      line-height: 130%;
      text-decoration: none;
    }

    .filt-t2 .pad .flt ul li a .rad {
      width: 14px;
      height: 14px;
      position: absolute;
      z-index: 100;
      left: 0px;
      top: 7px;
      background: url("../../assets/images/radio-t1.png") no-repeat 0px 0px;
    }

    .filt-t2 .pad .flt ul li a:hover .rad {
      background-position: 0px -28px;
    }

    .filt-t2 .pad .flt ul li .active {
    }

    .filt-t2 .pad .flt ul li .active .rad {
      background-position: 0px -14px;
    }

    .filt-t2 .pad .flt ul li .active:hover .rad {
      background-position: 0px -42px;
    }

    .filt-t2 .pad .flt ul .w1 {
      width: 130px;
    }

    .filt-t2 .pad .flt ul .w2 {
      width: 155px;
    }

    .filt-t2 .pad .flt ul .w3 {
      width: 195px;
    }

    .filt-t2 .pad .flt ul .w4 {
      width: 190px;
    }

    .filt-t2 .pad .flt .clr {
      width: 100%;
      height: 1px;
      clear: both;
      overflow: hidden;
    }

    .filt-t2 .bdr {
      border-top: 1px solid #b5baaf;
    }
  }

  .map {
    width: 100%;
    height: 500px;
    position: relative;
    overflow: hidden;

    #map {
      width: 100%;
      height: 100%;
    }

    .zoom {
      width: 29px;
      height: 50px;
      position: absolute;
      z-index: 100;
      left: 17px;
      bottom: 37px;
      /* Without logo bottom:17px; */
    }

    .zoom .rl {
      width: 100%;
      height: 100%;
      position: relative;
    }

    .zoom .rl .in {
      width: 29px;
      height: 29px;
      position: absolute;
      z-index: 100;
      left: 0px;
      top: 0px;
      background: url("../../assets/images/zoom-plus-t1.png") no-repeat 0px 0px;
      cursor: pointer;
    }

    .zoom .rl .in:hover {
      background-position: 0px -29px;
    }

    .zoom .rl .out {
      width: 29px;
      height: 29px;
      position: absolute;
      z-index: 100;
      left: 0px;
      top: 22px;
      background: url("../../assets/images/zoom-minus-t1.png") no-repeat 0px 0px;
      cursor: pointer;
    }

    .zoom .rl .out:hover {
      background-position: 0px -29px;
    }

    .zoom .rl .sep {
      width: 21px;
      height: 1px;
      position: absolute;
      z-index: 100;
      left: 4px;
      top: 25px;
      background: #b5b3aa;
    }

    .back {
      position: absolute;
      z-index: 99;
      top: 15px;
      right: 15px;
      display: none;
    }

    .back a {
      float: right;
      padding: 7px 5px 6px 5px;
      line-height: 100%;
      background: $color-aubergine;
      color: #fff;
      font-size: 12px;
      text-decoration: none;
      border-radius: 3px;
    }

    .back a:hover {
      background: #097386;
    }
  }

  #xml-data {
    display: none;
  }
}

/* Remove logo + copyrights 
a[href^="http://maps.google.com/maps"]					{display:none!important;}
.gmnoprint a, .gmnoprint span 							{display:none;}
.gmnoprint div 											{background:none!important;}
*/
/* Tablets + phones
---------------------------------------------------------------------- */

@media only screen and (min-width: 0px) and (max-width: 768px) {
  .mapw {
    width: 100%;

    .filter .head-t1 {
      width: auto;
      padding: 0px 15px 0px 15px;
    }

    .filter .filt-t1 {
      height: auto;
      width: auto;
      padding: 0px 15px 0px 15px;
    }

    .filter .filt-t1 .lft {
      width: auto;
      position: static;
      clear: both;
    }

    .filter .filt-t1 .lft ul {
      width: 100%;
      padding: 0px;
    }

    .filter .filt-t1 .lft ul li {
      width: 100%;
      height: 70px;
      clear: both;
      background: none;
      // background: url("../../assets/images/border-t1.png) repeat-x left bottom;
    }

    .filter .filt-t1 .lft ul li a .lbl {
      padding: 0px 0px 0px 55px;
    }

    .filter .filt-t1 .lft ul li a .ico {
      left: 0px;
    }

    .filter .filt-t1 .lft ul li a br {
      display: none;
    }

    .filter .filt-t1 .rgt {
      position: static;
      clear: both;
      padding: 25px 0px 0px 0px;
      // background: url("../../assets/images/border-t1.png) repeat-x left top;
    }

    .filter .filt-t1 .rgt .mrls {
      text-align: left;
    }

    .filter .filt-t1 .rgt a br {
      display: none;
    }

    .filter .filt-t1 .rgt .mrls .ico {
      right: auto;
      bottom: auto;
      margin: 0px 0px 0px 12px;
    }

    .filter .filt-t1 .arr {
      right: 50%;
      margin: 0px -9px 0px 0px;
    }

    .filter .filt-t2 {
      width: auto;
      padding: 0px 15px 10px 15px;
      background: #f3f2ed;
    }

    .filter .filt-t2 .pad {
      padding: 20px 0px 0px 0px;
    }

    .filter .filt-t2 .pad .flt ul .w1 {
      width: 100%;
    }

    .filter .filt-t2 .pad .flt ul .w2 {
      width: 100%;
    }

    .filter .filt-t2 .pad .flt ul .w3 {
      width: 100%;
    }

    .filter .filt-t2 .pad .flt ul .w4 {
      width: 100%;
    }

    .map {
      width: 100%;
      height: 450px;
    }

    .map .zoom {
      display: none;
    }
  }
}
