.rectangle-presentation-module {
  padding: 30px;
  height: 30%;

  .rectangle-presentation-module-header {
    margin-bottom: 24px;
  }

  > h4 {
    padding-left: 30px;

    @include media-breakpoint-up(md) {
      padding: 0;
    }
  }

  > .form-select {
    margin-bottom: 24px;
    max-width: 84vw;
  }

  .rectangle-presentation-item-list-container {
    padding: 15px 0;
  }

  .rectangle-presentation-item {
    padding: 15px 30px;
    border-bottom: 1px solid rgba(#3b4956, 0.2);

    @include media-breakpoint-up(md) {
      padding: 30px 0;
    }

    &.first-item {
      border-top: 1px solid rgba(#3b4956, 0.2);
    }

    &.is-showing {
      display: block;
    }

    &.is-hidding {
      display: none;
    }

    .item-content {
      position: relative;
      display: flex;
      flex-direction: row;
    }

    .item-content-image {
      width: 100%;
      height: auto;

      @include media-breakpoint-up(md) {
        margin-right: 30px;
        flex: 0 0 240px;
      }

      img {
        display: block;
        width: 100%;
        height: 100%;
        margin: 0;
        max-width: 100%;
        left: 0;
        transform: none;
      }
    }

    .item-content-container {
      position: relative;
      padding-bottom: 25px;
      padding-right: 25px;
      min-height: 160px;

      @include media-breakpoint-up(md) {
        padding-left: 270px;
      }

      > a {
        position: absolute;
        left: 0;
        bottom: -2px;
        display: inline-block;
        transition: transform 1s ease;
        transform-origin: center;
        font-weight: bold;
        min-width: 100px;

        @include media-breakpoint-up(md) {
          padding-left: 270px;
        }

        &:after {
          content: "\63";
          font-family: "dong-maine" !important;
          display: inline-block;
          position: relative;
          top: 0;
          left: 15px;
          width: 15px;
          font-size: 8px;
          color: #3b4956;
        }
      }

      .item-intro {
        max-height: 47px;
        overflow: hidden;
      }

      .item-name {
        font-weight: 700;
      }

      .item-title {
        margin-bottom: 5px;

        @include media-breakpoint-up(md) {
          margin-bottom: 15px;
        }
      }
    }

    .item-content-expanded {
      height: 0;
      max-height: 0;
      overflow: hidden;

      .item-intro {
        margin-bottom: 15px;
      }
    }

    &.is-expanded {
      .item-content-expanded {
        max-height: 9999px;
        height: auto;
        transition: max-height 3s ease;
        margin-top: 20px;
      }

      .item-content-container {
        a.js-rectangle-presentation-expand-toggle {
          &:after {
            transform: rotate(180deg);
          }
        }
      }
    }
  }

  &.expandcollapserectangle-theme-default {
    h4,
    .rectangle-presentation-item .item-name,
    .rectangle-presentation-item .js-rectangle-presentation-expand-toggle {
      color: $color-dark-blue;

      &:after {
        color: $color-dark-blue;
      }
    }
  }

  &.expandcollapserectangle-theme-blue {
    h4,
    .rectangle-presentation-item .item-name,
    .rectangle-presentation-item .js-rectangle-presentation-expand-toggle {
      color: $color-blue;

      &:after {
        color: $color-blue;
      }
    }
  }

  &.expandcollapserectangle-theme-red {
    h4,
    .rectangle-presentation-item .item-name,
    .rectangle-presentation-item .js-rectangle-presentation-expand-toggle {
      color: $color-red;

      &:after {
        color: $color-red;
      }
    }
  }

  &.expandcollapserectangle-theme-aubergine {
    h4,
    .rectangle-presentation-item .item-name,
    .rectangle-presentation-item .js-rectangle-presentation-expand-toggle {
      color: $color-aubergine;

      &:after {
        color: $color-aubergine;
      }
    }
  }

  &.expandcollapserectangle-theme-radius-blue {
    h4,
    .rectangle-presentation-item .item-name,
    .rectangle-presentation-item .js-rectangle-presentation-expand-toggle {
      color: $color-radius-blue;

      &:after {
        color: $color-radius-blue;
      }
    }
  }
}
