.topImage {
  overflow: hidden;
  position: relative;
  background-color: $color-white;
  .topImage-headline {
    @include h2-bold;
    padding: $module-space-vertical-bottom $module-space-horisontal-left 0
      $module-space-horisontal-left;
    width: 100%;
    word-wrap: break-word;
  }
  .topImage-media {
    height: 56.25vw; // 16:9 format
    // max-height: calc(80vh - 100px); // make sure that the top image does not go over fold, the 100px is the menu
    display: block;
    overflow: hidden;
    position: relative;
  }
  .topImage-img {
    @include absolute-expand;
    @include background-image;
  }

  .topImage-media + .topImage-content {
    position: relative;
  }
  @include media-breakpoint-up(md) {
    .topImage-headline {
      padding: $module-space-vertical-bottom $module-space-horisontal-left;
    }
    .topImage-media {
      /*height: 620px;*/ //Remove 50px in height
      height: 500px;
    }
    .topImage-img {
      @include absolute-expand;
    }
    .topImage-media + .topImage-content {
      color: $color-white;
      width: 100%;
      .topImage-headline {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        // padding:100px 30px 40px 30px;
        @include text-background-gradient;

        &.no-gradient {
          background: transparent;
        }
      }
    }
  }
}
