.site-theme-radius {
  .btn {
    font-size: 22px;
    padding: 15px 25px;
    line-height: 10px;
    font-family: $font-family-semi-bold-radius;
    background-color: $color-radius-blue;
  }

  .btn-default {
    $props: map-get($buttonThemes, "radius-blue");
    @include btnTheme(
      map-get($props, "background-color"),
      map-get($props, "text-color")
    );
  }
}
