// Menu Dependant Settings
// Taller Margin if slider menu is in use.
.has-slider-menu {
  .article {
    margin-top: 60px;
  }
}

// Article Styling
.job__header {
  letter-spacing: normal;
  color: $color-blue;
  margin-bottom: 30px;
}
@include media-breakpoint-up(lg) {
  margin-bottom: 75px;
}
.job__btn-group {
  display: flex;
}
.job__sub-header {
  margin-bottom: 30px;
}

.job__aside-list {
  margin: 0 0 30px 0;
  padding: 0;
}
.job__aside-list-item {
  display: block;
  margin-bottom: 3px;

  strong {
    margin-right: 4px;
  }
}
.job__aside-header,
.job__aside-video {
  margin-bottom: 15px;
}
.job__aside-small {
  font-size: 14px;
}
.job__btn {
  text-decoration: none;
}

.job__aside-title {
  display: block;
  margin-bottom: 10px;
}

// .job__aside-title--main {
//   margin-bottom: 15px;
// }

.job__aside-title--video {
  margin-bottom: 10px;
}

.job__aside-video {
  width: 100%;
}
.job {
  h1,
  h2,
  h4,
  h5,
  strong {
    font-family: $font-family-bold !important;
  }
}
.job__aside-inner {
  padding: 30px 25px;
  background-color: $color-light-grey;
}
.job__content {
  margin-bottom: 0;
}

.btn.btn--mobile-center {
  display: block;
  margin: 0 auto;
  width: max-content;
}
@include media-breakpoint-up(lg) {
  .btn.btn--mobile-center {
    margin: 0;
    width: auto;
    display: initial;
  }
}
@include media-breakpoint-down(lg) {
  .row.row-widewidth .article {
    &.job__article {
      padding: 0;
      margin: 0 !important;
    }
  }
}

.article {
  $max-width: $legible-text-max-width;
  width: 100%;
  margin: 40px auto 20px !important;
  overflow-y: hidden;
  &.job__article {
    .job__article-content {
      margin-bottom: 0;
    }
    .job__content-column {
      padding-right: 15px;
      strong {
        @extend h5;
      }
      .job & p:first-child em {
        @extend h4;
        font-family: $font-family-regular !important;
        font-style: normal;
      }
    }
    @include media-breakpoint-up(md) {
      margin-top: -100px !important;
      z-index: 0;
    }

    .job__aside-benefit img {
      width: 30px !important;
    }
    img.job__aside-benefit-image {
      max-width: none;
      margin: 0 20px 0 0;
      flex-basis: 30px;
      width: 30px;
    }

    .article-content-inner {
      background-color: $color-white;
      padding: 15px;
      max-width: 1045px;
    }
    .job__content-inner {
      padding-bottom: 10px;
    }
    .breadcrumbs-container-ov {
      margin-bottom: 40px;
    }
    @include media-breakpoint-up(lg) {
      .article-content-inner {
        padding: 30px;
      }
      .col-lg-5 {
        flex: 0 0 37.8%;
        max-width: 37.8%;
      }
      .article-content-inner {
        width: 90%;
        padding: 30px;
        padding-bottom: 10px;
      }

      .breadcrumbs-container-ov {
        margin-bottom: 50px;
      }
    }

    .breadcrumbs--ov {
      margin-top: 0;
      font-size: 16px;
    }
  }

  &.article--narrow-media {
    margin-top: 20px !important;
    @include media-breakpoint-down(md) {
      margin-bottom: 0 !important;
    }
  }

  &.article--narrow-media-1 {
    @include media-breakpoint-down(md) {
      margin-bottom: 0 !important;
    }
  }

  > :first-child {
    @include media-breakpoint-up(md) {
      margin-top: 60px;
    }
  }

  > * {
    max-width: $max-width;
    margin-left: auto;
    margin-right: auto;
  }

  & > h1,
  & > .no-margin-top {
    margin-top: 0;
  }

  .article-headline {
    @include h2-bold;
    margin-bottom: 20px;
  }

  .article-subheadline {
    color: $color-blue;
    @include h3;
    margin-bottom: 20px;
    word-wrap: break-word;
    word-break: break-word;
    overflow-y: hidden; //In taiwanese(at least) the overflow auto inherit from richtext in articles gives it scrollbar in the y direction.
  }
  .article-subheadline--narrow {
    margin-bottom: 0;
  }
  .article-date {
    color: $color-text;
    margin-bottom: 20px;
  }

  .article-excerpt {
    @include h5-bold;
    margin-bottom: 20px;
  }

  .article-media {
    max-width: $max-width;
    margin-top: 30px;
    margin-bottom: 30px;
    &.article-media--narrow {
      margin-bottom: 10px;
    }
    @include media-breakpoint-up(md) {
      margin-left: auto;
      margin-right: auto;
    }

    img,
    .embed-responsive {
      width: 135.7% !important;
      max-width: calc(100vw - 20px);
      transform: translateX(-50%);
      position: relative;
      left: 50%;
      margin-top: 0;
      margin-bottom: 5px !important;
    }

    video:-ms-fullscreen {
      //fixes bug where "transform: translateX(-50%);" would not be disabled automatically by the browser in IE (worked fine on chrome). Caused the video to be placed 50% off in full-screen mode.
      transform: none;
    }

    .article-media-caption {
      width: 135.7%;
      max-width: calc(92vw - 32px);
      transform: translateX(-50%);
      position: relative;
      left: 50%;
    }

    .article-media-caption-inner {
      max-width: $legible-text-max-width;
    }
  }

  .article-content {
    max-width: none;
    width: 100%;
    display: flex;
    flex-direction: column-reverse;
    margin-top: 0px !important;
    margin-bottom: 30px;

    &.article-content--narrow {
      margin-bottom: 0;
    }
    p:last-child {
      margin-bottom: 0;
    }

    img,
    .embed-responsive {
      //video,: having <video> here would add unnecessary margin-top to video's in ArticleHeader. Removing video broke nothing, and fixed styling issue. contact JJE or DEG if video is actually needed here.
      width: 100%;
      max-width: calc(100vw - 20px);
      position: relative;
      margin-top: 30px;
      margin-bottom: 30px;
    }
    &.article-content--narrow {
      img,
      .embed-responsive {
        margin-top: 0;
        margin-bottom: 0;
      }
    }

    @include media-breakpoint-up(md) {
      display: block;
      margin-bottom: 25px;
      img {
        margin-top: 50px;
        margin-bottom: 50px;
      }
      &.article-content--narrow img {
        margin-top: 0;
        margin-bottom: 0;
      }
    }

    overflow: hidden;
  }

  .article-content-inner {
    width: 100%;
    max-width: $max-width;
    margin-left: auto;
    margin-right: auto;
    overflow: visible; //Bug-QC-517: added this to allow text to wrap when .rich-text class is also present (it has overflow auto). See http://de-test-cm.westeurope.cloudapp.azure.com/Privat/KASGA/KASGA-Text-page
  }
  //default placement is right
  .article-placeholder {
    width: 100%;
    float: right;
    padding: 30px 0;
    @include media-breakpoint-up(md) {
      width: 50%;
      padding: 0 0 30px 30px;
    }

    &.align-top {
      order: 1;
    }

    img {
      max-width: 100%;
      margin-top: 10px;
      margin-bottom: 10px;
    }

    video,
    .embed-responsive {
      //fixes yt-videos in text-with-container
      max-width: 100%;
      margin-top: 0;
      margin-bottom: 0;
      transform: none; //removes a 1px bleed on yt-videos where the poster was visible for 1px on the left.
      left: 0;
    }

    > * {
      width: 100%;

      img {
        margin-top: 0;
        margin-bottom: 5px;
        width: 100%;
      }
    }

    &.article-placeholder-left {
      float: left;

      //TO DO: article text alignment with 3 col alignment
      @include media-breakpoint-up(md) {
        width: 50%;
        padding: 0 30px 30px 0;
      }
    }

    &:empty {
      display: none;
    }
  }

  .videoComponent-img {
    z-index: -999;
    position: absolute;
    width: 100%;
    height: auto;
    left: 0;
  }
}

.articles-list {
  .article-main-list {
    overflow: hidden;
  }
}

.article + .article {
  margin-top: -25px !important;
}

.article-image {
  overflow: hidden;
}

.richtext > img {
  max-width: 100%;
  height: auto !important;
}

.editContainer {
  position: absolute;
  margin-top: -4px;
  margin-left: 10px;
  right: 9px;
  background-color: inherit;
  width: 28px;
  height: 28px;
  z-index: 999;
  overflow: hidden;
  background-image: url("/temp/iconcache/office/24x24/edit.png"); //url('/-/media/Feature/Tabs/Editor-pencil');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.footerWrapper .editContainer {
  margin-top: 2px;
}

.article-presentation {
  width: 100%;

  .announcement-list {
    z-index: 1;

    .list {
      @include media-breakpoint-down(md) {
        margin-top: -30px;
      }
    }

    .announcement-list-title {
      font-size: 1.5rem;
      color: $color-blue;

      @include media-breakpoint-down(md) {
        margin-bottom: 0;
      }

      .hidden-md-up {
        margin-bottom: 0;
      }
    }

    .articles-presentation-list .highlighted {
      height: calc(100px + 60px);
      display: table-cell;
      vertical-align: middle;

      .list-items-item-value-type-date {
        margin-bottom: 5px;
      }

      .list-items-item-value-type-description {
        font-size: 25px;
        line-height: 32px;
      }
    }

    .active-marker {
      position: absolute;
      top: 168px;
      right: -20px;
      z-index: 1;
      width: 0;
      height: 0;
      border-left: 20px solid #fff;
      border-bottom: 20px solid transparent;
      border-top: 20px solid transparent;
      border-right: none;
    }
  }

  .announcement-teaser .teaser .list-images-image {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position-x: center;
    background-position-y: center;
    opacity: 0;
    transition: opacity.2s;

    &.highlighted {
      opacity: 1;
    }
  }

  .announcement-teaser.hidden-md-up
    .teaser
    .teaser-media
    .news-presentation-video {
    height: auto;
    width: 100%;
  }
}

.news-module {
  .active-marker {
    position: absolute;
    top: 168px;
    /*right: -20px;*/
    left: 0;
    z-index: 1;
    width: 0;
    height: 0;
    border-left: 20px solid #fff;
    border-bottom: 20px solid transparent;
    border-top: 20px solid transparent;
    border-right: none;
  }

  .news-wrapper {
    display: flex;
  }

  &.news-module-image-left {
    .news-wrapper {
      flex-direction: row-reverse;
    }
  }

  .news-headline {
    font-size: 29px;
    padding: 30px;
    color: $color-blue;
    font-weight: 700;
    line-height: 22px;
    letter-spacing: normal;
    font-family: $font-family-regular !important;
  }

  .featured-news-image {
    display: block;

    img {
      height: 100%;
      width: 100%;
      display: block;
    }

    video,
    .youtube-video {
      position: relative;
      height: auto;
      width: 100%;

      @include media-breakpoint-down(md) {
        &.embed16-9 {
          height: 56.45vw;
          min-height: 0;
        }
      }
    }
  }

  .news-list {
    padding: 0;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .news-list-item {
    position: relative;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    box-sizing: border-box;
    min-height: 55px;
    max-height: 72px;
    height: auto;
    margin: 10px 0;
    padding: 0 30px;
    color: $color-text;

    &:after {
      content: "";
      display: block;
      width: 100%;
      position: absolute;
      left: 0;
      bottom: -10px;
      height: 1px;
      background-color: $color-border-grey;
    }
    .news-list-item-text {
      overflow: hidden;
    }

    .news-list-item-date,
    .news-list-item-text {
      font-size: 14px;
      line-height: 18px;
      max-width: 100%;
    }

    &.highlighted {
      max-height: 120px;

      .news-list-item-date {
        margin-bottom: 5px;
      }

      .news-list-item-text {
        font-size: 19px;
        line-height: 24px;
      }
    }
  }

  .news-image-list {
    position: relative;
    height: 100%;
    overflow: hidden;

    .news-presentation-video {
      background-color: #000;
      opacity: 0;
      width: 100%;
      height: auto;

      &.highlighted {
        opacity: 1;
      }
    }
  }

  .news-image-list-item {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position-x: center;
    background-position-y: center;
    opacity: 0;
    transition: opacity 0.2s;

    &.highlighted {
      opacity: 1;
    }
  }

  .news-cta {
    padding: 30px 0 30px 30px;

    .btn {
      display: inline-flex;
    }
  }
  // image left and text right stuff.
  &.news-module-image-left {
    .active-marker {
      left: inherit;
      right: 0;
      border-left: none;
      border-right: 20px solid #fff;
    }
  }
  // color themes
  &.theme-backdrop-2 {
    .active-marker {
      border-left-color: $color-backdrop-2;
      border-right-color: $color-backdrop-2;
    }

    .news-headline {
      color: $color-aubergine;
    }

    .news-list-item {
      &:after {
        background-color: rgba(0, 0, 0, 0.2);
      }

      &:hover {
        color: $color-aubergine;
      }
    }
  }
}
.row {
  &.row-narrow {
    @include media-breakpoint-up(md) {
      .article {
        max-width: none;
        padding: $module-no-space $module-space-horisontal-left $module-no-space
          $module-space-horisontal-right;
      }
    }
  }
  &.row-fullwidth {
    @include media-breakpoint-down(sm) {
      .article {
        max-width: none;
        padding: $module-no-space $component-container-mobile-padding-left $module-no-space
        $component-container-mobile-padding-right;
      }
    }
  }
  &.row-widewidth {
    @include media-breakpoint-down(sm) {
      .article {
        max-width: none;
        padding: $module-no-space $component-container-mobile-padding-left $module-no-space
        $component-container-mobile-padding-right;
      }
    }
  }
}

.article-corporate {
  @include media-breakpoint-up(md) {
    margin: 0 auto !important;
  }
  @include media-breakpoint-down(sm) {
    margin-top: 20px !important;
  }

  .article-corporate-headline {
    @include media-breakpoint-up(md) {
      margin-top: 20px;
    }
  }
}
