.header-offsetY {
  .basket {
    transform: translate3d(0, -45px, 0);
  }
}
.container-fluid {
  transition: margin-top 200ms ease-in-out;
  &.basket-margin {
    margin-top: 90px;
  }
}
.basket {
  top: 138px;
  left: 10px;
  transform: translate3d(0, 0, 0);
  margin: -10px -10px 10px;
  padding: 0;
  background-color: $color-dark-blue;
  color: $color-white;
  max-height: 0;
  transition: max-height 400ms ease-in-out, transform 200ms ease-in-out;
  position: fixed;
  z-index: -1;
  opacity: 0;
  width: 100%;
  &.basket-visible {
    max-height: 100px;
    z-index: 1;
    opacity: 1;
    .basket-inner {
      opacity: 1;
    }
  }

  .basket-inner {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    max-width: 1440px;
    margin-left: auto;
    margin-right: auto;
    padding: 20px 15px;
    opacity: 0;
    transition: opacity 200ms linear 200ms;
  }

  .basket-title {
    margin-right: 20px;
  }

  .btn-dropdown {
    background-color: $color-red;
    position: relative;
    z-index: 2;
    @include icon("basket");
    &:before {
      // font-size: 1.2em;
      transform: scale(1.5);
      padding: 17px 10px 17px 8px;
    }
  }

  .basket-popover {
    position: relative;
  }

  .popover {
    z-index: 1;
    padding: 30px;
    border-radius: 20px;
    left: -230px !important;
    right: -1px !important;
    top: -1px !important;
    max-width: none !important;
    .basket-popover-headline {
      margin-bottom: 20px;
    }
    .popover-title {
      margin-left: -30px;
      margin-right: -30px;
      margin-top: -30px;
      border-top-right-radius: 20px;
      border-top-left-radius: 20px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      height: 45px;
      background-color: $color-red;
      color: white;
    }

    .popover-body {
      padding: 30px;
      color: $color-text;
    }

    .popover-content {
      color: $color-text;
      padding: 20px 0 0 0;
    }

    table {
      width: 100%;
      margin-bottom: 15px;

      tr {
        &:last-child {
          border-top: 1px solid $color-backdrop-1-50;
        }
      }

      td {
        padding: 10px 0;

        &:first-child {
          font-family: $font-family-bold;
        }

        + td {
          text-align: right;
        }

        .icon {
          margin-left: 20px;
          color: $color-red;
        }
      }
    }
    .basket-popover-payment {
      margin-left: -30px;
      margin-right: -30px;
      margin-bottom: -30px;
      background-color: $color-body-background;
      padding: 30px;
      margin-top: 30px;
      border-top-right-radius: 0px;
      border-top-left-radius: 0px;
      border-bottom-left-radius: 20px;
      border-bottom-right-radius: 20px;
      img {
        max-width: 100%;
      }
    }
    .btn {
      width: 100%;
    }
    // top: 0!important;
  }
}
