.pagination {
  .page-item {
    &.active {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
      }
    }
  }
}
.list {
  .dataTables_wrapper .dataTables_paginate {
    .paginate_button {
      &.active {
        .site-theme-radius & {
          font-family: $font-family-bold-radius;
        }
      }
      .site-theme-radius & {
        color: $color-radius-darkgrey;
        padding: 0.5rem 0.875rem;
      }
    }
  }
}
