.communication-toolbox {
  .is-experience-editing
    &.headerSliderCorporate
    .sidenav-container
    .fixed-column {
    position: absolute;
  }

  .content {
    min-height: calc(100vh - 90px);
  }

  .header-navigation,
  .header-navigation-inner {
    padding: 0;
    height: 100%;
  }

  .header-tagline {
    font-size: 15px;
    line-height: 18px;
    font-family: "OrstedSansBold", "Helvetica Nue", Arial, sans-serif;
    align-self: flex-end;
  }

  .header-navigation-inner {
    overflow-y: auto;
    padding: 24px 24px 90px;
  }

  .menu-item-bottom-spacing {
    margin-bottom: 30px;
  }

  .header-logo {
    display: flex;
  }

  &.headerSliderCorporate {
    .sidenav-container {
      max-width: 1440px;
      margin: 0 auto;
      padding: 0;

      .fixed-column {
        position: fixed;
        height: calc(100% - 90px);
        max-width: 240px;
        width: percentage(2 / $grid-columns);
        z-index: 110;
        display: none;
      }
    }

    .sidenav {
      padding: 30px;
      border-right: 1px solid #818b94;
      display: none;
      background: #fff;
      overflow-y: auto;
      height: calc(100vh - 90px);
      max-width: 240px;
      width: 100%;

      a.active-menu {
        color: $color-blue;
      }

      .hidden-xl-up a {
        display: block;
      }

      a:hover {
        color: $color-blue;
      }
    }

    .sidenav-group {
      list-style: none;
      font-size: 16px;
      line-height: 24px;
      margin: 0;
      padding: 0;
      font-family: "OrstedSansBold", "Helvetica Nue", Arial, sans-serif;

      .sidenav-group {
        margin-left: 16px;
      }

      .sidenav-item-bottom-spacing {
        margin-bottom: 30px;
      }
    }

    .sidenav-item {
      padding-top: 3px;
      padding-bottom: 3px;
    }

    .headerSlider {
      border-bottom: 0;

      .header-logo-link {
        min-width: initial;
        margin-right: 8px;

        .header-logo-image-mobile {
          width: 60px;
          height: auto;
        }
      }

      .headerSlider-top {
        box-shadow: none;
        border-bottom: 1px solid #3b4956;
      }

      &.headerSlider-navigation-open + .sidenav-container {
        .fixed-column {
          max-width: 100%;
          width: 100%;
          display: block;
          z-index: 10000;
        }

        .sidenav {
          display: block;
          max-width: 100%;
          padding: 24px;
          border: 0;

          .sidenav-group {
            font-size: 14px;
            line-height: 24px;
            font-family: "OrstedSansBold", "Helvetica Nue", Arial, sans-serif;
            padding: 0;
          }

          .hidden-xl-up {
            padding-top: 26px;
            font-family: "OrstedSansRegular", "Helvetica Nue", Arial, sans-serif;
          }
        }
      }
    }
  }

  @media screen and (min-width: 1200px) {
    .responsive-tools {
      display: none;
    }
  }

  @include media-breakpoint-up(md) {
    .headerSlider-top-inner > .header-right > .header-tools > form {
      display: block;
    }
  }
}

@include media-breakpoint-up(xl) {
  .communication-toolbox {
    div.headerSlider-spacer {
      padding-top: 90px;
    }

    &.headerSliderCorporate {
      .sidenav-container .fixed-column {
        display: block;
      }

      .headerSlider {
        .responsive-tools .site-selector {
          display: none;
        }

        .header-logo-link {
          margin-right: 16px;
        }

        .headerSlider-top {
          height: 90px;

          &.slim {
            height: 90px;
          }
        }
      }

      .sidenav {
        display: block;
      }
    }

    .header-navigation,
    .headerMegaNavigation {
      display: none;
    }

    .header-tagline {
      font-size: 19px;
      line-height: 22px;
      align-self: flex-end;
    }
  }
}
