.megaNavigation {
  .megaNavigation-menu {
    .site-theme-radius & {
      font-size: $text-size-xsmall-radius;
    }
  }
  .megaNavigation-menu-siteselector {
    .megaNavigation-menu-siteselector-item {
      &.megaNavigation-menu-siteselector-item-active {
        .site-theme-radius & {
          font-family: $font-family-bold-radius;
        }
      }
    }
  }
  .megaNavigation-menu-item {
    &.active-sub,
    &.megaNavigation-item-active {
      .megaNavigation-menu-item-link {
        .site-theme-radius & {
          font-family: $font-family-bold-radius;
        }
      }
    }
  }
  .megaNavigation-menu-item-link {
    &.megaNavigation-item-active {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
      }
    }
    &.megaNavigation-menu-item-link-active {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
      }
    }
  }
  .megaNavigation-submenu-item-section {
    .megaNavigation-submenu-item-link {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
        cursor: text;
      }
    }
  }
  .megaNavigation-submenu-item-link {
    .site-theme-radius & {
      font-family: $font-family-bold-radius;
    }
    &:hover {
      .site-theme-radius & {
        color: $color-radius-turquoise;
      }
    }
  }
  .megaNavigation-submenu-item-submenu-item-link {
    &.megaNavigation-submenu-item-active {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
        color: $color-radius-turquoise;
      }
    }
  }
}
