.customSelect {
  select {
    .site-theme-radius & {
      font-family: $font-family-semi-bold-radius;
    }
    option {
      .site-theme-radius & {
        font-family: $font-family-regular-radius;
      }
    }
  }
  &.customSelect-inline {
    select {
      .site-theme-radius & {
        font-family: $font-family-semi-bold-radius;
      }
    }
  }
}
