.headerClassic-spacer {
  height: 129px;
}
.header-offsetY {
  .headerClassic {
    top: -70px;
    @include media-breakpoint-up($header-breakpoint) {
      top: -46px;
    }
  }
  &.mobilemenu-open {
    .headerClassic {
      top: 0;
      @include media-breakpoint-up($header-breakpoint) {
        top: 0;
      }
    }
  }
}
.headerClassic {
  border-bottom: 1px solid $color-body-background;

  // @include text( 16px,18px, 1);
  @extend .container-fluid;
  position: fixed;
  top: 0;
  z-index: 10;
  padding: 0;
  width: 100%;
  margin-bottom: 10px;

  .header-selfservice {
    min-width: 156px;
  }

  > * {
    padding: 0 5px;
  }
  transition: top $transition-duration $easing-snap;
  // transform: translate3d(0,0,0);
  // top:0;

  // on scroll down, hide some of the header
  .headerClassic-top {
    width: 100%;
    margin: 0;
    background-color: $color-backdrop-2;
    display: none;
    @include media-breakpoint-up($header-breakpoint) {
      display: block;
    }
  }
  .headerClassic-top-inner,
  .headerClassic-bottom-inner {
    @extend .row;
    &:first-child {
      padding-left: 5px;
    }
    &:last-child {
      padding-right: 5px;
    }
    flex-wrap: nowrap;
  }
  .headerClassic-top-inner {
    justify-content: flex-end;
  }
  .header-links {
    display: flex;
  }
  .header-link {
    padding: 12px 20px;
    display: inline-block;
    font-family: $font-family-regular;

    color: $color-dark-blue;
    font-size: 14px;
  }
  .link-logon {
    font-size: 1.4em;
  }
  .header-link-tab-style {
    color: $color-text;
    &.header-link-active {
      background-color: $color-white;
      color: $color-text;
      font-family: $font-family-bold;
    }
  }

  .headerClassic-bottom {
    width: 100%;
    margin: 0;
    background-color: $color-white;
  }
  .headerClassic-bottom-inner {
    position: relative;
    display: flex;
    height: $header-bottom-height-small;
    align-items: center;

    @include media-breakpoint-up($header-breakpoint) {
      justify-content: flex-start;
      align-items: flex-start;
      height: auto;
      padding-top: 23px;
      padding-bottom: 23px;
    }
  }
  .header-logo {
    align-self: center;
    z-index: 1;

    @include media-breakpoint-up($header-breakpoint) {
      margin-left: 20px;
    }

    @include media-breakpoint-up(lg) {
      margin-left: 30px;
      width: 184px;
    }
  }

  // ie fix for logos on narrow width
  @media screen and (-ms-high-contrast: active),
    (-ms-high-contrast: none) and (min-width: 769px) and (max-width: 992px) {
    .header-logo {
      width: 60%;
      max-width: 320px;
    }
  }

  .header-logo-image {
    width: auto;
    //width:$header-bottom-height-small*1.5;
    height: $header-bottom-height-small / 2.3;
    @include media-breakpoint-up($header-breakpoint) {
      // padding: $module-space-horisontal-left $module-space-horisontal-left $module-space-horisontal-left 0;
      padding-right: 15px;
      width: auto;
      height: 36px;
      max-width: 184px;
    }
  }
  @at-root .megaNavigation-small-font {
    .header-megaNavigation {
      margin-left: 0 !important;
    }

    .header-logo {
      margin-left: 0;
      @include media-breakpoint-up(lg) {
        margin-left: 20px;
      }
    }

    .header-logo-image {
      @include media-breakpoint-up($header-breakpoint) {
        padding-right: 15px;
        //width: 90px;
        width: auto;
        height: 55px;
      }
      @include media-breakpoint-up(lg) {
        // padding: $module-space-horisontal-left $module-space-horisontal-left $module-space-horisontal-left 0;
        padding-right: 48px;
        width: 100%;
        height: 36px;
      }
    }
  }
  .headerClassic-mobile-links {
    color: $color-text;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    & > a,
    & > .header-search,
    & > .login {
      width: 35px;
      text-align: center;
      i {
        color: $color-text;
      }
    }
    @include media-breakpoint-up($header-breakpoint) {
      display: none;
    }
  }
  .header-megaNavigation {
    display: flex;
    flex-grow: 1;
    justify-content: flex-end;
    @include media-breakpoint-up(md) {
      margin-left: 0px;
    }
    @include media-breakpoint-up(lg) {
      margin-left: 30px;
      // max-width: calc(100% - 320px);
    }
  }
  .headerClassic-bottom-left {
  }
  .headerClassic-bottom-right {
    display: none;
    @include media-breakpoint-up($header-breakpoint) {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      padding: 20px 0px 20px 0px; //defect 909 fix
      z-index: 1;
    }
  }
  .header-search {
    @include media-breakpoint-up($header-breakpoint) {
      margin-right: 20px;
      min-height: 41px;
    }
    position: relative;
    z-index: 100;
    .header-search-icon {
      transition: all $transition-duration ease-in-out $transition-duration/2;
      position: relative;
      left: 0;
      // transform: translate3d(0,0,0);
      opacity: 1;
      pointer-events: all;
      i {
        color: $color-text;
      }
      &:hover {
        i {
          color: $color-blue;
        }
      }
    }
    .header-search-form {
      transition: all $transition-duration ease-in-out $transition-duration/2;
      margin-bottom: 0;
      pointer-events: none;
      position: absolute;
      top: -10px;
      right: 0;
      width: calc(100vw - 80px);
      @include media-breakpoint-up($header-breakpoint) {
        width: 300px;
        padding-left: 50px;
      }
      background: white;
      background: linear-gradient(
        90deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 1) 10%
      ); /* w3c */
      opacity: 0;
      transform: translate3d(20px, 0, 0);
    }

    &.header-search-visible {
      .header-search-icon {
        transition: all $transition-duration ease-in-out;
        opacity: 0;
        left: -10px;
        pointer-events: none;
      }
      .header-search-form {
        opacity: 1;
        transform: translate3d(0, 0, 0);
        pointer-events: all;
      }
    }
  }
}

//fix for sitecore experience editor, the menu is now displayed below the experience editor pane
.is-experience-editing {
  #main {
    position: relative;
    z-index: 100;
  }
  .headerClassic {
    position: relative !important;
    transform: translateZ(0);
    z-index: 101;
  }
}
