.pageviewer {
  .pageviewer-nav {
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid $color-dark-blue;
  }
  .pageviewer-iframe {
    border: 0;
    width: 100%;
    height: 100%;
    min-height: 70vh;
    transition: height 150ms $easing-default;
  }
  .pageviewer-iframe-container {
    border: 1px dashed $color-dark-blue;
    width: 100%;
    transition: width 200ms $easing-default;
    margin-left: auto;
    margin-right: auto;
  }
}
.pageviewer-entry-info {
  margin-top: 10px;
}
.pageviewer-code {
  // display: none;
}
.pageviewer-entry {
  margin: 50px 0;
  padding-bottom: 50px;
  border-bottom: 1px solid $color-dark-blue;

  .class {
    display: inline-block;
    margin-bottom: 20px;
  }
  .pageviewer-code {
    margin-top: 30px;
  }
}
