.job-search-hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  min-height: 544px;
  height: auto;
  padding: 0;
  @include media-breakpoint-up(lg) {
    @include component-padding;
  }
  &.theme-image-light {
    color: $color-text;
    @include media-breakpoint-up(lg) {
      color: $color-white;
    }
  }
}

.job-search-hero__column {
  display: flex;
  z-index: 1;
  flex-direction: column;
  width: 100%;
  @include media-breakpoint-up(lg) {
    max-width: 66.66667%;
    &--wide {
      max-width: 91.66667%;
    }
  }
}

.job-search-hero__content {
  padding: 35px 25px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%;
  @include media-breakpoint-up(lg) {
    justify-content: center;
    padding: 0;
    display: flex;
  }
}

.job-search-hero__bg-media-container {
  @include absolute-expand;
  overflow: hidden;
  z-index: 0;
  height: 0;
  padding-bottom: 56.25%;
  display: table;
  position: relative;
  @include media-breakpoint-up(lg) {
    padding-bottom: 0;
    display: block;
    position: absolute;
    height: 100%;
  }
}
.select-spacer {
  margin-right: 5px;
}
.job-search-hero__bg-image {
  position: absolute;
  @include image-scale-up-transition;
  &:hover {
    @include image-scale-up;
  }
  @include background-image;
  width: 100%;
  height: 100%;
  transform: scale(1);
  z-index: -1;

  &:after {
    content: "";
    @include absolute-expand;
  }
}

.job-search-hero__bg-video {
  height: 100vh;
  object-fit: cover;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
  //Fix for IE 11
  .no-objectfit & {
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
  }
}

.job-search-hero__vacancies,
.job-search-hero__location {
  display: block;
  margin-bottom: 10px;
  @include media-breakpoint-up(md) {
    margin-bottom: 30px;
  }
}
.job-search-hero__vacancies {
  font-family: $font-family-bold !important;
  font-size: 16px;
}
.job-search-hero__location {
  font-size: 14px;
  font-family: $font-family-regular !important;
}
.job-search-hero .job-search-hero__heading {
  margin-bottom: 10px;
  font-family: $font-family-bold !important;
  @include media-breakpoint-up(md) {
    margin-bottom: 30px;
  }
}
.job-search-hero .job-search-hero__btn {
  @include media-breakpoint-down(sm) {
    width: 100%;
    &:hover {
      padding-left: 0;
      padding-right: 0;
      &:before {
        content: none;
      }
    }
  }
  text-align: center;
  display: block;
  font-size: 16px !important;
  @include media-breakpoint-up(md) {
    flex: none;
    width: auto;
    display: inline-flex;
    flex-grow: unset;
    text-align: initial;
  }
}

.job-search-hero__select-container {
  margin-right: 0;
  @include media-breakpoint-up(md) {
    margin-right: 10px;
  }
}

.row--padding-20 {
  width: 100%;
  padding: 20px 0;
}
