.table {
  .site-theme-radius & {
    font-size: $text-size-xsmall-radius-table;
  }
  thead {
    th {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
      }
    }
  }
}
.richtext {
  .table-mobile-head {
    .site-theme-radius & {
      height: 50px;
      font-family: $font-family-bold-radius;
      font-size: $text-size-xsmall-radius;
    }
  }
}
