.split50 {
  display: flex;

  .split50-left,
  .split50-right {
    position: relative;
    @include flex-1;

    @include media-breakpoint-up(md) {
      flex: 1 1 50%;
      max-width: 50%;
    }
  }

  flex-direction: column;

  @include media-breakpoint-up(md) {
    flex-direction: row;
  }
}
.split50:not(.social-media-feed) {
  @include flex-1;
}
