.col-in-viewport-inited{
    transform: translate3d(0,50px,0);
    transition: transform $transition-duration*5 ease-in-out, opacity $transition-duration*3 100ms;
    opacity: 0;
    +.col-in-viewport-inited{
        transition-delay: $transition-duration;
    }
    &.col-in-viewport-notransform{
        transform: none;
        transition-duration: 0ms;
    }
}

.col-was-in-viewport{
    transform: translate3d(0, 0,0);
    opacity: 1;
}

.in{
    opacity: 1 !important;
}
