.footer {
  border-top: 20px solid $color-blue;
  color: $color-text;
  font-size: $text-size-body-small;
  line-height: $line-height-body-small;

  > .row {
    padding: 40px 30px 30px 30px;

    > div {
      margin-bottom: 20px;

      @include media-breakpoint-up(xl) {
        margin-bottom: 0;
      }
    }
  }

  p + p {
    margin-bottom: 0;
  }

  .richtext {
    height: 100%;
    overflow: visible;
    p {
      margin-bottom: 0;
    }
  }

  .footer-heading {
    font-family: $font-family-bold;

    @include asian-fonts;
    @include asian-fontweight-bold;

    margin-bottom: 20px;
  }

  address {
    font-style: normal;
  }

  .additional-contact {
    margin-top: 20px;

    a {
      display: block;
    }
  }

  .arrow-link-style {
    display: block;
    text-decoration: none;

    & + .arrow-link-style {
      margin-top: 30px;
    }
  }

  .footer-social {
    .social-flex {
      display: flex;
      justify-content: left;
      align-items: center;
      flex-wrap: wrap;
    }

    .social-circle {
      justify-content: center;
      width: 45px;
      height: 45px;
      background-color: $color-blue;
      font-size: 20px;
      color: $color-white;
      border-radius: 50%;
      margin-right: 10px;
      margin-bottom: 10px;
      overflow: hidden;
      text-decoration: none;
      cursor: pointer;

      img {
        max-width: 30px;
        max-height: 30px;
      }

      &.is-type-facebook {
        @include icon("facebook");
      }
      &.is-type-twitter {
        @include icon("twitter");
      }
      &.is-type-linkedin {
        padding-top: 1px;
        @include icon("linkedin");
      }
      &.is-type-youtube {
        @include icon("youtube-play");
      }
    }
  }

  .trust-pilot-container {
    margin-top: 20px;
  }

  a:not([class*="link-arrow"]):hover {
    text-decoration: none;
    color: inherit;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /* IE10+ CSS styles go here */
  .footer {
    .footer-social {
      .social-circle {
        img {
          max-width: 20px;
        }
      }
    }
  }
}
