.search-limit-bounds {
  .input-wrapper {
    input {
      .site-theme-radius & {
        font-family: $font-family-regular-radius;
      }
    }
  }
}
.list {
  .dataTables_wrapper {
    .dataTables_paginate {
      .site-theme-radius & {
        font-size: $text-size-body-small-radius;
        line-height: $line-height-body-small-radius;
      }
      & > .paginate_button {
        .site-theme-radius & {
          font-size: $text-size-body-small-radius;
          line-height: $line-height-body-small-radius;
        }
      }
    }
  }
}
