.onward-links {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  flex-grow: 1;
  min-height: 220px;
  overflow: hidden;
  position: relative;
  background-color: $color-aqua; //onward-links always use a custom aubergine theme
  padding-top: 75px;
  padding-bottom: 75px;

  .onward-link {
    display: flex;
    flex-direction: column;
    .onward-media {
      .link-img {
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        color: white;
        background-position: 50%;
        background-size: cover;
        background-repeat: no-repeat;
        min-width: 227px;
        width: 227px;
        height: 227px;
        max-height: 100%;
        max-width: 100%;
        transform: scale(1);
        border-radius: 50%;
        margin: 0 auto;
        @include image-scale-up-transition;
        .icon {
          font-size: 5em;
        }
        &.is-icon {
          background-size: 40%;
        }
      }
    }
    .onward-content {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-weight: 600;
      padding-top: 15px;

      .onward-tagline {
        color: $color-aubergine;
        font-size: 18px;
        text-align: center;
      }
      .onward-cta a {
        color: $color-aubergine;
        text-align: center;
        margin-right: 0;
        padding-right: 0;

        &:hover {
          color: $color-aubergine !important;
        }
      }
    }

    &:hover .onward-media .link-img {
      @include image-scale-up;
    }
  }

  .onward-link + .onward-link {
    margin-top: 50px;
  }

  @include media-breakpoint-up(sm) {
    flex-direction: row;
    flex-wrap: wrap;
    padding: 75px 25px 25px 75px;

    .onward-link {
      margin-bottom: 50px;
      margin-right: 50px;
    }

    .onward-link + .onward-link {
      margin-top: 0;
    }
  }
}
