.social {
  padding: $spacing-large;
  text-align: center;
  height: 100%;

  .teaser & {
    padding: $spacing-medium 0 0;
    text-align: left;
  }

  .social-header {
    @include h3;
    margin-bottom: $spacing-medium;
  }

  .social-links {
    display: inline-flex;
    flex-flow: wrap;
    justify-content: center;
    list-style: none;
    padding: 0;

    @include media-breakpoint-up(md) {
      justify-content: flex-start;

      .social-links-link {
        margin-right: 50px;

        &:last-of-type {
          margin-right: 0;
        }
      }
    }
  }

  .social-links-link {
    position: relative;
    text-align: center;
    margin-bottom: $spacing-regular;
    margin-left: 20px;
    margin-right: 20px;

    .icon-img {
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 30px;
    }

    > a {
      display: block;

      &:hover {
        .social-links-link-icon {
          &:before {
            position: relative;
            z-index: 2;
          }

          &:after {
            position: absolute;
            display: block;
            background: rgba(#000, 0.05);
            top: 0;
            width: 60px;
            height: 60px;
            border-radius: 30px;
            content: "";
            z-index: 1;
          }
        }
      }
    }

    &.is-type-facebook {
      .social-links-link-icon {
        @include icon("facebook");
      }
    }

    &.is-type-twitter {
      .social-links-link-icon {
        @include icon("twitter");
      }
    }

    &.is-type-linkedin {
      .social-links-link-icon {
        @include icon("linkedin");
      }
    }

    &.is-type-youtube {
      .social-links-link-icon {
        @include icon("youtube-play");
      }
    }

    .social-links-link-label {
      display: block;
      font-family: $font-family-bold;

      @include asian-fonts;
      @include asian-fontweight-bold;

      font-size: 15px;
    }
  }
  @include media-breakpoint-down(sm) {
    .social-links-link {
      .social-links-link-label {
        font-size: 12px;
      }
    }
  }

  .social-links-link-icon {
    display: inline-block;
    width: 60px;
    height: 60px;
    border-radius: 30px;
    padding-top: 2px;
    margin-bottom: $spacing-tiny;
    color: $color-white;
    background-color: $color-aubergine;

    font-size: 32px;
    line-height: 1.5;

    img {
      max-width: 30px;
    }
  }
}

.theme-green .social {
  .social-links-link-icon {
    color: $color-aubergine;
  }
}

.theme-aubergine .social {
  .social-links-link-icon {
    color: $color-aqua;
  }
}

.social-media-feed {
  &.fifty-percent-feed {
    height: 100%;

    @include media-breakpoint-up(md) {
      min-height: 440px;
    }

    .is-experience-editing & {
      padding: 10px 0;
    }
  }

  .own {
    height: 100%;
    width: 100%;

    .own-feed {
      height: 100%;
      width: 100%;

      iframe {
        width: 100%;
        height: 100%;
      }
    }
  }

  .facebook {
    width: 100%;
    height: 100%;
    min-height: 440px;
  }

  .facebook-height {
    height: 100%;
  }
}

.social
  .social-links-link:not(.is-type-youtube):not(.is-type-twitter):not(.is-type-facebook):not(.is-type-linkedin)
  .social-links-link-icon:before {
  // Very ugly fix for li elems without icon, uglier than me
  content: "\01";
  visibility: hidden;
}

.wide-container,
.narrow-container {
  .col-12 .social-media-feed.fifty-percent-feed {
    width: 50%;
    position: relative;
    align-self: center;
    display: flex;
    padding-top: 30px;
    padding-bottom: 30px;

    @include media-breakpoint-down(md) {
      width: 100%;
    }

    .twitter-feed {
      width: 100%;

      iframe {
        width: 100% !important;
      }
    }

    .facebook-feed {
      text-align: center;
    }
  }
}

.social-media-feed__fallback {
  text-align: center;

  img {
    max-width: 100%;
    width: auto;
    display: inline-block;
  }
}

.social-media-feed--hide {
  display: none;
}

.coloredContentBox .coloredContentBox-cta--social-media-fifty {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: none;
}

.split50.social-media-feed {
  .split50-right:nth-of-type(1) .facebook-feed {
    text-align: right;
  }
}
