.headerSliderCorporate {
  .headerSearch-input,
  .tt-menu {
    font-weight: normal !important;
  }
  /*space content from header*/
  .headerSlider-spacer {
    padding-top: $header-height-small;
  }
  /* add overlay when menu or search opened*/
  /*header colors*/
  .headerSlider .headerSlider-top {
    background-color: #fff;
    height: 60px;
    box-shadow: inset 0px -1px 0px #f3f3f3;
    transition: height 0.2s ease;
  }

  .headerSlider .headerSlider-top a:hover {
    color: #4099da;
  }

  .headerSlider .headerSlider-top .btn-login {
    $props: map-get($buttonThemes, "blue");
    @include btnTheme(
      map-get($props, "background-color"),
      map-get($props, "text-color")
    );
  }

  .headerSlider .header-tools {
    align-items: center;
  }

  .headerSlider.headerSlider-offsetY {
    top: 0;
  }

  .headerSlider.nav-opened .nav-overlay {
    visibility: visible;
    z-index: -1;
    opacity: 0.5;
    transition: all 0.1s ease;
  }

  .headerSlider.nav-opened {
    z-index: 10000;
  }

  .nav-overlay {
    background: #000000;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    opacity: 0;
    visibility: hidden;
    z-index: -9999;
  }

  .btn-login {
    display: inline-block;

    &:focus {
      box-shadow: none;
    }
  }

  .headerMegaNavigation {
    /*flex based menu list*/
    /*menu styles for desktop and responsive*/
    .headerMegaNavigation-menu {
      margin: 0 0 0 29px;
      padding: 0;
      display: flex;
      width: 100%;
      flex-wrap: wrap;
      flex-direction: row;
      align-items: flex-start;
    }

    .headerMegaNavigation-menu li {
      list-style-type: none;
    }
    /* add overlay when menu or search opened*/
    /*header colors*/
    .headerSlider .headerSlider-top {
      background-color: #fff;
      height: 60px;
      box-shadow: inset 0px -1px 0px $color-light-grey;
      transition: height 0.2s ease;
    }

    .headerMegaNavigation-menu .headerMegaNavigation-menu-item {
      margin: 0 12px;
    }

    .headerMegaNavigation-menu-item-link {
      font-family: OrstedSansBold, sans;
      font-size: 16px;
      color: #3b4956;
      letter-spacing: 0.095px;
      line-height: 22px;
    }

    .headerMegaNavigation-menu-item-link.isPageAncestor {
      color: #4099da;
    }

    .headerMegaNavigation-menu
      .headerMegaNavigation-menu-item.active
      .headerMegaNavigation-menu-item-link {
      color: #4099da;
    }
    /*sub menu*/
    .megaNavigation-submenu {
      position: absolute;
      width: 100%;
      background: rgba(183, 173, 165, 0.25);
      background: linear-gradient(
          0deg,
          rgba(183, 173, 165, 0.25),
          rgba(183, 173, 165, 0.25)
        ),
        #ffffff;
      left: 0;
      top: 100%;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      padding: 10px 0 30px 0;
    }

    .megaNavigation-submenu ul {
      margin: 0;
      padding: 0;
    }

    .responsiveBackTo-menu {
      display: none;
    }

    .subMenu-title {
      display: none;
    }

    .megaNavigation-submenu-title {
      display: none;
    }

    .megaNavigation-submenu-inner {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      position: relative;
      max-height: calc(100vh - 60px);
      overflow-y: auto;
      overflow-x: hidden;
      z-index: 1;
      width: 100%;
      box-sizing: border-box;
    }

    .megaNavigation-submenu-item {
      width: 100%;
      box-sizing: border-box;
      margin-bottom: 10px;
    }

    .megaNavigation-submenu-item-section {
      border-right: 1px solid #d8d8d8;
      padding-right: 29px;
      display: flex;
      flex-direction: column;
    }

    .megaNavigation-submenu-item-link {
      font-family: OrstedSansBold, sans;
      font-size: 16px;
      letter-spacing: 0;
      line-height: 22px;
      font-weight: bold;
      padding: 0px 10px 0 0;
      display: block;
    }

    .megaNavigation-submenu-item-link:after {
      content: "";
      padding-left: 10px;
      font-family: OrstedSansBold, sans;
      font-size: 16px;
      position: absolute;
      bottom: -2px;
    }

    .megaNavigation-submenu-item-submenu-item {
      display: flex;
      flex-direction: column;
    }
  }

  @include media-breakpoint-up(xl) {
    .headerSliderCorporate .header-right {
      width: 31.5%;
    }
  }

  .header-right {
    /*contacts + language selection*/
    .site-select-menu {
      justify-content: space-between;
      display: flex;
      font-size: 16px;
      color: #3b4956;
      letter-spacing: 0;
      text-align: right;
      line-height: 22px;
      flex: 1 0 auto;

      .header-site-selector-item:nth-child(n + 2) {
        margin: 0 0 0 12px;
      }
    }

    .lang-selector {
      position: relative;
      margin: 0 12px 0 24px;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
    }

    .lang-selector .header-site-selector-item {
      order: 2;
    }

    .lang-selector .header-site-selector-item.item-active {
      order: 1;
      display: none;
    }
    /*search*/
    .headerSearch-ov {
      max-width: 240px;
      margin-right: 10px;
    }

    .headerSearch-ov .headerSearch-form.form {
      max-width: 100%;
    }

    .headerSearch-ov .headerSearch-input {
      background: rgba(153, 164, 174, 0.25);
      border-radius: 21px;
      border: none;
      font-size: 16px;
      color: #3b4956;
      letter-spacing: 0;
      line-height: 22px;
      padding-right: 47px !important;
      box-sizing: border-box;
      cursor: default;
    }

    .headerSearch-ov .headerSearch-input::placeholder {
      opacity: 0.6;
      font-family: OrstedSansRegular, sans;
      font-size: 16px;
      color: #3b4956;
      letter-spacing: 0;
      line-height: 22px;
    }

    .headerSearch-ov .btn {
      position: absolute;
      left: 100%;
      transform: translateX(-100%);
      top: 0;
      margin: 0 !important;
      background-color: transparent !important;
      color: #3b4956 !important;
      cursor: pointer;
      padding: 0;
      width: 45px;
      height: 100%;
      line-height: 45px;
      text-align: center;
      opacity: 0.6;
      font-size: 16px !important;
      z-index: 1;

      &:focus {
        box-shadow: 0 0 0 0px #99a4ae !important;
      }
    }

    .headerSearch-ov .btn:hover {
      opacity: 1;
    }
  }

  @include media-breakpoint-up(xl) {
    .headerSlider-spacer {
      padding-top: $header-height-large;
    }

    .headerSlider .headerSlider-top {
      height: 120px;
    }

    .headerSlider .headerSlider-top.slim {
      height: 70px;
    }

    .headerSlider {
      .header-logo {
        margin-left: 25px;
      }

      .header-logo-link {
        display: block;
        min-width: 110px;
      }

      .header-logo-image-desktop {
        height: 30px;
      }

      .header-search-ov {
        max-width: 240px;
      }

      .headerMegaNavigation-menu {
        align-items: center;
      }
      /*sub menu*/
      .megaNavigation-submenu {
        opacity: 0;
        visibility: hidden;
        z-index: -9999;
        flex-direction: row;
        padding: 10px 10px 10px 30px;
      }

      .megaNavigation-submenu.active {
        opacity: 1;
        visibility: visible;
        z-index: 1;
      }

      .subMenu-title {
        font-family: OrstedSansLight, sans;
        font-size: 24px;
        color: #3b4956;
        letter-spacing: 0;
        line-height: 35px;
        margin-bottom: 17px;
        display: block;
        padding-right: 15px;
      }

      .megaNavigation-submenu a {
        color: #3b4956;
        display: block;
        margin-bottom: 15px;
      }

      .megaNavigation-submenu-title {
        width: calc(25% - 30px);
        margin-top: 28px;
        display: block;
        position: relative;
        padding-right: 20px;
      }

      .megaNavigation-submenu-inner {
        max-height: calc(100vh - 120px);
        width: calc(75% + 30px);
        padding-left: 15px;
        padding-right: 20px;
      }

      .megaNavigation-submenu-item {
        max-width: 340px;
        min-width: 210px;
        width: 33%;
        flex-shrink: 1;
        padding: 30px 15px 16px 15px;
      }

      .megaNavigation-submenu-inner .megaNavigation-submenu-item-link {
        margin-bottom: 19px !important;
        width: 70%;
      }

      .megaNavigation-submenu-inner
        .megaNavigation-submenu-item-link.isPageAncestor {
        color: #4099da;
      }

      .megaNavigation-submenu-item-submenu
        .megaNavigation-submenu-item-submenu-item
        .megaNavigation-submenu-item-submenu-item-link,
      .megaNavigation-submenu
        .megaNavigation-submenu-title
        .megaNavigation-submenu-item-link {
        width: auto;
        position: relative;
        display: block;
      }

      .megaNavigation-submenu
        .megaNavigation-submenu-title
        .megaNavigation-submenu-item-link.isPageAncestor {
        color: #4099da;
        position: relative;
        display: block;
      }

      .megaNavigation-submenu-item-submenu
        .megaNavigation-submenu-item-submenu-item
        .megaNavigation-submenu-item-submenu-item-link.isPageAncestor {
        color: #4099da;
      }

      .megaNavigation-submenu-inner:before {
        content: "";
        position: absolute;
        top: 34px;
        left: 0;
        height: calc(100% - 74px);
        border-left: 1px solid #d8d8d8;
      }

      .responsive-tools .headerSearch-icon .headerSlider-navigation-responsive {
        left: 24px;
        top: 22px;
      }
    }

    #main section:before {
      background: #fff;
      content: "";
      position: absolute;
      width: 100%;
      height: 200px;
      top: -200px;
      z-index: -2;
    }
  }

  .responsive-tools .headerSearch-icon {
    width: 50px;
    height: 50px;
    right: 60px;
    top: 6px;
    line-height: 50px;
    text-align: center;
    position: absolute;
    z-index: 1;
    cursor: pointer;
    outline: none;
  }

  @include media-breakpoint-up(xl) {
    .responsive-tools .headerSearch-icon {
      position: relative;
      right: auto;
      top: auto;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .responsive-tools.search-opened
      .headerSlider-navigation-responsive:not(.headerMenu-icon) {
      position: relative;
      left: auto;
      top: auto;
    }

    .headerMenu-icon.headerSlider-navigation-responsive {
      display: none;
    }

    .responsive-tools.search-opened .headerSlider-navigation-responsive div {
      position: relative;
      top: auto;
      left: auto;
      transform: none;
    }

    .header-right .lang-selector {
      margin-right: 0;
    }

    .header-right {
      display: flex;
      align-items: center;
    }

    .responsive-tools {
      margin-right: 5px;
    }
  }

  @media screen and (min-width: 1400px) {
    .responsive-tools {
      display: none;
    }
  }

  @media screen and (max-width: 1200px) {
    .headerSlider .header-logo-link .header-logo-image-mobile {
      height: 24px;
      margin-left: 15px;
    }
    /*responsive search*/
    /*responsive search show/hide button*/
    .responsive-tools {
      height: 50px;
      width: 50px;
      /*language version*/
    }

    .responsive-tools .headerSearch-form {
      max-width: 100%;
    }

    .responsive-tools .site-selector {
      display: block;
      position: absolute;
      right: 110px;
      opacity: 1;
      margin-top: 0;
      pointer-events: all;
    }

    .responsive-tools .site-selector a:hover {
      color: #4099da;
    }

    .responsive-tools .site-selector .header-site-selector-item {
      height: 50px;
      width: 50px;
      text-align: center;
      line-height: 50px;
      display: none;
    }

    .responsive-tools
      .site-selector
      .header-site-selector-item:not(.item-active) {
      display: block;
      font-family: OrstedSansLight, sans;
      color: inherit;
    }

    .responsive-tools
      .site-selector
      .header-site-selector-item:not(.item-active):hover {
      color: #4099da;
    }

    .headerSlider-navigation-open .header-navigation {
      visibility: visible;
      opacity: 1;
      z-index: 1;
    }

    .header-navigation {
      position: fixed;
      top: 60px;
      width: 100%;
      left: 0;
      background: #fff;
      padding: 10px 0;
      visibility: hidden;
      opacity: 0;
      z-index: -999999;
      transition: all 0.1s ease;
    }

    .headerMegaNavigation {
      .headerMegaNavigation-menu {
        margin: 0;
        flex-direction: column;
      }

      .headerMegaNavigation-menu .headerMegaNavigation-menu-item {
        display: block;
        width: 100%;
        padding: 0 10px 0 20px;
        margin: 0;
        position: relative;
      }

      .headerMegaNavigation-menu .headerMegaNavigation-menu-item.active {
        display: inline-block;
        padding-bottom: 10px;
      }

      .headerMegaNavigation-menu
        .headerMegaNavigation-menu-item.active
        .headerMegaNavigation-menu-item-link::after {
        width: auto;
        height: auto;
        line-height: inherit;
        position: relative;
        content: "";
        padding-left: 10px;
        font-weight: bold;
        font-size: 16px;
        transform: rotate(0deg);
        font-family: OrstedSansBold, sans !important;
      }

      .headerMegaNavigation-menu
        .headerMegaNavigation-menu-item.active
        .megaNavigation-submenu {
        transform: translate3d(0, 0, 0);
      }

      .headerMegaNavigation-menu.active .headerMegaNavigation-menu-item {
        display: none;
      }

      .headerMegaNavigation-menu.active > .active {
        display: block;
      }

      .headerMegaNavigation-menu.active
        .headerMegaNavigation-menu-item.hidden-md-up {
        display: none !important;
      }

      .headerMegaNavigation-menu-item.hidden-md-up {
        display: block !important;
      }

      .headerMegaNavigation-menu-item.hidden-md-up
        .headerMegaNavigation-menu-item-link {
        font-family: OrstedSansRegular, sans;
        letter-spacing: 0;
      }

      .headerMegaNavigation-menu-item.hidden-md-up
        .headerMegaNavigation-menu-item-link::after {
        display: none;
      }

      .headerMegaNavigation-menu-item.responsive-authentication {
        text-align: center;
      }

      .headerMegaNavigation-menu-item .headerMegaNavigation-menu-item-link {
        padding: 14px 0;
        display: block;
        position: relative;

        &.btn-login {
          display: inline-block;
          padding: 10px 16px;
        }
      }

      .headerMegaNavigation-menu-item
        .headerMegaNavigation-menu-item-link::after {
        content: "\63";
        position: absolute;
        width: 50px;
        height: 50px;
        top: 0;
        right: 0;
        line-height: 50px;
        text-align: center;
        font-family: dong-maine !important;
        font-style: normal !important;
        transform: rotate(-90deg);
        font-size: 12px;
      }

      .megaNavigation-submenu {
        transform: translate3d(-100%, 0, 0);
        transition: transfrom 0.5s ease;
      }

      .megaNavigation-submenu-item {
        width: 100%;
      }

      .megaNavigation-submenu-item:last-child {
        padding-bottom: 3em;
      }

      .megaNavigation-submenu-item-link {
        padding-left: 20px;
        padding-top: 14px;
        padding-bottom: 13px;
        height: 50px;
      }

      .megaNavigation-submenu-item-submenu-item-link {
        height: 50px;
        padding: 14px 10px 0 20px;
        display: block;
      }

      .megaNavigation-submenu-inner {
        max-height: calc(100vh - 255px);
      }
      /*submenu*/
      .responsiveBackTo-menu {
        font-family: OrstedSansRegular, sans;
        font-size: 14px;
        letter-spacing: 0;
        line-height: 20px;
        padding-left: 20px;
        padding-top: 14px;
        cursor: pointer;
      }

      .responsiveBackTo-menu.active {
        display: block;
      }

      .responsiveBackTo-menu:hover {
        color: #4099da;
      }

      .responsiveBackTo-menu:before {
        content: "\63";
        font-family: dong-maine !important;
        transform: rotate(90deg);
        display: inline-block;
        font-size: 7px;
        padding-right: 3px;
      }
    }

    .headerSlider-navigation-responsive {
      display: inline-flex;
      width: 50px;
      height: 50px;
      -webkit-box-align: center;
      align-items: center;
      -webkit-box-pack: center;
      justify-content: center;
      cursor: pointer;
      position: relative;
    }

    .responsive-tools .headerSearch-icon .headerSlider-navigation-responsive {
      position: absolute;
      left: 0;
      top: 0;
      display: none;
      pointer-events: none;
    }

    .authentication-tools {
      display: none;
    }
  }

  @media screen and (max-width: 1199px) and (orientation: landscape) {
    .header-navigation {
      .header-navigation-inner {
        max-height: calc(100vh - 60px);
        overflow-y: auto;
      }
    }

    .headerMegaNavigation {
      .headerMegaNavigation-menu {
        &.active {
          height: calc(100vh - 108px);
        }
      }
    }
  }

  @media screen and (max-width: 1399px) {
    .header-tools .header-search-desktop {
      display: none;
    }

    .site-select-menu {
      display: none;
    }

    .header-right {
      .site-select-menu {
        margin: 0px 20px 0px 0px;
      }
    }

    .responsive-tools .header-search-ov {
      width: 100vw;
      position: absolute;
      top: 100%;
      left: 0;
      background-color: #fff;
      padding: 20px;
      opacity: 0;
      visibility: hidden;
      z-index: -9999;
      max-width: 100%;
    }
    /*controll buttons in responsive menus*/
    .responsive-tools.search-opened .header-search-ov {
      z-index: 1;
      visibility: visible;
      opacity: 1;
    }

    .responsive-tools .twitter-typeahead {
      width: 100%;
    }

    .responsive-tools .headerSearch-ov {
      max-width: 100%;
    }

    .responsive-tools.search-opened .headerSearch-icon .link-search {
      display: none;
    }

    .responsive-tools .headerSearch-icon .headerSlider-navigation-responsive {
      position: absolute;
      display: none;
    }

    .headerSlider-navigation-responsive div {
      margin: auto;
      width: 20px;
      height: 15px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
    }

    .responsive-tools.search-opened
      .headerSlider-navigation-responsive:not(.headerMenu-icon) {
      display: block;
    }

    .headerSlider-navigation-responsive span {
      display: block;
      position: absolute;
      height: 2px;
      width: 20px;
      background-color: #3b4956;
      opacity: 1;
      left: 0;
      -webkit-transform: rotate(0);
      transform: rotate(0);
      -webkit-transition: 0.25s ease-in-out;
      transition: 0.25s ease-in-out;
    }

    .headerSlider-navigation-responsive span:nth-child(1) {
      top: 0;
      -webkit-transform-origin: left center;
      transform-origin: left center;
    }

    .headerSlider-navigation-responsive span:nth-child(2) {
      top: 6px;
      -webkit-transform-origin: left center;
      transform-origin: left center;
    }

    .headerSlider-navigation-responsive span:nth-child(3) {
      top: 12px;
      -webkit-transform-origin: left center;
      transform-origin: left center;
    }

    .headerSlider-navigation-responsive.navigation-active span:nth-child(1) {
      transform-origin: left center;
      transform: rotate(45deg);
      top: -1px;
      left: 3px;
    }

    .headerSlider-navigation-responsive.navigation-active span:nth-child(2) {
      opacity: 0;
    }

    .headerSlider-navigation-responsive.navigation-active span:nth-child(3) {
      transform-origin: left center;
      transform: rotate(-45deg);
      top: 13px;
      left: 3px;
    }
  }

  &--login-active {
    .header-search-desktop {
      display: none;
    }

    .responsive-tools.search-opened .header-search-desktop {
      display: block;
    }

    .responsive-tools .header-search-ov {
      width: 100vw;
      position: absolute;
      top: 100%;
      left: 0;
      background-color: #fff;
      padding: 20px;
      opacity: 0;
      visibility: hidden;
      z-index: -9999;
      max-width: 100%;
    }

    .responsive-tools.search-opened .header-search-ov {
      z-index: 1;
      visibility: visible;
      opacity: 1;
    }

    .responsive-tools .twitter-typeahead {
      width: 100%;
    }

    .responsive-tools .headerSearch-ov {
      max-width: 100%;
    }

    .responsive-tools.search-opened .headerSearch-icon .link-search {
      display: none;
    }

    .responsive-tools .headerSearch-icon .headerSlider-navigation-responsive {
      position: absolute;
      display: none;
    }

    .headerSlider-navigation-responsive div {
      margin: auto;
      width: 20px;
      height: 15px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
    }

    .responsive-tools {
      display: block;

      .login-responsive {
        position: absolute;
        width: 50px;
        height: 50px;
        right: 150px;
        top: 6px;
        line-height: 50px;
        text-align: center;
        z-index: 1;
        cursor: pointer;
        outline: none;

        .login {
          float: none;
        }

        .login-button {
          font-size: 24px;
          display: inline-block;
          line-height: 50px;
        }

        .link-logon {
          font-size: inherit;
        }

        &:after {
          content: "";
          clear: both;
          display: table;
        }
      }
    }

    @media screen and (min-width: 1199px) {
      .responsive-tools {
        .login-responsive {
          display: none;
        }
      }
    }

    @media screen and (max-width: 1199px) {
      .responsive-tools {
        .login-responsive {
          .hidden-sm-down {
            display: none;
          }

          .popover {
            width: 100%;
            position: fixed;
            top: 60px !important;
            left: 0 !important;
            display: block;
            border-top-left-radius: 0;
            border-top-right-radius: 0;
            padding: 10px 20px;
          }
        }
      }
    }
  }
}
/* To make it work on IE - Changed CSS property Initial to Inherit */
.megaNavigation-submenu-li-anchor:after {
  position: inherit !important;
}
