.energyMarketChartContainer {
  background-color: white;
  padding: 10px;
}

.chartLoader {
  display: block;
  border: 4px solid #ffffff;
  border-top: 4px solid #4e9dd4;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

#downloadCsvData {
  float: right;
  .downloadLink {
    color: #e85757;
  }
}

.chartLoaderContainer {
  height: 37px;
  margin-top: 0px;
  margin-bottom: 5px;
  text-align: center;
  width: 100%;
}

.chartGreyBackground {
  background-color: #eaeaea;
}

.chartNextButton,
.hourlyChartNextButton {
  background-image: url("../../assets/images/arrow-right.svg");
  background-repeat: no-repeat;
  background-position: center;
  max-width: 15px;
  margin-left: -15px;
  padding-right: 5px;
  z-index: 1;
  @media (min-width: 768px) {
    display: none;
  }
}
.chartPrevButton,
.hourlyChartPrevButton {
  background-image: url("../../assets/images/arrow-left.svg");
  background-repeat: no-repeat;
  background-position: center;
  max-width: 15px;
  margin-right: -15px;
  padding-left: 5px;
  z-index: 1;
  @media (min-width: 768px) {
    display: none;
  }
}
#hourlyMarketChart {
  .canvasLabel {
    width: 100%;
    text-align: center;
    margin-top: -20px;
    font-size: 16px;
    font-family: $font-family-bold;
    color: $color-dark-blue;
  }
}
