.careerSlider {
  padding-top: 40px;

  .careers-title {
    //styleName: H3 / D Bold;
    font-family: $font-family-regular;
    font-size: 40px;
    font-style: normal;
    font-weight: 700;
    line-height: 55px;
    letter-spacing: 0em;
    text-align: left;

    padding-left: 8%;

    @include media-breakpoint-down(lg) {
      //styleName: H5 / M Bold;
      font-family: Orsted Sans;
      font-size: 19px;
      font-style: normal;
      font-weight: 700;
      line-height: 28px;
      letter-spacing: 0em;
      text-align: left;
      padding-left: 27px;
    }
  }

  .career-swiper-container {
    width: 100%;
    padding-top: 40px;
    padding-bottom: 37px;
    overflow: hidden;

    @include media-breakpoint-down(lg) {
      padding-top: 30px;
      padding-bottom: 17px;

      &.in-viewport-inited.in-viewport {
        animation: bounceMobileSlider 0.2s linear;
        animation-delay: 0.5s;
      }
    }

    .swiper-button-prev {
      left: 50px;

      &:hover {
        transform: translate3d(-10px, 0, 0);
      }
    }

    .swiper-button-next {
      right: 50px;

      &:hover {
        transform: translate3d(10px, 0, 0);
      }
    }

    .swiper-button-prev,
    .swiper-button-next {
      width: 48px;
      height: 48px;
      display: flex;
      justify-content: center;
      align-items: center;
      top: 125px;
      background: $color-white;
      color: $color-cold-grey-75;
      border-radius: 50%;
      opacity: 0.75;
      transform: translate3d(0, 0, 0);
      transition: transform 0.2s cubic-bezier(0.165, 0.84, 0.26, 0.98);

      &:hover {
        opacity: 1;
      }

      @include media-breakpoint-down(lg) {
        display: none;
      }
    }

    .swiper-button-disabled {
      display: none;
    }
  }

  .swiper-slide {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    max-width: 310px;

    img {
      object-fit: cover;
      height: 175px;
      z-index: -1;
      display: inline-block;
      max-height: 175px;
    }

    & > span {
      font-family: $font-family-regular !important;
      font-style: normal;
      margin-top: 24px;
    }

    @include media-breakpoint-down(lg) {
      width: calc(100% - 20px);
      height: auto;

      img {
        object-fit: cover;
        width: 100%;
        height: 100%;
        z-index: -1;
        display: inline-block;
        // max-height: 120px;
      }
    }

    .title {
      //styleName: H5 / D Bold;
      font-family: $font-family-bold;
      font-size: 19px;
      font-style: normal;
      font-weight: 700;
      line-height: 36px;
      letter-spacing: 0em;
      text-align: left;
      padding-top: 24px;

      @include media-breakpoint-down(lg) {
        padding-top: 17px;
      }
    }

    .sub-title {
      //styleName: Body / regular;
      font-family: $font-family-regular;
      font-size: $text-size-body;
      font-style: normal;
      font-weight: 400;
      line-height: 24px;
      letter-spacing: 0em;
      text-align: left;
      padding-top: 20px;
      padding-bottom: 16px;
    }

    .link,
    .link-arrow {
      //styleName: H5 / D Bold;
      font-family: $font-family-bold;
      font-size: 19px;
      font-style: normal;
      font-weight: 700;
      line-height: 36px;
      letter-spacing: 0em;
      text-align: left;
    }

    .arrow {
      //styleName: H4 / D Regular;
      font-family: $font-family-regular;
      font-size: 19px;
      font-style: normal;
      font-weight: 700;
      line-height: 23px;
      text-align: center;
      margin-left: 10px;
    }

    @include media-breakpoint-down(lg) {
      margin-top: 20px;

      .title {
        font-size: $text-size-body;
        line-height: 24px;
        padding-top: 16px;
      }

      .sub-title {
        font-size: $text-size-body;
        line-height: 24px;
        padding-top: 16px;
      }

      .link,
      .link-arrow {
        font-size: $text-size-body;
        line-height: 24px;
      }

      .arrow {
        font-size: 19px;
        line-height: 24px;
      }
    }
  }

  .swiper-button-prev,
  .swiper-button-prev:after,
  .swiper-button-next,
  .swiper-button-next:after {
    content: none;
    background-image: none;
    opacity: 0;

    @include media-breakpoint-down(lg) {
      display: none;
    }
  }

  .swiper-button-disabled {
    display: none;
  }
}

@keyframes arrowMoveLeft {
  from,
  to {
    transform: translate3d(0, 0, 0);
  }

  50% {
    transform: translate3d(-10px, 0, 0);
  }
}

@keyframes arrowMoveRight {
  from,
  to {
    transform: translate3d(0, 0, 0);
  }

  50% {
    transform: translate3d(10px, 0, 0);
  }
}

@keyframes bounceMobileSlider {
  from,
  to {
    margin-left: 0;
  }

  50% {
    margin-left: -5px;
  }
}

.swiper-career-theme-- {
  &white-cold-grey {
    background-color: $color-white;

    .careers-title,
    .title,
    .sub-title {
      color: $color-dark-blue;
    }

    .arrow path {
      fill: $color-blue;
    }

    .link,
    .link-arrow {
      color: $color-blue;
    }
  }

  &aubergine {
    background: $color-aubergine;

    .careers-title,
    .title,
    .sub-title {
      color: white;
    }

    .link,
    .link-arrow {
      color: white;
    }

    .arrow path {
      fill: white;
    }
  }

  &blue {
    background: $color-blue;

    .careers-title,
    .title,
    .sub-title {
      color: $color-white;
    }

    .arrow path {
      fill: $color-white;
    }

    .link,
    .link-arrow {
      color: $color-white;
    }
  }

  &cold-grey-50-white {
    background-color: $color-backdrop-1;

    .careers-title,
    .title,
    .sub-title {
      color: $color-white;
    }

    .arrow path {
      fill: $color-white;
    }

    .link,
    .link-arrow {
      color: $color-white;
    }
  }

  &cold-grey-5 {
    background-color: $color-cold-grey-5;

    .careers-title,
    .title,
    .sub-title {
      color: $color-dark-blue;
    }

    .arrow path {
      fill: $color-blue;
    }

    .link,
    .link-arrow {
      color: $color-blue;
    }
  }
}
