.container-fluid form,
.form,
.toaster-placeholder form {
  .form-group {
    legend {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
      }
    }
    input[type="checkbox"],
    input[type="radio"] {
      & ~ span {
        .site-theme-radius & {
          a:hover {
            color: $color-radius-turquoise;
          }
        }
      }
      &:checked ~ span {
        .site-theme-radius & {
          color: $color-radius-blue;

          a {
            color: $color-radius-blue;
          }

          font-family: $font-family-bold-radius;
        }
      }
    }
    &.has-success {
      &:after {
        .site-theme-radius & {
          font-family: $font-family-bold-radius;
        }
      }
    }
    &.has-danger,
    &.has-error {
      &:after {
        .site-theme-radius & {
          font-family: $font-family-bold-radius;
        }
      }
    }
    .form-control-help-bs-tooltip {
      .site-theme-radius & {
        font-family: $font-family-regular-radius;
      }
    }
    label {
      .form-control-help {
        .site-theme-radius & {
          font-family: $font-family-regular-radius;
        }
      }
    }
    .form-control-help {
      .site-theme-radius & {
        font-family: $font-family-regular-radius;
      }
      &:hover {
        &:after {
          .site-theme-radius & {
            font-family: $font-family-regular-radius;
            font-size: $text-size-label-radius;
          }
        }
      }
    }
  }
  .is-inline-form-control {
    label.control-label {
      .site-theme-radius & {
        font-family: $font-family-bold-radius;
        font-size: $text-size-label-radius;
      }
    }
  }
}
.tooltip-inner {
  .site-theme-radius & {
    font-family: $font-family-regular-radius;
  }
}
