.row > [class*='col'] {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    >*{
        margin-bottom:0;
    }
}

.row{
    max-width: 1440px;
    margin-left:auto;
    margin-right:auto;

    &.row-center > [class*='col'] {
        padding-left: 20px;
        padding-right: 20px;
        margin-left: auto;
        margin-right: auto;

        @include media-breakpoint-up(md){	
           padding-left: 0;
           padding-right: 0;
        } 
    }

    &.row-narrow{
        @include media-breakpoint-up(lg){
            width: 66.666%;
            width: 100%;
            max-width:960px;
        }
        @include media-breakpoint-down(sm){	
            .col-sm-12,.col-12 {
                padding-left: 20px;
                padding-right: 20px;
            }
        } 
    }

    &.row-fullwidth{
        max-width: none;
    }
    > .col-12{
        min-height: 0px;
    }
    > .col-sm-12{
        @include media-breakpoint-down(sm){
            min-height: 0px;
        }
    }
    > .col-md-12{
        @include media-breakpoint-down(md){
            min-height: 0px;
        }
    }
    > .col-lg-12{
        @include media-breakpoint-down(lg){
            min-height: 0px;
        }
    }
    > .col-xl-12{
        @include media-breakpoint-down(xl){
            min-height: 0px;
        }
    }
}

.search_without_category {
    padding-left: 20px;
    .search-result-col.col-in-viewport-inited.col-in-viewport.col-was-in-viewport, .list.search_result_list.col-in-viewport-inited.col-was-in-viewport {
        padding-left: 0 !important;
    }
}

.col-1-of-10 {
    @include media-breakpoint-up(lg) {
        flex: 0 0 10%;
        max-width: 10%;
    }
}

.col-6-of-10 {
    @include media-breakpoint-up(lg) {
        flex: 0 0 60%;
        max-width: 60%;
    }
}

.offset-1-of-10 {
    @include media-breakpoint-up(lg) {
        margin-left: 10%;
    }
}
