.selectDropdown {
  &.dropdown {
    overflow: hidden;
    border-radius: 35px;
    width: 100%;
  }

  .dropdown-backdrop {
    display: none;
  }

  &.open,
  &:hover {
    box-shadow: 0 0 0 3px $color-cold-grey-25;
  }

  &.open {
    border-radius: 22px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;

    &::after {
      transform: rotate(180deg);
    }

    &.dropdown-menu-open--top {
      border-radius: 22px;
      border-top-right-radius: 0;
      border-top-left-radius: 0;
    }
  }

  button.dropdown-toggle[type="button"] {
    font-family: $font-family-regular;
    border-radius: 35px;
    border: 1px solid $color-cold-grey-25;
    background: $color-white;
    color: $color-text;
    padding: 10px 0 10px 24px;
    width: 100%;
    text-align: left;
    min-height: 44px;
    max-height: 44px;
    float: initial;
    transform: translateZ(1px); // fix repaint when overflow happened
    display: flex;

    &::after {
      display: none;
    }

    &::before {
      content: none;
    }

    &:disabled {
      color: $color-cold-grey-25;
    }
  }

  &.open button.dropdown-toggle[type="button"] {
    border-bottom-color: transparent;
    border-radius: 22px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }

  &.open.dropdown-menu-open--top button.dropdown-toggle[type="button"] {
    border-radius: 22px;
    border-top-right-radius: 0;
    border-top-left-radius: 0;
    border-top-color: transparent;
    border-bottom-color: $color-cold-grey-25;
  }

  .dropdown-display-text {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .dropdown-display-addition {
    font-family: $font-family-bold;
    margin-left: 3px;
  }

  .dropdown-display-wrapper {
    display: inline-flex;
    overflow: hidden;
    white-space: nowrap;
    width: calc(100% - 50px);
  }

  &.dropdown::after {
    content: "\63";
    position: absolute;
    right: 25px;
    top: 0;
    font-family: "dong-maine";
    height: 100%;
    display: inline-flex;
    align-items: center;
    color: $color-blue;
    pointer-events: none;
  }

  &.dropdown-menu {
    width: 100%;
    margin-top: -1px;
    border-width: 0;
    background: $color-white;
    border-radius: 22px;
    border-top-right-radius: 0;
    border-top-left-radius: 0;
    padding: 0;
    margin: 0;
    overflow: auto;
    box-shadow: 0 0 0 3px $color-cold-grey-25;s

    ul {
      padding: 0;
    }

    &.dropdown-menu-open--top {
      border-radius: 22px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
  }

  .dropdown-items-wrapepr {
    max-height: 390px;
    overflow: auto;
    height: 100%;

    &::-webkit-scrollbar {
      width: 6px;
    }

    &::-webkit-scrollbar-track {
      background: none;
    }

    &::-webkit-scrollbar-thumb {
      background: #ccd1d7;
      border-radius: 20px;
    }
  }
  
  .dropdown-options {
    padding: 0;
    margin: 0;
  }

  .dropdown-item {
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 10px 24px;
    white-space: normal;
    min-height: 42px;

    &.focused,
    &:hover {
      background: $color-cold-grey-light;
    }

    &:active {
      color: $color-text;
    }

    &[disabled] {
      cursor: initial;
      color: $color-cold-grey-light;
    }
  }

  .dropdown-item-icon {
    margin-right: 16px;
    max-width: 24px;
    max-height: 24px;
  }

  .dropdown-label-toggle {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    @include media-breakpoint-up(md) {
      top: -1000px;
    }
  }

  select {
    opacity: 0;
    width: 100%;
    height: 100%;
  }
}
