.table {
  width: 100% !important;
  font-size: 14px;

  margin: 0px;

  // Theming: default is site-theme-blue. site-theme-red overwrites it.
  thead {
    background-color: $color-aqua;
    color: $color-text;
  }

  @include media-breakpoint-up(md) {
    &:not(:first-child) {
      margin-top: $spacing-extralarge;
    }
  }

  @include media-breakpoint-down(sm) {
    &:not(.table-no-collapse) {
      thead {
        display: none;
      }

      td {
        display: block;

        &:before {
          display: block;
          content: attr(data-label);
          font-family: $font-family-bold;
          .site-theme-radius & {
            font-family: $font-family-bold-radius;
          }
        }
      }
    }
  }

  thead {
    th {
      border: none;
      font-family: $font-family-bold;
    }

    th:first-child {
      // Hacky IE Fix for border-radius-bleed.
      padding-right: 0px;
    }
  }

  .tabs-content:not(.is-padded) & {
    thead {
      color: $color-dark-blue;
      background-color: $color-white;
    }
  }

  tbody {
    td {
      border: none;
      color: $color-dark-blue;

      a {
        // color: $color-aubergine;
        @extend .link-inline;
      }
    }

    tr:nth-child(even) {
      background-color: rgba(#d8d1ca, 0.3);
    }
  }
}

/* Styling for header on mobile */
.richtext {
  .table-mobile-head {
    @include media-breakpoint-up(md) {
      display: none;
      color: transparent;
    }
    background-color: $color-aqua;
    color: $color-text;

    padding: 12px;
    width: 100%;
    min-height: 46px;

    font-family: $font-family-bold;
    font-size: 14px;
    text-align: left;
  }
}

/* Tables placed with richtext editor now has the correct styling */
.richtext table {
  @extend .table;
}

/* Fixes styling on regulatory reporting tables */
.gnw_table {
  @extend .table;
}
.gnw_table tbody td {
  text-align: left;
}
.gnw_table .gnw_uline {
  width: 100%;
}
