#ui-datepicker-div.ui-datepicker {
  width: 22em !important;
  font-size: 12px;
  border-radius: 19px;
  padding: 0;
  overflow: hidden;
  border: 0.5px solid lightskyblue;
  font-family: $font-family-regular;

  .ui-datepicker-header {
    border: 0;
    padding: 10px 15px;
    background: $color-blue;
    color: $color-white;
    font-size: 14px;
    font-weight: normal;
    border-radius: 0;
    line-height: 1.5em;

    .ui-datepicker-prev,
    .ui-datepicker-next {
      span {
        top: 80%;
        border: 0;
      }
    }
    .ui-datepicker-prev-hover {
      left: 2px;
      top: 2px;
    }
    .ui-datepicker-next-hover {
      right: 2px;
      top: 2px;
    }
  }

  .ui-state-hover {
    border: 0;
    background: 0;
  }

  .ui-datepicker-calendar {
    th {
      padding: 4px 10px;
      text-align: center;
      color: $color-dark-blue;
      font-family: $font-family-bold;
    }

    td {
      a.ui-state-default {
        padding: 4px 10px;
        text-align: center;
        border: 0;
        background: 0;

        &.ui-state-highlight {
          color: $color-blue;
          font-family: $font-family-bold;
        }
        &.ui-state-active {
          position: relative;
          color: $color-white;

          &:before {
            position: absolute;
            top: 1px;
            left: 5px;
            width: 24px;
            height: 24px;
            z-index: -1;

            display: block;
            content: " ";

            background: $color-blue;
            border-radius: 12px;
          }
        }
      }
    }
  }
}

.datepicker {
  border-radius: 20px;
  padding-top: 0;
  font-size: 12px;

  .datepicker-title {
    border-top-left-radius: 19px;
    border-top-right-radius: 19px;
    padding: 10px 15px;

    background: $color-blue;
    color: $color-white;
    font-size: 14px;
    font-weight: normal;
  }

  th {
    padding: 4px 10px;
    text-align: center;

    &.dow {
      color: $color-dark-blue;
      font-family: $font-family-bold;
    }

    &.datepicker-switch {
      padding: 10px;
      font-size: 15px;
      pointer-events: none;
    }

    &.prev {
      cursor: pointer;
    }

    &.next {
      cursor: pointer;
    }
  }

  td {
    padding: 4px 10px;
    text-align: center;
    cursor: pointer;

    &.day {
      color: $color-dark-blue;

      &.old,
      &.new {
        color: $color-backdrop-1;
      }
    }

    &.today {
      color: $color-blue;
      font-family: $font-family-bold;
    }

    &.active {
      position: relative;
      color: $color-white;

      &:before {
        position: absolute;
        top: 1px;
        left: 5px;
        width: 24px;
        height: 24px;
        z-index: -1;

        display: block;
        content: " ";

        background: $color-blue;
        border-radius: 12px;
      }
    }
  }
}
