/**
    Simple Mixin for CircleTeaser Theme Colors
    - Requires mixins; btn(), link-arrow-color()
    - Button color is _always_ $color-white.
 */
@mixin circleTeaserTheme($text-color, $accent-color) {
  .teaser-content {
    .teaser-text {
      color: $text-color;
    }

    .teaser-quote .inner-quote,
    .teaser-quotation {
      color: $text-color;
    }

    .teaser-cta {
      .btn {
        @include btnTheme($accent-color, $color-white);
      }
    }

    @include link-arrow-color($accent-color);
  }

  .theme-base-color-background {
    background-color: $text-color !important;
  }

  .background-color-forced-transparent {
    background-color: inherit !important;
  }

  .background-color-forced-white {
    background-color: #ffffff !important;
  }
}

.teaserCircle {
  display: flex;
  // justify-content: flex-end;
  flex-direction: column;
  flex-grow: 1;
  min-height: 220px;
  overflow: hidden;
  position: relative;
  margin-bottom: 50px !important;

  .teaser-media {
    margin-bottom: 20px;
  }

  .teaser-img {
    @include image-scale-up-transition;
    background-position: 50%;
    background-size: cover;
    background-repeat: no-repeat;
    width: 227px;
    height: 227px;
    max-height: 100%;
    max-width: 100%;
    transform: scale(1);
    border-radius: 50%;
    margin: auto;
  }

  .circle-teaser-icon {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 140px;
  }

  &:hover .teaser-img {
    @include image-scale-up;
  }

  .teaser-content {
    background: none !important;
    height: auto;
    // margin: auto; // vertical centering
    width: 100%;
    max-width: $legible-text-max-width;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    min-width: 20%;
    word-wrap: break-word;

    @include media-breakpoint-up(md) {
      text-align: left;
    }

    .teaser-text {
      margin: 0 0 1rem;
    }
  }

  .teaser-quotation {
    font-size: rem(60);
    font-family: $font-family-bold;
    margin-bottom: -10px;
  }

  .teaser-quote {
    .inner-quote {
      padding: 0.5rem 0;

      p {
        border-left: 0px;
        padding: 0;
        margin: 0;
      }

      @extend .h4-italic;
    }

    footer {
      color: $color-text; //TODO: best-practice, where should this be placed?
      cite {
        font-family: $font-family-bold;

        font-style: normal;
        display: block;
      }

      p {
        margin: 0;
        padding: 0;
        border: 0;
      }
    }
  }

  @include media-breakpoint-up(md) {
    flex-direction: row;

    .teaser-media {
      margin-right: 35px;
      margin-bottom: 0;
    }

    &.teaser-image-right {
      flex-direction: row-reverse;

      .teaser-media {
        margin-right: 0;
        margin-left: 30px;
      }
    }
  }

  &.theme-white-blue {
    @include circleTeaserTheme($color-blue, $color-dark-blue);
  }

  &.theme-white-aubergine {
    @include circleTeaserTheme($color-aubergine, $color-dark-blue);
  }

  &.theme-white-red {
    @include circleTeaserTheme($color-red, $color-dark-blue);
  }

  &.theme-white {
    @include circleTeaserTheme($color-dark-blue, $color-blue);
  }

  &.theme-white-dark-blue-red {
    @include circleTeaserTheme($color-dark-blue, $color-red);
  }

  &.theme-radius {
    // @include circleTeaserTheme($color-dark-blue, $color-radius);
  }
}

.row {
  &.row-narrow {
    @include media-breakpoint-up(md) {
      .teaserCircle {
        padding: $module-space-vertical-top $module-space-horisontal-left
          $module-space-vertical-bottom $module-space-horisontal-right;
      }
    }
    @include media-breakpoint-down(sm) {
      .teaserCircle {
        padding: $module-space-vertical-top $module-no-space
          $module-space-vertical-bottom $module-no-space;
      }
    }
  }
  &.row-fullwidth {
    .teaserCircle {
      padding: $module-space-vertical-top $module-space-horisontal-left
        $module-space-vertical-bottom $module-space-horisontal-right;
    }
  }
  &.row-widewidth {
    .teaserCircle {
      padding: $module-space-vertical-top $module-space-horisontal-left
        $module-space-vertical-bottom $module-space-horisontal-right;
    }
  }
}
