.pageLanguageSwitcher {
  max-height: 44px;
  max-width: 185px;
  width: 185px;
  z-index: 9000;

  .selectDropdown {
    &.dropdown {
      overflow: visible;
    }

    button.dropdown-toggle[type="button"] {
      padding: 10px;
    }
  }

  &.pageLanguageSwitcher--floating {
    position: fixed;
    bottom: 30px;
    right: 30px;
    width: 104px;
    max-width: 104px;

    .dropdown::after {
      display: none;
    }

    .selectDropdown {
      overflow: visible;

      &.open,
      &:hover {
        box-shadow: none;
      }

      .dropdown-display-wrapper {
        width: 100%;
      }

      .dropdown-label-toggle {
        top: -1000px;
      }
    }
  }

  &.pageLanguageSwitcher--hidden {
    animation: fade-out 0.3s ease-out;
  }

  &.pageLanguageSwitcher--show {
    animation: fade-in 0.3s ease-out;
  }
}

.pageLanguageSwitcher-dropdown-menu {
  &.selectDropdown .dropdown-item {
    padding: 10px;
  }

  &.pageLanguageSwitcher--floating {
    position: absolute;
    max-width: 104px;
    width: 104px;
    min-width: 104px;

    &.selectDropdown.dropdown-menu {
      box-shadow: none;
      border: 1px solid $color-cold-grey-25;
    }

    .dropdown-items-wrapepr {
      overflow: hidden;
    }
  }
}
