.ui-autocomplete.ui-menu {
  @at-root .is-experience-editing & {
    z-index: 999;
  }
  .ui-menu-item {
    color: $color-text;
    font-family: $font-family-regular;

    font-size: 15px;
    padding: 0;

    .ui-menu-item-wrapper {
      padding: 3px 1em 3px 1em;

      &.ui-state-active {
        background: #4e9dd4;
        color: $white;
      }
    }
  }
}

.ui-widget-content {
  border: 1px solid #aaaaaa;
  background: #ffffff;
  color: #222222;
}

.ui-widget-content {
  width: 15%;
}

@media only screen and (max-width: 900px) {
  .ui-widget-content {
    width: 30%;
  }
}

.ui-menu {
  list-style: none;
  padding: 0;
  margin: 0;
  display: block;
  outline: none;
  z-index: 1051 !important;
}
