.factBox {
  .factBox-element + .factBox-element {
    margin-top: $header-spacing-bottom;

    @include media-breakpoint-down(md) {
      margin-top: $header-spacing-small-bottom;
    }
  }

  .factBox-headline {
    font-family: $font-family-bold !important;

    @include media-breakpoint-down(md) {
      order: -2;
      margin-bottom: $header-spacing-small-bottom;
    }
  }

  &.factBox-note .theme-keyline-border {
    @include media-breakpoint-down(md) {
      border-top-style: solid;
      border-top-width: 1px;
      border-bottom-style: solid;
      border-bottom-width: 1px;
    }
  }

  .factBox__inner {
    padding: $component-container-padding;
    @include align-with-text;

    @include media-breakpoint-down(md) {
      padding: $component-container-padding-mobile;
    }

    @include media-breakpoint-down(md) {
      display: flex;
      flex-direction: column;

      > .factBox-headline {
        margin-top: 0;
        margin-bottom: 16px;
      }
    }
  }

  .factBox-body.factBox-element {
    display: flex;

    .factBox-section {
      flex: 1;

      &:first-child {
        margin-right: 32px;
  
        @include media-breakpoint-down(md) {
          margin-right: 0;
          margin-bottom: $header-spacing-small-bottom;
        }
      }

      @include media-breakpoint-down(md) {
        display: flex;
        flex-direction: column;

        .factBox-media { 
          width: 100%;
          order: -1;
          margin-top: 0;
          margin-bottom: $header-spacing-small-bottom;
        }

        > :only-child {
          margin-bottom: 0;
        }
      }
    }

    @include media-breakpoint-down(md) {
      flex-direction: column;
      margin-top: 0;
      
      &:first-child {
        margin-top: 0;
      }
    }
  }

  .factBox-header > :last-child {
    @include media-breakpoint-down(md) {
      margin-bottom: $header-spacing-small-bottom;
    }
  }

  .body {
    font-size: $text-size-body;
    line-height: $line-height-body;
  }

  @include media-breakpoint-down(md) {
    .full-container &,
    .wide-container & {
      margin-left: 27px;
      margin-right: 27px;
    }
    .full-container .article &,
    .wide-container .article & {
      margin-left: 0;
      margin-right: 0;
    }
  }

  .factBox-content.factBox-element {
    > :first-child {
      margin-top: 0;
    }

    @include media-breakpoint-down(md) {
      margin-top: 0;
    }

    img {
      width: 100%;
      margin-bottom: 16px;
    }

    &.richtext {
      overflow-x: hidden; /* IE hack to prevent unnecessary horizontal scroll bar. */
      overflow-wrap: break-word;
      word-wrap: break-word; /* IE11 version of overflow-wrap */

      &:not(.richtext-teaser) p {
        margin-bottom: 0;
      }
    }
  }

  .factBox-media.factBox-element {
    .factBox-media__wrapper {
      position: relative;

      &::before {
        content: "";
        padding-top: 56.25%;
        display: block;
        z-index: -1;
      }

      img,
      .video-js.vjs-fluid,
      .teaser-video-iframe  {
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        z-index: 1;
      }
    }

    @include media-breakpoint-down(md) {
      order: -1;
      margin-top: 0;
      margin-bottom: $header-spacing-small-bottom;

      & ~ .factBox-headline {
        margin-bottom: $header-spacing-small-bottom;
      }
    }
  }

  a {
    @extend .link-inline;
  }

  .btn {
    text-decoration: none;
  }

  .factBox-cta {
    a {
      font-size: $text-size-body;
      line-height: $line-height-body;
    }
  }
}

div.article-placeholder {
  & > article.factBox {
    .factBox-element + .factBox-element {
      margin-top: $header-spacing-small-bottom;
      margin-bottom: 0
    }

    @include media-breakpoint-down(md) {
      .factBox-media.factBox-element ~ .factBox-headline.factBox-element,
      .factBox-media.factBox-element {
        margin-top: 0;
        margin-bottom: $header-spacing-small-bottom;
      }

      .factBox-content.factBox-element,
      .factBox-body.factBox-element {
        margin-top: 0;
      }
    }

    .factBox-cta {
      margin-top: $header-spacing-small-bottom;
    }
  }

  &.article-placeholder-left {
    & > article.factBox {
      width: 334px;
      margin-left: auto;

      @include media-breakpoint-down(md) {
        width: 100%;
      }
    }
  }

  &.article-placeholder-right {
    & > article.factBox {
      width: 334px;

      @include media-breakpoint-down(md) {
        width: 100%;
      }
    }
  }
}
