.teaser {
  &.teaser-horisontal {
    flex-direction: column;

    // Trigger a youtube video in an iframe to cut of the sides on resize. To avoid black borders.
    .yt-embed {
      width: 100%;
    }
    .yt-embed > .yt-embed__iframe {
      width: 100%;
      height: calc((100vw * 9) / 16);
    }

    .teaser-media {
      @include aspect-ratio-16-9;
      position: relative;
    }

    .teaser-img {
      position: absolute;
    }

    @include media-breakpoint-up(md) {
      &.teaser-media-video {
        .teaser-video {
          background-color: black;
          width: 100%;
          min-height: 100%;
        }

        line-height: 0;

        span.teaser-headline {
          line-height: normal;
        }
      }
    }

    @include media-breakpoint-down(sm) {
      .teaser-video {
        min-height: 100%;
        min-width: 100%;
        position: absolute !important;
        @include absoluteCenter;
        max-width: 100%;
      }
    }

    .teaser-content {
      background: none !important;
      height: auto;
    }

    @include media-breakpoint-up(md) {
      flex-direction: row;

      .teaser-media {
        height: auto;
        padding-bottom: 0;
        display: block;
        flex: 1 0 50%;
        max-width: 50%;
      }

      .teaser-content {
        padding: 40px;
        flex: 1 0 50%;
        max-width: 50%;
      }

      .teaser-link {
        left: 50%;
      }

      &.teaser-image-right {
        flex-direction: row-reverse;

        .teaser-link {
          left: 0;
          right: 50%;
        }

        .teaser-content {
          padding-left: 40px;
          padding-right: 40px;
        }
      }
    }
  }
}

.row-widewidth,
.row-fullwidth {
  .teaser {
    &.teaser-horisontal .teaser-content {
      padding: 40px 120px;
    }
  }
}
