.proceed {
  background-color: $color-white;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;

  .btn {
    cursor: pointer;
  }

  .btn-back {
    align-self: flex-start;
    justify-content: flex-start;
    color: $color-text;
  }
  .btn-back::after {
    content: "" !important;
  }
  .btn-continue {
    margin-left: auto !important;

    &:disabled {
      opacity: 0.5;
    }
  }
}
