.timeline {
  .timeline-header {
    .site-theme-radius & {
      background-color: $color-radius-blue;
    }
  }
  .timeline-title {
    .site-theme-radius & {
      font-family: $font-family-semi-bold-radius;
      font-size: $text-size-body-radius;
      line-height: $line-height-body-radius;
      color: $color-white;
    }
  }
  .timeline-anchor {
    .site-theme-radius & {
      font-family: $font-family-semi-bold-radius;
      color: $color-radius-darkgrey;
      font-size: $text-size-body-radius;
      line-height: $line-height-body;
    }
    &:hover {
      &:before {
        .site-theme-radius & {
          border: 4px solid $color-radius-turquoise;
        }
      }
    }
    &.timeline-anchor-active {
      &:before {
        .site-theme-radius & {
          background-color: $color-radius-turquoise;
        }
      }
      .site-theme-radius & {
        color: $color-radius-turquoise;
      }
    }
  }
  .timeline-card-title {
    .site-theme-radius & {
      font-family: $font-family-semi-bold-radius;
      font-size: $text-size-body-radius;
      line-height: $line-height-body-radius;
    }
  }
  .timeline-card-cta {
    .link-arrow {
      .site-theme-radius & {
        font-family: $font-family-semi-bold-radius;
        color: $color-radius-blue;
        font-size: $text-size-body-radius;
        line-height: $line-height-body-radius;
      }
    }
  }
}
