$space: 16px;
$space-lg: 20px;
$border-color: $color-backdrop-3;
$font-family-regular: "OrstedSansRegular", sans;
$font-family-light: "OrstedSansLight", sans;
$font-family-bold: "OrstedSansBold", sans;
$font-family-black: "OrstedSansBlack", sans;
$font-family-italic: "OrstedSansItalic", sans;

.calendar {
  margin: 20px 5% 0 5%;
  max-width: 90%;
}

.calendar--headline {
  font-size: 24px;
  font-family: $font-family-light !important;
  margin-bottom: $space;
}

.calendar--list {
  padding: 0;
}

.calendar__add-btn {
  width: 42px;
  height: 46px;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  &:hover {
    cursor: pointer;
  }
}
.calendar__add-icon {
  pointer-events: none;
}
.calendar__popover {
  display: none;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
  border: 1px solid #e7e9ec;
  border-radius: 6px;
  position: absolute;
  top: 31px;
  right: 21px;
  background-color: white;
  z-index: 1;
}

.calendar__popover-list {
  list-style: none;
  padding: 15px 0;
  margin: 0;
  width: 100%;
}
.calendar__popover-list-item {
  padding: 0;
  margin: 0 0 5px 0;
  width: 100%;
  &:last-child {
    margin-bottom: 0;
  }
}
.calendar__popover-link {
  white-space: nowrap;
  display: flex;
  width: 100%;
  padding: 3px 25px 3px 15px;
  align-items: center;
  text-align: left;
  transition: background-color 0.2s ease-in-out;
  font-size: $text-size-body;
  &:hover {
    color: $color-text;
    background-color: $color-grey;
  }
}
.calendar__popover-icon {
  flex: 0 0 21px;
  width: 21px;
  display: block;
  margin-right: 10px;
}
.calendar__popover-icon--apple {
  height: 16px;
  background: url("./images/Apple.svg") no-repeat center center;
}

.calendar__popover-icon--yahoo {
  height: 19px;
  background: url("./images/Yahoo.svg") no-repeat center center;
}

.calendar__popover-icon--google {
  height: 16px;
  background: url("./images/Google.svg") no-repeat center center;
}

.calendar__popover-icon--outlook {
  height: 18px;
  background: url("./images/Outlook.svg") no-repeat center center;
}

.calendar--itemRow {
  &::before,
  &::after {
    clear: both;
    content: "";
    display: table;
    width: 100%;
  }
}

.calendar--dateCol,
.calendar--titleCol,
.calendar--CTACol {
  float: left;
}

.calendar--dateCol {
  line-height: 1;
  width: 15%;
  text-transform: capitalize;
}

.calendar--titleCol {
  width: 70%;
}

.calendar--CTACol {
  text-align: right;
  width: 15%;
}

.calendar--dateEmphasize {
  font-size: 30px;
}

.calendar--listItem {
  padding: $space 0;
  font-size: 16px;
  border-top: 1px solid $border-color;
  list-style: none;
  position: relative;

  &:last-child {
    border-bottom: 1px solid $border-color;
  }
}

.calendar--itemTitleRow {
  display: block;
  margin-bottom: $space;

  &::after {
    content: "";
    display: table;
    clear: both;
  }
}

.calendar--itemTitle {
  font-family: $font-family-bold !important;
  font-size: 16px;
}

.calendar--addIcon {
  width: 30px;
  height: 30px;
  float: left;
}

.calendar--clockIcon,
.calendar--locationIcon {
  width: 18px;
  height: 18px;
  vertical-align: middle;
  margin-right: 8px;
}

.calendar--verticalSpan {
  vertical-align: middle;
}

.calendar--infos {
  display: inline-block;
  font-size: inherit;
  font-family: $font-family-light !important;
}

.calendar--info.calendar--time {
  min-width: 160px;
}

.calendar--info {
  display: block;
  margin-bottom: $space / 4;
}

.calendar--detailContent {
  font-size: inherit;
  margin: $space 0 0 0;
  word-break: break-word;
  word-wrap: break-word;
}

.calendar--CTABtn {
  background-color: transparent;
  border: none;
  outline: none;
  padding: 0;
  position: relative;
  cursor: pointer;
}

.calendar--CTABtnSpan {
  position: absolute;
  color: white;
  left: 50%;
  bottom: 1px;
  transform: translateX(-50%);
  font-size: 14px;
  line-height: 1;
  font-family: $font-family-bold !important;
  letter-spacing: 2px;

  &:empty {
    width: 4px;
    height: 14px;

    &::before,
    &::after {
      content: "";
      display: inline-block;
      background-color: white;
      width: 100%;
      height: 100%;
      left: 0;
      bottom: 0;
      position: absolute;
    }

    &::after {
      left: 0;
      bottom: 0;
      transform: rotate(90deg);
    }
  }
}

.calendar--listFooter {
  color: $color-blue;
  text-align: right;
  font-size: 14px;
}

.calendar--calendarIcon {
  width: 45px;
  height: 45px;
}

.calendar--iconCol {
  position: absolute;
  padding-top: 16px;
}

.calendar--listItem--empty {
  .calendar--info {
    font-size: 28px;
    font-family: $font-family-bold !important;
  }

  .calendar--detailContent {
    font-size: 16px;
    font-family: $font-family-regular !important;
    padding-left: 60px;
    margin-top: 20px;
  }

  .calendar--infos {
    padding-left: 60px;
  }
}

@media screen and (min-width: 780px) {
  .calendar--headline {
    font-size: 40px;
    margin-bottom: $space-lg * 2;
  }

  .calendar--listItem {
    padding: $space-lg * 2 0;
    font-size: 19px;
  }

  .calendar--itemTitleRow {
    display: initial;

    &::after {
      content: none;
    }
  }

  .calendar--itemTitle {
    font-size: 25px;
  }

  .calendar--dateCol {
    font-size: 21px;
  }

  .calendar--dateEmphasize {
    font-size: 40px;
  }

  .calendar--infos {
    width: 80%;
    margin: $space-lg / 2 10% 0 10%;
  }

  .calendar--info {
    display: inline-block;

    & + & {
      margin-left: $space-lg * 2;
    }
  }

  .calendar--iconCol {
    position: absolute;
    padding-left: 3.5%;
    padding-top: 0;
  }

  .calendar--addIcon {
    width: 45px;
    height: 45px;
  }

  .calendar--dateCol {
    width: 10%;
    position: absolute;
  }

  .calendar--CTACol {
    width: 10%;
    position: absolute;
    right: 0;
  }

  .calendar--titleCol {
    width: 80%;
    margin-left: 10%;
  }

  .calendar--detailContent {
    margin: $space-lg * 2 10% 0 10%;
    width: 80%;
  }

  .calendar--CTABtnSpan {
    font-size: 18px;
    bottom: 3px;

    &:empty {
      width: 4px;
      height: 18px;
    }
  }

  .calendar--listFooter {
    font-size: 21px;
  }

  .calendar--listItem--empty {
    .calendar--detailContent {
      padding-left: 0;
    }

    .calendar--infos {
      padding-left: 0;
    }
  }
}

[data-theme="calendar-theme-blue"] {
  .calendar--dateCol {
    color: $color-blue;
  }

  .calendar--addIcon,
  .calendar__add-icon-background {
    fill: $color-blue;
  }

  .calendar--listFooter {
    color: $color-blue;
  }

  .calendar--calendarIcon {
    fill: $color-blue;
  }
}

[data-theme="calendar-theme-aubergine"] {
  .calendar--dateCol {
    color: $color-aubergine;
  }

  .calendar--addIcon,
  .calendar__add-icon-background {
    fill: $color-aubergine;
  }

  .calendar--listFooter {
    color: $color-aubergine;
  }

  .calendar--calendarIcon {
    fill: $color-aubergine;
  }
}
